import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import Parser from 'html-react-parser';

/** Material UI  */
import { makeStyles } from '@material-ui/core/styles';
import { createMuiTheme } from '@material-ui/core/styles';
import { ThemeProvider } from '@material-ui/styles';

/** Material Component */
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import Chip from '@material-ui/core/Chip';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';

import InputAdornment from '@material-ui/core/InputAdornment';
import AccountCircle from '@material-ui/icons/AccountCircle';

/** Icon */
import VisibilityIcon from '@material-ui/icons/Visibility';
import DoneIcon from '@material-ui/icons/Done';
import FavoriteIcon from '@material-ui/icons/Favorite';
import SettingsIcon from '@material-ui/icons/Settings';
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos';
import CodeIcon from '@material-ui/icons/Code';
import Avatar from '@material-ui/core/Avatar';
import ContactPhoneIcon from '@material-ui/icons/ContactPhone';
import PeopleIcon from '@material-ui/icons/People';
import CloudUploadIcon from '@material-ui/icons/CloudUpload';
import PublicIcon from '@material-ui/icons/Public';
import LocalShippingIcon from '@material-ui/icons/LocalShipping';
import InsertDriveFileRoundedIcon from '@material-ui/icons/InsertDriveFileRounded';
import DraftsIcon from '@material-ui/icons/Drafts';
import PhoneIcon from '@material-ui/icons/Phone';
import BusinessIcon from '@material-ui/icons/Business';
import AttachFileIcon from '@material-ui/icons/AttachFile';

/** External React Component */
import { toast } from 'react-toastify';

const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#428bca',
    },
    secondary: {
      main: '#d9534f',
    },
  },
});

const useStyles = makeStyles((theme) => ({
  layout: {
    textAlign: 'text-center',
    color: '#428bca',
  },
  cardGrid: {
    paddingLeft: '1em',
    paddingRight: '1em',
  },
  input: {
    display: 'none',
  },
}));

export const JobSubmit = () => {
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);
  const [pdf, setPdf] = React.useState(null);
  const [url, setURL] = React.useState(null);

  function readURL(input) {
    if (input.files && input.files[0]) {
      const reader = new FileReader();

      reader.onload = function (e) {
        setURL(e.target.result);
      };
      reader.readAsDataURL(input.files[0]);
      /* Convert to base64 string */
    }
  }

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
    toast.success('CV submit !!!');
  };

  return (
    <React.Fragment>
      <ThemeProvider theme={theme}>
        <Grid container className={classes.layout}>
          <Button
            variant="contained"
            color="primary"
            endIcon={<InsertDriveFileRoundedIcon />}
            onClick={handleClickOpen}
          >
            Apply
          </Button>

          {/** Dialog */}
          <Dialog
            open={open}
            onClose={handleClose}
            aria-labelledby="form-dialog-title"
          >
            <DialogTitle id="form-dialog-title">
              <span
                style={{
                  fontFamily: 'Quicksand',
                  fontSize: '1em',
                  fontWeight: '650',
                }}
              >
                {' '}
                Applicant Information{' '}
              </span>
            </DialogTitle>
            <DialogContent>
              <DialogContentText
                style={{
                  fontFamily: 'Quicksand',
                  fontSize: '1em',
                  fontWeight: '650',
                }}
              >
                To apply to this job, please enter your email address and attach
                your ciriculum vitae here. We will reply shorty.
              </DialogContentText>
              <Grid container>
                <Grid item xs={6} style={{ paddingRight: '0.5em' }}>
                  <TextField
                    autoFocus
                    margin="dense"
                    id="name"
                    label={
                      <span
                        style={{
                          fontFamily: 'Quicksand',
                          fontSize: '1em',
                          fontWeight: '650',
                        }}
                      >
                        Email Address{' '}
                      </span>
                    }
                    type="email"
                    fullWidth
                    InputProps={{
                      startAdornment: (
                        <InputAdornment position="start">
                          <DraftsIcon color="primary" />
                        </InputAdornment>
                      ),
                    }}
                  />

                  <TextField
                    autoFocus
                    margin="dense"
                    id="phone"
                    label={
                      <span
                        style={{
                          fontFamily: 'Quicksand',
                          fontSize: '1em',
                          fontWeight: '650',
                        }}
                      >
                        Phone Number{' '}
                      </span>
                    }
                    type="phone"
                    fullWidth
                    InputProps={{
                      startAdornment: (
                        <InputAdornment position="start">
                          <PhoneIcon color="primary" />
                        </InputAdornment>
                      ),
                    }}
                  />
                </Grid>
                <Grid item xs={6} style={{ paddingLeft: '0.5em' }}>
                  <TextField
                    autoFocus
                    margin="dense"
                    id="name"
                    label={
                      <span
                        style={{
                          fontFamily: 'Quicksand',
                          fontSize: '1em',
                          fontWeight: '650',
                        }}
                      >
                        Your name{' '}
                      </span>
                    }
                    type="name"
                    fullWidth
                    InputProps={{
                      startAdornment: (
                        <InputAdornment position="start">
                          <AccountCircle color="primary" />
                        </InputAdornment>
                      ),
                    }}
                  />

                  <TextField
                    autoFocus
                    margin="dense"
                    id="company"
                    label={
                      <span
                        style={{
                          fontFamily: 'Quicksand',
                          fontSize: '1em',
                          fontWeight: '650',
                        }}
                      >
                        Your Company{' '}
                      </span>
                    }
                    type="company"
                    fullWidth
                    InputProps={{
                      startAdornment: (
                        <InputAdornment position="start">
                          <BusinessIcon color="primary" />
                        </InputAdornment>
                      ),
                    }}
                  />
                </Grid>
                <Grid item xs={12}>
                  <input
                    id="icon-button-file"
                    accept="application/pdf,application/vnd.ms-excel"
                    type="file"
                    name="file"
                    className={classes.input}
                    onChange={(event) => {
                      readURL(event.target);
                      setPdf(event.target.files[0]);
                    }}
                  />
                  <label htmlFor="icon-button-file">
                    <Button
                      variant="text"
                      color="primary"
                      aria-label="upload picture"
                      component="span"
                      startIcon={<AttachFileIcon />}
                    >
                      Attach you CV
                    </Button>
                  </label>
                  (only pdf accepted)
                  <br />
                  {url && <span style={{ color: '#428bca' }}>{pdf.name}</span>}
                </Grid>
              </Grid>
            </DialogContent>
            <DialogActions>
              <Button onClick={handleClose} color="primary">
                Cancel
              </Button>
              <Button onClick={handleClose} color="primary">
                Submit
              </Button>
            </DialogActions>
          </Dialog>
        </Grid>
      </ThemeProvider>
    </React.Fragment>
  );
};

const mapStateToProps = (state) => ({
  loading: state.job.loading,
  job: state.job.job,
});

export default connect(mapStateToProps)(JobSubmit);
