import React, { useState, useEffect } from 'react';
import { connect } from 'react-redux';
import { updateForm } from '../../../action/job';

/** Material UI  */
import { makeStyles } from '@material-ui/core/styles';
import { createMuiTheme } from '@material-ui/core/styles';
import { ThemeProvider } from '@material-ui/styles';

/** Material Component */
import Grid from '@material-ui/core/Grid';
import InputAdornment from '@material-ui/core/InputAdornment';
import TextField from '@material-ui/core/TextField';
import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';
import Avatar from '@material-ui/core/Avatar';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import FormGroup from '@material-ui/core/FormGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormLabel from '@material-ui/core/FormLabel';
import Checkbox from '@material-ui/core/Checkbox';
import FormHelperText from '@material-ui/core/FormHelperText';
import Autocomplete from '@material-ui/lab/Autocomplete';

/** Material Icon */
import WorkIcon from '@material-ui/icons/Work';
import WorkOutlineIcon from '@material-ui/icons/WorkOutline';
import InsertDriveFileOutlinedIcon from '@material-ui/icons/InsertDriveFileOutlined';
import LocalAtmIcon from '@material-ui/icons/LocalAtm';
import MonetizationOnIcon from '@material-ui/icons/MonetizationOn';
import DescriptionOutlinedIcon from '@material-ui/icons/DescriptionOutlined';
import AccessTimeIcon from '@material-ui/icons/AccessTime';

import { languages } from './resource/stack';

/** Draft JS  */
import CustomToolbarEditor from '../Editor';

const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#428bca',
    },
    secondary: {
      main: '#111',
    },
  },
});

const useStyles = makeStyles((theme) => ({
  avatar: {
    margin: 'auto',
    background: '#428bca',
    color: '#fff',
  },
  title: {
    textAlign: 'center',
    color: '#428bca',
    fontSize: '1.25em',
  },
  FormControl: {
    marginTop: '1em',
    marginBottom: '1em',
  },
  label: {
    fontFamily: 'Quicksand',
    fontWeight: '700',
    fontSize: '1.35 em',
  },
  labelSelect: {
    fontFamily: 'Quicksand',
    fontWeight: '700',
    fontSize: '1.15em',
  },
  checkForm: {
    background: '#cccccc',
    color: '#111',
    fontFamily: 'Quicksand',
    paddingLeft: '0.5em',
    paddingRight: '0.5em',
    paddingTop: '0.5em',
    paddingBottom: '0.15em',
  },
  checkboxLabel: {
    color: '#111',
    fontFamily: 'Quicksand',
    fontWeight: '600',
  },
  textfield: {
    fontFamily: 'Quicksand',
  },
  span: {
    fontFamily: 'Quicksand',
    fontWeight: '600',
    fontSize: '1em',
  },
  helper: {
    fontFamily: 'Quicksand',
    fontWeight: '700',
    fontSize: '1em',
  },
}));

export const Step4 = ({ form, updateForm }) => {
  const classes = useStyles();
  const [editorState, setEditorState] = useState(
    'Please type job description here...'
  );
  const [contract, setContract] = useState({
    Temporary: false,
    Commission: false,
    NewGrad: false,
    Internship: false,
    Contract: false,
    Permanent: false,
  });

  const [salary, setSalary] = useState({
    minimum: null,
    maximum: null,
    currency: '',
    paymentCircle: '',
  });

  const handleChangeContract = (event) => {
    const contractHolder = { ...contract };
    contractHolder[event.target.name] = event.target.checked;
    setContract(contractHolder);
  };

  const handleChangeSalary = (event) => {
    const salarytHolder = { ...salary };
    salarytHolder[event.target.name] = event.target.value;
    setSalary(salarytHolder);
  };

  useEffect(() => {
    updateForm('contract', contract);
  }, [contract]);

  useEffect(() => {
    updateForm('salary', salary);
  }, [salary]);

  const { jobTitle, companyName, location } = form;

  return (
    <React.Fragment>
      <ThemeProvider theme={theme}>
        <Grid container>
          <Grid item xs={1} sm={2} md={3}></Grid>
          <Grid item xs={10} sm={8} md={6}>
            <Avatar
              className={classes.avatar}
              style={{ width: 50, height: 50 }}
            >
              <WorkIcon style={{ fontSize: 30 }} />
            </Avatar>
            <div className={classes.title}>
              <span>Job Details </span>
            </div>

            {/**
             * Check
             * form
             * */}
            <div className={classes.checkForm}>
              <ul
                style={{
                  listStyleType: 'none',
                }}
              >
                <li>Job title: {jobTitle}</li>
                <li>Company name: {companyName}</li>
                <li>Location: {location}</li>
              </ul>
            </div>

            {/**
             * Type of employment
             */}

            <FormControl className={classes.FormControl} fullWidth>
              <InputLabel
                htmlFor="EmploymentType"
                className={classes.labelSelect}
              >
                Type of employment
                <span style={{ color: '#FF0000	' }}>*</span>
              </InputLabel>

              <Select
                id="EmploymentType"
                defaultValue=""
                onChange={(event) =>
                  updateForm('employmentType', event.target.value)
                }
                startAdornment={
                  <InputAdornment position="start">
                    <WorkOutlineIcon style={{ color: '#428bca' }} />
                  </InputAdornment>
                }
              >
                <MenuItem value={'Fulltime'}>
                  {'Fulltime ( >= 40 hours )'}
                </MenuItem>
                <MenuItem value={'Partime'}>
                  {'Partime ( < 40 hours )'}
                </MenuItem>
              </Select>
            </FormControl>

            {/**
             * Contract type
             */}
            <FormControl className={classes.FormControl} fullWidth>
              <FormLabel className={classes.labelSelect} component="legend">
                <InsertDriveFileOutlinedIcon color="primary" />
                Contract type
              </FormLabel>
              <FormGroup>
                <Grid container>
                  <Grid item xs={6} sm={4} md={4}>
                    {/**
                     * Temporary
                     * Commission
                     * Contract
                     */}
                    <FormControlLabel
                      control={
                        <Checkbox
                          onChange={(event) => {
                            handleChangeContract(event);
                          }}
                          name="Temporary"
                          color="primary"
                        />
                      }
                      label={
                        <span className={classes.checkboxLabel}>Temporary</span>
                      }
                    />
                    <FormControlLabel
                      control={
                        <Checkbox
                          onChange={(event) => {
                            handleChangeContract(event);
                          }}
                          name="Commission"
                          color="primary"
                        />
                      }
                      label={
                        <span className={classes.checkboxLabel}>
                          Commission
                        </span>
                      }
                    />

                    {/**
                     * End
                     */}
                  </Grid>
                  <Grid item xs={6} sm={4} md={4}>
                    {/**
                     * NewGrad
                     * Internship
                     * Permanent
                     */}
                    <FormControlLabel
                      control={
                        <Checkbox
                          onChange={(event) => {
                            handleChangeContract(event);
                          }}
                          name="NewGrad"
                          color="primary"
                        />
                      }
                      label={
                        <span className={classes.checkboxLabel}>NewGrad</span>
                      }
                    />
                    <FormControlLabel
                      control={
                        <Checkbox
                          onChange={(event) => {
                            handleChangeContract(event);
                          }}
                          name="Internship"
                          color="primary"
                        />
                      }
                      label={
                        <span className={classes.checkboxLabel}>
                          Internship
                        </span>
                      }
                    />

                    {/**
                     * End
                     */}
                  </Grid>
                  <Grid item xs={6} sm={4} md={4}>
                    <FormControlLabel
                      control={
                        <Checkbox
                          onChange={(event) => {
                            handleChangeContract(event);
                          }}
                          name="Contract"
                          color="primary"
                        />
                      }
                      label={
                        <span className={classes.checkboxLabel}>Contract</span>
                      }
                    />
                    <FormControlLabel
                      control={
                        <Checkbox
                          onChange={(event) => {
                            handleChangeContract(event);
                          }}
                          name="Permanent"
                          color="primary"
                        />
                      }
                      label={
                        <span className={classes.checkboxLabel}>Permanent</span>
                      }
                    />
                  </Grid>
                </Grid>
              </FormGroup>
            </FormControl>
            {/**
             * End of Contract type
             */}

            {/**
             * Salary
             * */}
            <InputLabel htmlFor="Salary" className={classes.label}>
              <LocalAtmIcon /> Salary
              <span style={{ color: '#FF0000	' }}>*</span>
              <br></br>
              <span
                style={{
                  fontFamily: 'Quicksand',
                  fontWeight: '700',
                  fontSize: '0.85em',
                }}
              >
                Compensation
              </span>
            </InputLabel>
            <Grid container>
              <Grid item xs={6} sm={3} md={3}>
                <FormControl className={classes.FormControl}>
                  <Grid container alignItems="flex-end">
                    <Grid item xs={2}>
                      <MonetizationOnIcon color="primary" />
                    </Grid>
                    <Grid
                      item
                      xs={10}
                      style={{
                        paddingLeft: '0.25em',
                      }}
                    >
                      <TextField
                        type="number"
                        label={
                          <span
                            style={{
                              fontFamily: 'Quicksand',
                              fontWeight: '600',
                              fontSize: '1em',
                            }}
                          >
                            Minimum
                          </span>
                        }
                        color="primary"
                        className={classes.textfield}
                        name="minimum"
                        onChange={(event) => {
                          handleChangeSalary(event);
                        }}
                      />
                    </Grid>
                  </Grid>
                </FormControl>
              </Grid>
              <Grid item xs={6} sm={3} md={3}>
                <FormControl className={classes.FormControl}>
                  <Grid container alignItems="flex-end">
                    <Grid item xs={2}>
                      <MonetizationOnIcon color="primary" />
                    </Grid>
                    <Grid
                      item
                      xs={10}
                      style={{
                        paddingLeft: '0.25em',
                      }}
                    >
                      <TextField
                        type="number"
                        label={
                          <span
                            style={{
                              fontFamily: 'Quicksand',
                              fontWeight: '600',
                              fontSize: '1em',
                              paddingLeft: '0.1em',
                            }}
                          >
                            Maximal
                          </span>
                        }
                        color="primary"
                        className={classes.textfield}
                        name="maximum"
                        onChange={(event) => {
                          handleChangeSalary(event);
                        }}
                      />
                    </Grid>
                  </Grid>
                </FormControl>
              </Grid>
              <Grid item xs={6} sm={2} md={2}>
                <Grid
                  container
                  alignItems="flex-end"
                  style={{ marginTop: '1em' }}
                >
                  <FormControl fullWidth className={classes.FormControl}>
                    <Select
                      id="Currency"
                      color="primary"
                      defaultValue=""
                      name="currency"
                      onChange={(event) => {
                        handleChangeSalary(event);
                      }}
                    >
                      <MenuItem style={{ color: '#428bca' }} value={'Dollar'}>
                        {' '}
                        {<i className="fa">&#xf155; </i>}
                      </MenuItem>
                      <MenuItem style={{ color: '#428bca' }} value={'Euro'}>
                        {' '}
                        {<i className="fa">&#xf153; </i>}
                      </MenuItem>
                      <MenuItem style={{ color: '#428bca' }} value={'Hryvnia'}>
                        {' '}
                        {<i className="fa">&#xf6f2; </i>}
                      </MenuItem>
                      <MenuItem style={{ color: '#428bca' }} value={'Lira'}>
                        {' '}
                        {<i className="fa">&#xf195; </i>}
                      </MenuItem>
                      <MenuItem style={{ color: '#428bca' }} value={'UK Pound'}>
                        {' '}
                        {<i className="fa">&#xf154; </i>}
                      </MenuItem>
                      <MenuItem style={{ color: '#428bca' }} value={'Rupee'}>
                        {' '}
                        {<i className="fa">&#xf156; </i>}
                      </MenuItem>
                      <MenuItem style={{ color: '#428bca' }} value={'Won'}>
                        {' '}
                        {<i className="fa">&#xf159; </i>}
                      </MenuItem>
                      <MenuItem style={{ color: '#428bca' }} value={'Yen'}>
                        {' '}
                        {<i className="fa">&#xf157; </i>}
                      </MenuItem>
                    </Select>
                    <FormHelperText id="money" className={classes.helper}>
                      Currency
                    </FormHelperText>
                  </FormControl>
                </Grid>
              </Grid>

              <Grid
                item
                xs={6}
                sm={4}
                md={4}
                style={{
                  paddingLeft: '0.5em',
                }}
              >
                <Grid
                  container
                  alignItems="flex-end"
                  style={{ marginTop: '1em' }}
                >
                  <FormControl fullWidth className={classes.FormControl}>
                    <Select
                      id="Pertime"
                      defaultValue=""
                      color="primary"
                      name="paymentCircle"
                      onChange={(event) => {
                        handleChangeSalary(event);
                      }}
                    >
                      <MenuItem
                        style={{ color: '#428bca', fontFamily: 'Quicksand' }}
                        value={'hour'}
                      >
                        Per hour
                      </MenuItem>
                      <MenuItem
                        style={{ color: '#428bca', fontFamily: 'Quicksand' }}
                        value={'day'}
                      >
                        Per day
                      </MenuItem>
                      <MenuItem
                        style={{ color: '#428bca', fontFamily: 'Quicksand' }}
                        value={'week'}
                      >
                        Per week
                      </MenuItem>
                      <MenuItem
                        style={{ color: '#428bca', fontFamily: 'Quicksand' }}
                        value={'month'}
                      >
                        Per month
                      </MenuItem>
                      <MenuItem
                        style={{ color: '#428bca', fontFamily: 'Quicksand' }}
                        value={'year'}
                      >
                        Per year
                      </MenuItem>
                    </Select>
                    <FormHelperText id="money" className={classes.helper}>
                      <AccessTimeIcon color="primary" fontSize="small" />{' '}
                      Payment Circle
                    </FormHelperText>
                  </FormControl>
                </Grid>
              </Grid>
            </Grid>
            {/** End of Salary */}

            {/**
             * Job Description
             */}
            <Grid
              style={{
                marginTop: '1em',
              }}
              container
            >
              <div className={classes.title}>
                <span>
                  <DescriptionOutlinedIcon color="primary" /> Job Description{' '}
                </span>
              </div>
              <span className={classes.span}>
                Describe the responsibilities of this job required work
                experience , skills or education
              </span>
              <div
                style={{
                  boxShadow: '2px 2px 5px 2px rgba(0,0,0,0.35)',
                  border: '1px #428bca solid',
                  padding: '0.75em',
                  height: 'fit-content',
                }}
              >
                <CustomToolbarEditor content="description" />
              </div>
              {/**
               * Tech Stack
               */}
              <FormControl fullWidth className={classes.FormControl}>
                <Autocomplete
                  multiple
                  id="stack"
                  options={languages}
                  getOptionLabel={(option) => option.name}
                  filterSelectedOptions
                  color="primary"
                  renderInput={(params) => (
                    <TextField
                      {...params}
                      variant="outlined"
                      label="Tech Stack"
                      placeholder="Click to choose tech stack..."
                      color="primary"
                    />
                  )}
                  onChange={(event, value) => {
                    updateForm(
                      'stack',
                      value.map((item) => item.name)
                    );
                  }}
                />
              </FormControl>
            </Grid>
          </Grid>
          {/** End Job Description */}

          <Grid item xs={1} sm={2} md={3}></Grid>
        </Grid>
      </ThemeProvider>
    </React.Fragment>
  );
};

const mapStateToProps = (state) => ({
  form: state.job.form,
});

export default connect(mapStateToProps, { updateForm })(Step4);
