import React from 'react';
import { connect } from 'react-redux';
import FileImage from '../../action/utils/getImage';

/** Material UI  */
import { makeStyles } from '@material-ui/core/styles';
import { createMuiTheme } from '@material-ui/core/styles';
import { ThemeProvider } from '@material-ui/styles';
import Grid from '@material-ui/core/Grid';

/** Icon */
import BusinessIcon from '@material-ui/icons/Business';
import LocationOnIcon from '@material-ui/icons/LocationOn';
import WorkIcon from '@material-ui/icons/Work';

import { setCurrent, clearCurrent } from '../../action/job';

const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#428bca',
    },
    secondary: {
      main: '#111',
    },
    success: {
      main: '#5cb85c',
    },
    danger: {
      main: '#d9534f',
    },
  },
});

const useStyles = makeStyles((theme) => ({
  item: {
    padding: '0.5em',
    '&:hover': {
      background: 'rgba(0, 136, 240, 0.1)',
      borderLeft: '2px solid #428bca',
      cursor: 'pointer',
    },
  },
}));

const JobItem = ({ item, setCurrent }) => {
  const classes = useStyles();

  const { jobTitle, image, company, location, userInfo, _id } = item;

  return (
    <ThemeProvider theme={theme}>
      <div
        style={{
          display: 'inline-block',
          textAlign: 'center',
          borderRight: ' #428bca 2px solid',
        }}
      >
        <Grid
          container
          className={classes.item}
          onClick={() => {
            setCurrent(_id);
          }}
        >
          <Grid item xs={2}>
            {image && FileImage(image)}
          </Grid>

          <Grid item xs={10}>
            <span
              style={{
                fontWeight: '650',
                fontSize: '0.75em',
              }}
            >
              <WorkIcon fontSize="small" style={{ color: '#0275d8' }} />{' '}
              {jobTitle}
              <br />
              <BusinessIcon
                fontSize="small"
                style={{ color: '#f0ad4e' }}
              />{' '}
              {company}
              <br />
              <LocationOnIcon
                fontSize="small"
                style={{ color: '#d9534f' }}
              />{' '}
              {location}
            </span>
          </Grid>
        </Grid>
      </div>
    </ThemeProvider>
  );
};

export default connect(null, { setCurrent, clearCurrent })(JobItem);
