import React from 'react';

/** Spring */
import { Spring } from 'react-spring/renderprops';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import { createMuiTheme } from '@material-ui/core/styles';
import { MuiThemeProvider } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import VisibilitySensor from 'react-visibility-sensor';
import Button from '@material-ui/core/Button';
import ButtonGroup from '@material-ui/core/ButtonGroup';

/** React Responsive */
import { useMediaQuery } from 'react-responsive';

/** Image */
import Img1 from '../img/people1.jpg';
import Img2 from '../img/people2.jpg';

import FacebookIcon from '@material-ui/icons/Facebook';
import TwitterIcon from '@material-ui/icons/Twitter';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import IconButton from '@material-ui/core/IconButton';

const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#428bca',
    },
    secondary: {
      main: '#111',
    },
  },
});

const useStyles = makeStyles((theme) => ({
  title: {
    fontFamily: 'Quicksand',
    fontWeight: '600',
    fontSize: '1.65em',
    color: '#428bca',
    margin: 'auto',
  },
  titleSmall: {
    fontFamily: 'Quicksand',
    fontWeight: '600',
    fontSize: '0.85em',
    color: '#428bca',
    margin: 'auto',
  },
  titleHeadline: {
    fontFamily: "'Poiret One', cursive",
    fontWeight: '100',
    fontSize: '50px',
    color: '#428bca',
    margin: 'auto',
    marginTop: '3em',
    marginBottom: '2em',
  },
  titleHeadlineSmall: {
    fontFamily: "'Poiret One', cursive",
    fontWeight: '100',
    fontSize: '20px',
    color: '#428bca',
    margin: 'auto',
    marginTop: '2em',
    marginBottom: '1em',
  },
  divider: {
    height: '3px',
    width: '60px',
    backgroundColor: '#428bca',
    marginTop: '2em',
    marginBottom: '1em',
    margin: 'auto',
  },
  dividerSmall: {
    height: '3px',
    width: '60px',
    backgroundColor: '#428bca',
    marginTop: '2em',
    marginBottom: '1em',
    margin: 'auto',
  },
  testinomial: {
    padding: '1em',
    background: '#fff',
    color: '#979797',
    fontFamily: 'Quicksand',
    transition: 'background .2s linear, color .2s linear',
    '&:hover': {
      background: '#428bca',
      color: '#fff',
      '& h2': {
        color: '#fff',
      },
      '& h4': {
        color: '#fff',
      },
      '& svg': {
        color: '#fff',
      },
    },
  },
  testinomialName: {
    color: '#111',
    fontFamily: 'Poiret One',
  },
  testinomialJob: {
    color: '#428bca',
    fontFamily: 'Quicksand',
    fontSize: '1em',
  },
  img: {
    maxWidth: '100%',
    maxHeight: '100%',
    display: 'block',
    transform: 'scale(1)',
    transition: 'all 0.5s',
    '&:hover': {
      transform: 'scale(1.25)',
      transition: 'all 0.5s',
    },
  },
}));

const Part5 = () => {
  const classes = useStyles();
  const isSmall = useMediaQuery({ minWidth: 0, maxWidth: 599 });

  return (
    <MuiThemeProvider theme={theme}>
      <Grid
        container
        style={{ padding: '1em', overflowX: 'hidden', overflowY: 'hidden' }}
        alignItems="center"
      >
        <Grid item xs={12}>
          <div data-aos="fade-down">
            <div style={{ textAlign: 'center' }}>
              {isSmall && [
                <div className={classes.dividerSmall}></div>,
                <br />,
                <span className={classes.titleSmall}>Testinomial</span>,
                <br />,

                <span className={classes.titleHeadlineSmall}>
                  What is users thinking about us ?
                </span>,
              ]}
              {!isSmall && [
                <div className={classes.divider}></div>,
                <br />,
                <span className={classes.title}>Testinomial</span>,
                <br />,
                <span className={classes.titleHeadline}>
                  What is users thinking about us ?
                </span>,
              ]}
            </div>
          </div>
        </Grid>
        <Grid container style={{ padding: '1.5em' }}>
          {/* Testimonial 1*/}
          <Grid xs={2} sm={4} md={2}>
            <div data-aos="fade-right">
              <div
                style={{
                  overflowX: 'hidden',
                  overflowY: 'hidden',
                }}
              >
                <img src={Img1} className={classes.img} />
              </div>
            </div>
          </Grid>
          <Grid xs={10} sm={8} md={4} className={classes.testinomial}>
            <div data-aos="fade-right">
              <h2 className={classes.testinomialName}>Jordan Peterson</h2>
              <h4 className={classes.testinomialJob}>CO-FOUNDER</h4>
              {isSmall && (
                <span style={{ fontSize: '0.85em' }}>
                  Simple site with powerful functionality, help me found a job
                  right after graduation, keep using devmeet ever since.
                </span>
              )}
              {!isSmall && (
                <span style={{ fontSize: '1.25em' }}>
                  Simple site with powerful functionality, help me found a job
                  right after graduation, keep using devmeet ever since.
                </span>
              )}
              <br />
              <ButtonGroup
                variant="text"
                color="primary"
                aria-label="text primary button group"
              >
                <IconButton>
                  <FacebookIcon color="primary" />
                </IconButton>
                <IconButton>
                  <LinkedInIcon color="primary" />
                </IconButton>
                <IconButton>
                  <TwitterIcon color="primary" />
                </IconButton>
              </ButtonGroup>
            </div>
          </Grid>

          {/* Testimonial 2*/}

          <Grid xs={2} sm={4} md={2}>
            <div data-aos="fade-left">
              <div
                style={{
                  overflowX: 'hidden',
                  overflowY: 'hidden',
                }}
              >
                {' '}
                <img src={Img2} width="100%" className={classes.img} />
              </div>
            </div>
          </Grid>
          <Grid xs={10} sm={8} md={4} className={classes.testinomial}>
            <div data-aos="fade-left">
              <h2 className={classes.testinomialName}>Joe Rogan</h2>
              <h4 className={classes.testinomialJob}>CO-FOUNDER</h4>
              {isSmall && (
                <span style={{ fontSize: '0.85em' }}>
                  When i first open my startup, short of good developers is one
                  of the biggest problems of your business, devmeet make it
                  easier for us to find the best one and FAST !
                </span>
              )}
              {!isSmall && (
                <span style={{ fontSize: '1.25em' }}>
                  When i first open my startup, short of good developers is one
                  of the biggest problems of your business, devmeet make it
                  easier for us to find the best one and FAST !
                </span>
              )}
              <br />
              <ButtonGroup
                variant="text"
                color="primary"
                aria-label="text primary button group"
              >
                <IconButton>
                  <FacebookIcon color="primary" />
                </IconButton>
                <IconButton>
                  <LinkedInIcon color="primary" />
                </IconButton>
                <IconButton>
                  <TwitterIcon color="primary" />
                </IconButton>
              </ButtonGroup>
            </div>
          </Grid>
        </Grid>
      </Grid>
    </MuiThemeProvider>
  );
};

export default Part5;
