import { GET_CODE, ADD_CODE, CODE_ERR, DEL_CODE } from './type';
import { setAlert } from '../action/alert';
import api from '../reducers/utils/baseURL';

// Get all code snippet of me
export const getCodeOfMe = () => async (dispatch) => {
  try {
    const my_id = JSON.parse(localStorage.getItem('user'))._id;
    const res = await api.get(`/code/${my_id}`);

    dispatch({
      type: GET_CODE,
      payload: res.data,
    });
  } catch (error) {
    dispatch({
      type: CODE_ERR,
      payload: {
        msg: error.response.statusText,
        status: error.response.status,
      },
    });
  }
};

// Get all code snippet
export const getCode = (user_id) => async (dispatch) => {
  try {
    const res = await api.get(`/code/${user_id}`);

    dispatch({
      type: GET_CODE,
      payload: res.data.code,
    });
  } catch (error) {
    dispatch({
      type: CODE_ERR,
      payload: {
        msg: error.response.statusText,
        status: error.response.status,
      },
    });
  }
};

// Add new code Snippet
export const addCode = (formData) => async (dispatch) => {
  try {
    const body = JSON.stringify(formData);
    const config = {
      headers: {
        'Content-Type': 'application/json',
      },
    };

    const res = await api.post('/code', body, config);

    dispatch(setAlert('New code snippet added !', 'success'));

    dispatch({
      type: ADD_CODE,
      payload: res.data,
    });
  } catch (error) {
    dispatch({
      type: CODE_ERR,
      payload: {
        msg: error.response.statusText,
        status: error.response.status,
      },
    });
  }
};

// Delete Code Snippet
export const delCode = (code_id) => async (dispatch) => {
  try {
    const my_id = JSON.parse(localStorage.getItem('user'))._id;

    const res = await api.delete(`/code/${my_id}/${code_id}`);

    dispatch(setAlert(' Code snippet deleted !', 'danger'));

    dispatch({
      type: DEL_CODE,
      payload: res.data,
    });
  } catch (error) {
    dispatch({
      type: CODE_ERR,
      payload: {
        msg: error.response.statusText,
        status: error.response.status,
      },
    });
  }
};
