import React from 'react';
import { useLocation } from 'react-router-dom';
import { useHistory } from 'react-router-dom';
import { withRouter } from 'react-router-dom';

import { makeStyles } from '@material-ui/core/styles';
import { createMuiTheme } from '@material-ui/core/styles';
import { ThemeProvider } from '@material-ui/styles';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormLabel from '@material-ui/core/FormLabel';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import Switch from '@material-ui/core/Switch';

import HomeIcon from '@material-ui/icons/Home';
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos';
import SpeedDial from '@material-ui/lab/SpeedDial';
import SpeedDialAction from '@material-ui/lab/SpeedDialAction';
import DirectionsIcon from '@material-ui/icons/Directions';

const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#0275d8',
    },
    secondary: {
      main: '#292b2c',
    },
  },
});

const useStyles = makeStyles((theme) => ({
  root: {
    transform: 'translateZ(0px)',
    flexGrow: 1,
  },
  exampleWrapper: {
    position: 'relative',
    marginTop: theme.spacing(3),
    height: 380,
  },
  radioGroup: {
    margin: theme.spacing(1, 0),
  },
  speedDial: {
    position: 'fixed',
    bottom: 0,
    right: 0,
    '&.MuiSpeedDial-directionUp, &.MuiSpeedDial-directionLeft': {
      bottom: theme.spacing(2),
      right: theme.spacing(2),
    },
    '&.MuiSpeedDial-directionDown, &.MuiSpeedDial-directionRight': {
      top: theme.spacing(2),
      left: theme.spacing(2),
    },
  },
}));

const actions = [
  { icon: <HomeIcon color="secondary" />, name: 'Home' },
  { icon: <ArrowBackIosIcon color="secondary" />, name: 'Back' },
  { icon: <ArrowForwardIosIcon color="secondary" />, name: 'Forward' },
];

const SpeedDials = () => {
  const history = useHistory();
  const classes = useStyles();
  const [direction, setDirection] = React.useState('left');
  const [open, setOpen] = React.useState(false);
  const [hidden, setHidden] = React.useState(false);

  const handleDirectionChange = (event) => {
    setDirection(event.target.value);
  };

  const handleHiddenChange = (event) => {
    setHidden(event.target.checked);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const handleNavigate = () => {};

  const handleOpen = () => {
    setOpen(true);
  };

  let location = useLocation();
  switch (location.pathname) {
    case '/':
    case '/login':
    case '/register':
      return <div>{false}</div>;
  }

  return (
    <ThemeProvider theme={theme}>
      <SpeedDial
        ariaLabel="SpeedDial example"
        className={classes.speedDial}
        hidden={hidden}
        icon={<DirectionsIcon />}
        onClose={handleClose}
        onOpen={handleOpen}
        open={open}
        direction={direction}
        color="primary"
      >
        <SpeedDialAction
          color="secondary"
          key="Forward"
          icon={<ArrowForwardIosIcon style={{ color: '#0275d8' }} />}
          tooltipTitle="Forward"
          onClick={() => {
            history.goForward();
          }}
        />

        <SpeedDialAction
          color="secondary"
          key="Home"
          icon={<HomeIcon style={{ color: '#0275d8' }} />}
          tooltipTitle="Home"
          onClick={() => {
            history.push('/dashboard');
          }}
        />

        <SpeedDialAction
          color="secondary"
          key="Back"
          icon={<ArrowBackIosIcon style={{ color: '#0275d8' }} />}
          tooltipTitle="Back"
          onClick={() => {
            history.goBack();
          }}
        />
      </SpeedDial>
    </ThemeProvider>
  );
};

export default SpeedDials;
