import React, { Fragment } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import Spinner from '../../layout/Spinner';

/** Material UI */
import { makeStyles } from '@material-ui/core/styles';
import { createMuiTheme } from '@material-ui/core/styles';

/** Material Icon*/
import BusinessCenterIcon from '@material-ui/icons/BusinessCenter';

/** UI Customize Component */
import Table from '../../Components/components/Table/Table.js';
import Card from '../../Components/components/Card/Card.js';
import CardHeader from '../../Components/components/Card/CardHeader.js';
import CardBody from '../../Components/components/Card/CardBody.js';

const useStyles = makeStyles((theme) => ({
  spinner: {
    position: 'absolute',
    left: '50%',
    top: '50%',
  },
  table: {
    marginTop: '4em',
    boxShadow: '2px 2px 5px 2px rgba(0,0,0,0.35)',
  },
  content: {
    paddingLeft: '2em',
    paddingRight: '1em',
    paddingTop: '1em',
  },
  cardCategoryWhite: {
    color: 'rgba(255,255,255,.62)',
    fontSize: '14px',
  },
  cardTitleWhite: {
    color: '#FFFFFF',
    minHeight: 'auto',
    fontWeight: '300',
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: '3px',
    textDecoration: 'none',
  },
  icon: {
    cursor: 'pointer',
  },
  card: {
    marginTop: '4em',
    boxShadow: '2px 2px 5px 2px rgba(0,0,0,0.35)',
  },
  IconButton: {
    marginRight: '5px',
  },
  profileItem: {
    borderRight: '#428bca solid 1px',
  },
  profileItemFeature: {
    fontWeight: '300',
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    color: '#111',
  },
  description: {
    width: '50%',
    margin: 'auto',
    marginBottom: '0.75em',
  },
  cardCategory: {
    fontWeight: '300',
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    color: '#7a7979',
  },
  cardTitle: {
    fontWeight: '300',
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
  },
}));

const IndividualExperience = ({ loading, experience }) => {
  const classes = useStyles();

  if (loading) {
    return (
      <div className={classes.spinner}>
        <Spinner color="#0275d8" loading={true} size="50" />
      </div>
    );
  }

  return (
    <Fragment>
      <Card className={classes.table}>
        <CardHeader color="primary">
          <h4>
            <BusinessCenterIcon /> Experience
          </h4>
          <p>Click the "Edit Experience" tab to add or remove new item</p>
        </CardHeader>
        <CardBody>
          <Table
            tableHeaderColor="primary"
            tableHead={['Company', 'Job', 'From', 'To', 'Current']}
            tableData={experience}
          />
        </CardBody>
      </Card>
    </Fragment>
  );
};

IndividualExperience.propTypes = {
  loading: PropTypes.bool,
};

const mapStateToProps = (state) => ({
  loading: state.profile.loading,
});

export default connect(mapStateToProps)(IndividualExperience);
