import React from 'react';

/** Spring */
import { Spring } from 'react-spring/renderprops';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import { createMuiTheme } from '@material-ui/core/styles';
import { MuiThemeProvider } from '@material-ui/core/styles';
import VisibilitySensor from 'react-visibility-sensor';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import ButtonGroup from '@material-ui/core/ButtonGroup';

/** Material UI */
import FacebookIcon from '@material-ui/icons/Facebook';
import TwitterIcon from '@material-ui/icons/Twitter';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import IconButton from '@material-ui/core/IconButton';
import LocationOnIcon from '@material-ui/icons/LocationOn';

/** React Responsive */
import { useMediaQuery } from 'react-responsive';

import logo from '../img/logo.png';

const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#428bca',
    },
    secondary: {
      main: '#111',
    },
  },
});

const useStyles = makeStyles((theme) => ({
  titleHeadline: {
    fontFamily: 'Poiret One',
    fontWeight: '800',
    fontSize: '35px',
    color: '#428bca',
    margin: 'auto',
    marginTop: '3em',
    marginBottom: '2em',
  },
  titleHeadlineSmall: {
    fontFamily: 'Poiret One',
    fontWeight: '800',
    fontSize: '25px',
    color: '#428bca',
    margin: 'auto',
    marginTop: '2em',
    marginBottom: '1em',
  },
  titleHead: {
    fontFamily: 'Quicksand',
    fontWeight: '800',
    fontSize: '18px',
    color: '#428bca',
    marginTop: '2em',
    marginBottom: '1em',
  },
  titleHeadSmall: {
    fontFamily: 'Quicksand',
    fontWeight: '800',
    fontSize: '15px',
    color: '#428bca',
    marginTop: '1.5em',
    marginBottom: '1.5em',
  },
  titleParagraph: {
    fontFamily: 'Quicksand',
    fontWeight: '100',
    fontSize: '1.25em',
    color: '#666666',
    margin: 'auto',
    marginTop: '1em',
    marginBottom: '1em',
  },
  divider: {
    height: '3px',
    width: '60px',
    margin: 'auto',
    backgroundColor: '#428bca',
  },
  divider_small: {
    height: '3px',
    width: '60px',
    margin: 'auto',
    backgroundColor: '#428bca',
    marginTop: '3em',
    marginBottom: '1em',
  },
  gridPaddingLarge: {
    padding: '7em',
  },
  gridPaddingSmall: {
    padding: '1.5em',
  },
}));
const Part10 = () => {
  const classes = useStyles();
  const isSmall = useMediaQuery({ minWidth: 0, maxWidth: 599 });

  return (
    <MuiThemeProvider theme={theme}>
      <Grid container>
        <Grid xs={12}>
          <div className={classes.divider}></div>
        </Grid>
        <Grid
          item
          xs={12}
          sm={6}
          md={6}
          className={
            isSmall ? classes.gridPaddingSmall : classes.gridPaddingLarge
          }
        >
          <div data-aos="fade-down">
            <img src={logo} alt="Logo" style={{ width: '125px' }} />
            {!isSmall && (
              <div>
                <span className={classes.titleHeadline}>
                  <LocationOnIcon fontSize="large" /> Location{' '}
                </span>

                <h5 className={classes.titleHead}>ADDRESS</h5>

                <p className={classes.titleParagraph}>
                  Brooklyn Gold LLC 257 Gold St, Brooklyn, NY 11201, United
                  States
                </p>
                <h5 className={classes.titleHead}>PHONE</h5>
                <p className={classes.titleParagraph}>(+84) 898299182</p>
              </div>
            )}
            {isSmall && (
              <div>
                <span className={classes.titleHeadlineSmall}>
                  <LocationOnIcon /> Location{' '}
                </span>
                <h5 className={classes.titleHeadSmall}>ADDRESS</h5>

                <p className={classes.titleParagraph}>
                  Brooklyn Gold LLC 257 Gold St, Brooklyn, NY 11201, United
                  States
                </p>
                <h5 className={classes.titleHead}>PHONE</h5>
                <p className={classes.titleParagraph}>(+84) 898299182</p>
              </div>
            )}
            <ButtonGroup
              variant="text"
              color="primary"
              aria-label="text primary button group"
            >
              <IconButton>
                <FacebookIcon color="primary" />
              </IconButton>
              <IconButton>
                <LinkedInIcon color="primary" />
              </IconButton>
              <IconButton>
                <TwitterIcon color="primary" />
              </IconButton>
            </ButtonGroup>
          </div>
        </Grid>
        <Grid
          item
          xs={false}
          sm={6}
          md={6}
          className={
            isSmall ? classes.gridPaddingSmall : classes.gridPaddingLarge
          }
        >
          <div data-aos="fade-up">
            {!isSmall && (
              <iframe
                src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d2461.6676457698336!2d-73.98448458938034!3d40.69826157006153!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x89c25a355459b09d%3A0xe071c8684f799703!2sBrooklyn%20Gold%20LLC!5e1!3m2!1sen!2s!4v1590953598440!5m2!1sen!2s"
                width="550"
                height="550"
                frameborder="0"
                allowfullscreen=""
                aria-hidden="false"
                tabindex="0"
              ></iframe>
            )}
            {isSmall && (
              <iframe
                src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d2461.6676457698336!2d-73.98448458938034!3d40.69826157006153!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x89c25a355459b09d%3A0xe071c8684f799703!2sBrooklyn%20Gold%20LLC!5e1!3m2!1sen!2s!4v1590953598440!5m2!1sen!2s"
                width="350"
                height="350"
                frameborder="0"
                allowfullscreen=""
                aria-hidden="false"
                tabindex="0"
              ></iframe>
            )}
          </div>
        </Grid>
      </Grid>
    </MuiThemeProvider>
  );
};

export default Part10;
