import React, { useEffect } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import {
  getCurrentProfile,
  delEducation,
  delExperience,
} from '../../../action/profile';
import { loadUser } from '../../../action/auth';
import { Link } from 'react-router-dom';
import Spinner from '../../layout/Spinner';
import TaskList from './Components/TaskList';
import LineGraph from './Components/LineGraph';
import BarGraph from './Components/BarGraph';
import BipolarGraph from './Components/BipolarGraph';
import BarGraphClose from './Components/BarGraphClose';
import CardMedia from './Components/CardMedia';

/** Material UI */
import { makeStyles } from '@material-ui/core/styles';
import { createMuiTheme, ThemeProvider } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';

/** Material Icon*/
import HomeWorkIcon from '@material-ui/icons/HomeWork';
import SchoolIcon from '@material-ui/icons/School';
import CheckIcon from '@material-ui/icons/Check';
import ClearIcon from '@material-ui/icons/Clear';
import DeleteIcon from '@material-ui/icons/Delete';
import PersonIcon from '@material-ui/icons/Person';
import WorkIcon from '@material-ui/icons/Work';
import BusinessCenterIcon from '@material-ui/icons/BusinessCenter';
import LocationOnIcon from '@material-ui/icons/LocationOn';
import LinkIcon from '@material-ui/icons/Link';
import DashboardIcon from '@material-ui/icons/Dashboard';

/** UI Customize Component */
import Table from '../../Components/components/Table/Table.js';
import Card from '../../Components/components/Card/Card.js';
import CardHeader from '../../Components/components/Card/CardHeader.js';
import CardBody from '../../Components/components/Card/CardBody.js';
import Button from '../../Components/components/CustomButtons/Button.js';
import CardAvatar from '../../Components/components/Card/CardAvatar.js';

/** Spring */
import { Spring } from 'react-spring/renderprops';

/** Utils */
var moment = require('moment');

var Chartist = require('chartist');
const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#111',
    },
    secondary: {
      main: '#428bca',
    },
    success: {
      main: '#5cb85c',
    },
    danger: {
      main: '#d9534f',
    },
  },
});

const useStyles = makeStyles((theme) => ({
  spinner: {
    position: 'absolute',
    left: '50%',
    top: '50%',
  },
  table: {
    marginTop: '4em',
    boxShadow: '2px 2px 5px 2px rgba(0,0,0,0.35)',
    border: '1px #5B5B5B solid',
  },
  content: {
    paddingLeft: '1em',
    paddingRight: '1em',
    paddingTop: '1em',
    background: '#e5e5e5',
  },
  cardCategoryWhite: {
    color: 'rgba(255,255,255,.62)',
    fontSize: '14px',
  },
  cardTitleWhite: {
    color: '#FFFFFF',
    minHeight: 'auto',
    fontWeight: '500',
    marginBottom: '3px',
    textDecoration: 'none',
  },
  icon: {
    cursor: 'pointer',
  },
  card: {
    marginTop: '4em',
    boxShadow: '2px 2px 5px 2px rgba(0,0,0,0.35)',
    border: '1px #5B5B5B solid',
  },
  IconButton: {
    marginRight: '5px',
  },
  profileItem: {
    borderRight: '#428bca solid 1px',
    fontWeight: '500',
  },
  profileItemFeature: {
    fontWeight: '500',
    color: '#111',
  },
  description: {
    width: '50%',
    margin: 'auto',
    marginBottom: '0.75em',
    fontWeight: '500',
  },
  cardCategory: {
    fontWeight: '600',
    color: '#666565',
  },
  cardTitle: {
    fontWeight: '500',
  },
}));

const DashBoard = ({
  profile: { profile, loading },
  auth: { user, isAuthenticated },
  getCurrentProfile,
  delEducation,
  delExperience,
  loadUser,
}) => {
  const classes = useStyles();

  useEffect(() => {
    /** Checking on token */
    loadUser();
    getCurrentProfile();
  }, []);

  /** Checking on loading if (loading) => show spinner*/
  if (loading === true) {
    return (
      <div className={classes.spinner}>
        <Spinner color="#0275d8" loading={true} size="50px" />
      </div>
    );
  } else {
    /** Checking on profile exist if (!profile) => show edit profile*/
    if (profile) {
      /** Process Education and Experience item */
      const educationList = profile.education.map((item) => [
        item.school,
        item.degree,
        moment(item.from).format('MMM Y'),
        moment(item.to).format('MMM Y'),
        item.current ? (
          <CheckIcon color="action" />
        ) : (
          <ClearIcon color="danger" />
        ),
        item.gpa,
        <DeleteIcon
          className={classes.icon}
          onClick={() => {
            delEducation(item._id);
          }}
        />,
      ]);

      const experienceList = profile.experience.map((item) => [
        <img src={`//logo.clearbit.com/${item.company}.com`} width="55px" />,
        item.title,
        moment(item.from).format('MMM Y'),
        moment(item.to).format('MMM Y'),
        item.current ? (
          <CheckIcon color="action" />
        ) : (
          <ClearIcon color="danger" />
        ),
        <DeleteIcon
          className={classes.icon}
          onClick={() => {
            delExperience(item._id);
          }}
        />,
      ]);

      return (
        <Spring
          from={{ opacity: 0 }}
          to={{ opacity: 1 }}
          config={{ duration: 150 }}
        >
          {(props) => (
            <div style={props}>
              <ThemeProvider theme={theme}>
                <Grid item xs={12} sm={12} md={12} className={classes.content}>
                  {/** Dashboard for Experience */}
                  <Button
                    color="primary"
                    variant="outlined"
                    startIcon={<DashboardIcon />}
                  >
                    Dashboard
                  </Button>

                  {/**
                   * Overview
                   * Profile
                   * User
                   * */}
                  <Grid container>
                    <Grid item xs={12} sm={12} md={6}>
                      <Card className={classes.card} profile>
                        <CardAvatar profile>
                          <img
                            src={`https://avatars.dicebear.com/api/avataaars/${profile.user._id}.svg?`}
                            alt="..."
                          />
                        </CardAvatar>
                        <CardBody profile>
                          <h4 className={classes.cardTitle}>
                            {user.name.toUpperCase()}
                          </h4>
                          <h6 className={classes.cardCategory}>
                            {profile.status.toUpperCase()}
                          </h6>

                          <p className={classes.description}>{profile.bio}</p>

                          {/** Information */}
                          <Grid container>
                            <Grid
                              item
                              xs={12}
                              sm={4}
                              md={4}
                              className={classes.profileItem}
                            >
                              <HomeWorkIcon style={{ color: '#428bca' }} />
                              <h5 className={classes.profileItemFeature}>
                                {profile.company}
                              </h5>
                            </Grid>
                            <Grid
                              item
                              xs={12}
                              sm={4}
                              md={4}
                              className={classes.profileItem}
                            >
                              <LocationOnIcon style={{ color: '#428bca' }} />
                              <h5 className={classes.profileItemFeature}>
                                {profile.location}
                              </h5>
                            </Grid>
                            <Grid
                              item
                              xs={12}
                              sm={4}
                              md={4}
                              className={classes.profileItem}
                            >
                              <LinkIcon style={{ color: '#428bca' }} />
                              <h5 className={classes.profileItemFeature}>
                                {profile.website}
                              </h5>
                            </Grid>
                          </Grid>

                          {/** Button List */}
                          <Link
                            to="/edit-profile"
                            style={{ textDecoration: 'none' }}
                          >
                            <Button color="primary" round>
                              <PersonIcon />
                            </Button>
                          </Link>

                          <Link
                            to="/edit-education"
                            style={{ textDecoration: 'none' }}
                          >
                            <Button color="primary" round>
                              <SchoolIcon />
                            </Button>
                          </Link>

                          <Link
                            to="/edit-experience"
                            style={{ textDecoration: 'none' }}
                          >
                            <Button color="primary" round>
                              <WorkIcon />
                            </Button>
                          </Link>
                        </CardBody>
                      </Card>
                    </Grid>

                    {/**
                     * Basic
                     * Information
                     * */}
                    <Grid
                      item
                      xs={12}
                      sm={12}
                      md={6}
                      style={{
                        paddingRight: '0.5em',
                        paddingLeft: '0.5em',
                        paddingTop: '2em',
                      }}
                    >
                      <CardMedia />
                    </Grid>

                    {/**
                     * Line Graph
                     * */}
                    <Grid
                      item
                      xs={12}
                      sm={12}
                      md={6}
                      style={{
                        paddingRight: '0.5em',
                        paddingLeft: '0.5em',
                      }}
                    >
                      <LineGraph />
                    </Grid>
                    {/**
                     * Bar Graph
                     * */}
                    <Grid
                      item
                      xs={12}
                      sm={12}
                      md={6}
                      style={{
                        paddingRight: '0.5em',
                        paddingLeft: '0.25em',
                      }}
                    >
                      <BarGraph />
                    </Grid>

                    {/**
                     * Bipolar Graph
                     * */}
                    <Grid
                      item
                      xs={12}
                      sm={12}
                      md={6}
                      style={{
                        paddingRight: '0.25em',
                        paddingLeft: '0.5em',
                      }}
                    >
                      <BipolarGraph />
                    </Grid>

                    {/**
                     * Pie Graph
                     * */}
                    <Grid
                      item
                      xs={12}
                      sm={12}
                      md={6}
                      style={{
                        paddingRight: '0.25em',
                        paddingLeft: '0.5em',
                      }}
                    >
                      <BarGraphClose />
                    </Grid>

                    {/** Display
                     * Task List
                     * */}
                    <Grid
                      item
                      xs={12}
                      sm={12}
                      md={12}
                      style={{
                        paddingRight: '0.5em',
                        paddingLeft: '0.25em',
                        paddingTop: '2em',
                      }}
                    >
                      <TaskList />
                    </Grid>
                  </Grid>

                  <Grid container>
                    <Grid
                      item
                      xs={12}
                      sm={12}
                      md={6}
                      style={{
                        paddingRight: '0.5em',
                      }}
                    >
                      {/** Display
                       * User
                       * Working
                       * Experience
                       * */}
                      <Card className={classes.table}>
                        <CardHeader color="primary">
                          <h4>
                            <BusinessCenterIcon /> Experience
                          </h4>
                          <p>
                            Click the "Edit Experience" tab to add or remove new
                            item
                          </p>
                        </CardHeader>
                        <CardBody>
                          <Table
                            tableHeaderColor="primary"
                            tableHead={[
                              'Company',
                              'Job Title',
                              'From',
                              'To',
                              'Current Job',
                              '',
                            ]}
                            tableData={experienceList}
                          />
                        </CardBody>
                      </Card>
                    </Grid>
                    <Grid
                      item
                      xs={12}
                      sm={12}
                      md={6}
                      style={{
                        paddingLeft: '0.5em',
                      }}
                    >
                      {/** Display
                       * User
                       * Education
                       * */}

                      <Card className={classes.table}>
                        <CardHeader color="primary">
                          <h4>
                            <SchoolIcon /> Education
                          </h4>
                          <p>
                            Click the "Edit Education" tab to add or remove new
                            item
                          </p>
                        </CardHeader>
                        <CardBody>
                          <Table
                            tableHeaderColor="primary"
                            tableHead={[
                              'School',
                              'Degree',
                              'From',
                              'To',
                              'Graduated',
                              'GPA',
                            ]}
                            tableData={educationList}
                          />
                        </CardBody>
                      </Card>
                    </Grid>
                  </Grid>
                </Grid>
              </ThemeProvider>
            </div>
          )}
        </Spring>
      );
    }

    if (!profile) {
      return <div> {false} </div>;
    }
  }
};

DashBoard.propTypes = {
  profile: PropTypes.object,
  getCurrentProfile: PropTypes.func,
  delEducation: PropTypes.func,
  delExperience: PropTypes.func,
  auth: PropTypes.object,
};

const mapStateToProps = (state) => ({
  profile: state.profile,
  auth: state.auth,
});

export default connect(mapStateToProps, {
  getCurrentProfile,
  loadUser,
  delEducation,
  delExperience,
})(DashBoard);
