import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';

/** Redux */
import { connect } from 'react-redux';
import { getUsersProfile, searchProfile } from '../../../action/profile';

/** Local Component */
import Spinner from '../../layout/Spinner';
import DevItem from '../Dev/DevItem';

/** Material UI */
import { makeStyles } from '@material-ui/core/styles';
import { createMuiTheme, ThemeProvider } from '@material-ui/core/styles';
import FormControl from '@material-ui/core/FormControl';
import Grid from '@material-ui/core/Grid';
import Button from '../../Components/components/CustomButtons/Button.js';
import ButtonGroup from '@material-ui/core/ButtonGroup';
import Card from '../../Components/components/Card/Card.js';
import CardHeader from '../../Components/components/Card/CardHeader.js';
import CardBody from '../../Components/components/Card/CardBody.js';
import CardFooter from '../../Components/components/Card/CardFooter.js';
import Input from '@material-ui/core/Input';
import InputAdornment from '@material-ui/core/InputAdornment';
import Divider from '@material-ui/core/Divider';

/** Material Icon */
import PeopleSharpIcon from '@material-ui/icons/PeopleSharp';
import SearchSharpIcon from '@material-ui/icons/SearchSharp';
import RotateLeftIcon from '@material-ui/icons/RotateLeft';
import PageviewIcon from '@material-ui/icons/Pageview';

/** Spring */
import { Spring } from 'react-spring/renderprops';

const useStyles = makeStyles((theme) => ({
  margin: {
    margin: theme.spacing(1),
  },
  cardCategoryWhite: {
    color: 'rgba(255,255,255,.62)',
    margin: '0',
    fontSize: '14px',
    marginTop: '0',
    marginBottom: '0',
  },
  cardTitleWhite: {
    color: '#FFFFFF',
    marginTop: '0px',
    minHeight: 'auto',
    fontWeight: '300',
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: '3px',
    textDecoration: 'none',
  },
  spinner: {
    position: 'absolute',
    left: '50%',
    top: '50%',
  },
  card: {
    boxShadow: '2px 2px 5px 2px rgba(0,0,0,0.35)',
    marginRight: '0.5em',
    marginLeft: '0.5em',
  },
  cardBodyTitle: {
    color: '428bca',
    fontWeight: '400',
    fontSize: '1.5em',
  },
}));

const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#428bca',
    },
    secondary: {
      main: '#111',
    },
    success: {
      main: '#5cb85c',
    },
    danger: {
      main: '#d9534f',
    },
  },
});

const Dev = ({ profiles, loading, getUsersProfile, searchProfile }) => {
  useEffect(() => {
    getUsersProfile();
  }, []);

  const classes = useStyles();
  const [search, setSearch] = useState('');

  const searchFunc = () => {
    searchProfile(search);
    setSearch('');
  };

  /** Checking on loading if (loading) => show spinner*/
  if (loading === true) {
    return (
      <div className={classes.spinner}>
        <Spinner color="#0275d8" loading={true} size="50" />
      </div>
    );
  }

  if (profiles) {
    return (
      <Spring
        from={{ opacity: 0 }}
        to={{ opacity: 1 }}
        config={{ duration: 150 }}
      >
        {(props) => (
          <div style={props}>
            <ThemeProvider theme={theme}>
              <Grid
                container
                style={{ padding: '0.5em', background: '#e5e5e5' }}
              >
                <Grid
                  item
                  xs={12}
                  sm={12}
                  md={8}
                  style={{ paddingLeft: '1em', paddingRight: '1em' }}
                >
                  <Card className={classes.card}>
                    <CardHeader color="primary">
                      <h4 className={classes.cardTitleWhite}>
                        <PeopleSharpIcon /> People
                      </h4>
                      <p className={classes.cardCategoryWhite}>
                        Find more developers, programmers, professionals and
                        enthusiast...
                      </p>
                    </CardHeader>
                    <CardBody>
                      <h3 className={classes.cardBodyTitle}>
                        Search for more friends...
                      </h3>
                      <form>
                        <FormControl fullWidth>
                          <Input
                            name="search"
                            onChange={(event) => {
                              setSearch(event.target.value);
                            }}
                            value={search}
                            id="standard-adornment-search"
                            startAdornment={
                              <InputAdornment position="start">
                                <SearchSharpIcon style={{ color: '#428bca' }} />
                              </InputAdornment>
                            }
                          />
                        </FormControl>
                      </form>
                    </CardBody>
                    <CardFooter>
                      <ButtonGroup
                        variant="text"
                        color="primary"
                        aria-label="Button-Group"
                      >
                        <Button
                          size="lg"
                          onClick={() => {
                            searchFunc();
                          }}
                          startIcon={<PageviewIcon />}
                        >
                          Search
                        </Button>
                        <Button
                          size="lg"
                          onClick={() => {
                            getUsersProfile();
                            setSearch('');
                          }}
                          startIcon={<RotateLeftIcon />}
                        >
                          Reset
                        </Button>
                      </ButtonGroup>
                    </CardFooter>
                  </Card>
                </Grid>
                <Grid item xs={false} sm={false} md={4}>
                  {false}
                </Grid>

                <Divider color="primary" />
                {/**
                 * List of Items
                 */}

                {profiles.map((item, index) => (
                  <DevItem
                    key={index}
                    user={item.user}
                    status={item.status}
                    company={item.company}
                    location={item.location}
                    skill={item.skill}
                    bio={item.bio}
                    website={item.website}
                  />
                ))}
              </Grid>
            </ThemeProvider>
          </div>
        )}
      </Spring>
    );
  }
};

Dev.propTypes = {
  profiles: PropTypes.array,
  loading: PropTypes.bool,
  getUsersProfile: PropTypes.func,
};

const mapStateToProps = (state) => ({
  profiles: state.profile.profiles,
  loading: state.profile.loading,
});

export default connect(mapStateToProps, { getUsersProfile, searchProfile })(
  Dev
);
