import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import ProgrammingLanguageSuggestion from '../../suggestion/ProgrammingLanguage';
import Spinner from '../../layout/Spinner';
import { connect } from 'react-redux';
import { createProfile, getCurrentProfile } from '../../../action/profile';
import { withRouter } from 'react-router-dom';
import { loadUser } from '../../../action/auth';
import { Redirect } from 'react-router';

/** Material UI */
import { makeStyles } from '@material-ui/core/styles';
import { createMuiTheme, ThemeProvider } from '@material-ui/core/styles';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';
import Grid from '@material-ui/core/Grid';
import Input from '@material-ui/core/Input';
import InputAdornment from '@material-ui/core/InputAdornment';
import TextField from '@material-ui/core/TextField';

/** Customize Material UI */
import GridItem from '../../Components/components/Grid/GridItem.js';
import GridContainer from '../../Components/components/Grid/GridContainer.js';
import Button from '../../Components/components/CustomButtons/Button.js';
import Card from '../../Components/components/Card/Card.js';
import CardHeader from '../../Components/components/Card/CardHeader.js';
import CardBody from '../../Components/components/Card/CardBody.js';
import CardFooter from '../../Components/components/Card/CardFooter.js';

/** Material Icon */
import WorkSharpIcon from '@material-ui/icons/WorkSharp';
import BusinessSharpIcon from '@material-ui/icons/BusinessSharp';
import LinkSharpIcon from '@material-ui/icons/LinkSharp';
import ExploreSharpIcon from '@material-ui/icons/ExploreSharp';
import GitHubIcon from '@material-ui/icons/GitHub';
import TwitterIcon from '@material-ui/icons/Twitter';
import YouTubeIcon from '@material-ui/icons/YouTube';
import FacebookIcon from '@material-ui/icons/Facebook';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import EditIcon from '@material-ui/icons/Edit';

/** Local Component */
import EditProfileShowCase from '../Dashboard/EditProfileShowCase';

/** Spring */
import { Spring } from 'react-spring/renderprops';

const useStyles = makeStyles((theme) => ({
  margin: {
    margin: theme.spacing(1),
  },
  cardCategoryWhite: {
    color: 'rgba(255,255,255,.62)',
    margin: '0',
    fontSize: '14px',
    marginTop: '0',
    marginBottom: '0',
  },
  cardTitleWhite: {
    color: '#FFFFFF',
    marginTop: '0px',
    minHeight: 'auto',
    fontWeight: '300',
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: '3px',
    textDecoration: 'none',
  },
  spinner: {
    position: 'absolute',
    left: '50%',
    top: '50%',
  },
  card: {
    boxShadow: '2px 2px 5px 2px rgba(0,0,0,0.35)',
    paddingRight: '1em',
    paddingLeft: '1em',
    border: '1px #5B5B5B solid',
  },
}));

const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#428bca',
    },
    secondary: {
      main: '#111',
    },
    success: {
      main: '#5cb85c',
    },
    danger: {
      main: '#d9534f',
    },
  },
});
const EditProfile = ({
  loading,
  profile,
  createProfile,
  getCurrentProfile,
  loadUser,
  user,
  authLoading,
}) => {
  const classes = useStyles();

  useEffect(() => {
    loadUser();
    getCurrentProfile();
  }, []);

  const [formData, setFormData] = useState({
    company: profile ? profile.company : '',
    website: profile ? profile.website : '',
    location: profile ? profile.location : '',
    status: profile ? profile.status : '',
    skill: profile ? profile.skill : [],
    bio: profile ? profile.bio : '',
    githubUsername: profile ? profile.githubUsername : '',
    facebook: profile ? (profile.connect ? profile.connect.facebook : '') : '',
    twitter: profile ? (profile.connect ? profile.connect.twitter : '') : '',
    github: profile ? (profile.connect ? profile.connect.github : '') : '',
    linkedin: profile ? (profile.connect ? profile.connect.linkedin : '') : '',
  });

  const [displaySkill, setDisplaySkill] = useState('');
  const [redirect, setRedirect] = useState(false);
  const {
    company,
    website,
    location,
    status,
    skill,
    bio,
    githubUsername,
    facebook,
    twitter,
    github,
    linkedin,
  } = formData;

  const onChange = (event) => {
    setFormData({ ...formData, [event.target.name]: event.target.value });
  };

  const deleteSkill = (index) => {
    let currentSkill = skill;
    currentSkill.splice(index, 1);
    setFormData({ ...formData, skill: currentSkill });
  };

  const addSkill = (newSkill) => {
    let currentSkill = skill;
    currentSkill.push(newSkill);
    setFormData({ ...formData, skill: currentSkill });
  };

  useEffect(() => {
    if (formData.skill) {
      setDisplaySkill(
        formData.skill.map((eachSkill, index) => (
          <h5 className="d-inline mr-1" key={index}>
            <span className="badge badge-secondary font-weight-light">
              {eachSkill}{' '}
              <a
                href="/#"
                onClick={() => {
                  deleteSkill(index);
                }}
                style={{ cursor: 'pointer' }}
              >
                &times;
              </a>
            </span>
          </h5>
        ))
      );
    }
  }, [formData]);

  const onSubmit = (event) => {
    event.preventDefault();

    let edit = profile ? true : false;

    const postProfile = {
      company,
      website,
      location,
      status,
      skill,
      bio,
      githubUsername,
      connect: {
        facebook,
        twitter,
        github,
        linkedin,
      },
    };
    createProfile(postProfile, edit);
    setRedirect(true);
  };

  if (redirect) {
    return <Redirect to="/dashboard" />;
  }

  /** Checking on loading if (loading) => show spinner*/
  if (loading === true || authLoading === true) {
    return (
      <div className={classes.spinner}>
        <Spinner color="#0275d8" loading={true} size="50" />
      </div>
    );
  }

  return (
    <Spring
      from={{ opacity: 0 }}
      to={{ opacity: 1 }}
      config={{ duration: 150 }}
    >
      {(props) => (
        <div style={props}>
          <ThemeProvider theme={theme}>
            {/** Form  */}
            <Grid container spacing={1}>
              {/**
               * Edit profile
               * */}
              <GridItem xs={12} sm={12} md={8}>
                <form
                  onSubmit={(event) => {
                    onSubmit(event);
                  }}
                >
                  <Card className={classes.card}>
                    {/**
                     * Start of Edit profile
                     */}
                    <CardHeader color="primary">
                      <h4 className={classes.cardTitleWhite}>
                        <EditIcon style={{ color: '#fff' }} /> Edit Profile
                      </h4>
                      <p className={classes.cardCategoryWhite}>
                        Complete your profile
                      </p>
                    </CardHeader>
                    <CardBody>
                      <GridContainer>
                        {/**
                         *  Company
                         */}
                        <GridItem xs={12} sm={12} md={6}>
                          <FormControl>
                            <InputLabel htmlFor="standard-adornment-Company">
                              Company
                            </InputLabel>
                            <Input
                              name="company"
                              onChange={onChange}
                              value={company}
                              className={classes.margin}
                              id="standard-adornment-Company"
                              startAdornment={
                                <InputAdornment position="start">
                                  <BusinessSharpIcon
                                    style={{ color: '#428bca' }}
                                  />
                                </InputAdornment>
                              }
                            />
                          </FormControl>
                        </GridItem>
                        <GridItem xs={12} sm={12} md={6}>
                          <FormControl>
                            <InputLabel htmlFor="standard-adornment-Website">
                              Website
                            </InputLabel>
                            <Input
                              name="website"
                              onChange={onChange}
                              value={website}
                              className={classes.margin}
                              id="standard-adornment-Website"
                              startAdornment={
                                <InputAdornment position="start">
                                  <LinkSharpIcon style={{ color: '#428bca' }} />
                                </InputAdornment>
                              }
                            />
                          </FormControl>
                        </GridItem>
                      </GridContainer>

                      <GridContainer>
                        <GridItem xs={12} sm={12} md={6}>
                          <FormControl>
                            <InputLabel htmlFor="Github">
                              Github Username
                            </InputLabel>

                            <Input
                              name="githubUsername"
                              onChange={onChange}
                              value={githubUsername}
                              className={classes.margin}
                              startAdornment={
                                <InputAdornment position="start">
                                  <GitHubIcon style={{ color: '#428bca' }} />
                                </InputAdornment>
                              }
                            />
                          </FormControl>
                        </GridItem>
                        <GridItem xs={12} sm={12} md={6}>
                          <FormControl>
                            <InputLabel htmlFor="Location">Location</InputLabel>

                            <Input
                              name="location"
                              onChange={onChange}
                              value={location}
                              className={classes.margin}
                              startAdornment={
                                <InputAdornment position="start">
                                  <ExploreSharpIcon
                                    style={{ color: '#428bca' }}
                                  />
                                </InputAdornment>
                              }
                            />
                          </FormControl>
                        </GridItem>
                      </GridContainer>

                      <GridContainer>
                        <GridItem xs={12} sm={12} md={12}>
                          <FormControl>
                            <TextField
                              name="status"
                              onChange={onChange}
                              value={status}
                              className={classes.margin}
                              id="standard-select-qualification"
                              select
                              label="Qualification"
                              InputProps={{
                                startAdornment: (
                                  <InputAdornment position="start">
                                    <WorkSharpIcon
                                      style={{ color: '#428bca' }}
                                    />
                                  </InputAdornment>
                                ),
                              }}
                            >
                              <MenuItem
                                value="Junior Developer"
                                style={{ backgroundColor: '#fff' }}
                              >
                                {'Junior Developer'}
                              </MenuItem>
                              <MenuItem
                                value="Senior Developer"
                                style={{ backgroundColor: '#fff' }}
                              >
                                {'Senior Developer'}
                              </MenuItem>
                              <MenuItem
                                value="Manager"
                                style={{ backgroundColor: '#fff' }}
                              >
                                {'Manager'}
                              </MenuItem>
                              <MenuItem
                                value="Architect"
                                style={{ backgroundColor: '#fff' }}
                              >
                                {'Architect'}
                              </MenuItem>
                              <MenuItem
                                value="Middle Developer"
                                style={{ backgroundColor: '#fff' }}
                              >
                                {'Middle Developer'}
                              </MenuItem>
                              <MenuItem
                                value="Software Engineer"
                                style={{ backgroundColor: '#fff' }}
                              >
                                {'Software Engineer'}
                              </MenuItem>
                            </TextField>
                          </FormControl>
                        </GridItem>
                      </GridContainer>
                      <GridContainer>
                        <GridItem xs={12} sm={12} md={12}>
                          <FormControl>
                            <h6 style={{ color: 'grey', fontWeight: '300' }}>
                              Choose your skills
                            </h6>
                            <div className="input-group mb-3">
                              <div className="input-group-prepend">
                                <span
                                  className="input-group-text"
                                  id="basic-addon1"
                                >
                                  <i
                                    className="fas fa-laptop"
                                    style={{ color: '#428bca' }}
                                  ></i>
                                </span>
                              </div>
                              <ProgrammingLanguageSuggestion
                                addSkill={addSkill}
                              />
                            </div>
                            <small>
                              Type the tech stack or programming language you
                              influenced and click "Add"
                            </small>
                            {displaySkill}
                          </FormControl>
                        </GridItem>
                      </GridContainer>

                      <GridContainer>
                        <GridItem xs={12} sm={12} md={6}>
                          <FormControl>
                            <InputLabel htmlFor="standard-adornment-Twitter">
                              Twitter
                            </InputLabel>
                            <Input
                              name="twitter"
                              value={twitter}
                              onChange={(event) => onChange(event)}
                              className={classes.margin}
                              id="standard-adornment-Twitter"
                              startAdornment={
                                <InputAdornment position="start">
                                  <TwitterIcon style={{ color: '#428bca' }} />
                                </InputAdornment>
                              }
                            />
                          </FormControl>
                        </GridItem>
                        <GridItem xs={12} sm={12} md={6}>
                          <FormControl>
                            <InputLabel htmlFor="standard-adornment-YouTube">
                              YouTube
                            </InputLabel>
                            <Input
                              className={classes.margin}
                              id="standard-adornment-YouTube"
                              startAdornment={
                                <InputAdornment position="start">
                                  <YouTubeIcon style={{ color: '#428bca' }} />
                                </InputAdornment>
                              }
                            />
                          </FormControl>
                        </GridItem>
                      </GridContainer>

                      <GridContainer>
                        <GridItem xs={12} sm={12} md={6}>
                          <FormControl>
                            <InputLabel htmlFor="Facebook">Facebook</InputLabel>

                            <Input
                              name="facebook"
                              value={facebook}
                              onChange={(event) => onChange(event)}
                              className={classes.margin}
                              startAdornment={
                                <InputAdornment position="start">
                                  <FacebookIcon style={{ color: '#428bca' }} />
                                </InputAdornment>
                              }
                            />
                          </FormControl>
                        </GridItem>
                        <GridItem xs={12} sm={12} md={6}>
                          <FormControl>
                            <InputLabel htmlFor="Location">Linkedin</InputLabel>

                            <Input
                              name="linkedin"
                              value={linkedin}
                              onChange={(event) => onChange(event)}
                              className={classes.margin}
                              startAdornment={
                                <InputAdornment position="start">
                                  <LinkedInIcon style={{ color: '#428bca' }} />
                                </InputAdornment>
                              }
                            />
                          </FormControl>
                        </GridItem>
                      </GridContainer>

                      <GridContainer>
                        <GridItem xs={12} sm={12} md={12}>
                          <TextField
                            className={classes.margin}
                            label="About me"
                            multiline
                            rows={4}
                            variant="outlined"
                            style={{ width: '100%' }}
                            name="bio"
                            value={bio}
                            onChange={(event) => {
                              onChange(event);
                            }}
                          />
                        </GridItem>
                      </GridContainer>
                    </CardBody>
                    <CardFooter>
                      <Button type="submit" color="primary">
                        Update Profile
                      </Button>
                    </CardFooter>
                  </Card>
                </form>
              </GridItem>
              {/**
               * Profile Show case
               * */}
              {user && (
                <EditProfileShowCase user={user} bio={bio} status={status} />
              )}
            </Grid>
          </ThemeProvider>
        </div>
      )}
    </Spring>
  );
};

EditProfile.propTypes = {
  createProfile: PropTypes.func.isRequired,
  getCurrentProfile: PropTypes.func.isRequired,
  profile: PropTypes.object.isRequired,
};

const mapStateToProps = (state) => ({
  profile: state.profile.profile,
  loading: state.profile.loading,
  authLoading: state.auth.loading,
  user: state.auth.user,
});

export default connect(mapStateToProps, {
  createProfile,
  getCurrentProfile,
  loadUser,
})(withRouter(EditProfile));
