import React, { useState, useEffect } from 'react';

/** Redux */
import { delCode } from '../../action/code';
import { connect } from 'react-redux';

/** Material UI Component */
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import { makeStyles } from '@material-ui/core/styles';
import { createMuiTheme } from '@material-ui/core/styles';
import IconButton from '@material-ui/core/IconButton';
import FileCopyIcon from '@material-ui/icons/FileCopy';
import DeleteIcon from '@material-ui/icons/Delete';

/** Code editor */
import Editor from 'react-simple-code-editor';
import { highlight, languages } from 'prismjs/components/prism-core';
import 'prismjs/components/prism-clike';
import 'prismjs/components/prism-javascript';
import 'prismjs/themes/prism-coy.css';

/** UI Customize Component */
import Card from '../Components/components/Card/Card.js';
import { CopyToClipboard } from 'react-copy-to-clipboard';
import { toast } from 'react-toastify';

const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#428bca',
    },
    secondary: {
      main: '#111',
    },
    success: {
      main: '#5cb85c',
    },
    danger: {
      main: '#d9534f',
    },
  },
});

const useStyles = makeStyles((theme) => ({
  spinner: {
    position: 'absolute',
    left: '50%',
    top: '50%',
  },
  CodeSnippet: {
    marginTop: '1em',
    boxShadow: '2px 2px 5px 2px rgba(0,0,0,0.35)',
    padding: '1em',
    '&.hover': {
      cursor: 'pointer',
    },
  },
}));

const CodeSnippet = ({ delCode, ...props }) => {
  const classes = useStyles();
  const [codePackage, setCodePackage] = useState({
    description: props.description ? props.description : '',
    code: props.content ? props.content : '',
    language: props.language ? props.language : [],
    id: props._id ? props._id : -1,
  });

  const { code, description, language, id } = codePackage;

  return (
    <Grid
      container
      style={{
        paddingLeft: '1em',
        paddingRight: '1em',
      }}
    >
      <Card className={classes.CodeSnippet}>
        <Grid container>
          <Grid item xs={6}>
            <h5 style={{ color: '#428bca' }}>
              <i className="fas fa-terminal"></i> {description}
            </h5>
            <span>
              {language.map((eachLanguage, index) => (
                <a key={index} href="#">{` #${eachLanguage} `}</a>
              ))}
            </span>
          </Grid>
          <Grid
            item
            xs={5}
            style={{
              padingLeft: '1em',
              padingRight: '1em',
            }}
          >
            <CopyToClipboard
              text={code}
              onCopy={() => {
                toast.success('Code copied to Clipboard !');
              }}
            >
              <Button
                startIcon={<FileCopyIcon />}
                style={{
                  color: '#428bca',
                  textAlign: 'center',
                  padding: '1em',
                }}
                size="small"
              >
                Copy to clipboard
              </Button>
            </CopyToClipboard>
          </Grid>
          <Grid item xs={1}>
            <IconButton
              style={{ color: '#d9534f' }}
              aria-label="delete"
              onClick={() => {
                delCode(id);
              }}
            >
              <DeleteIcon />
            </IconButton>
          </Grid>
        </Grid>

        <Editor
          value={code}
          onValueChange={() => {}}
          highlight={(code) => highlight(code, languages.js)}
          padding={14}
          style={{
            fontFamily:
              '"Fira code", "Fira Mono", "Consolas", "Menlo", "Courier", "monospace"',
            fontSize: 16,
            boxShadow: '2px 2px 5px 2px rgba(0,0,0,0.35)',
            background: '#ebebeb',
            height: 'fit-content',
          }}
        />
      </Card>
    </Grid>
  );
};

export default connect(null, { delCode })(CodeSnippet);
