import React from 'react';
/** Spring */
import { Spring } from 'react-spring/renderprops';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import { createMuiTheme } from '@material-ui/core/styles';
import { MuiThemeProvider } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import VisibilitySensor from 'react-visibility-sensor';
import Button from '@material-ui/core/Button';
import ButtonGroup from '@material-ui/core/ButtonGroup';

/** React Responsive */
import { useMediaQuery } from 'react-responsive';

/** Img */
import Mockup1 from '../img/mockup1.png';
import Mockup2 from '../img/mockup2.png';
import Mockup3 from '../img/mockup3.png';

import DevicesOtherSharpIcon from '@material-ui/icons/DevicesOtherSharp';
import PhonelinkSetupIcon from '@material-ui/icons/PhonelinkSetup';

const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#428bca',
    },
    secondary: {
      main: '#111',
    },
  },
});

const useStyles = makeStyles((theme) => ({
  title: {
    fontFamily: 'Quicksand',
    fontWeight: '600',
    fontSize: '1.65em',
    color: '#428bca',
    margin: 'auto',
    marginTop: '5em',
  },
  titleSmall: {
    fontFamily: 'Quicksand',
    fontWeight: '600',
    fontSize: '0.85em',
    color: '#428bca',
    margin: 'auto',
    marginTop: '1.5em',
  },
  titleHeadline: {
    fontFamily: "'Poiret One', cursive",
    fontWeight: '100',
    fontSize: '50px',
    color: '#428bca',
    margin: 'auto',
    marginBottom: '2em',
  },
  titleHeadlineSmall: {
    fontFamily: "'Poiret One', cursive",
    fontWeight: '100',
    fontSize: '20px',
    color: '#428bca',
    margin: 'auto',
    marginBottom: '1em',
  },
  divider: {
    height: '3px',
    width: '60px',
    backgroundColor: '#428bca',
    marginTop: '2em',
    marginBottom: '1em',
    margin: 'auto',
  },
  dividerSmall: {
    height: '3px',
    width: '60px',
    backgroundColor: '#428bca',
    marginTop: '2em',
    marginBottom: '1em',
    margin: 'auto',
  },
  icon: {
    color: '#428bca',
    fontSize: '55px',
  },
  iconSmall: {
    color: '#428bca',
    fontSize: '30px',
  },
}));

const Part6 = () => {
  const classes = useStyles();
  const isExtra = useMediaQuery({ minWidth: 0, maxWidth: 599 });
  const isSmall = useMediaQuery({ minWidth: 600, maxWidth: 960 });
  const isMedium = useMediaQuery({ minWidth: 961, maxWidth: 1280 });
  const isLarge = useMediaQuery({ minWidth: 1281 });
  const [show, setShow] = React.useState(false);

  function onChange(isVisible) {
    if (isVisible) {
      setShow(true);
    }
  }

  return (
    <VisibilitySensor>
      {({ isVisible }) => (
        <Spring to={{ opacity: isVisible ? 1 : 0 }}>
          {({ opacity }) => (
            <MuiThemeProvider theme={theme}>
              <Grid container style={{ padding: '1em' }}>
                <Grid xs={12} sm={6} md={6}>
                  {/** Extra Small */}
                  {isExtra && (
                    <div style={{ position: 'relative', width: '100%' }}>
                      <Spring
                        from={{
                          opacity: 0,
                          transform: 'translate3d(-200px,0,0)',
                        }}
                        to={{ opacity: 1, transform: 'translate3d(0px,0,0)' }}
                        config={{ duration: 500 }}
                        reset={{ show }}
                      >
                        {(props) => (
                          <div style={props}>
                            <img
                              src={Mockup2}
                              alt="Mockup2"
                              style={{
                                width: '100%',
                                position: 'absolute',
                              }}
                            />
                          </div>
                        )}
                      </Spring>

                      <Spring
                        from={{
                          opacity: 0,
                          transform: 'translate3d(-100px,0,0)',
                        }}
                        to={{ opacity: 1, transform: 'translate3d(0px,0,0)' }}
                        config={{ duration: 500, delay: 500 }}
                        reset={{ show }}
                      >
                        {(props) => (
                          <div style={props}>
                            <img
                              src={Mockup3}
                              alt="Mockup3"
                              style={{
                                width: '60%',
                                position: 'absolute',
                                top: '110px',
                                left: '100px',
                              }}
                            />
                          </div>
                        )}
                      </Spring>

                      <Spring
                        from={{
                          opacity: 0,
                          transform: 'translate3d(100px,0,0)',
                        }}
                        to={{ opacity: 1, transform: 'translate3d(0px,0,0)' }}
                        config={{ duration: 500, delay: 1000 }}
                        reset={{ show }}
                      >
                        {(props) => (
                          <div style={props}>
                            <img
                              src={Mockup1}
                              alt="Mockup1"
                              style={{
                                width: '30%',
                                position: 'absolute',
                                top: '180px',
                                left: '240px',
                              }}
                            />
                          </div>
                        )}
                      </Spring>
                    </div>
                  )}
                  {/** End extra Small */}

                  {/** Start Small  */}
                  {isSmall && (
                    <div style={{ position: 'relative', width: '100%' }}>
                      <Spring
                        from={{
                          opacity: 0,
                          transform: 'translate3d(-200px,0,0)',
                        }}
                        to={{ opacity: 1, transform: 'translate3d(0px,0,0)' }}
                        config={{ duration: 500 }}
                        reset={{ show }}
                      >
                        {(props) => (
                          <div style={props}>
                            <img
                              src={Mockup2}
                              alt="Mockup2"
                              style={{
                                width: '100%',
                                position: 'absolute',
                              }}
                            />
                          </div>
                        )}
                      </Spring>

                      <Spring
                        from={{
                          opacity: 0,
                          transform: 'translate3d(-100px,0,0)',
                        }}
                        to={{ opacity: 1, transform: 'translate3d(0px,0,0)' }}
                        config={{ duration: 500, delay: 500 }}
                        reset={{ show }}
                      >
                        {(props) => (
                          <div style={props}>
                            <img
                              src={Mockup3}
                              alt="Mockup3"
                              style={{
                                width: '60%',
                                position: 'absolute',
                                top: '100px',
                                left: '100px',
                              }}
                            />
                          </div>
                        )}
                      </Spring>

                      <Spring
                        from={{
                          opacity: 0,
                          transform: 'translate3d(100px,0,0)',
                        }}
                        to={{ opacity: 1, transform: 'translate3d(0px,0,0)' }}
                        config={{ duration: 500, delay: 1000 }}
                        reset={{ show }}
                      >
                        {(props) => (
                          <div style={props}>
                            <img
                              src={Mockup1}
                              alt="Mockup1"
                              style={{
                                width: '30%',
                                position: 'absolute',
                                top: '170px',
                                left: '250px',
                              }}
                            />
                          </div>
                        )}
                      </Spring>
                    </div>
                  )}
                  {/** End Small  */}

                  {/** Start Medium  */}
                  {isMedium && (
                    <div style={{ position: 'relative', width: '100%' }}>
                      <Spring
                        from={{
                          opacity: 0,
                          transform: 'translate3d(-200px,0,0)',
                        }}
                        to={{ opacity: 1, transform: 'translate3d(0px,0,0)' }}
                        config={{ duration: 500 }}
                        reset={{ show }}
                      >
                        {(props) => (
                          <div style={props}>
                            <img
                              src={Mockup2}
                              alt="Mockup2"
                              style={{
                                width: '100%',
                                position: 'absolute',
                              }}
                            />
                          </div>
                        )}
                      </Spring>

                      <Spring
                        from={{
                          opacity: 0,
                          transform: 'translate3d(-100px,0,0)',
                        }}
                        to={{ opacity: 1, transform: 'translate3d(0px,0,0)' }}
                        config={{ duration: 500, delay: 500 }}
                        reset={{ show }}
                      >
                        {(props) => (
                          <div style={props}>
                            <img
                              src={Mockup3}
                              alt="Mockup3"
                              style={{
                                width: '60%',
                                position: 'absolute',
                                top: '150px',
                                left: '140px',
                              }}
                            />
                          </div>
                        )}
                      </Spring>

                      <Spring
                        from={{
                          opacity: 0,
                          transform: 'translate3d(100px,0,0)',
                        }}
                        to={{ opacity: 1, transform: 'translate3d(0px,0,0)' }}
                        config={{ duration: 500, delay: 1000 }}
                        reset={{ show }}
                      >
                        {(props) => (
                          <div style={props}>
                            <img
                              src={Mockup1}
                              alt="Mockup1"
                              style={{
                                width: '30%',
                                position: 'absolute',
                                top: '230px',
                                left: '60px',
                              }}
                            />
                          </div>
                        )}
                      </Spring>
                    </div>
                  )}
                  {/** End Medium  */}

                  {/** Start Large  */}
                  {isLarge && (
                    <div style={{ position: 'relative', width: '100%' }}>
                      <Spring
                        from={{
                          opacity: 0,
                          transform: 'translate3d(-200px,0,0)',
                        }}
                        to={{ opacity: 1, transform: 'translate3d(0px,0,0)' }}
                        config={{ duration: 500 }}
                        reset={{ show }}
                      >
                        {(props) => (
                          <div style={props}>
                            <img
                              src={Mockup2}
                              alt="Mockup2"
                              style={{
                                width: '100%',
                                position: 'absolute',
                              }}
                            />
                          </div>
                        )}
                      </Spring>

                      <Spring
                        from={{
                          opacity: 0,
                          transform: 'translate3d(-100px,0,0)',
                        }}
                        to={{ opacity: 1, transform: 'translate3d(0px,0,0)' }}
                        config={{ duration: 500, delay: 500 }}
                        reset={{ show }}
                      >
                        {(props) => (
                          <div style={props}>
                            <img
                              src={Mockup3}
                              alt="Mockup3"
                              style={{
                                width: '60%',
                                position: 'absolute',
                                top: '200px',
                                left: '150px',
                              }}
                            />
                          </div>
                        )}
                      </Spring>
                      <Spring
                        from={{
                          opacity: 0,
                          transform: 'translate3d(100px,0,0)',
                        }}
                        to={{ opacity: 1, transform: 'translate3d(0px,0,0)' }}
                        config={{ duration: 500, delay: 1000 }}
                        reset={{ show }}
                      >
                        {(props) => (
                          <div style={props}>
                            <img
                              src={Mockup1}
                              alt="Mockup1"
                              style={{
                                width: '30%',
                                position: 'absolute',
                                top: '320px',
                                left: '370px',
                              }}
                            />
                          </div>
                        )}
                      </Spring>
                    </div>
                  )}
                  {/** End large */}
                </Grid>

                {isExtra && <div style={{ height: '30vh' }}></div>}
                {/** Content Columne  */}
                <Grid xs={12} sm={6} md={6}>
                  <div data-aos="fade-up-left">
                    {!isExtra && (
                      <div
                        style={{
                          alignItems: 'center',
                        }}
                      >
                        {!isExtra && (
                          <Grid container>
                            <Grid item xs={12} style={{ marginBottom: '6em' }}>
                              <div
                                style={{
                                  textAlign: 'center',
                                }}
                              >
                                <div className={classes.divider}></div>
                                <span className={classes.titleHeadline}>
                                  Compatible in all devices
                                </span>
                              </div>
                            </Grid>

                            <Grid item xs={2}>
                              <div style={{ textAlign: 'left' }}>
                                <DevicesOtherSharpIcon
                                  className={classes.icon}
                                  fontSize="large"
                                />{' '}
                              </div>
                            </Grid>
                            <Grid item xs={10} style={{ paddingLeft: '2em' }}>
                              <div style={{ textAlign: 'left' }}>
                                <span className={classes.title}>
                                  Ready to go <b>mobile</b>
                                </span>
                                <br />
                                All device is compatible, mobile app version is
                                coming soon.
                              </div>
                            </Grid>

                            <Grid item xs={2} style={{ marginTop: '2.5em' }}>
                              <div style={{ textAlign: 'left' }}>
                                <PhonelinkSetupIcon
                                  className={classes.icon}
                                  fontSize="large"
                                />{' '}
                              </div>
                            </Grid>

                            <Grid
                              item
                              xs={10}
                              style={{ paddingLeft: '2em', marginTop: '2.5em' }}
                            >
                              <div style={{ textAlign: 'left' }}>
                                <span className={classes.title}>
                                  Personal <b>Customizable</b>
                                </span>
                                <br />
                                Deep customization for best experience.
                              </div>
                            </Grid>
                          </Grid>
                        )}
                      </div>
                    )}

                    {isExtra && (
                      <div
                        style={{
                          alignItems: 'center',
                        }}
                      >
                        {isExtra && (
                          <Grid container>
                            <Grid item xs={12} style={{ marginBottom: '3em' }}>
                              <div
                                style={{
                                  textAlign: 'center',
                                }}
                              >
                                <div className={classes.dividerSmall}></div>
                                <span className={classes.titleHeadlineSmall}>
                                  Compatible in all devices
                                </span>
                              </div>
                            </Grid>

                            <Grid item xs={2}>
                              <div style={{ textAlign: 'left' }}>
                                <DevicesOtherSharpIcon
                                  className={classes.iconSmall}
                                  fontSize="large"
                                />{' '}
                              </div>
                            </Grid>
                            <Grid item xs={10} style={{ paddingLeft: '1em' }}>
                              <div style={{ textAlign: 'left' }}>
                                <span className={classes.titleSmall}>
                                  Ready to go <b>mobile</b>
                                </span>
                                <br />
                                All device is compatible, mobile app version is
                                coming soon.
                              </div>
                            </Grid>

                            <Grid item xs={2} style={{ marginTop: '1.5em' }}>
                              <div style={{ textAlign: 'left' }}>
                                <PhonelinkSetupIcon
                                  className={classes.iconSmall}
                                  fontSize="large"
                                />{' '}
                              </div>
                            </Grid>

                            <Grid
                              item
                              xs={10}
                              style={{ paddingLeft: '1em', marginTop: '1.5em' }}
                            >
                              <div style={{ textAlign: 'left' }}>
                                <span className={classes.titleSmall}>
                                  Personal <b>Customizable</b>
                                </span>
                                <br />
                                Deep customization for best experience.
                              </div>
                            </Grid>
                          </Grid>
                        )}
                      </div>
                    )}
                  </div>
                </Grid>
              </Grid>
            </MuiThemeProvider>
          )}
        </Spring>
      )}
    </VisibilitySensor>
  );
};

export default Part6;
