import React, { useState } from 'react';

/** Redux */
import { connect } from 'react-redux';
import { addEducation } from '../../../action/profile';
import { toast } from 'react-toastify';

/** Material UI */
import { makeStyles } from '@material-ui/core/styles';
import { createMuiTheme } from '@material-ui/core/styles';
import { ThemeProvider } from '@material-ui/styles';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import InputAdornment from '@material-ui/core/InputAdornment';
import Button from '../../Components/components/CustomButtons/Button';
import FormControl from '@material-ui/core/FormControl';

/** Material UI Icon */
import AccountBalanceIcon from '@material-ui/icons/AccountBalance';
import CalendarTodayIcon from '@material-ui/icons/CalendarToday';

/** Spring */
import { Spring } from 'react-spring/renderprops';

/** React External Component */
import DatePicker from 'react-datepicker';
import Calendar from 'react-calendar';
import 'react-datepicker/dist/react-datepicker.css';
import 'react-calendar/dist/Calendar.css';

const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#428bca',
    },
    secondary: {
      main: '#111',
    },
    success: {
      main: '#5cb85c',
    },
    danger: {
      main: '#d9534f',
    },
  },
});

const useStyles = makeStyles((theme) => ({
  button: {
    width: '35%',
    margin: 'auto',
  },
  itemTitle: {
    fontSize: '0.85em',
    color: '#428bca',
    fontWeight: '300',
  },
  marginTop: {
    marginTop: '1.5em',
  },
  calenderTitle: {
    fontWeight: '350',
    fontSize: '1.15em',
    marginTop: '1.15em',
    marginEnd: '1.15em',
  },
}));

const InitEducation = ({ addEducation }) => {
  const classes = useStyles();
  const [formData, setFormData] = useState({
    school: null,
    degree: null,
    current: false,
    gpa: null,
  });
  const [startDate, setStartDate] = useState(new Date());
  const [endDate, setEndDate] = useState(new Date());
  const { school, degree, current, gpa } = formData;

  /**
   * Submit
   */
  const onSubmit = (event) => {
    event.preventDefault();
    if (!school || !degree || !gpa) {
      for (const property in formData) {
        if (!formData[property]) {
          (function () {
            toast.error(`Please fill in "${property}" fields !`);
          })();
        }
      }
      return;
    }

    const postForm = {
      school,
      degree,
      current,
      gpa,
      from: startDate,
      to: endDate,
    };

    const sample = {
      school: 'University of alberta',
      from: '2006-2-10',
      to: '2010-2-10',
      degree: 'Software System',
      current: false,
    };

    addEducation(postForm);

    setFormData({
      school: '',
      degree: '',
      current: false,
      gpa: 0,
    });
  };

  /**
   * Onchange
   */
  const onChange = (event) => {
    event.preventDefault();
    setFormData({
      ...formData,
      [event.target.name]: event.target.value,
    });
  };

  return (
    <React.Fragment>
      <ThemeProvider theme={theme}>
        <Typography
          color="primary"
          variant="h6"
          align="center"
          gutterBottom
          style={{
            fontFamily: 'Quicksand',
          }}
        >
          <AccountBalanceIcon color="primary" /> Edit your education
        </Typography>
        <form
          onSubmit={(event) => {
            onSubmit(event);
          }}
        >
          <Grid container spacing={3}>
            <Grid item xs={12} md={6}>
              <FormControl fullWidth>
                <TextField
                  required
                  id="school"
                  label="School"
                  name="school"
                  autoComplete="school"
                  fullWidth
                  onChange={onChange}
                  value={school}
                  InputProps={{
                    startAdornment: (
                      <InputAdornment position="start" color="primary">
                        <i
                          className="fas fa-school"
                          style={{ color: '#428bca', fontSize: '1.15em' }}
                        ></i>
                      </InputAdornment>
                    ),
                  }}
                />
                <TextField
                  required
                  id="degree"
                  label="Degree"
                  name="degree"
                  autoComplete="degree"
                  fullWidth
                  onChange={onChange}
                  value={degree}
                  InputProps={{
                    startAdornment: (
                      <InputAdornment position="start" color="primary">
                        <i
                          className="fas fa-user-graduate"
                          style={{ color: '#428bca', fontSize: '1.15em' }}
                        ></i>
                      </InputAdornment>
                    ),
                  }}
                />
              </FormControl>
            </Grid>
            <Grid item xs={12} md={6}>
              <FormControl fullWidth>
                <TextField
                  required
                  id="gpa"
                  label="Gpa"
                  name="gpa"
                  autoComplete="gpa"
                  fullWidth
                  onChange={onChange}
                  value={gpa}
                  type="number"
                  inputProps={{ step: '0.01' }}
                  InputProps={{
                    step: '0.01',
                    startAdornment: (
                      <InputAdornment position="start" color="primary">
                        <i
                          className="fas fa-file-excel"
                          style={{ color: '#428bca', fontSize: '1.15em' }}
                        ></i>
                      </InputAdornment>
                    ),
                  }}
                />
                <FormControlLabel
                  spacing={theme}
                  control={
                    <Checkbox
                      checked={current}
                      onChange={(event) =>
                        setFormData({
                          ...formData,
                          current: !current,
                        })
                      }
                      color="primary"
                    />
                  }
                  label="Are you graduated yet?"
                />
              </FormControl>
            </Grid>
            <Grid item xs={12} md={6}>
              <FormControl fullWidth>
                <Typography
                  color="primary"
                  variant="h6"
                  align="center"
                  className={classes.calenderTitle}
                >
                  <CalendarTodayIcon color="primary" />
                  Started date
                </Typography>
                <Calendar onChange={(date) => setStartDate(date)} />
              </FormControl>
            </Grid>
            <Grid item xs={12} md={6}>
              <FormControl fullWidth>
                <Typography
                  color="primary"
                  variant="h6"
                  align="center"
                  className={classes.calenderTitle}
                >
                  <CalendarTodayIcon color="primary" />
                  Ended date
                </Typography>
                <Calendar onChange={(date) => setEndDate(date)} />
              </FormControl>
            </Grid>
            <Button
              variant="contained"
              color="primary"
              round
              className={classes.button}
              type="submit"
            >
              Update user information
            </Button>
          </Grid>
        </form>
      </ThemeProvider>
    </React.Fragment>
  );
};

export default connect(null, { addEducation })(InitEducation);
