import React from 'react';

/** UI Customize Component */
import Card from '../../../Components/components/Card/Card.js';
import CardHeader from '../../../Components/components/Card/CardHeader.js';
import ChartistGraph from 'react-chartist';
import CardBody from '../../../Components/components/Card/CardBody.js';
import CardFooter from '../../../Components/components/Card/CardFooter.js';
import AccessTime from '@material-ui/icons/AccessTime';

/** Material UI */
import { makeStyles } from '@material-ui/core/styles';
/** Material Icon */
import ArrowUpward from '@material-ui/icons/ArrowUpward';

import {
  whiteColor,
  primaryColor,
  grayColor,
  hexToRgb,
} from '../../../Components/assets/jss/material-dashboard-react.js';

const dashboardStyle = {
  successText: {
    color: primaryColor[0],
  },
  upArrowCardCategory: {
    width: '16px',
    height: '16px',
  },
  stats: {
    color: whiteColor[0],
    display: 'inline-flex',
    fontSize: '12px',
    lineHeight: '22px',
    fontWeight: '600',
    '& svg': {
      top: '4px',
      width: '16px',
      height: '16px',
      position: 'relative',
      marginRight: '3px',
      marginLeft: '3px',
    },
    '& .fab,& .fas,& .far,& .fal,& .material-icons': {
      top: '4px',
      fontSize: '16px',
      position: 'relative',
      marginRight: '3px',
      marginLeft: '3px',
    },
  },
  cardCategory: {
    color: whiteColor[0],
    margin: '0',
    fontSize: '14px',
    marginTop: '0',
    paddingTop: '10px',
    marginBottom: '0',
    fontWeight: '600',
  },
  cardCategoryWhite: {
    color: 'rgba(' + hexToRgb(whiteColor) + ',.62)',
    margin: '0',
    fontSize: '14px',
    fontWeight: '500',
    marginTop: '0',
    marginBottom: '0',
  },
  cardTitle: {
    color: whiteColor[0],
    marginTop: '0px',
    minHeight: 'auto',
    fontWeight: '300',
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: '3px',
    textDecoration: 'none',
    '& small': {
      color: whiteColor[1],
      fontWeight: '400',
      lineHeight: '1',
    },
  },
  cardTitleWhite: {
    color: whiteColor,
    marginTop: '0px',
    minHeight: 'auto',
    fontWeight: '300',
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: '3px',
    textDecoration: 'none',
    '& small': {
      color: grayColor[1],
      fontWeight: '400',
      lineHeight: '1',
    },
  },
  graphStyle: {
    '& .ct-label': { color: '#fff' },
    '& .ct-line': {
      stroke: '#fff',
      strokeWidth: '2px',
    },
    '& .ct-point': {
      stroke: '#fff',
    },
    '& .ct-grids line': {
      stroke: '#fff',
    },
  },
};
const useStyles = makeStyles(dashboardStyle);
var Chartist = require('chartist');
const LineGraph = () => {
  const classes = useStyles();
  var delays = 100,
    durations = 1500;
  const dailySalesChart = {
    data: {
      labels: ['M', 'T', 'W', 'T', 'F', 'S', 'S'],
      series: [[12, 17, 7, 17, 23, 18, 38]],
    },
    options: {
      lineSmooth: Chartist.Interpolation.cardinal({
        tension: 0,
      }),
      low: 0,
      high: 50, // creative tim: we recommend you to set the high sa the biggest value + something for a better look
      chartPadding: {
        top: 0,
        right: 0,
        bottom: 0,
        left: 0,
      },
    },
    // for animation
    animation: {
      draw: function (data) {
        if (data.type === 'line' || data.type === 'area') {
          data.element.animate({
            d: {
              begin: 600,
              dur: 700,
              from: data.path
                .clone()
                .scale(1, 0)
                .translate(0, data.chartRect.height())
                .stringify(),
              to: data.path.clone().stringify(),
              easing: Chartist.Svg.Easing.easeOutQuint,
            },
          });
        } else if (data.type === 'point') {
          data.element.animate({
            opacity: {
              begin: (data.index + 1) * delays,
              dur: durations,
              from: 0,
              to: 1,
              easing: 'ease',
            },
          });
        }
      },
    },
  };

  return (
    <Card chart>
      <CardHeader color="primary">
        <ChartistGraph
          data={dailySalesChart.data}
          type="Line"
          options={dailySalesChart.options}
          listener={dailySalesChart.animation}
          className={classes.graphStyle}
        />
      </CardHeader>
      <CardBody>
        <h4 className={classes.cardTitle}>Follower Tracking</h4>
        <p className={classes.cardCategory}>
          <span className={classes.successText}>
            <ArrowUpward className={classes.upArrowCardCategory} /> 55%
          </span>{' '}
          increase in today follwer.
        </p>
      </CardBody>
      <CardFooter chart>
        <div className={classes.stats}>
          <AccessTime /> updated 4 minutes ago
        </div>
      </CardFooter>
    </Card>
  );
};

export default LineGraph;
