import {
  PROFILE_ERROR,
  GET_PROFILE,
  GET_PROFILES,
  CLEAR_PROFILE,
  UPDATE_PROFILE,
  GET_REPOS,
  SEARCH_PROFILE,
} from '../action/type';

const initialState = {
  profile: null,
  profiles: [],
  loading: true,
  error: {},
  repos: [],
};

export default (state = initialState, { type, payload }) => {
  switch (type) {
    case GET_PROFILE:
    case UPDATE_PROFILE:
      return { ...state, profile: payload, loading: false };
    case PROFILE_ERROR:
      return { ...state, error: payload, loading: false };
    case CLEAR_PROFILE:
      return { ...state, profile: null, repos: [], loading: false };
    case GET_PROFILES:
      return { ...state, profiles: payload, loading: false };
    case GET_REPOS:
      return { ...state, repos: payload, loading: false };
    case SEARCH_PROFILE:
      let search = payload;
      let re = new RegExp(search, 'i');
      return {
        ...state,
        profiles: state.profiles.filter((profile) =>
          re.test(profile.user.name.replace(/\s/g, ''))
        ),
      };
    default:
      return state;
  }
};
