import React from 'react';
import { connect } from 'react-redux';
import FileImage from '../../action/utils/getImage';

/** Material UI  */
import { makeStyles } from '@material-ui/core/styles';
import { createMuiTheme } from '@material-ui/core/styles';
import { ThemeProvider } from '@material-ui/styles';
import Grid from '@material-ui/core/Grid';
import Chip from '@material-ui/core/Chip';

/** Icon */
import CodeIcon from '@material-ui/icons/Code';
import Avatar from '@material-ui/core/Avatar';
import BusinessIcon from '@material-ui/icons/Business';
import LocationOnIcon from '@material-ui/icons/LocationOn';
import PeopleIcon from '@material-ui/icons/People';

import { setCurrent, clearCurrent } from '../../action/job';

const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#428bca',
    },
    secondary: {
      main: '#111',
    },
    success: {
      main: '#5cb85c',
    },
    danger: {
      main: '#d9534f',
    },
  },
});

const useStyles = makeStyles((theme) => ({
  item: {
    padding: '1em',
    '&:hover': {
      background: 'rgba(0, 136, 240, 0.1)',
      borderLeft: '2px solid #428bca',
      cursor: 'pointer',
    },
  },
}));

const JobItem = ({ item, setCurrent }) => {
  const classes = useStyles();

  const {
    jobTitle,
    image,
    company,
    location,
    remote,
    salary,
    userInfo,
    stack,
    _id,
  } = item;
  const { companySize } = userInfo;

  const getRemote = (remote) => {
    if (remote === 'Temporalily due to Covid 19') {
      return (
        <span
          style={{
            color: '#0275d8',
            fontWeight: '750',
          }}
        >
          <i className="fas fa-user-md"></i> Temporalily due to Covid 19{' '}
        </span>
      );
    }

    if (remote === 'Remote Job') {
      return (
        <span
          style={{
            color: '#5bc0de',
            fontWeight: '750',
          }}
        >
          <i className="fas fa-globe-europe"></i> Remote Job{' '}
        </span>
      );
    }

    if (remote === 'Visa Sponsors') {
      return (
        <span
          style={{
            color: '#d9534f',
            fontWeight: '750',
          }}
        >
          <i className="fas fa-truck"></i> Visa Sponsors{' '}
        </span>
      );
    }
  };

  return (
    <ThemeProvider theme={theme}>
      <Grid
        container
        className={classes.item}
        onClick={() => {
          setCurrent(_id);
        }}
      >
        <Grid item xs={2}>
          {image && FileImage(image)}
        </Grid>
        <Grid
          item
          xs={10}
          style={{
            paddingLeft: '1em',
            fontWeight: '650',
            fontSize: '0.65em',
          }}
        >
          <h4
            style={{
              fontWeight: '700',
              color: '#0275d8',
            }}
          >
            {jobTitle}
          </h4>
          <h6
            style={{
              fontWeight: '750',
            }}
          >
            <BusinessIcon style={{ color: '#f0ad4e' }} /> {company}
            <br />
            <LocationOnIcon style={{ color: '#d9534f' }} /> {location}
            <br />
            <PeopleIcon color="primary" /> {`${companySize}`}
            {'  '}
            {getRemote(remote)}
          </h6>

          <span
            style={{
              fontWeight: '700',
              fontSize: '1.5em',
            }}
          >
            {`${salary.minimum} to ${salary.maximum} ${salary.currency} /${salary.paymentCircle}`}
          </span>
          <br />
          {stack &&
            stack.map((item, index) => (
              <Chip
                key={index}
                size="small"
                avatar={
                  <Avatar>
                    <CodeIcon fontSize="small" />
                  </Avatar>
                }
                label={item}
                color="primary"
                style={{ marginRight: '0.1em' }}
              />
            ))}
        </Grid>
      </Grid>
      {/** Divider */}
      <div
        style={{
          background: '#ccc',
          height: '1px',
          width: '100%',
        }}
      ></div>
    </ThemeProvider>
  );
};

export default connect(null, { setCurrent, clearCurrent })(JobItem);
