import React from 'react';

/** Material UI Icon */
import EmojiObjectsIcon from '@material-ui/icons/EmojiObjects';

/** UI Customize Component */
import Card from '../../../Components/components/Card/Card.js';
import { makeStyles } from '@material-ui/core/styles';
import { createMuiTheme, ThemeProvider } from '@material-ui/core/styles';

/** Spring */
import { Spring } from 'react-spring/renderprops';

const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#428bca',
    },
    secondary: {
      main: '#111',
    },
    success: {
      main: '#5cb85c',
    },
    danger: {
      main: '#d9534f',
    },
  },
});

const useStyles = makeStyles((theme) => ({
  hint: {
    position: 'fixed',
    bottom: '0',
    right: '0',
    boxShadow: '2px 2px 5px 2px rgba(0,0,0,0.35)',
    border: '1px #5B5B5B solid',
    padding: '1em',
    width: '30%',
    marginRight: '1em',
  },
}));

const Hint1 = () => {
  const classes = useStyles();
  return (
    <Spring
      from={{ opacity: 0 }}
      to={{ opacity: 1 }}
      config={{ duration: 2500 }}
    >
      {(props) => (
        <div style={props}>
          <ThemeProvider theme={theme}>
            <Card className={classes.hint}>
              <h5 style={{ color: '#428bca' }}>
                <EmojiObjectsIcon /> User Information
              </h5>
              <h6 style={{ color: '#428bca' }}>
                We collect some basic information about you for employment
                reference, including:
              </h6>
              <ul
                style={{
                  color: '#428bca',
                }}
              >
                <li>Your current employer or company name.</li>
                <li>Your location for job reference.</li>
                <li>Your personal website (optional).</li>
                <li>Your github username (optional).</li>
                <li>Your tech stack and your favorite programming language.</li>
                <li>Your job title.</li>
                <li>Your social media link.</li>
                <li>A short biography about you.</li>
              </ul>
            </Card>
          </ThemeProvider>
        </div>
      )}
    </Spring>
  );
};

export default Hint1;
