import React from 'react';
import Drawer from '@material-ui/core/Drawer';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import LogImg from '../../img/300Logo.png';
import { useLocation } from 'react-router-dom';
import { Link } from 'react-router-dom';
import { makeStyles } from '@material-ui/core/styles';

/** Material Components */
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import Tooltip from '@material-ui/core/Tooltip';

/** Material Icon */
import DynamicFeedIcon from '@material-ui/icons/DynamicFeed';
import DashboardIcon from '@material-ui/icons/Dashboard';
import EditIcon from '@material-ui/icons/Edit';
import SupervisedUserCircleSharpIcon from '@material-ui/icons/SupervisedUserCircleSharp';
import CodeSharpIcon from '@material-ui/icons/CodeSharp';
import AccountBoxSharpIcon from '@material-ui/icons/AccountBoxSharp';
import WorkSharpIcon from '@material-ui/icons/WorkSharp';
import SchoolIcon from '@material-ui/icons/School';
import WorkIcon from '@material-ui/icons/Work';
import GroupAddIcon from '@material-ui/icons/GroupAdd';
import BallotIcon from '@material-ui/icons/Ballot';

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    width: 'auto',
  },
  title: {
    position: 'relative',
    left: '27%',
    color: '#fff',
    fontFamily: "'Quicksand', sans-serif",
  },
  img: {
    position: 'relative',
    left: '27%',
  },
  drawerPaperLarge: {
    width: '16.67%',
    backgroundColor: '#428bca',
    color: '#fff',
  },

  drawerPaperSmall: {
    width: '8.33%',
    backgroundColor: '#428bca',
    color: '#fff',
  },
  content: {
    flexGrow: 1,
  },
  ListItem: {
    color: '#fff',
  },
  Divider: {
    backgroundColor: '#fff',
  },

  toolbar: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'flex-end',
    padding: theme.spacing(0, 1),
    // necessary for content to be below app bar
    ...theme.mixins.toolbar,
  },
}));

const SmallSideBar = () => {
  const classes = useStyles();
  const [anchorEl, setAnchorEl] = React.useState(null);

  React.useEffect(() => {}, [window.outerWidth]);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  let location = useLocation();
  switch (location.pathname) {
    case '/':
    case '/login':
    case '/register':
      return <div>{false}</div>;
  }

  return (
    <div className={classes.root}>
      <Drawer
        variant="permanent"
        classes={{
          paper: classes.drawerPaperSmall,
        }}
        anchor="left"
      >
        <img src={LogImg} width="30" className={classes.img} />
        <Divider className={classes.Divider} color="primary" />
        <List>
          {/** Dashboard */}

          {location.pathname !== '/init' ? (
            <Link
              to="/dashboard"
              style={{ textDecoration: 'none', color: '#fff' }}
            >
              <ListItem button>
                <ListItemIcon className={classes.ListItem}>
                  <DashboardIcon />
                </ListItemIcon>
              </ListItem>
            </Link>
          ) : (
            <Tooltip title="You have to finnish your profile first">
              <ListItem button>
                <ListItemIcon className={classes.ListItem}>
                  <DashboardIcon />
                </ListItemIcon>
              </ListItem>
            </Tooltip>
          )}

          {/** Edit */}
          {location.pathname !== '/init' ? (
            <ListItem button onClick={handleClick}>
              <ListItemIcon className={classes.ListItem}>
                <EditIcon />
              </ListItemIcon>
            </ListItem>
          ) : (
            <Tooltip title="You have to finnish your profile first">
              <ListItem button>
                <ListItemIcon className={classes.ListItem}>
                  <EditIcon />
                </ListItemIcon>
              </ListItem>
            </Tooltip>
          )}

          {/** Menu */}
          <Menu
            id="simple-menu"
            anchorEl={anchorEl}
            keepMounted
            open={Boolean(anchorEl)}
            onClose={handleClose}
          >
            {/** Profile */}
            <Link
              to="/edit-profile"
              style={{ textDecoration: 'none', color: '#428bca' }}
            >
              <MenuItem>
                <AccountBoxSharpIcon />
              </MenuItem>
            </Link>

            {/** Experience */}
            <Link
              to="/edit-experience"
              style={{ textDecoration: 'none', color: '#428bca' }}
            >
              <MenuItem>
                <WorkSharpIcon />
              </MenuItem>
            </Link>

            {/** Education */}
            <Link
              to="/edit-education"
              style={{ textDecoration: 'none', color: '#428bca' }}
            >
              <MenuItem>
                <SchoolIcon />
              </MenuItem>
            </Link>
          </Menu>

          {/** Feed */}
          <Link to="/posts" style={{ textDecoration: 'none', color: '#fff' }}>
            <ListItem button>
              <ListItemIcon className={classes.ListItem}>
                <DynamicFeedIcon />
              </ListItemIcon>
            </ListItem>
          </Link>

          {/** People */}
          <Link to="/dev" style={{ textDecoration: 'none', color: '#fff' }}>
            <ListItem button>
              <ListItemIcon className={classes.ListItem}>
                <SupervisedUserCircleSharpIcon />
              </ListItemIcon>
            </ListItem>
          </Link>

          {/** Code Snippet */}
          <Link to="/code" style={{ textDecoration: 'none', color: '#fff' }}>
            <ListItem button>
              <ListItemIcon className={classes.ListItem}>
                <CodeSharpIcon />
              </ListItemIcon>
            </ListItem>
          </Link>

          {/** Job */}
          <Link to="/job" style={{ textDecoration: 'none', color: '#428bca' }}>
            <ListItem button>
              <ListItemIcon className={classes.ListItem}>
                <WorkIcon />
              </ListItemIcon>
            </ListItem>
          </Link>

          {/** Hire */}
          <Link to="/hire" style={{ textDecoration: 'none', color: '#fff' }}>
            <ListItem button>
              <ListItemIcon className={classes.ListItem}>
                <GroupAddIcon />
              </ListItemIcon>
            </ListItem>
          </Link>

          {/** Mangement  */}
          <Link
            to="/management"
            style={{ textDecoration: 'none', color: '#fff' }}
          >
            <ListItem button>
              <ListItemIcon className={classes.ListItem}>
                <BallotIcon />
              </ListItemIcon>
            </ListItem>
          </Link>
        </List>
      </Drawer>
    </div>
  );
};

export default SmallSideBar;
