import React from 'react';

/** UI Customize Component */
import Card from '../../../Components/components/Card/Card.js';
import CardHeader from '../../../Components/components/Card/CardHeader.js';
import ChartistGraph from 'react-chartist';
import CardBody from '../../../Components/components/Card/CardBody.js';
import CardFooter from '../../../Components/components/Card/CardFooter.js';
import AccessTime from '@material-ui/icons/AccessTime';

/** Material UI */
import { makeStyles } from '@material-ui/core/styles';

import {
  whiteColor,
  primaryColor,
  grayColor,
  hexToRgb,
} from '../../../Components/assets/jss/material-dashboard-react.js';

const dashboardStyle = {
  successText: {
    color: primaryColor[0],
  },
  upArrowCardCategory: {
    width: '16px',
    height: '16px',
  },
  stats: {
    color: whiteColor[0],
    display: 'inline-flex',
    fontSize: '12px',
    fontWeight: '600',
    lineHeight: '22px',
    '& svg': {
      top: '4px',
      width: '16px',
      height: '16px',
      position: 'relative',
      marginRight: '3px',
      marginLeft: '3px',
    },
    '& .fab,& .fas,& .far,& .fal,& .material-icons': {
      top: '4px',
      fontSize: '16px',
      position: 'relative',
      marginRight: '3px',
      marginLeft: '3px',
    },
  },
  cardCategory: {
    color: whiteColor[0],
    margin: '0',
    fontSize: '14px',
    marginTop: '0',
    paddingTop: '10px',
    marginBottom: '0',
    fontWeight: '600',
  },
  cardCategoryWhite: {
    color: 'rgba(' + hexToRgb(whiteColor) + ',.62)',
    margin: '0',
    fontSize: '14px',
    marginTop: '0',
    marginBottom: '0',
  },
  cardTitle: {
    color: whiteColor[0],
    marginTop: '0px',
    minHeight: 'auto',
    fontWeight: '300',
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: '3px',
    textDecoration: 'none',
    '& small': {
      color: whiteColor[1],
      fontWeight: '400',
      lineHeight: '1',
    },
  },
  cardTitleWhite: {
    color: whiteColor,
    marginTop: '0px',
    minHeight: 'auto',
    fontWeight: '300',
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: '3px',
    textDecoration: 'none',
    '& small': {
      color: grayColor[1],
      fontWeight: '400',
      lineHeight: '1',
    },
  },
  graphStyle: {
    '& .ct-label': { color: '#fff' },

    '& .ct-point': {
      stroke: '#fff',
    },
    '& .ct-series-a .ct-bar': {
      stroke: '#fff',
    },
    '& .ct-series-b .ct-bar': {
      stroke: '#5bc0de',
    },
    '& .ct-series-c .ct-bar': {
      stroke: '#5cb85c',
    },
  },
};
const useStyles = makeStyles(dashboardStyle);
var Chartist = require('chartist');

const BarGraphClose = () => {
  const classes = useStyles();
  var delays = 100,
    durations = 1500;

  const emailsSubscriptionChart = {
    data: {
      labels: [
        'Jan',
        'Feb',
        'Mar',
        'Apr',
        'May',
        'Jun',
        'Jul',
        'Aug',
        'Sep',
        'Oct',
        'Nov',
        'Dec',
      ],
      series: [
        [5, 4, 3, 7, 5, 10, 3, 4, 8, 10, 6, 8],
        [3, 2, 9, 5, 4, 6, 4, 6, 7, 8, 7, 4],
      ],
    },

    options: {
      lineSmooth: Chartist.Interpolation.cardinal({
        tension: 0,
      }),
      showPoint: false,
      showArea: true,
      chartPadding: {
        top: 0,
        right: 0,
        bottom: 0,
        left: 0,
      },
      seriesBarDistance: 10,

      axisX: {
        labelInterpolationFnc: function (value, index) {
          return index % 2 === 0 ? value : null;
        },
      },
    },
    // for animation
    animation: {
      draw: function (data) {
        if (data.type === 'bar') {
          data.element.animate({
            opacity: {
              begin: (data.index + 1) * delays,
              dur: durations,
              from: 0,
              to: 1,
              easing: 'ease',
            },
          });
        }
      },
    },

    responsiveOptions: [
      [
        'screen and (max-width: 640px)',
        {
          seriesBarDistance: 5,
          axisX: {
            labelInterpolationFnc: function (value) {
              return value[0];
            },
          },
        },
      ],
    ],
  };

  return (
    <Card chart>
      <CardHeader color="primary">
        <ChartistGraph
          className={classes.graphStyle}
          data={emailsSubscriptionChart.data}
          type="Bar"
          options={emailsSubscriptionChart.options}
          responsiveOptions={emailsSubscriptionChart.responsiveOptions}
          listener={emailsSubscriptionChart.animation}
        />
      </CardHeader>
      <CardBody>
        <h4 className={classes.cardTitle}>Sale Yearly</h4>
        <p className={classes.cardCategory}>Last Year Performance</p>
      </CardBody>
      <CardFooter chart>
        <div className={classes.stats}>
          <AccessTime /> campaign sent 1 day ago
        </div>
      </CardFooter>
    </Card>
  );
};

export default BarGraphClose;
