import React from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';

/** Material UI  */
import { makeStyles } from '@material-ui/core/styles';
import { createMuiTheme } from '@material-ui/core/styles';
import { ThemeProvider } from '@material-ui/styles';
import Paper from '@material-ui/core/Paper';
import Stepper from '@material-ui/core/Stepper';
import Step from '@material-ui/core/Step';
import StepLabel from '@material-ui/core/StepLabel';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import Avatar from '@material-ui/core/Avatar';
import Grid from '@material-ui/core/Grid';

/** Material UI Icon */
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos';
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import DoneAllIcon from '@material-ui/icons/DoneAll';

/** Depend Component */
import Step1 from './Step/Step1';
import Step2 from './Step/Step2';
import Step3 from './Step/Step3';
import Step4 from './Step/Step4';
import Step5 from './Step/Step5';
import Step6 from './Step/Step6';

/** Spring */
import { Spring } from 'react-spring/renderprops';

/** Reponsive  */
import { useMediaQuery } from 'react-responsive';

const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#428bca',
    },
    secondary: {
      main: '#111',
    },
  },
});

const useStyles = makeStyles((theme) => ({
  layout: {
    width: 'auto',
    marginLeft: theme.spacing(1),
    [theme.breakpoints.up(600 + theme.spacing(2) * 2)]: {
      width: '95%',
      height: 'fit-content',
      marginLeft: '2em',
      marginRight: '2em',
      marginBottom: '2em',
      marginTop: '2em',
    },
    boxShadow: '2px 2px 5px 2px rgba(0,0,0,0.35)',
    border: '1px #5B5B5B solid',
    borderRadius: '0.75em',
  },
  paper: {
    marginTop: theme.spacing(3),
    marginBottom: theme.spacing(3),
    padding: theme.spacing(2),
  },
  stepper: {
    padding: theme.spacing(1, 0, 1),
  },
  buttons: {
    display: 'flex',
    justifyContent: 'flex-end',
  },
  button: {
    marginTop: theme.spacing(3),
    marginLeft: theme.spacing(1),
  },
  avatar: {
    margin: 'auto',
    background: '#fff',
    color: '#428bca',
  },
}));

const steps = [
  'Job Posting',
  'Get started',
  'Overview',
  'Job Details',
  'Qualification',
  'Your Company',
  'Preview',
];

function getStepContent(step) {
  switch (step) {
    case 1:
      return <Step1 />;
    case 2:
      return <Step2 />;
    case 3:
      return <Step3 />;
    case 4:
      return <Step4 />;
    case 5:
      return <Step5 />;
    case 6:
      return <Step6 />;
    default:
      throw new Error('Unknown step');
  }
}

function Copyright() {
  /** Responsive */
  const isSmall = useMediaQuery({ minWidth: 0, maxWidth: 599 });

  return (
    <React.Fragment>
      {!isSmall && [
        <Typography
          variant="h6"
          color="textSecondary"
          align="center"
          style={{
            marginBottom: '1em',
          }}
          key={1}
        >
          By using <span style={{ color: '#428bca' }}>Devmeet.com</span> , you
          agree that this job will be posted and applications will be processed
          in accordance with
          <span style={{ color: '#428bca' }}>
            Devmeet's Cookie Policy, Privacy Policy and Terms of Service.
          </span>
        </Typography>,
        <Typography
          variant="h5"
          color="textSecondary"
          align="center"
          style={{
            marginBottom: '1em',
          }}
          key={2}
        >
          {'Copyright © '}
          <a color="primary" href="https://material-ui.com/">
            devmeet.com
          </a>{' '}
          {new Date().getFullYear()}
          {'.'}
        </Typography>,
      ]}

      {isSmall && [
        <Typography
          variant="body1"
          color="textSecondary"
          align="center"
          style={{
            padding: '2em',
          }}
          key={1}
        >
          By using <span style={{ color: '#428bca' }}>Devmeet.com</span> , you
          agree that this job will be posted and applications will be processed
          in accordance with
          <span style={{ color: '#428bca' }}>
            Devmeet's Cookie Policy, Privacy Policy and Terms of Service.
          </span>
        </Typography>,
        <Typography
          variant="body1"
          color="textSecondary"
          align="center"
          style={{
            marginBottom: '1em',
          }}
          key={2}
        >
          {'Copyright © '}
          <a color="primary" href="https://material-ui.com/">
            devmeet.com
          </a>{' '}
          {new Date().getFullYear()}
          {'.'}
        </Typography>,
      ]}
    </React.Fragment>
  );
}

export const Hire = () => {
  const classes = useStyles();
  const [activeStep, setActiveStep] = React.useState(1);

  const handleNext = () => {
    setActiveStep(activeStep + 1);
  };

  const handleBack = () => {
    setActiveStep(activeStep - 1);
  };

  return (
    <React.Fragment>
      <ThemeProvider theme={theme}>
        <Grid container>
          <Grid item xs={12}>
            <main className={classes.layout}>
              <Paper className={classes.paper}>
                <div
                  style={{
                    width: '100%',
                    overflow: 'auto',
                  }}
                >
                  <Stepper activeStep={activeStep} className={classes.stepper}>
                    {steps.map((label) => (
                      <Step key={label}>
                        <StepLabel>{label}</StepLabel>
                      </Step>
                    ))}
                  </Stepper>
                </div>
                <React.Fragment>
                  {activeStep === steps.length ? (
                    <React.Fragment>
                      <Grid container>
                        <Grid item xs={false} sm={1} md={2}></Grid>
                        <Grid
                          item
                          xs={12}
                          sm={12}
                          md={8}
                          style={{ padding: '1em' }}
                        >
                          <Avatar
                            className={classes.avatar}
                            style={{ width: 50, height: 50 }}
                          >
                            <DoneAllIcon style={{ fontSize: 50 }} />
                          </Avatar>

                          <Typography
                            variant="h5"
                            gutterBottom
                            style={{
                              fontFamily: 'Quicksand',
                            }}
                          >
                            Your Job Post is completed, now you can manage them
                            at
                            <Link
                              to="/management"
                              style={{ color: '#428bca', marginLeft: '0.5em' }}
                            >
                              Job Post Management
                            </Link>
                          </Typography>
                        </Grid>
                        <Grid item xs={false} sm={1} md={2}></Grid>
                      </Grid>
                    </React.Fragment>
                  ) : (
                    <React.Fragment>
                      <Spring
                        from={{ opacity: 0 }}
                        to={{ opacity: 1 }}
                        config={{ duration: 1000 }}
                      >
                        {(props) => (
                          <div style={props}>{getStepContent(activeStep)}</div>
                        )}
                      </Spring>

                      <div className={classes.buttons}>
                        {activeStep !== 1 && (
                          <Button
                            variant="text"
                            color="primary"
                            onClick={handleBack}
                            className={classes.button}
                            startIcon={<ArrowBackIosIcon />}
                            size="large"
                          >
                            Back
                          </Button>
                        )}
                        <Button
                          variant="text"
                          color="primary"
                          onClick={handleNext}
                          className={classes.button}
                          endIcon={<ArrowForwardIosIcon />}
                          size="large"
                        >
                          {activeStep === steps.length - 1 ? 'Finish' : 'Next'}
                        </Button>
                      </div>
                    </React.Fragment>
                  )}
                </React.Fragment>
              </Paper>
              <Copyright />
            </main>
          </Grid>
        </Grid>
      </ThemeProvider>
    </React.Fragment>
  );
};

const mapStateToProps = (state) => ({});

const mapDispatchToProps = {};

export default connect(mapStateToProps, mapDispatchToProps)(Hire);
