import React, { useEffect, useState, Fragment } from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';

/** Import from Redux */
import { addExperience, delExperience } from '../../../action/profile';
import { connect } from 'react-redux';
import { getCurrentProfile } from '../../../action/profile';
import { loadUser } from '../../../action/auth';

/** External React Component */
import { toast } from 'react-toastify';
import Spinner from '../../layout/Spinner';

/** Material UI */
import { makeStyles } from '@material-ui/core/styles';
import { createMuiTheme, ThemeProvider } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Input from '@material-ui/core/Input';
import InputAdornment from '@material-ui/core/InputAdornment';
import FormControl from '@material-ui/core/FormControl';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import InputLabel from '@material-ui/core/InputLabel';

/** Material UI Icon */
import SchoolIcon from '@material-ui/icons/School';
import DeleteIcon from '@material-ui/icons/Delete';
import CheckIcon from '@material-ui/icons/Check';
import ClearIcon from '@material-ui/icons/Clear';
import WorkIcon from '@material-ui/icons/Work';
import BusinessCenterIcon from '@material-ui/icons/BusinessCenter';
import TextField from '@material-ui/core/TextField';
import EditIcon from '@material-ui/icons/Edit';

/** UI Customize Component */
import Table from '../../Components/components/Table/Table.js';
import Button from '../../Components/components/CustomButtons/Button.js';
import Card from '../../Components/components/Card/Card.js';
import CardHeader from '../../Components/components/Card/CardHeader.js';
import CardBody from '../../Components/components/Card/CardBody.js';
import CardAvatar from '../../Components/components/Card/CardAvatar.js';
import GridItem from '../../Components/components/Grid/GridItem.js';
import GridContainer from '../../Components/components/Grid/GridContainer.js';
import CardFooter from '../../Components/components/Card/CardFooter.js';

/** Spring */
import { Spring } from 'react-spring/renderprops';

/** React External Component */
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';

/** Logo */
import Logo from '../../img/300Logo.png';

/** Utils */
var moment = require('moment');

const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#428bca',
    },
    secondary: {
      main: '#111',
    },
    success: {
      main: '#5cb85c',
    },
    danger: {
      main: '#d9534f',
    },
  },
});

const useStyles = makeStyles((theme) => ({
  spinner: {
    position: 'absolute',
    left: '50%',
    top: '50%',
  },
  table: {
    marginTop: '4em',
    boxShadow: '2px 2px 5px 2px rgba(0,0,0,0.35)',
    padding: '1em',
  },
  icon: {
    cursor: 'pointer',
    color: '#428bca',
    marginTop: '0.25em',
    marginBottom: '0.25em',
  },
  card: {
    marginTop: '4em',
    boxShadow: '2px 2px 5px 2px rgba(0,0,0,0.35)',
    border: '1px #5B5B5B solid',
  },
  content: {
    paddingLeft: '1em',
    paddingRight: '1em',
    paddingTop: '1em',
    background: '#e5e5e5',
  },
  cardTitle: {
    color: '#111',
    marginTop: '0px',
    minHeight: 'auto',
    fontWeight: '300',
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: '3px',
    textDecoration: 'none',
    fontSize: '15px',
  },
  cardCategoryWhite: {
    color: 'rgba(255,255,255,.62)',
    fontSize: '14px',
  },
  cardTitleWhite: {
    color: '#FFFFFF',
    marginTop: '0px',
    minHeight: 'auto',
    fontWeight: '300',
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: '3px',
    textDecoration: 'none',
  },
  margin: {
    margin: theme.spacing(1),
  },
}));

const EditExperience = ({
  profile,
  user,
  loading,
  getCurrentProfile,
  addExperience,
  delExperience,
  loadUser,
}) => {
  useEffect(() => {
    loadUser();
    getCurrentProfile();
  }, []);

  const classes = useStyles();
  const [formData, setFormData] = useState({
    title: null,
    company: null,
    description: '',
    current: false,
  });
  const [startDate, setStartDate] = useState(new Date());
  const [endDate, setEndDate] = useState(new Date());
  const { title, company, description, current } = formData;

  const onChange = (event) => {
    event.preventDefault();
    setFormData({
      ...formData,
      [event.target.name]: event.target.value,
    });
  };

  const onSubmit = (event) => {
    event.preventDefault();

    if (!title || !company || !description) {
      for (const property in formData) {
        if (!formData[property]) {
          (function () {
            toast.error(`Please fill in "${property}" fields !`);
          })();
        }
      }

      return;
    }

    const postForm = {
      title,
      company,
      description,
      current,
      from: startDate,
      to: endDate,
    };

    addExperience(postForm);
  };

  /** Checking on loading if (loading) => show spinner*/
  if (loading) {
    return (
      <div className={classes.spinner}>
        <Spinner color="#0275d8" loading={true} size="50" />
      </div>
    );
  }

  if (profile) {
    const experienceList = profile.experience.map((item) => [
      <img src={`//logo.clearbit.com/${item.company}.com`} width="55" />,
      item.title,
      moment(item.from).format('MMM Y'),
      moment(item.to).format('MMM Y'),
      item.current ? (
        <CheckIcon color="action" />
      ) : (
        <ClearIcon color="danger" />
      ),
      <DeleteIcon
        className={classes.icon}
        onClick={() => {
          delExperience(item._id);
        }}
      />,
    ]);

    return (
      <ThemeProvider theme={theme}>
        <Grid item xs={12} sm={12} md={12} className={classes.content}>
          {/** Dashboard for Education */}
          <Spring
            from={{ opacity: 0 }}
            to={{ opacity: 1 }}
            config={{ duration: 150 }}
          >
            {(props) => (
              <div style={props}>
                <Grid container>
                  <Grid
                    item
                    xs={12}
                    sm={12}
                    md={6}
                    style={{ paddingRight: '0.75em' }}
                  >
                    <form
                      onSubmit={(event) => {
                        onSubmit(event);
                      }}
                    >
                      <Card className={classes.card}>
                        <CardHeader color="primary">
                          <h4 className={classes.cardTitleWhite}>
                            <EditIcon /> Edit Experience
                          </h4>
                          <p className={classes.cardCategoryWhite}>
                            Complete your profile's experience
                          </p>
                        </CardHeader>
                        <CardBody>
                          <GridItem xs={12} sm={12} md={12}>
                            <FormControl
                              fullWidth
                              size="medium"
                              margin="normal"
                            >
                              <InputLabel htmlFor="standard-adornment-Company">
                                Company
                              </InputLabel>
                              <Input
                                name="company"
                                onChange={(event) => {
                                  onChange(event);
                                }}
                                color="primary"
                                id="standard-adornment-Company"
                                startAdornment={
                                  <InputAdornment position="start">
                                    <i
                                      style={{ color: '#428bca' }}
                                      className="fas fa-building fa-lg"
                                    ></i>
                                  </InputAdornment>
                                }
                              />
                            </FormControl>

                            <FormControl
                              fullWidth
                              size="medium"
                              margin="normal"
                            >
                              <InputLabel htmlFor="standard-adornment-title">
                                Job Title
                              </InputLabel>
                              <Input
                                name="title"
                                onChange={(event) => {
                                  onChange(event);
                                }}
                                id="standard-adornment-title"
                                startAdornment={
                                  <InputAdornment position="start">
                                    <i
                                      style={{ color: '#428bca' }}
                                      className="fas fa-address-card fa-lg "
                                    ></i>
                                  </InputAdornment>
                                }
                              />
                            </FormControl>

                            <FormControl
                              fullWidth
                              size="medium"
                              margin="normal"
                            >
                              <TextField
                                className={classes.margin}
                                id="outlined-multiline-static"
                                label="Job description"
                                multiline
                                rows={4}
                                name="description"
                                value={description}
                                onChange={(event) => {
                                  onChange(event);
                                }}
                              />
                            </FormControl>

                            <FormControl
                              fullWidth
                              size="medium"
                              margin="normal"
                            >
                              <FormControlLabel
                                control={
                                  <Checkbox
                                    checked={current}
                                    onChange={(event) =>
                                      setFormData({
                                        ...formData,
                                        current: !current,
                                      })
                                    }
                                    name="current"
                                    color="primary"
                                  />
                                }
                                label={
                                  <span
                                    style={{
                                      fontFamily: 'Quicksand',
                                      fontSize: '0.85em',
                                      fontWeight: '650',
                                    }}
                                  >
                                    Are you still working there ?
                                  </span>
                                }
                              />
                            </FormControl>
                          </GridItem>

                          <GridContainer>
                            <GridItem xs={12} sm={12} md={6}>
                              <FormControl
                                fullWidth
                                size="medium"
                                margin="normal"
                              >
                                <h5
                                  style={{
                                    color: 'grey',
                                    fontSize: '1em',
                                    fontWeight: '400',
                                  }}
                                >
                                  <i
                                    className="fa fa-calendar"
                                    style={{ color: '#428bca' }}
                                  ></i>{' '}
                                  Started Date
                                </h5>
                                <DatePicker
                                  selected={startDate}
                                  onChange={(date) => setStartDate(date)}
                                  showYearDropdown
                                  dateFormatCalendar="MMMM"
                                  yearDropdownItemNumber={10}
                                  scrollableYearDropdown
                                  inline
                                />
                              </FormControl>
                            </GridItem>
                            <GridItem xs={12} sm={12} md={6}>
                              <FormControl
                                fullWidth
                                size="medium"
                                margin="normal"
                              >
                                <h5
                                  style={{
                                    color: 'grey',
                                    fontSize: '1em',
                                    fontWeight: '400',
                                  }}
                                >
                                  <i
                                    className="fa fa-calendar-check-o"
                                    style={{ color: '#428bca' }}
                                  ></i>{' '}
                                  Ended Date
                                </h5>
                                <DatePicker
                                  selected={endDate}
                                  onChange={(date) => setEndDate(date)}
                                  showYearDropdown
                                  dateFormatCalendar="MMMM"
                                  yearDropdownItemNumber={10}
                                  scrollableYearDropdown
                                  inline
                                />
                              </FormControl>
                            </GridItem>
                          </GridContainer>
                        </CardBody>

                        <CardFooter>
                          <Button type="submit" color="primary">
                            Update Profile
                          </Button>
                        </CardFooter>
                      </Card>
                    </form>
                  </Grid>
                  <Grid
                    item
                    xs={12}
                    sm={12}
                    md={6}
                    style={{ paddingRight: '0.75em' }}
                  >
                    {' '}
                    <Card className={classes.table}>
                      <CardHeader color="primary">
                        <h4>
                          <BusinessCenterIcon style={{ color: '#fff' }} />{' '}
                          Experience
                        </h4>
                        <p>
                          Click the "Edit Experience" tab to add or remove new
                          item
                        </p>
                      </CardHeader>
                      <CardBody>
                        <Table
                          tableHeaderColor="primary"
                          tableHead={[
                            'Company',
                            'Job Title',
                            'From',
                            'To',
                            'Current Job',
                            '',
                          ]}
                          tableData={experienceList}
                        />
                      </CardBody>
                    </Card>
                  </Grid>
                </Grid>
              </div>
            )}
          </Spring>
        </Grid>
      </ThemeProvider>
    );
  } else {
    return (
      <div
        style={{
          textAlign: 'center',
        }}
      >
        <img src={Logo} alt="Logo" width="300" />
        <h1
          style={{
            margin: 'auto',
            marginTop: '1em',
          }}
        >
          SOME THING WENT WRONG
        </h1>
      </div>
    );
  }
};

EditExperience.propTypes = {
  addExperience: PropTypes.func.isRequired,
  delExperience: PropTypes.func.isRequired,
  getCurrentProfile: PropTypes.func.isRequired,
  profile: PropTypes.object.isRequired,
};

const mapPropsToState = (state) => ({
  profile: state.profile.profile,
  loading: state.profile.loading,
});
export default connect(mapPropsToState, {
  addExperience,
  getCurrentProfile,
  delExperience,
  loadUser,
})(EditExperience);
