import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';

/** Redux Action */
import { addEducation, delEducation } from '../../../action/profile';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import { getCurrentProfile } from '../../../action/profile';
import { toast } from 'react-toastify';
import { loadUser } from '../../../action/auth';

/** Local Component */
import Spinner from '../../layout/Spinner';
import Logo from '../../img/300Logo.png';

/** Material UI */
import { makeStyles } from '@material-ui/core/styles';
import { createMuiTheme, ThemeProvider } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Input from '@material-ui/core/Input';
import InputAdornment from '@material-ui/core/InputAdornment';
import FormControl from '@material-ui/core/FormControl';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import InputLabel from '@material-ui/core/InputLabel';

/** Material UI Icon */
import SchoolIcon from '@material-ui/icons/School';
import DeleteIcon from '@material-ui/icons/Delete';
import CheckIcon from '@material-ui/icons/Check';
import ClearIcon from '@material-ui/icons/Clear';
import EditIcon from '@material-ui/icons/Edit';

/** UI Customize Component */
import Table from '../../Components/components/Table/Table.js';
import Button from '../../Components/components/CustomButtons/Button.js';
import Card from '../../Components/components/Card/Card.js';
import CardHeader from '../../Components/components/Card/CardHeader.js';
import CardBody from '../../Components/components/Card/CardBody.js';
import CardAvatar from '../../Components/components/Card/CardAvatar.js';
import GridItem from '../../Components/components/Grid/GridItem.js';
import GridContainer from '../../Components/components/Grid/GridContainer.js';
import CardFooter from '../../Components/components/Card/CardFooter.js';

/** Spring */
import { Spring } from 'react-spring/renderprops';

/** React External Component */
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';

/** Utils */
var moment = require('moment');

const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#428bca',
    },
    secondary: {
      main: '#111',
    },
    success: {
      main: '#5cb85c',
    },
    danger: {
      main: '#d9534f',
    },
  },
});

const useStyles = makeStyles((theme) => ({
  spinner: {
    position: 'absolute',
    left: '50%',
    top: '50%',
  },
  table: {
    marginTop: '4em',
    boxShadow: '2px 2px 5px 2px rgba(0,0,0,0.35)',
    padding: '1em',
  },
  icon: {
    cursor: 'pointer',
    color: '#428bca',
    marginTop: '0.25em',
    marginBottom: '0.25em',
  },
  card: {
    marginTop: '4em',
    marginRight: '0.5em',
    boxShadow: '2px 2px 5px 2px rgba(0,0,0,0.35)',
    padding: '1em',
  },
  content: {
    paddingLeft: '1em',
    paddingRight: '1em',
    paddingTop: '1em',
  },
  cardCategory: {
    color: 'grey',
    fontSize: '11px',
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
  },
  cardTitle: {
    color: '#111',
    marginTop: '0px',
    minHeight: 'auto',
    fontWeight: '300',
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: '3px',
    textDecoration: 'none',
    fontSize: '15px',
  },
  cardCategoryWhite: {
    color: 'rgba(255,255,255,.62)',
    margin: '0',
    fontSize: '14px',
    marginTop: '0',
    marginBottom: '0',
  },
  cardTitleWhite: {
    color: '#FFFFFF',
    marginTop: '0px',
    minHeight: 'auto',
    fontWeight: '300',
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: '3px',
    textDecoration: 'none',
  },
  profileItem: {
    borderRight: '#428bca solid 1px',
  },
  profileItemFeature: {
    fontSize: '13px',
    fontWeight: '300',
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    color: '#428bca',
  },
}));

const EditEducation = ({
  profile,
  user,
  getCurrentProfile,
  addEducation,
  delEducation,
  loading,
  loadUser,
}) => {
  useEffect(() => {
    loadUser();
    getCurrentProfile();
  }, []);
  const classes = useStyles();
  const [formData, setFormData] = useState({
    school: null,
    degree: null,
    current: false,
    gpa: null,
  });
  const [startDate, setStartDate] = useState(new Date());
  const [endDate, setEndDate] = useState(new Date());
  const { school, degree, current, gpa } = formData;

  /**
   * Submit
   */
  const onSubmit = (event) => {
    event.preventDefault();
    if (!school || !degree || !gpa) {
      for (const property in formData) {
        if (!formData[property]) {
          (function () {
            toast.error(`Please fill in "${property}" fields !`);
          })();
        }
      }

      return;
    }

    const postForm = {
      school,
      degree,
      current,
      gpa,
      from: startDate,
      to: endDate,
    };
    addEducation(postForm);
  };

  /**
   * Onchange
   */
  const onChange = (event) => {
    event.preventDefault();
    setFormData({
      ...formData,
      [event.target.name]: event.target.value,
    });
  };

  if (loading === true) {
    return (
      <div className={classes.spinner}>
        <Spinner color="#0275d8" loading={true} size="50" />
      </div>
    );
  }

  /** Checking on profile exist if (!profile) => show edit profile*/
  if (profile) {
    /** Process Education and Experience item */
    const educationList = profile.education.map((item) => [
      item.school,
      item.degree,
      moment(item.from).format('MMM Y'),
      moment(item.to).format('MMM Y'),
      item.current ? (
        <CheckIcon color="action" />
      ) : (
        <ClearIcon color="danger" />
      ),
      item.gpa,
      <DeleteIcon
        className={classes.icon}
        onClick={() => {
          delEducation(item._id);
        }}
      />,
    ]);

    return (
      <ThemeProvider theme={theme}>
        <Grid item xs={12} sm={12} md={12} className={classes.content}>
          {/** Dashboard for Education */}
          <Spring
            from={{ opacity: 0 }}
            to={{ opacity: 1 }}
            config={{ duration: 150 }}
          >
            {(props) => (
              <div style={props}>
                <Grid container>
                  <Grid
                    item
                    xs={12}
                    sm={12}
                    md={3}
                    style={{ paddingRight: '0.75em' }}
                  >
                    <Card className={classes.card} profile>
                      <CardAvatar profile>
                        <a href="#pablo" onClick={(e) => e.preventDefault()}>
                          <img
                            src={`https://avatars.dicebear.com/api/avataaars/${user._id}.svg?`}
                            alt="..."
                          />
                        </a>
                      </CardAvatar>
                      <CardBody profile>
                        <h5 className={classes.cardTitle}>
                          {user.name.toUpperCase()}
                        </h5>

                        <h6 className={classes.cardCategory}>
                          {profile.status.toUpperCase()}
                        </h6>

                        {/** Information */}
                        <Grid container>
                          <Grid
                            item
                            xs={6}
                            sm={6}
                            md={6}
                            className={classes.profileItem}
                          >
                            <i
                              className="fas fa-user-graduate fa-lg"
                              style={{ color: '#428bca' }}
                            ></i>
                            {profile.education.length > 0 ? (
                              <h6 className={classes.profileItemFeature}>
                                {
                                  profile.education[
                                    profile.education.length - 1
                                  ].degree
                                }
                              </h6>
                            ) : (
                              <h6 className={classes.profileItemFeature}>
                                Not Given
                              </h6>
                            )}
                          </Grid>
                          <Grid
                            item
                            xs={6}
                            sm={6}
                            md={6}
                            className={classes.profileItem}
                          >
                            <i
                              className="fas fa-university fa-lg"
                              style={{ color: '#428bca' }}
                            ></i>
                            {profile.education.length > 0 ? (
                              <h6 className={classes.profileItemFeature}>
                                {
                                  profile.education[
                                    profile.education.length - 1
                                  ].school
                                }
                              </h6>
                            ) : (
                              <h6 className={classes.profileItemFeature}>
                                Not given
                              </h6>
                            )}
                          </Grid>
                        </Grid>
                      </CardBody>
                    </Card>
                  </Grid>

                  <Grid item xs={12} sm={12} md={9}>
                    <Card className={classes.table}>
                      <CardHeader color="primary">
                        <h4>
                          <SchoolIcon /> Education
                        </h4>
                        <p>
                          Click the "Edit Education" tab to add or remove new
                          item
                        </p>
                      </CardHeader>
                      <CardBody>
                        <Table
                          tableHeaderColor="primary"
                          tableHead={[
                            'School',
                            'Degree',
                            'From',
                            'To',
                            'Graduated',
                            'GPA',
                          ]}
                          tableData={educationList}
                        />
                      </CardBody>
                    </Card>
                  </Grid>

                  {/**
                   * FORM
                   */}
                  <Grid item xs={12} sm={12} md={12}>
                    <form
                      onSubmit={(event) => {
                        onSubmit(event);
                      }}
                    >
                      <Card className={classes.card}>
                        <CardHeader color="primary">
                          <h4 className={classes.cardTitleWhite}>
                            <EditIcon /> Edit Education
                          </h4>
                          <p className={classes.cardCategoryWhite}>
                            Complete your profile's education
                          </p>
                        </CardHeader>
                        <CardBody>
                          <GridContainer>
                            {/**
                             * School - Degree - GPA
                             */}
                            <GridItem xs={12} sm={12} md={6}>
                              <FormControl
                                fullWidth
                                size="medium"
                                margin="normal"
                              >
                                <InputLabel htmlFor="standard-adornment-School">
                                  School
                                </InputLabel>
                                <Input
                                  name="school"
                                  onChange={(event) => {
                                    onChange(event);
                                  }}
                                  color="primary"
                                  id="standard-adornment-School"
                                  startAdornment={
                                    <InputAdornment position="start">
                                      <i
                                        className="fas fa-university fa-lg"
                                        style={{ color: '#428bca' }}
                                      ></i>
                                    </InputAdornment>
                                  }
                                />
                              </FormControl>

                              <FormControl
                                fullWidth
                                size="medium"
                                margin="normal"
                              >
                                <InputLabel htmlFor="standard-adornment-Degree">
                                  Degree
                                </InputLabel>
                                <Input
                                  name="degree"
                                  onChange={(event) => {
                                    onChange(event);
                                  }}
                                  id="standard-adornment-Degree"
                                  startAdornment={
                                    <InputAdornment position="start">
                                      <i
                                        className="fas fa-award fa-lg"
                                        style={{ color: '#428bca' }}
                                      ></i>
                                    </InputAdornment>
                                  }
                                />
                              </FormControl>

                              <FormControl
                                fullWidth
                                size="medium"
                                margin="normal"
                              >
                                <InputLabel htmlFor="standard-adornment-gpa">
                                  GPA
                                </InputLabel>
                                <Input
                                  name="gpa"
                                  onChange={(event) => {
                                    onChange(event);
                                  }}
                                  type="number"
                                  inputProps={{ step: '0.01' }}
                                  color="primary"
                                  id="standard-adornment-gpa"
                                  startAdornment={
                                    <InputAdornment position="start">
                                      <i
                                        className="fa fa-sticky-note"
                                        style={{ color: '#428bca' }}
                                      ></i>
                                    </InputAdornment>
                                  }
                                />
                              </FormControl>

                              <FormControl
                                fullWidth
                                size="medium"
                                margin="normal"
                              >
                                <FormControlLabel
                                  control={
                                    <Checkbox
                                      checked={current}
                                      onChange={(event) =>
                                        setFormData({
                                          ...formData,
                                          current: !current,
                                        })
                                      }
                                      name="current"
                                      color="primary"
                                    />
                                  }
                                  label="Are you graduated yet ?"
                                />
                              </FormControl>
                            </GridItem>

                            <GridItem xs={12} sm={12} md={6}>
                              <GridContainer>
                                <GridItem xs={12} sm={12} md={6}>
                                  <FormControl
                                    fullWidth
                                    size="medium"
                                    margin="normal"
                                  >
                                    <h5
                                      style={{
                                        color: 'grey',
                                        fontSize: '1em',
                                        fontWeight: '400',
                                      }}
                                    >
                                      <i
                                        className="fa fa-calendar"
                                        style={{ color: '#428bca' }}
                                      ></i>{' '}
                                      Started Date
                                    </h5>
                                    <DatePicker
                                      selected={startDate}
                                      onChange={(date) => setStartDate(date)}
                                      showYearDropdown
                                      dateFormatCalendar="MMMM"
                                      yearDropdownItemNumber={10}
                                      scrollableYearDropdown
                                      inline
                                    />
                                  </FormControl>
                                </GridItem>
                                <GridItem xs={12} sm={12} md={6}>
                                  <FormControl
                                    fullWidth
                                    size="medium"
                                    margin="normal"
                                  >
                                    <h5
                                      style={{
                                        color: 'grey',
                                        fontSize: '1em',
                                        fontWeight: '400',
                                      }}
                                    >
                                      <i
                                        className="fa fa-calendar-check-o"
                                        style={{ color: '#428bca' }}
                                      ></i>{' '}
                                      Ended Date
                                    </h5>
                                    <DatePicker
                                      selected={endDate}
                                      onChange={(date) => setEndDate(date)}
                                      showYearDropdown
                                      dateFormatCalendar="MMMM"
                                      yearDropdownItemNumber={10}
                                      scrollableYearDropdown
                                      inline
                                    />
                                  </FormControl>
                                </GridItem>
                              </GridContainer>
                            </GridItem>
                          </GridContainer>
                        </CardBody>
                        <CardFooter>
                          <Button type="submit" color="primary">
                            Update Profile
                          </Button>
                        </CardFooter>
                      </Card>
                    </form>
                  </Grid>
                </Grid>
              </div>
            )}
          </Spring>
        </Grid>
      </ThemeProvider>
    );
  } else {
    return (
      <ThemeProvider>
        <div className="text-center">
          <img src={Logo} alt="Logo" width="350" />
          <h1 color="primary"> Something went wrong !</h1>
        </div>
      </ThemeProvider>
    );
  }
};

EditEducation.propTypes = {
  addEducation: PropTypes.func.isRequired,
  delEducation: PropTypes.func.isRequired,
  loadUser: PropTypes.func.isRequired,
  getCurrentProfile: PropTypes.func.isRequired,
  profile: PropTypes.object.isRequired,
};

const mapPropsToState = (state) => ({
  profile: state.profile.profile,
  loading: state.profile.loading,
  user: state.auth.user,
});

export default connect(mapPropsToState, {
  addEducation,
  delEducation,
  getCurrentProfile,
  loadUser,
})(EditEducation);
