import React from 'react';
import video from '../resource/video.mp4';
import Logo from '../img/logo.png';

/** Material UI */
import Button from '@material-ui/core/Button';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import { createMuiTheme } from '@material-ui/core/styles';
import { MuiThemeProvider } from '@material-ui/core/styles';

import { Link } from 'react-router-dom';
/** React Responsive */
import { useMediaQuery } from 'react-responsive';

import '../style/Part1.css';

/** Material Icon */
import PlayArrowIcon from '@material-ui/icons/PlayArrow';
const CustomButtonLarge = withStyles({
  root: {
    margin: 'auto',
    boxShadow: 'none',
    textTransform: 'none',
    fontSize: 25,
    padding: '15px 15px',
    border: '2px solid',
    lineHeight: 1.5,
    color: '#fff',
    backgroundColor: '#428bca',
    borderColor: '#428bca',
    borderRadius: '0px',
    fontFamily: 'Quicksand',
    '&:hover': {
      backgroundColor: '#fff',
      color: '#428bca',
      borderColor: '#428bca',
      boxShadow: 'none',
    },
    '&:active': {
      boxShadow: 'none',
      backgroundColor: '#0275d8',
      borderColor: '#005cbf',
    },
    '&:focus': {
      boxShadow: '0 0 0 0.2rem rgba(0,123,255,.5)',
    },
  },
})(Button);

const CustomButtonSmall = withStyles({
  root: {
    margin: 'auto',
    boxShadow: 'none',
    textTransform: 'none',
    fontSize: 16,
    padding: '10px 10px',
    border: '2px solid',
    lineHeight: 1.5,
    color: '#fff',
    backgroundColor: '#428bca',
    borderColor: '#428bca',
    borderRadius: '0px',
    fontFamily: 'Quicksand',
    '&:hover': {
      backgroundColor: '#fff',
      color: '#428bca',
      borderColor: '#428bca',
      boxShadow: 'none',
    },
    '&:active': {
      boxShadow: 'none',
      backgroundColor: '#0275d8',
      borderColor: '#005cbf',
    },
    '&:focus': {
      boxShadow: '0 0 0 0.2rem rgba(0,123,255,.5)',
    },
  },
})(Button);

const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#428bca',
    },
    secondary: {
      main: '#111',
    },
  },
});

const useStyles = makeStyles((theme) => ({
  button: {
    fontWeight: '1000',
  },
  title: {
    flexGrow: 1,
    fontFamily: "'Orbitron', sans-serif",
  },
}));

const Part1 = () => {
  const classes = useStyles();
  const isSmall = useMediaQuery({ minWidth: 0, maxWidth: 599 });

  return (
    <React.Fragment>
      <header className="v-header ">
        <div className="fullscreen-video-wrap">
          <video src={video} autoPlay="true" loop="true" muted></video>
        </div>
        <div className="header-overlay"></div>
        <div className="header-content">
          <img src={Logo} alt="Logo" width="150" />
          <h1>Dev Meet</h1>
          <h2>Join our awesome community today</h2>
          <p>
            Social network, recruitment, discussion and employment for all
            developers, professional and enthusiast programmers
          </p>

          {!isSmall && (
            <Link to="/register" style={{ textDecoration: 'none' }}>
              <CustomButtonLarge>Get started</CustomButtonLarge>
            </Link>
          )}
          {isSmall && (
            <Link to="/register" style={{ textDecoration: 'none' }}>
              <CustomButtonSmall>Get started</CustomButtonSmall>
            </Link>
          )}
        </div>
      </header>
    </React.Fragment>
  );
};

export default Part1;
