import React, { useState, useEffect } from 'react';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import Autocomplete from '@material-ui/lab/Autocomplete';

/** Redux */
import { connect } from 'react-redux';
import { createProfile, getCurrentProfile } from '../../../action/profile';
import { loadUser } from '../../../action/auth';

import InputAdornment from '@material-ui/core/InputAdornment';
import FormControl from '@material-ui/core/FormControl';
import { makeStyles } from '@material-ui/core/styles';
import { createMuiTheme } from '@material-ui/core/styles';
import { ThemeProvider } from '@material-ui/styles';

/** Material UI Icon */
import AssignmentIndIcon from '@material-ui/icons/AssignmentInd';
import LinkIcon from '@material-ui/icons/Link';
import BusinessIcon from '@material-ui/icons/Business';
import ExploreIcon from '@material-ui/icons/Explore';
import WorkIcon from '@material-ui/icons/Work';
import CodeIcon from '@material-ui/icons/Code';
import EditIcon from '@material-ui/icons/Edit';
import GitHubIcon from '@material-ui/icons/GitHub';
import FacebookIcon from '@material-ui/icons/Facebook';
import TwitterIcon from '@material-ui/icons/Twitter';
import LinkedInIcon from '@material-ui/icons/LinkedIn';

/** Local component */
import { languages } from '../InitProfile/programmingList';
import { jobTitle } from '../InitProfile/jobTitle';
import Button from '../../Components/components/CustomButtons/Button';
import GoogleMap from '../../utils/GoogleMap';

const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#428bca',
    },
    secondary: {
      main: '#111',
    },
  },
});

const useStyles = makeStyles((theme) => ({
  button: {
    width: '35%',
    margin: 'auto',
  },
  itemTitle: {
    fontSize: '0.85em',
    color: '#428bca',
    fontWeight: '300',
  },
  marginTop: {
    marginTop: '1.5em',
  },
}));

const InitUser = function ({ createProfile, getCurrentProfile, loadUser }) {
  const classes = useStyles();

  useEffect(() => {
    loadUser();
    getCurrentProfile();
  }, []);

  const [formData, setFormData] = useState({
    company: '',
    website: '',
    location: '',
    status: '',
    skill: [],
    bio: '',
    githubUsername: '',
    facebook: '',
    twitter: '',
    github: '',
    linkedin: '',
  });

  const {
    company,
    website,
    location,
    status,
    skill,
    bio,
    githubUsername,
    facebook,
    twitter,
    github,
    linkedin,
  } = formData;

  const onChange = (event) => {
    setFormData({ ...formData, [event.target.name]: event.target.value });
  };

  const onSubmit = (event) => {
    event.preventDefault();
    const postProfile = {
      company,
      website,
      location,
      status,
      skill,
      bio,
      githubUsername,
      connect: {
        facebook,
        twitter,
        github,
        linkedin,
      },
    };
    console.log(postProfile);

    createProfile(postProfile, false);
  };

  return (
    <ThemeProvider theme={theme}>
      <React.Fragment>
        <Typography
          color="primary"
          variant="h6"
          gutterBottom
          align="center"
          style={{
            fontFamily: 'Quicksand',
          }}
        >
          <AssignmentIndIcon color="primary" /> User information
        </Typography>
        <form
          onSubmit={(event) => {
            onSubmit(event);
          }}
        >
          <Grid container spacing={3}>
            <Grid item xs={12} sm={6}>
              <FormControl fullWidth>
                <TextField
                  required
                  id="company"
                  name="company"
                  label="Company"
                  autoComplete="company"
                  onChange={onChange}
                  value={company}
                  InputProps={{
                    startAdornment: (
                      <InputAdornment position="start">
                        <BusinessIcon color="primary" />
                      </InputAdornment>
                    ),
                  }}
                />
              </FormControl>
            </Grid>
            <Grid item xs={12} sm={6}>
              <FormControl fullWidth>
                <TextField
                  required
                  id="website"
                  name="website"
                  label="Website"
                  autoComplete="website"
                  onChange={onChange}
                  value={website}
                  InputProps={{
                    startAdornment: (
                      <InputAdornment position="start">
                        <LinkIcon color="primary" />
                      </InputAdornment>
                    ),
                  }}
                />
              </FormControl>
            </Grid>
            <Grid item xs={6}>
              <FormControl fullWidth>
                <TextField
                  required
                  id="location"
                  name="location"
                  label="Location"
                  autoComplete="location"
                  onChange={onChange}
                  value={location}
                  InputProps={{
                    startAdornment: (
                      <InputAdornment position="start">
                        <ExploreIcon color="primary" />
                      </InputAdornment>
                    ),
                  }}
                />
              </FormControl>
            </Grid>

            <Grid item xs={12} sm={6}>
              <FormControl fullWidth>
                <TextField
                  required
                  id="githubUsername"
                  name="githubUsername"
                  label="githubUsername"
                  autoComplete="githubUsername"
                  onChange={onChange}
                  value={githubUsername}
                  InputProps={{
                    startAdornment: (
                      <InputAdornment position="start">
                        <GitHubIcon color="primary" />
                      </InputAdornment>
                    ),
                  }}
                />
              </FormControl>
            </Grid>
            <Grid item xs={12}>
              <Typography
                color="primary"
                variant="h6"
                align="left"
                className={classes.itemTitle}
              >
                <CodeIcon color="primary" />
                Tech stack
              </Typography>

              <FormControl fullWidth>
                <Autocomplete
                  multiple
                  id="skill"
                  options={languages}
                  getOptionLabel={(option) => option.name}
                  style={{ width: '100%' }}
                  renderInput={(params) => (
                    <TextField {...params} variant="outlined" />
                  )}
                  onChange={(event, value) => {
                    setFormData({
                      ...formData,
                      skill: value.map((item) => item.name),
                    });
                  }}
                />
              </FormControl>
            </Grid>
            <Grid item xs={12}>
              <FormControl fullWidth>
                <Typography
                  color="primary"
                  variant="h6"
                  align="left"
                  className={classes.itemTitle}
                >
                  <WorkIcon color="primary" />
                  Job
                </Typography>
                <Autocomplete
                  id="jobTitle"
                  options={jobTitle}
                  getOptionLabel={(option) => option.name}
                  style={{ width: '100%' }}
                  renderInput={(params) => (
                    <TextField {...params} variant="outlined" />
                  )}
                  onChange={(event, value) => {
                    setFormData({ ...formData, status: value.name });
                  }}
                />
              </FormControl>
            </Grid>
            <Grid item xs={12} sm={6}>
              <FormControl fullWidth>
                <Typography
                  color="primary"
                  variant="h6"
                  align="left"
                  style={{
                    fontFamily: 'Quicksand',
                  }}
                >
                  <i className="fas fa-hashtag" color="primary"></i> Social
                  Media
                </Typography>
                <TextField
                  id="facebook"
                  name="facebook"
                  label="Facebook"
                  fullWidth
                  autoComplete="facebook"
                  InputProps={{
                    startAdornment: (
                      <InputAdornment position="start">
                        <FacebookIcon color="primary" />
                      </InputAdornment>
                    ),
                  }}
                  onChange={onChange}
                  value={facebook}
                />

                <TextField
                  id="linkedin"
                  name="linkedin"
                  label="Linkedin"
                  fullWidth
                  autoComplete="linkedin"
                  InputProps={{
                    startAdornment: (
                      <InputAdornment position="start">
                        <LinkedInIcon color="primary" />
                      </InputAdornment>
                    ),
                  }}
                  onChange={onChange}
                  value={linkedin}
                />

                <TextField
                  id="twitter"
                  name="twitter"
                  label="Twitter"
                  fullWidth
                  autoComplete="twitter"
                  InputProps={{
                    startAdornment: (
                      <InputAdornment position="start">
                        <TwitterIcon color="primary" />
                      </InputAdornment>
                    ),
                  }}
                  onChange={onChange}
                  value={twitter}
                />
                <TextField
                  id="github"
                  name="github"
                  label="Github"
                  fullWidth
                  autoComplete="github"
                  InputProps={{
                    startAdornment: (
                      <InputAdornment position="start">
                        <GitHubIcon color="primary" />
                      </InputAdornment>
                    ),
                  }}
                  onChange={onChange}
                  value={github}
                />
              </FormControl>
            </Grid>
            <Grid item xs={12} sm={6}>
              <FormControl fullWidth>
                <Typography
                  color="primary"
                  variant="h6"
                  align="left"
                  style={{
                    fontFamily: 'Quicksand',
                  }}
                >
                  <EditIcon color="primary" /> Biography
                </Typography>
                <TextField
                  className={classes.marginTop}
                  label="About me"
                  multiline
                  rows={4}
                  variant="outlined"
                  style={{ width: '100%' }}
                  name="bio"
                  value={bio}
                  onChange={(event) => {
                    onChange(event);
                  }}
                />
              </FormControl>
            </Grid>
            <Button
              variant="contained"
              color="primary"
              round
              className={classes.button}
              type="submit"
            >
              Update user information
            </Button>
          </Grid>
        </form>
      </React.Fragment>
    </ThemeProvider>
  );
};
export default connect(null, { createProfile, getCurrentProfile, loadUser })(
  InitUser
);
