import { ADD_CODE, GET_CODE, CODE_ERR, DEL_CODE } from '../action/type';

const initialState = {
  loading: true,
  code: [],
  error: {},
};

export default function (state = initialState, action) {
  const { type, payload } = action;
  switch (type) {
    case GET_CODE:
    case DEL_CODE:
    case ADD_CODE: {
      return {
        ...state,
        code: payload.code.slice().reverse(),
        loading: false,
      };
    }

    case CODE_ERR: {
      return {
        ...state,
        error: payload,
      };
    }

    default: {
      return state;
    }
  }
}
