import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import IndividualExperience from '../Profile/IndividualExperience';
import IndividualEducation from '../Profile/IndividualEducation';
import Spinner from '../../layout/Spinner';
import Repos from '../../Repos/Repos';
import { connect } from 'react-redux';
import { getProfileByID, getGithubInfo } from '../../../action/profile';

/** Material UI */
import { makeStyles } from '@material-ui/core/styles';
import { createMuiTheme, ThemeProvider } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';

/** UI Customize Component */
import Button from '../../Components/components/CustomButtons/Button.js';
import CardAvatar from '../../Components/components/Card/CardAvatar.js';
import Card from '../../Components/components/Card/Card.js';
import CardHeader from '../../Components/components/Card/CardHeader.js';
import CardBody from '../../Components/components/Card/CardBody.js';
import CardFooter from '../../Components/components/Card/CardFooter.js';

/** Material Icon*/
import CheckIcon from '@material-ui/icons/Check';
import ClearIcon from '@material-ui/icons/Clear';
import WorkOutlineIcon from '@material-ui/icons/WorkOutline';
import LocationOnIcon from '@material-ui/icons/LocationOn';
import LinkIcon from '@material-ui/icons/Link';
import GitHubIcon from '@material-ui/icons/GitHub';
import TwitterIcon from '@material-ui/icons/Twitter';
import FacebookIcon from '@material-ui/icons/Facebook';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import KeyboardArrowDownSharpIcon from '@material-ui/icons/KeyboardArrowDownSharp';

/** Spring */
import { Spring } from 'react-spring/renderprops';

/** Utils */
var moment = require('moment');

const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#111',
    },
    secondary: {
      main: '#428bca',
    },
    success: {
      main: '#5cb85c',
    },
    danger: {
      main: '#d9534f',
    },
  },
});

const useStyles = makeStyles((theme) => ({
  spinner: {
    position: 'absolute',
    left: '50%',
    top: '50%',
  },
  icon: {
    cursor: 'pointer',
  },
  card: {
    marginTop: '4em',
    boxShadow: '2px 2px 5px 2px rgba(0,0,0,0.35)',
  },
  cardTitle: {
    fontWeight: '300',
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
  },
}));

export const IndividualProfile = ({
  match,
  isAuthenticated,
  profile,
  loading,
  getProfileByID,
  getGithubInfo,
  repos,
}) => {
  const classes = useStyles();
  const [displayRepos, setDisplayRepos] = useState(false);
  const [isReposExist, setReposExist] = useState(false);

  useEffect(() => {
    getProfileByID(match.params.user_id);
  }, []);

  useEffect(() => {
    if (profile) {
      if (profile.githubUsername) {
        setReposExist(true);
        getGithubInfo(profile.githubUsername);
      } else {
        setReposExist(false);
      }
    }
  }, [loading]);

  /** Checking on loading if (loading) => show spinner*/
  if (loading) {
    return (
      <div className={classes.spinner}>
        <Spinner color="#0275d8" loading={true} size="50" />
      </div>
    );
  } else {
    /** Process Education and Experience item */
    const educationList = profile.education.map((item) => [
      item.school,
      item.degree,
      moment(item.from).format('Y'),
      moment(item.to).format('Y'),
      item.current ? (
        <CheckIcon style={{ color: '#5cb85c' }} />
      ) : (
        <ClearIcon style={{ color: '#d9534f' }} />
      ),
      item.gpa,
    ]);

    const experienceList = profile.experience.map((item) => [
      <img src={`//logo.clearbit.com/${item.company}.com`} width="55" />,
      item.title,
      moment(item.from).format('Y'),
      moment(item.to).format('Y'),
      item.current ? (
        <CheckIcon style={{ color: '#5cb85c' }} />
      ) : (
        <ClearIcon style={{ color: '#d9534f' }} />
      ),
    ]);

    return (
      <Spring
        from={{ opacity: 0 }}
        to={{ opacity: 1 }}
        config={{ duration: 150 }}
      >
        {(props) => (
          <div style={props}>
            <ThemeProvider theme={theme}>
              <Grid container>
                {/** DISPLAY PROFILE USER */}
                <Grid item xs={false} sm={1} md={2}></Grid>

                <Grid item xs={12} sm={10} md={8} style={{ padding: '1em' }}>
                  {/**
                   * Name, job and location and tech stack...
                   */}
                  <Card className={classes.card} profile>
                    <CardHeader color="primary">
                      <CardAvatar profile>
                        <a href="#pablo" onClick={(e) => e.preventDefault()}>
                          <img
                            src={`https://avatars.dicebear.com/api/avataaars/${profile.user._id}.svg?`}
                            alt="..."
                          />
                        </a>
                      </CardAvatar>
                    </CardHeader>

                    <CardBody profile>
                      <h5 className={classes.cardTitle}>
                        {profile.user.name.toUpperCase()}
                      </h5>
                      <h6 className={classes.cardCategory}>
                        {profile.status.toUpperCase()}
                      </h6>
                      <p className={classes.description}>{profile.bio}</p>
                    </CardBody>
                    <CardFooter>
                      <Grid container xs={12} sm={12} md={12}>
                        <Grid
                          item
                          xs={4}
                          sm={4}
                          md={4}
                          style={{
                            borderRight: '1px solid #428bca',
                            borderLeft: '1px solid #428bca',
                          }}
                        >
                          <WorkOutlineIcon
                            color="primary"
                            style={{ fontSize: 25 }}
                          />
                          <h4>{profile.company}</h4>
                        </Grid>

                        <Grid
                          item
                          xs={4}
                          sm={4}
                          md={4}
                          style={{ borderRight: '1px solid #428bca' }}
                        >
                          <LocationOnIcon
                            color="primary"
                            style={{ fontSize: 25 }}
                          />
                          <h4>{profile.location}</h4>
                        </Grid>

                        <Grid
                          item
                          xs={4}
                          sm={4}
                          md={4}
                          style={{ borderRight: '1px solid #428bca' }}
                        >
                          <LinkIcon color="primary" style={{ fontSize: 25 }} />
                          <h4>{profile.website}</h4>
                        </Grid>

                        <Grid item xs={12} sm={12} md={12}>
                          <GitHubIcon style={{ fontSize: 35, color: '#111' }} />
                          <TwitterIcon
                            style={{ fontSize: 35, color: '#00acee' }}
                          />
                          <FacebookIcon
                            style={{ fontSize: 35, color: '#3b5998' }}
                          />
                          <LinkedInIcon
                            style={{ fontSize: 35, color: '#0e76a8' }}
                          />
                        </Grid>

                        <Grid item xs={12} sm={12} md={12}>
                          <span
                            style={{ color: '#428bca', fontSize: '1.45em' }}
                          >
                            {profile.skill.map(
                              (eachSkill, index) => `#${eachSkill}, `
                            )}
                          </span>
                        </Grid>

                        <Grid
                          item
                          xs={12}
                          sm={12}
                          md={12}
                          style={{ marginTop: '0.45em' }}
                        >
                          <Button
                            color="primary"
                            round
                            style={{ margin: 'auto' }}
                          >
                            FOLLOW
                          </Button>
                        </Grid>
                      </Grid>
                    </CardFooter>
                  </Card>
                </Grid>
                {/**
                 * Education...
                 */}
                <Grid
                  xs={12}
                  sm={6}
                  md={6}
                  style={{ paddingRight: '0.5em', paddingLeft: '0.5em' }}
                >
                  <IndividualEducation
                    loading={loading}
                    education={educationList}
                  />
                </Grid>
                {/**
                 * Experience...
                 */}
                <Grid
                  xs={12}
                  sm={6}
                  md={6}
                  style={{ paddingRight: '0.5em', paddingLeft: '0.5em' }}
                >
                  <IndividualExperience
                    loading={loading}
                    experience={experienceList}
                  />
                </Grid>

                {/**
                 * Repository
                 */}
                <Grid
                  xs={12}
                  sm={12}
                  md={12}
                  style={{ paddingRight: '0.5em', paddingLeft: '0.5em' }}
                >
                  <Button
                    variant="contained"
                    color="primary"
                    startIcon={<GitHubIcon />}
                    endIcon={<KeyboardArrowDownSharpIcon />}
                    onClick={() => {
                      setDisplayRepos(!displayRepos);
                    }}
                  >
                    Show Github Repositories
                  </Button>
                  {/**
                   * Show Repository
                   */}

                  {displayRepos ? (
                    isReposExist ? (
                      <Repos repos={repos} />
                    ) : (
                      <h1>No Repos</h1>
                    )
                  ) : (
                    <p
                      style={{
                        fontStyle: 'italic',
                        color: '#808080',
                        fontFamily: 'Roboto',
                        fontWeight: '400',
                        fontSize: '1em',
                        marginBottom: '3em',
                      }}
                    >
                      * Hint: Click to show user github repositories
                    </p>
                  )}
                </Grid>
              </Grid>
            </ThemeProvider>
          </div>
        )}
      </Spring>
    );
  }
};

IndividualProfile.propTypes = {
  isAuthenticated: PropTypes.bool,
  profile: PropTypes.object,
  loading: PropTypes.bool,
  getProfileByID: PropTypes.func,
  match: PropTypes.object,
};

const mapStateToProps = (state) => ({
  isAuthenticated: state.auth.isAuthenticated,
  profile: state.profile.profile,
  repos: state.profile.repos,
  loading: state.profile.loading,
});

export default connect(mapStateToProps, { getProfileByID, getGithubInfo })(
  IndividualProfile
);
