import {
  ADD_JOB,
  GET_JOBS,
  GET_JOB,
  DELETE_JOB,
  GET_TOTAL_JOB,
  JOB_ERR,
  UPDATE_FORM,
  SET_CURRENT,
  CLEAR_CURRENT,
} from '../action/type';

const initialState = {
  jobs: [],
  job: null,
  loading: true,
  length: null,
  error: {},
  form: {},
  current: null,
};

export default function (state = initialState, action) {
  const { type, payload } = action;

  switch (type) {
    case UPDATE_FORM: {
      return {
        ...state,
        form: { ...state.form, [payload.name]: payload.value },
      };
    }

    case GET_JOBS: {
      return {
        ...state,
        jobs: payload,
        job: payload[0],
        loading: false,
      };
    }
    case GET_JOB: {
      return {
        ...state,
        job: payload,
        loading: false,
      };
    }
    case ADD_JOB: {
      return {
        ...state,
        jobs: [...state.jobs, payload],
      };
    }
    case GET_TOTAL_JOB: {
      return {
        ...state,
        length: payload,
      };
    }
    case DELETE_JOB: {
      return {
        ...state,
        jobs: state.jobs.filter((job) => job._id !== payload),
      };
    }
    case SET_CURRENT: {
      return {
        ...state,
        job: state.jobs.filter((job) => job._id === payload)[0],
        current: payload,
      };
    }

    case CLEAR_CURRENT: {
      return {
        ...state,
        current: null,
      };
    }

    case JOB_ERR: {
      return {
        ...state,
        error: payload,
        loading: false,
      };
    }
    default: {
      return state;
    }
  }
}
