import React, { useEffect } from 'react';

/** Local Component */
import RepoItem from '../Repos/ReposItem';
import PropTypes from 'prop-types';
import Spinner from '../layout/Spinner';

/** Redux */
import { getGithubInfo } from '../../action/profile';
import { connect } from 'react-redux';

/** Material UI */
import { makeStyles } from '@material-ui/core/styles';
import { createMuiTheme, ThemeProvider } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';

/** UI Customize Component */
import Button from '../Components/components/CustomButtons/Button.js';
import CardAvatar from '../Components/components/Card/CardAvatar.js';
import Card from '../Components/components/Card/Card.js';
import CardHeader from '../Components/components/Card/CardHeader.js';
import CardBody from '../Components/components/Card/CardBody.js';
import CardFooter from '../Components/components/Card/CardFooter.js';

/** Material Icon*/
import CheckIcon from '@material-ui/icons/Check';
import ClearIcon from '@material-ui/icons/Clear';
import WorkOutlineIcon from '@material-ui/icons/WorkOutline';
import LocationOnIcon from '@material-ui/icons/LocationOn';
import LinkIcon from '@material-ui/icons/Link';
import GitHubIcon from '@material-ui/icons/GitHub';
import TwitterIcon from '@material-ui/icons/Twitter';
import FacebookIcon from '@material-ui/icons/Facebook';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import KeyboardArrowDownSharpIcon from '@material-ui/icons/KeyboardArrowDownSharp';

/** Spring */
import { Spring } from 'react-spring/renderprops';

/** Utils */
var moment = require('moment');

const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#111',
    },
    secondary: {
      main: '#428bca',
    },
    success: {
      main: '#5cb85c',
    },
    danger: {
      main: '#d9534f',
    },
  },
});

const useStyles = makeStyles((theme) => ({
  spinner: {
    position: 'absolute',
    left: '50%',
    top: '50%',
  },
  table: {
    marginTop: '4em',
    boxShadow: '2px 2px 5px 2px rgba(0,0,0,0.35)',
  },
  content: {
    paddingLeft: '2em',
    paddingRight: '1em',
    paddingTop: '1em',
  },
  cardCategoryWhite: {
    color: 'rgba(255,255,255,.62)',
    fontSize: '14px',
  },
  cardTitleWhite: {
    color: '#FFFFFF',
    minHeight: 'auto',
    fontWeight: '300',
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: '3px',
    textDecoration: 'none',
  },
  icon: {
    cursor: 'pointer',
  },
  card: {
    marginTop: '4em',
    boxShadow: '2px 2px 5px 2px rgba(0,0,0,0.35)',
  },
  IconButton: {
    marginRight: '5px',
  },
  profileItem: {
    borderRight: '#428bca solid 1px',
  },
  profileItemFeature: {
    fontWeight: '300',
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    color: '#111',
    fontSize: '1em',
  },
  description: {
    width: '50%',
    margin: 'auto',
    marginBottom: '0.75em',
  },
  cardCategory: {
    fontWeight: '300',
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    color: '#7a7979',
  },
  cardTitle: {
    fontWeight: '300',
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
  },
}));

const Repos = function ({ repos, loading, user }) {
  const classes = useStyles();
  if (loading) {
    return (
      <div className={classes.spinner}>
        <Spinner color="#0275d8" loading={true} size="50" />
      </div>
    );
  }

  return (
    <Spring
      from={{ opcacity: 0 }}
      to={{ opcacity: 1 }}
      config={{ duration: 600 }}
    >
      {(props) => (
        <div style={props}>
          <Card className={classes.table}>
            <CardHeader color="primary">
              <h4>
                <GitHubIcon /> Github Repositories
              </h4>
              <p>Repositories of {user.name}</p>
            </CardHeader>
            <CardBody>
              <Grid container>
                {repos.map((repo) => (
                  <RepoItem repo={repo} />
                ))}
              </Grid>
            </CardBody>
          </Card>
        </div>
      )}
    </Spring>
  );
};

Repos.propTypes = {
  loading: PropTypes.bool,
  repos: PropTypes.array,
  user: PropTypes.object,
};

const mapStateToProps = (state) => ({
  loading: state.profile.loading,
  user: state.profile.profile.user,
});

export default connect(mapStateToProps)(Repos);
