import {
  REGISTER_FAIL,
  REGISTER_SUCCESS,
  USER_LOADED,
  AUTH_ERROR,
  LOGIN_SUCCESS,
  LOGIN_FAIL,
  LOG_OUT,
  CLEAR_PROFILE,
} from '../action/type';
import { setAlert } from '../action/alert';
import setAuthToken from '../reducers/utils/setAuthToken';
import api from '../reducers/utils/baseURL';

/**
 * Loading User
 */
export const loadUser = () => async (dispatch) => {
  if (localStorage.getItem('token')) {
    setAuthToken(localStorage.getItem('token'));
  }

  try {
    const res = await api.get('/auth');
    dispatch({
      type: USER_LOADED,
      payload: res.data,
    });
  } catch (error) {
    dispatch({
      type: AUTH_ERROR,
    });
  }
};

/**
 * Login
 */
export const login = ({ email, password }) => async (dispatch) => {
  try {
    const config = {
      headers: {
        'Content-Type': 'application/json',
      },
    };

    const body = JSON.stringify({
      email,
      password,
    });

    const res = await api.post('/auth', body, config);

    dispatch({
      type: LOGIN_SUCCESS,
      payload: res.data,
    });

    dispatch(setAlert('Welcome back !', 'success'));
  } catch (error) {
    if (error) {
      const errors = error.response.data.errors;
      errors.forEach((err) => {
        dispatch(setAlert(err.msg, 'danger'));
      });
    }
    dispatch({
      type: LOGIN_FAIL,
    });
  }
};

/**
 * Register New User
 */
export const register = ({ name, email, password }) => async (dispatch) => {
  const config = {
    headers: {
      'Content-Type': 'application/json',
    },
  };
  const body = JSON.stringify({
    name,
    email,
    password,
  });
  try {
    const res = await api.post('/users', body, config);
    dispatch({
      type: REGISTER_SUCCESS,
      payload: res.data,
    });
    dispatch(setAlert('Welcome to our site!', 'success'));
  } catch (error) {
    const errors = error.response.data.errors;
    errors.forEach((err) => dispatch(setAlert(err.msg, 'danger')));
    dispatch({
      type: REGISTER_FAIL,
    });
  }
};

/**
 * Logout
 */
export const logout = () => async (dispatch) => {
  dispatch(setAlert('Good bye :) ', 'warning'));
  dispatch({
    type: CLEAR_PROFILE,
  });
  dispatch({
    type: LOG_OUT,
  });
};
