import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import Spinner from '../layout/Spinner';
import {
  getPosts,
  addPost,
  likePost,
  unlikePost,
  delPost,
  addPostWithImage,
} from '../../action/post';
import { setAlert } from '../../action/alert';
import { toast } from 'react-toastify';
import { connect } from 'react-redux';

/** Material UI */
import { makeStyles, withStyles } from '@material-ui/core/styles';
import { createMuiTheme, ThemeProvider } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import Tooltip from '@material-ui/core/Tooltip';

/** Material Icon*/
import IconButton from '@material-ui/core/IconButton';
import SendSharpIcon from '@material-ui/icons/SendSharp';
import PhotoCameraSharpIcon from '@material-ui/icons/PhotoCameraSharp';
import EmojiEmotionsSharpIcon from '@material-ui/icons/EmojiEmotionsSharp';
import AttachFileIcon from '@material-ui/icons/AttachFile';
import PostAddIcon from '@material-ui/icons/PostAdd';
import HighlightOffIcon from '@material-ui/icons/HighlightOff';

/** UI Customize Component */
import Card from '../Components/components/Card/Card.js';

/** Related Component */
import PostItem from './PostItem';
import JobBanner from '../Job/JobBanner';

/** Logo */
import Logo from '../img/300Logo.png';

/** Spring */
import { Spring } from 'react-spring/renderprops';

/** Just a prototype will be copy later */
import axios from 'axios';

/** Reponsive  */
import { useMediaQuery } from 'react-responsive';

const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#428bca',
    },
    secondary: {
      main: '#111',
    },
    success: {
      main: '#5cb85c',
    },
    danger: {
      main: '#d9534f',
    },
  },
});

const useStyles = makeStyles((theme) => ({
  spinner: {
    position: 'absolute',
    left: '50%',
    top: '50%',
  },
  table: {
    marginTop: '4em',
    boxShadow: '2px 2px 5px 2px rgba(0,0,0,0.35)',
    border: '1px #5B5B5B solid',
  },
  content: {
    paddingLeft: '2em',
    paddingRight: '1em',
    paddingTop: '1em',
    background: '#e5e5e5',
  },
  cardCategoryWhite: {
    color: 'rgba(255,255,255,.62)',
    fontSize: '14px',
  },
  cardTitleWhite: {
    color: '#FFFFFF',
    minHeight: 'auto',
    fontWeight: '300',
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: '3px',
    textDecoration: 'none',
  },
  icon: {
    cursor: 'pointer',
  },
  card: {
    marginTop: '4em',
    boxShadow: '2px 2px 5px 2px rgba(0,0,0,0.35)',
  },
  IconButton: {
    marginRight: '5px',
  },
  profileItem: {
    borderRight: '#428bca solid 1px',
  },
  profileItemFeature: {
    fontWeight: '300',
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    color: '#111',
  },
  description: {
    width: '50%',
    margin: 'auto',
    marginBottom: '0.75em',
  },
  cardCategory: {
    fontWeight: '300',
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    color: '#7a7979',
  },
  cardTitle: {
    fontWeight: '300',
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
  },
  feedColumn: {
    paddingLeft: '0.75em',
    paddingRight: '0.75em',
  },
  newPost: {
    boxShadow: '1px 1px 3px 1px rgba(0,0,0,0.35)',
    border: '1px solid gray',
    padding: '1.25em',
  },
  Post: {
    boxShadow: '1px 1px 3px 1px rgba(0,0,0,0.35)',
    border: '1px solid gray',
    padding: '1.25em',
    marginTop: '1em',
    marginBottom: '1em',
  },
  TextField: {
    marginTop: '0.5em',
    marginBottom: '0.5em',
  },
  input: {
    display: 'none',
  },
}));

const CssTextField = withStyles({
  root: {
    '& label.Mui-focused': {
      color: '#428bca',
    },
    '& .MuiInput-underline:after': {
      borderBottomColor: '#428bca',
    },
    '& .MuiOutlinedInput-root': {
      '& fieldset': {
        borderColor: '#428bca',
      },
      '&:hover fieldset': {
        borderColor: '#428bca',
      },
      '&.Mui-focused fieldset': {
        borderColor: '#428bca',
      },
    },
  },
})(TextField);

const Posts = ({ loading, posts, getPosts, addPost, addPostWithImage }) => {
  const classes = useStyles();
  const [newPost, setNewPost] = useState({
    text: '',
  });
  const [count, setCount] = useState(0);
  const [image, setImage] = useState(null);
  const [url, setURL] = useState(null);

  /** Responsive */
  const isSmall = useMediaQuery({ minWidth: 0, maxWidth: 599 });
  const isMedium = useMediaQuery({ minWidth: 600, maxWidth: 951 });

  function readURL(input) {
    if (input.files && input.files[0]) {
      const reader = new FileReader();

      reader.onload = function (e) {
        setURL(e.target.result);
      };

      reader.readAsDataURL(input.files[0]); // convert to base64 string
    }
  }

  useEffect(() => {
    getPosts();
  }, []);

  /**
   * Easter Egg
   */
  useEffect(() => {
    if (count === 10) {
      (async function getJoke() {
        fetch('https://sv443.net/jokeapi/v2/joke/Programming?type=single', {
          method: 'GET',
        })
          .then(function (response) {
            return response.json();
          })
          .then(function (data) {
            toast.success(data.joke);
          });

        setCount(0);
      })();
    }
  }, [count]);

  const onChange = (event) => {
    setNewPost({
      text: event.target.value,
    });
  };

  const onSubmit = async (event) => {
    event.preventDefault();

    if (image) {
      addPostWithImage(image, newPost);
    }
    getPosts();
    setURL(null);
    setNewPost({
      text: '',
    });

    toast.success('Post added !');
  };

  if (loading) {
    return (
      <div className={classes.spinner}>
        <Spinner color="#0275d8" loading={true} size="50px" />
      </div>
    );
  }

  if (!posts) {
    return (
      <div style={{ textAlign: 'center' }}>
        <img
          src={Logo}
          alt="Logo"
          width="300"
          style={{ borderRadius: '50%', boxShadow: '2px 2px 2px 2px 2px #222' }}
        />
        <h4 style={{ color: '#428bca', fontWeight: '400' }}>Wow such empty</h4>
      </div>
    );
  }

  return (
    <Spring
      from={{ opacity: 0 }}
      to={{ opacity: 1 }}
      config={{ duration: 150 }}
    >
      {(props) => (
        <div style={props}>
          <ThemeProvider theme={theme}>
            <Grid
              container
              style={{
                background: '#e5e5e5',
              }}
            >
              {/**
               * Add new Post
               */}
              <Grid
                item
                xs={12}
                sm={12}
                md={8}
                style={{
                  paddingLeft: '1.5em',
                  paddingRight: '1.5em',
                }}
              >
                <form onSubmit={onSubmit}>
                  <Card className={classes.newPost}>
                    <Grid container>
                      <Grid item xs={12} sm={5} md={6}>
                        <Button
                          style={{
                            marginTop: '0em',
                            background: '#fff',
                            color: '#428bca',
                          }}
                          startIcon={<PostAddIcon />}
                          onClick={async () => {
                            setCount(count + 1);
                          }}
                        >
                          <span
                            style={{
                              fontWeight: '300',
                              fontFamily:
                                "'Roboto', 'Helvetica', 'Arial', sans-serif",
                              color: '#428bca',
                              marginRight: '1em',
                            }}
                          >
                            Add New posts
                          </span>{' '}
                          <span
                            style={{
                              fontWeight: '500',
                              fontFamily:
                                "'Roboto', 'Helvetica', 'Arial', sans-serif",
                              color: '#919191',
                            }}
                          >
                            {count !== 0 && `${count}/10`}
                          </span>
                        </Button>
                        <p
                          style={{
                            color: 'grey',
                            fontStyle: 'italic',
                            paddingLeft: '1em',
                          }}
                        >
                          * Hint: You guys like joke ?
                        </p>
                      </Grid>
                    </Grid>

                    {/**
                     * Text Field to send Posts
                     */}
                    <CssTextField
                      className={classes.TextField}
                      label="What are you thinking..."
                      variant="outlined"
                      multiline
                      rows={4}
                      rowsMax={4}
                      value={newPost.text}
                      onChange={(event) => {
                        onChange(event);
                      }}
                    />

                    {/**
                     * Button List
                     */}
                    <Grid container>
                      <Grid item xs={6} sm={6} md={6}>
                        {/**
                         * Image
                         */}
                        <input
                          id="icon-button-file"
                          accept="image/*"
                          type="file"
                          name="file"
                          className={classes.input}
                          onChange={(event) => {
                            readURL(event.target);
                            setImage(event.target.files[0]);
                          }}
                        />

                        <label htmlFor="icon-button-file">
                          <IconButton
                            color="primary"
                            aria-label="upload picture"
                            component="span"
                          >
                            <PhotoCameraSharpIcon />
                          </IconButton>
                        </label>

                        {/**
                         * Emoji
                         */}
                        <IconButton color="primary">
                          <EmojiEmotionsSharpIcon />
                        </IconButton>

                        {/**
                         * Clear Image
                         */}
                        {url && (
                          <Tooltip title="Clear Image">
                            <IconButton>
                              <HighlightOffIcon
                                color="primary"
                                onClick={() => {
                                  setURL(null);
                                }}
                              />
                            </IconButton>
                          </Tooltip>
                        )}

                        {/**
                         * Upload
                         */}
                        {isSmall ? (
                          <Button
                            variant="contained"
                            type="submit"
                            fullWidth={false}
                            round="true"
                            style={{ background: '#428bca', color: '#fff' }}
                          >
                            <SendSharpIcon />
                          </Button>
                        ) : (
                          <Button
                            variant="contained"
                            type="submit"
                            fullWidth={false}
                            round="true"
                            style={{ background: '#428bca', color: '#fff' }}
                          >
                            <SendSharpIcon style={{ marginRight: '0.75em' }} />{' '}
                            Add post{' '}
                          </Button>
                        )}
                      </Grid>

                      <Grid
                        item
                        xs={6}
                        sm={6}
                        md={6}
                        style={{ textAlign: 'textRight', padding: '1em' }}
                      >
                        {url && (
                          <img
                            src={url}
                            width=""
                            style={{
                              objectFit: 'cover',
                              width: '100%',
                              height: '100%',
                            }}
                          />
                        )}
                      </Grid>
                    </Grid>
                  </Card>
                </form>

                <Grid
                  container
                  style={{ padingLeft: '1em', padingRight: '1em' }}
                >
                  {/** List of Posts */}
                  {posts.map((post, idx) => (
                    <PostItem post={post} key={idx} index={idx} />
                  ))}
                </Grid>
              </Grid>

              {/** Adversitement */}
              {!isSmall && !isMedium && (
                <Grid
                  item
                  xs={12}
                  sm={12}
                  md={4}
                  style={{
                    paddingTop: '1.5em',
                    paddingBottom: '1em',
                  }}
                >
                  <JobBanner />
                </Grid>
              )}
            </Grid>
          </ThemeProvider>
        </div>
      )}
    </Spring>
  );
};

Posts.propTypes = {
  loading: PropTypes.bool,
  posts: PropTypes.array,
  getPosts: PropTypes.func,
};

const mapStateToMap = (state) => ({
  loading: state.post.loading,
  posts: state.post.posts,
});

export default connect(mapStateToMap, {
  getPosts,
  addPost,
  likePost,
  unlikePost,
  delPost,
  setAlert,
  addPostWithImage,
})(Posts);
