import React from 'react';
import { Link } from 'react-router-dom';

/** Material UI */
import { makeStyles } from '@material-ui/core/styles';
import { createMuiTheme, ThemeProvider } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Button from '../../Components/components/CustomButtons/Button.js';
import Card from '../../Components/components/Card/Card.js';
import CardBody from '../../Components/components/Card/CardBody.js';
import CardFooter from '../../Components/components/Card/CardFooter.js';
import CardAvatar from '../../Components/components/Card/CardAvatar.js';

/** Material Icon */
import WorkOutlineIcon from '@material-ui/icons/WorkOutline';
import LocationOnIcon from '@material-ui/icons/LocationOn';
import LinkIcon from '@material-ui/icons/Link';

import GitHubIcon from '@material-ui/icons/GitHub';
import TwitterIcon from '@material-ui/icons/Twitter';
import FacebookIcon from '@material-ui/icons/Facebook';
import LinkedInIcon from '@material-ui/icons/LinkedIn';

/** Spring */
import { Spring } from 'react-spring/renderprops';

const useStyles = makeStyles((theme) => ({
  margin: {
    margin: theme.spacing(1),
  },
  cardName: {
    color: '#428bca',
    fontSize: '1.45em',
    fontWeight: '400',
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
  },
  cardStatus: {
    color: '#858585',
    fontSize: '1.15em',
    fontWeight: '400',
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
  },
  card: {
    boxShadow: '2px 2px 5px 2px rgba(0,0,0,0.35)',
    border: '1px #5B5B5B solid',
    height: 'fit-content',
  },
}));

const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#428bca',
    },
    secondary: {
      main: '#111',
    },
    success: {
      main: '#5cb85c',
    },
    danger: {
      main: '#d9534f',
    },
  },
});

const DevItem = ({
  user: { name, avatar, _id },
  status,
  company,
  location,
  skill,
  bio,
  website,
}) => {
  const classes = useStyles();
  return (
    <ThemeProvider theme={theme}>
      <Grid
        item
        xs={12}
        sm={12}
        md={6}
        style={{
          marginTop: '1em',
          paddingRight: '1em',
          paddingLeft: '1em',
          boxSizing: 'border-box',
        }}
      >
        <Card className={classes.card} profile>
          <CardAvatar profile>
            <a href="#pablo" onClick={(e) => e.preventDefault()}>
              <img
                src={`https://avatars.dicebear.com/api/avataaars/${_id}.svg?`}
                alt="..."
              />
            </a>
          </CardAvatar>
          <CardBody profile>
            <h5 className={classes.cardName}>{name}</h5>

            <h6 className={classes.cardStatus}>{status}</h6>
            <Grid container spacing={1}>
              <Grid
                item
                xs={12}
                sm={4}
                md={4}
                style={{
                  borderRight: '1px solid #428bca',
                  borderLeft: '1px solid #428bca',
                }}
              >
                <WorkOutlineIcon
                  style={{ fontSize: '25px', color: '#428bca' }}
                />
                <h6>{company}</h6>
              </Grid>

              <Grid
                item
                xs={12}
                sm={4}
                md={4}
                style={{ borderRight: '1px solid #428bca' }}
              >
                <LocationOnIcon
                  style={{ fontSize: '25px', color: '#428bca' }}
                />
                <h6>{location}</h6>
              </Grid>

              <Grid
                item
                xs={12}
                sm={4}
                md={4}
                style={{ borderRight: '1px solid #428bca' }}
              >
                <LinkIcon style={{ fontSize: '25px', color: '#428bca' }} />
                <h6>{website}</h6>
              </Grid>

              <Grid item xs={12} sm={12} md={12}>
                <p style={{ width: '75%', margin: 'auto' }}>
                  {bio.length < 80 ? bio : `${bio.substring(0, 80)} ...`}
                  <br></br>
                  {bio.length > 80 && <a href="#">Read more</a>}
                </p>
              </Grid>

              <Grid item xs={12} sm={12} md={12}>
                <span style={{ color: '#428bca', fontSize: '1.15em' }}>
                  {skill.map((eachSkill) => `#${eachSkill}, `)}
                </span>
              </Grid>
            </Grid>
          </CardBody>
          <CardFooter>
            <Grid container>
              <Grid item xs={3} sm={3} md={3}>
                <GitHubIcon style={{ fontSize: 35, color: '#111' }} />
              </Grid>

              <Grid item xs={3} sm={3} md={3}>
                <TwitterIcon style={{ fontSize: 35, color: '#00acee' }} />
              </Grid>

              <Grid item xs={3} sm={3} md={3}>
                <FacebookIcon style={{ fontSize: 35, color: '#3b5998' }} />
              </Grid>

              <Grid item xs={3} sm={3} md={3}>
                <LinkedInIcon style={{ fontSize: 35, color: '#0e76a8' }} />
              </Grid>

              <Grid
                item
                xs={12}
                sm={12}
                md={12}
                style={{ marginTop: '0.25em' }}
              >
                <Link to={`/profile/${_id}`} style={{ textDecoration: 'none' }}>
                  <Button color="primary" round style={{ margin: 'auto' }}>
                    VIEW PROFILE
                  </Button>
                </Link>
              </Grid>
            </Grid>
          </CardFooter>
        </Card>
      </Grid>
    </ThemeProvider>
  );
};

export default DevItem;
