import {
  GET_POSTS,
  POST_ERR,
  GET_POST,
  ADD_POST,
  DEL_POST,
  UPDATE_LIKE,
  ADD_COMMENT,
  DEL_COMMENT,
  UPDATE_POST,
} from '../action/type';

const initialState = {
  posts: [],
  post: null,
  loading: true,
  error: {},
};

export default function (state = initialState, action) {
  const { type, payload } = action;
  switch (type) {
    case GET_POSTS: {
      return {
        ...state,
        posts: payload,
        loading: false,
      };
    }

    case GET_POST: {
      return {
        ...state,
        post: payload,
        loading: false,
      };
    }

    case ADD_POST: {
      return {
        ...state,
        posts: [...state.posts, payload],
      };
    }

    case UPDATE_POST: {
      return {
        ...state,
        posts: [...state.posts, payload],
      };
    }

    case DEL_POST: {
      return {
        ...state,
        posts: state.posts.filter((post) => post._id !== payload),
      };
    }

    case UPDATE_LIKE: {
      return {
        ...state,
        posts: state.posts.map((post) =>
          post._id === payload.id ? { ...post, likes: payload.likes } : post
        ),
        post: { ...state.post, likes: payload.likes },
      };
    }

    case ADD_COMMENT: {
      return {
        ...state,
        post: payload,
      };
    }

    case DEL_COMMENT: {
      return {
        ...state,
        post: payload,
      };
    }

    case POST_ERR: {
      return {
        ...state,
        error: payload,
        loading: false,
      };
    }

    default:
      return state;
  }
}
