import React from 'react';
import { useLocation } from 'react-router-dom';
import { makeStyles } from '@material-ui/core/styles';

/** Redux */
import { connect } from 'react-redux';
import { getCurrentProfile } from '../../action/profile';

/** Reponsive  */
import { useMediaQuery } from 'react-responsive';

/** Related Component */
import SmallSideBar from './Res/SideBarSmall';
import LargeSideBar from './Res/SideBarLarge';
import MobileSideBar from './Res/SideBarMobile';

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    width: 'auto',
  },
  title: {
    position: 'relative',
    left: '27%',
    color: '#fff',
    fontFamily: "'Quicksand', sans-serif",
  },
  img: {
    position: 'relative',
    left: '27%',
  },
  drawerPaperLarge: {
    width: '16.67%',
    backgroundColor: '#428bca',
    color: '#fff',
  },

  drawerPaperSmall: {
    width: '8.33%',
    backgroundColor: '#428bca',
    color: '#fff',
  },
  content: {
    flexGrow: 1,
  },
  ListItem: {
    color: '#fff',
  },
  Divider: {
    backgroundColor: '#fff',
  },

  toolbar: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'flex-end',
    padding: theme.spacing(0, 1),
    // necessary for content to be below app bar
    ...theme.mixins.toolbar,
  },
}));

const SideBar = function ({ match, profile, getCurrentProfile }) {
  const classes = useStyles();

  const isLarge = useMediaQuery({ minWidth: 960 });
  const isMedium = useMediaQuery({ minWidth: 600, maxWidth: 951 });
  const isSmall = useMediaQuery({ minWidth: 0, maxWidth: 599 });

  let location = useLocation();
  switch (location.pathname) {
    case '/':
    case '/login':
    case '/register':
      return <div>{false}</div>;
  }

  return (
    <div className={classes.root}>
      {isLarge && <LargeSideBar />}
      {isMedium && <SmallSideBar />}
      {isSmall && <div></div>}
    </div>
  );
};

const mapStateToProps = (state) => ({ profile: state.profile.profile });

export default connect(mapStateToProps, { getCurrentProfile })(SideBar);
