import React, { useState } from 'react';
import { Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';

/** Material UI Component */
import { makeStyles } from '@material-ui/core/styles';
import { createMuiTheme } from '@material-ui/core/styles';
import { ThemeProvider } from '@material-ui/styles';

import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Paper from '@material-ui/core/Paper';
import Box from '@material-ui/core/Box';
import Grid from '@material-ui/core/Grid';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import Typography from '@material-ui/core/Typography';
import Avatar from '@material-ui/core/Avatar';
import loginImg from './resource/login.jpg';
import LockIcon from '@material-ui/icons/Lock';
import EmailIcon from '@material-ui/icons/Email';
import InputAdornment from '@material-ui/core/InputAdornment';

/** Material Component UI */
import { login } from '../../../action/auth';
import Copyright from '../../utils/Copyright';

/** Spring */
import { Spring } from 'react-spring/renderprops';

const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#428bca',
    },
    secondary: {
      main: '#111',
    },
  },
});

const useStyles = makeStyles((theme) => ({
  root: {
    height: '100vh',
  },
  overlay: {
    position: 'relative',
    width: '100%',
    height: '100%',
    backgroundColor: '#428bca',
    opacity: 0.65,
  },
  image: {
    backgroundImage: `url(${loginImg})`,
    backgroundRepeat: 'no-repeat',
    backgroundColor:
      theme.palette.type === 'light'
        ? theme.palette.grey[50]
        : theme.palette.grey[900],
    backgroundSize: 'cover',
    backgroundPosition: 'center',
  },
  paper: {
    margin: theme.spacing(8, 4),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: '#428bca',
  },
  form: {
    width: '100%',
    marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
    color: '#fff',
    background: '#428bca',
    '&:hover': {
      background: '#428bca',
      opacity: 0.8,
    },
  },
  TextField: {
    color: 'primary',
  },
  notchedOutline: {},
  focused: {},
}));

const Login = ({ isAuthenticated, login }) => {
  const [formData, setFormData] = useState({
    email: '',
    password: '',
  });
  const { email, password } = formData;
  const classes = useStyles();

  const onChange = (event) => {
    setFormData({ ...formData, [event.target.name]: event.target.value });
  };

  const onSubmit = async (event) => {
    event.preventDefault();
    login(formData);
  };

  if (isAuthenticated) {
    return <Redirect to="/dashboard" />;
  }

  return (
    <Spring
      from={{ opacity: 0, transform: 'translate3d(-50%,0,0)' }}
      to={{ opacity: 1, transform: 'translate3d(0%,0,0)' }}
      config={{ duration: 600 }}
    >
      {(props) => (
        <div style={props}>
          <ThemeProvider theme={theme}>
            <Grid container component="main" className={classes.root}>
              <Grid item xs={false} sm={4} md={8} className={classes.image}>
                <div className={classes.overlay}></div>
              </Grid>

              <Grid
                item
                xs={12}
                sm={8}
                md={4}
                component={Paper}
                elevation={6}
                square
              >
                <div className={classes.paper}>
                  <Avatar className={classes.avatar}>
                    <LockOutlinedIcon style={{ color: '#fff' }} />
                  </Avatar>
                  <Typography component="h1" variant="h5">
                    Login
                  </Typography>
                  <form
                    onSubmit={(event) => onSubmit(event)}
                    className={classes.form}
                    noValidate
                  >
                    <TextField
                      margin="normal"
                      fullWidth
                      id="email"
                      label="Email Address"
                      name="email"
                      autoComplete="email"
                      autoFocus
                      value={email}
                      onChange={(event) => {
                        onChange(event);
                      }}
                      InputProps={{
                        startAdornment: (
                          <InputAdornment position="start">
                            <EmailIcon style={{ color: '#428bca' }} />
                          </InputAdornment>
                        ),
                        className: classes.TextField,
                      }}
                      InputLabelProps={{
                        style: { color: '#428bca' },
                      }}
                    />
                    <TextField
                      margin="normal"
                      required
                      fullWidth
                      name="password"
                      label="Password"
                      type="password"
                      id="password"
                      autoComplete="current-password"
                      value={password}
                      onChange={(event) => {
                        onChange(event);
                      }}
                      InputProps={{
                        startAdornment: (
                          <InputAdornment position="start">
                            <LockIcon style={{ color: '#428bca' }} />
                          </InputAdornment>
                        ),
                        className: classes.TextField,
                      }}
                      InputLabelProps={{
                        style: { color: '#428bca' },
                      }}
                    />
                    <FormControlLabel
                      control={<Checkbox value="remember" color="primary" />}
                      label="Remember Me"
                    />
                    <Button
                      type="submit"
                      fullWidth
                      variant="contained"
                      className={classes.submit}
                    >
                      Login
                    </Button>
                    <Grid container>
                      <Grid item xs>
                        <a href="#" variant="body2">
                          Forgot password?
                        </a>
                      </Grid>
                      <Grid item>
                        <Link to="/register" style={{ textDecoration: 'none' }}>
                          {"Don't have an account? Sign Up"}
                        </Link>
                      </Grid>
                    </Grid>
                    <Box mt={5}>
                      <Copyright />
                    </Box>
                  </form>
                </div>
              </Grid>
            </Grid>
          </ThemeProvider>
        </div>
      )}
    </Spring>
  );
};

const mapPropsToState = function (state) {
  return {
    isAuthenticated: state.auth.isAuthenticated,
  };
};

export default connect(mapPropsToState, { login })(Login);
