import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import { Link } from 'react-router-dom';

import Logo from '../img/logo.png';

import ExitToAppIcon from '@material-ui/icons/ExitToApp';

const useStyles = makeStyles((theme) => ({
  title: {
    flexGrow: 1,
    fontFamily: "'Orbitron', sans-serif",
    color: '#fff',
  },
  icon: {
    color: '#fff',
  },
  titleDark: {
    flexGrow: 1,
    fontFamily: "'Orbitron', sans-serif",
    color: '#111',
  },
  iconDark: {
    color: '#111',
  },
  item: {
    color: '#428bca',
    fontFamily: 'Quicksand',
  },
}));

export default function ButtonAppBar() {
  const classes = useStyles();
  const [scrollPosition, setSrollPosition] = React.useState(0);
  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const handleScroll = () => {
    const position = window.pageYOffset;
    setSrollPosition(position);
  };

  React.useEffect(() => {
    window.addEventListener('scroll', handleScroll, { passive: true });

    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, []);

  return (
    <div style={{ zIndex: 3 }}>
      <AppBar color="transparent">
        <Toolbar>
          <img src={Logo} alt="Logo" style={{ width: '35px' }} />

          {scrollPosition < 1132 && [
            <Typography variant="h5" className={classes.title}>
              Devmeet
            </Typography>,
            <Button className={classes.icon} onClick={handleClick}>
              <MoreVertIcon />
            </Button>,
            <Menu
              id="simple-menu"
              anchorEl={anchorEl}
              keepMounted
              open={Boolean(anchorEl)}
              onClose={handleClose}
            >
              <Link to="/login" style={{ textDecoration: 'none' }}>
                <MenuItem onClick={handleClose} className={classes.titleDark}>
                  <i
                    className="fas fa-sign-in-alt"
                    style={{ marginRight: '0.5em' }}
                  ></i>{' '}
                  Login
                </MenuItem>
              </Link>

              <Link to="/register" style={{ textDecoration: 'none' }}>
                <MenuItem onClick={handleClose} className={classes.titleDark}>
                  <i
                    className="fas fa-sign-out-alt"
                    style={{ marginRight: '0.5em' }}
                  ></i>{' '}
                  Register
                </MenuItem>
              </Link>
            </Menu>,
          ]}

          {scrollPosition >= 1132 && [
            <Typography variant="h5" className={classes.titleDark}>
              Devmeet
            </Typography>,
            <Button className={classes.iconDark} onClick={handleClick}>
              <MoreVertIcon />
            </Button>,
            <Menu
              id="simple-menu"
              anchorEl={anchorEl}
              keepMounted
              open={Boolean(anchorEl)}
              onClose={handleClose}
            >
              <Link to="/login" style={{ textDecoration: 'none' }}>
                <MenuItem onClick={handleClose} className={classes.titleDark}>
                  <i
                    className="fas fa-sign-in-alt"
                    style={{ marginRight: '0.5em' }}
                  ></i>{' '}
                  Login
                </MenuItem>
              </Link>

              <Link to="/register" style={{ textDecoration: 'none' }}>
                <MenuItem onClick={handleClose} className={classes.titleDark}>
                  <i
                    className="fas fa-sign-out-alt"
                    style={{ marginRight: '0.5em' }}
                  ></i>{' '}
                  Register
                </MenuItem>
              </Link>
            </Menu>,
          ]}
        </Toolbar>
      </AppBar>
    </div>
  );
}
