import React from 'react';

/** Material UI Icon */
import EmojiObjectsIcon from '@material-ui/icons/EmojiObjects';

/** UI Customize Component */
import Card from '../../../Components/components/Card/Card.js';
import { makeStyles } from '@material-ui/core/styles';
import { createMuiTheme, ThemeProvider } from '@material-ui/core/styles';

/** Spring */
import { Spring } from 'react-spring/renderprops';

const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#428bca',
    },
    secondary: {
      main: '#111',
    },
    success: {
      main: '#5cb85c',
    },
    danger: {
      main: '#d9534f',
    },
  },
});

const useStyles = makeStyles((theme) => ({
  hint: {
    position: 'fixed',
    bottom: '0',
    right: '0',
    boxShadow: '2px 2px 5px 2px rgba(0,0,0,0.35)',
    border: '1px #5B5B5B solid',
    padding: '1em',
    width: '30%',
    marginRight: '1em',
  },
}));

const Hint2 = () => {
  const classes = useStyles();
  return (
    <Spring
      from={{ opacity: 0 }}
      to={{ opacity: 1 }}
      config={{ duration: 1000 }}
    >
      {(props) => (
        <div style={props}>
          <ThemeProvider theme={theme}>
            <Card className={classes.hint}>
              <h5 style={{ color: '#428bca' }}>
                <EmojiObjectsIcon /> User's education
              </h5>
              <h6 style={{ color: '#428bca' }}>
                Please fill in your lastest education, if you are current
                studying, don't check the "Graducation" check box
              </h6>
            </Card>
          </ThemeProvider>
        </div>
      )}
    </Spring>
  );
};

export default Hint2;
