import React from 'react';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import { createMuiTheme } from '@material-ui/core/styles';
import Carousel, { Dots } from '@brainhubeu/react-carousel';
import '@brainhubeu/react-carousel/lib/style.css';
import Grid from '@material-ui/core/Grid';

import Company1 from '../img/Company1.png';
import Company2 from '../img/Company2.png';
import Company3 from '../img/Company3.png';
import Company4 from '../img/Company4.png';
import Company5 from '../img/Company5.png';
import Company6 from '../img/Company6.png';
import Company7 from '../img/Company7.png';
import Company8 from '../img/Company8.png';
import Company9 from '../img/Company9.png';
import Company10 from '../img/Company10.png';
import Company11 from '../img/Company11.png';
import Company12 from '../img/Company12.png';
import Company13 from '../img/Company13.png';

/** React Responsive */
import { useMediaQuery } from 'react-responsive';

import '../style/Part9.css';

const useStyles = makeStyles((theme) => ({
  titleHeadline: {
    fontFamily: 'Poiret One',
    fontWeight: '300',
    fontSize: '55px',
    color: '#428bca',
    margin: 'auto',
    marginTop: '3em',
    marginBottom: '2em',
  },
  titleHeadlineSmall: {
    fontFamily: 'Poiret One',
    fontWeight: '100',
    fontSize: '30px',
    color: '#428bca',
    margin: 'auto',
    marginTop: '2em',
    marginBottom: '1em',
  },
  divider: {
    height: '3px',
    width: '60px',
    backgroundColor: '#428bca',
    marginTop: '2em',
    marginBottom: '1em',
    margin: 'auto',
  },
  dividerSmall: {
    height: '3px',
    width: '60px',
    backgroundColor: '#428bca',
    marginTop: '2em',
    marginBottom: '1em',
    margin: 'auto',
  },
}));
const Part9 = () => {
  const classes = useStyles();
  const isSmall = useMediaQuery({ minWidth: 0, maxWidth: 599 });

  return (
    <Grid container>
      <Grid item xs={12}>
        <div className={classes.divider}></div>
      </Grid>
      <Grid
        xs={12}
        style={{
          textAlign: 'center',
        }}
      >
        <div data-aos="fade-down-left">
          {!isSmall && (
            <span className={classes.titleHeadline}>They trust us</span>
          )}
          {isSmall && (
            <span className={classes.titleHeadlineSmall}>They trust us</span>
          )}
        </div>
      </Grid>
      <Grid xs={12}>
        <Carousel
          slidesPerPage={4}
          infinite
          autoPlay={2000}
          animationSpeed={1000}
          stopAutoPlayOnHover
        >
          <img
            src={Company1}
            width={!isSmall ? '150' : '60'}
            className="company"
          />
          <img
            src={Company2}
            width={!isSmall ? '150' : '60'}
            className="company"
          />
          <img
            src={Company3}
            width={!isSmall ? '150' : '60'}
            className="company"
          />
          <img
            src={Company4}
            width={!isSmall ? '150' : '60'}
            className="company"
          />
          <img
            src={Company5}
            width={!isSmall ? '150' : '60'}
            className="company"
          />
          <img
            src={Company6}
            width={!isSmall ? '150' : '60'}
            className="company"
          />
          <img
            src={Company7}
            width={!isSmall ? '150' : '60'}
            className="company"
          />
          <img
            src={Company8}
            width={!isSmall ? '150' : '60'}
            className="company"
          />
          <img
            src={Company9}
            width={!isSmall ? '150' : '60'}
            className="company"
          />
          <img
            src={Company10}
            width={!isSmall ? '150' : '60'}
            className="company"
          />
          <img
            src={Company11}
            width={!isSmall ? '150' : '60'}
            className="company"
          />
          <img
            src={Company12}
            width={!isSmall ? '150' : '60'}
            className="company"
          />
          <img
            src={Company13}
            width={!isSmall ? '150' : '60'}
            className="company"
          />
        </Carousel>
      </Grid>
    </Grid>
  );
};

export default Part9;
