import {
  GET_POSTS,
  POST_ERR,
  GET_POST,
  ADD_POST,
  DEL_POST,
  UPDATE_LIKE,
  ADD_COMMENT,
  DEL_COMMENT,
  UPDATE_POST,
} from './type';
import { setAlert } from '../action/alert';
import { fileUpload } from '../action/utils/upload';
import api from '../reducers/utils/baseURL';

// Get all posts
export const getPosts = () => async (dispatch) => {
  try {
    const res = await api.get('/posts');

    dispatch({
      type: GET_POSTS,
      payload: res.data,
    });
  } catch (error) {
    dispatch({
      type: POST_ERR,
      payload: {
        msg: error.response.statusText,
        status: error.response.status,
      },
    });
  }
};

// Show a thread
export const getThread = (id) => async (dispatch) => {
  try {
    const res = await api.get(`/posts/${id}`);

    dispatch({
      type: GET_POST,
      payload: res.data,
    });
  } catch (error) {
    dispatch({
      type: POST_ERR,
      payload: {
        msg: error.response.statusText,
        status: error.response.status,
      },
    });
  }
};

// add a post
export const addPost = (text) => async (dispatch) => {
  try {
    const body = JSON.stringify(text);
    const config = {
      headers: {
        'Content-Type': 'application/json',
      },
    };

    const res = await api.post(`/posts`, body, config);

    dispatch({
      type: ADD_POST,
      payload: res.data,
    });

    dispatch(setAlert('Post added !', 'success'));
  } catch (error) {
    dispatch({
      type: POST_ERR,
      payload: {
        msg: error.response.statusText,
        status: error.response.status,
      },
    });
  }
};

// delete a post
export const delPost = (_id) => async (dispatch) => {
  try {
    await api.delete(`/posts/${_id}`);

    dispatch({
      type: DEL_POST,
      payload: _id,
    });

    dispatch(setAlert('Post deleted !', 'danger'));
  } catch (error) {
    dispatch({
      type: POST_ERR,
      payload: {
        msg: error.response.statusText,
        status: error.response.status,
      },
    });
  }
};

// Like a post
export const likePost = (_id) => async (dispatch) => {
  try {
    const res = await api.put(`/posts/like/${_id}`);
    if (res.data.msg !== 'User already like this post') {
      dispatch({
        type: UPDATE_LIKE,
        payload: {
          id: _id,
          likes: res.data,
        },
      });
    }
  } catch (error) {
    dispatch({
      type: POST_ERR,
      payload: {
        msg: error.response.statusText,
        status: error.response.status,
      },
    });
  }
};

// Unlike a post
export const unlikePost = (_id) => async (dispatch) => {
  try {
    const res = await api.put(`/posts/unlike/${_id}`);
    dispatch({
      type: UPDATE_LIKE,
      payload: {
        id: _id,
        likes: res.data,
      },
    });
  } catch (error) {
    dispatch({
      type: POST_ERR,
      payload: {
        msg: error.response.statusText,
        status: error.response.status,
      },
    });
  }
};

// Add comment
export const addComment = (comment, post) => async (dispatch) => {
  try {
    const body = JSON.stringify(comment);
    const config = {
      headers: {
        'Content-Type': 'application/json',
      },
    };

    const res = await api.post(`/posts/comment/${post._id}`, body, config);

    dispatch({
      type: ADD_COMMENT,
      payload: res.data,
    });
    dispatch(
      setAlert(
        'You will be notify if there new comment to this post!',
        'success'
      )
    );
  } catch (error) {
    dispatch({
      type: POST_ERR,
      payload: {
        msg: error.response.statusText,
        status: error.response.status,
      },
    });
  }
};

// Delete comment
export const delComment = (comment, post) => async (dispatch) => {
  try {
    const res = await api.delete(`/posts/comment/${post._id}/${comment._id}`);

    dispatch({
      type: DEL_COMMENT,
      payload: res.data,
    });

    dispatch(setAlert('Comment deleted!', 'success'));
  } catch (error) {
    if (error) {
      const errors = error.response.data.errors;
      errors.forEach((err) => {
        dispatch(setAlert(err.msg, 'danger'));
      });
    }

    dispatch({
      type: POST_ERR,
      payload: {
        msg: error.response.statusText,
        status: error.response.status,
      },
    });
  }
};

export const addPostWithImage = (Image, text) => async (dispatch) => {
  try {
    /** Handle Image */
    let Image_id;
    let Post_id;
    fileUpload(Image)
      .then((res) => {
        Image_id = res.data.file.id;
      })
      .then(async () => {
        /** Handle Text  */
        const bodyText = JSON.stringify(text);
        const configText = {
          headers: {
            'Content-Type': 'application/json',
          },
        };
        const res_Post = await api.post(`/posts`, bodyText, configText);
        Post_id = res_Post.data._id;
      })
      .then(async () => {
        const addImg = JSON.stringify({
          image: Image_id,
        });
        const configAddImg = {
          headers: {
            'Content-Type': 'application/json',
          },
        };
        await api
          .put(`/posts/image/${Post_id}`, addImg, configAddImg)
          .then((res) => {
            dispatch({
              type: UPDATE_POST,
              payload: res.data,
            });
          });
      });
  } catch (error) {
    if (error) {
      const errors = error.response.data.errors;
      errors.forEach((err) => {
        dispatch(setAlert(err.msg, 'danger'));
      });
    }

    dispatch({
      type: POST_ERR,
      payload: {
        msg: error.response.statusText,
        status: error.response.status,
      },
    });
  }
};
