import React, { Fragment, useEffect } from 'react';
import CodeSnippet from './CodeSnippet';
import Spinner from '../layout/Spinner';
import Grid from '@material-ui/core/Grid';

import { makeStyles } from '@material-ui/core/styles';
import { connect } from 'react-redux';
import { getCodeOfMe } from '../../action/code';

const CodeSnippetList = ({ loading, userCode, getCodeOfMe }) => {
  useEffect(() => {
    getCodeOfMe();
  }, []);

  if (loading === true) {
    return (
      <div
        style={{
          position: 'relative',
          left: '50%',
          top: '50%',
        }}
      >
        <Spinner color="#0275d8" loading={true} size="50px" />
      </div>
    );
  }

  return (
    <Fragment>
      <Grid container>
        <Grid item xs={12} sm={6} md={6}>
          {userCode.map(
            (item, index) =>
              index % 2 == 0 && (
                <CodeSnippet
                  key={index}
                  description={item.description}
                  content={item.content}
                  language={item.language}
                  _id={item._id}
                />
              )
          )}
        </Grid>
        <Grid item xs={12} sm={6} md={6}>
          {userCode.map(
            (item, index) =>
              index % 2 != 0 && (
                <CodeSnippet
                  key={index}
                  description={item.description}
                  content={item.content}
                  language={item.language}
                  _id={item._id}
                />
              )
          )}
        </Grid>
      </Grid>
    </Fragment>
  );
};

const mapStateToProps = (state) => ({
  loading: state.code.loading,
  userCode: state.code.code,
});

export default connect(mapStateToProps, { getCodeOfMe })(CodeSnippetList);
