import React, { useState } from 'react';

/** Redux  */
import { Link } from 'react-router-dom';
import { Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import { setAlert } from '../../../action/alert';
import { register } from '../../../action/auth';
import { toast } from 'react-toastify';

/** Material UI Component */
import { makeStyles } from '@material-ui/core/styles';
import { createMuiTheme } from '@material-ui/core/styles';
import { ThemeProvider } from '@material-ui/styles';

import Copyright from '../../utils/Copyright';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Paper from '@material-ui/core/Paper';
import Box from '@material-ui/core/Box';
import Grid from '@material-ui/core/Grid';

import Typography from '@material-ui/core/Typography';
import Avatar from '@material-ui/core/Avatar';
import registerImg from './resource/register.jpg';
import EmailIcon from '@material-ui/icons/Email';
import InputAdornment from '@material-ui/core/InputAdornment';

import LockIcon from '@material-ui/icons/Lock';
import LockOpenIcon from '@material-ui/icons/LockOpen';
import AccountBoxIcon from '@material-ui/icons/AccountBox';

/** Spring */
import { Spring } from 'react-spring/renderprops';

/** Style  */
import '../css/Global.css';

const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#428bca',
    },
    secondary: {
      main: '#111',
    },
  },
});

const useStyles = makeStyles((theme) => ({
  root: {
    height: '100vh',
  },
  overlay: {
    position: 'relative',
    width: '100%',
    height: '100%',
    backgroundColor: '#428bca',
    opacity: 0.65,
  },
  image: {
    backgroundImage: `url(${registerImg})`,
    backgroundRepeat: 'no-repeat',
    backgroundColor:
      theme.palette.type === 'light'
        ? theme.palette.grey[50]
        : theme.palette.grey[900],
    backgroundSize: 'cover',
    backgroundPosition: 'center',
  },
  paper: {
    margin: theme.spacing(8, 4),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: '#428bca',
  },
  form: {
    width: '100%',
    marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
  TextField: {
    color: 'primary',
    floatingLabelFocusStyle: {
      color: 'red',
    },
  },
  notchedOutline: {},
  focused: {},
}));

const Register = ({ setAlert, register, isAuthenticated }) => {
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    password: '',
    password2: '',
  });
  const { name, email, password, password2 } = formData;
  const classes = useStyles();

  const onChange = (event) =>
    setFormData({ ...formData, [event.target.name]: event.target.value });

  const onSubmit = async (event) => {
    event.preventDefault();

    if (password === '' || password2 === '') {
      (function () {
        toast.error('Password can not be empty', {});
      })();
    }

    if (name === '') {
      (function () {
        toast.error('Name can not be empty', {});
      })();
    }

    if (email === '') {
      (function () {
        toast.error('Email can not be empty', {
          position: 'top-right',
        });
      })();
    }

    if (email === '' || name === '' || password === '' || password2 === '') {
      return;
    }

    if (password2 !== password) {
      setAlert('Password do not match', 'danger');
    } else {
      register({ name, email, password });
    }
  };

  if (isAuthenticated) {
    return <Redirect to="/init" />;
  }

  return (
    <Spring
      from={{ opacity: 0, transform: 'translate3d(-50%,0,0)' }}
      to={{ opacity: 1, transform: 'translate3d(0%,0,0)' }}
    >
      {(props) => (
        <div style={props}>
          <ThemeProvider theme={theme}>
            <Grid container component="main" className={classes.root}>
              <Grid item xs={12} sm={8} md={4} component={Paper} square>
                <div className={classes.paper}>
                  <Avatar className={classes.avatar}>
                    <LockOpenIcon style={{ color: '#fff' }} />
                  </Avatar>
                  <Typography component="h1" variant="h5">
                    Register
                  </Typography>

                  {/**
                   * Form
                   */}

                  <form
                    onSubmit={(event) => onSubmit(event)}
                    className={classes.form}
                    noValidate
                  >
                    {/** User Name */}
                    <TextField
                      margin="normal"
                      fullWidth
                      id="name"
                      label="User Name"
                      name="name"
                      autoComplete="name"
                      autoFocus
                      value={name}
                      onChange={(event) => {
                        onChange(event);
                      }}
                      InputProps={{
                        startAdornment: (
                          <InputAdornment position="start">
                            <AccountBoxIcon style={{ color: '#428bca' }} />
                          </InputAdornment>
                        ),
                        className: classes.TextField,
                      }}
                      InputLabelProps={{
                        style: { color: '#428bca' },
                      }}
                    />

                    {/** Email */}
                    <TextField
                      margin="normal"
                      fullWidth
                      id="email"
                      label="Email Address"
                      name="email"
                      autoComplete="email"
                      autoFocus
                      value={email}
                      onChange={(event) => {
                        onChange(event);
                      }}
                      InputProps={{
                        startAdornment: (
                          <InputAdornment position="start">
                            <EmailIcon style={{ color: '#428bca' }} />
                          </InputAdornment>
                        ),
                        className: classes.TextField,
                      }}
                      InputLabelProps={{
                        style: { color: '#428bca' },
                      }}
                    />

                    <Grid container>
                      <Grid item xs={6} sm={2} md={6}>
                        {/** Password */}
                        <TextField
                          margin="normal"
                          required
                          name="password"
                          label="Password"
                          type="password"
                          id="password"
                          autoComplete="current-password"
                          value={password}
                          onChange={(event) => {
                            onChange(event);
                          }}
                          InputProps={{
                            startAdornment: (
                              <InputAdornment position="start">
                                <LockIcon style={{ color: '#428bca' }} />
                              </InputAdornment>
                            ),
                            className: classes.TextField,
                          }}
                          style={{ paddingRight: '1em' }}
                        />
                      </Grid>

                      <Grid item xs={6} sm={2} md={6}>
                        {/** Password 2 */}
                        <TextField
                          margin="normal"
                          required
                          name="password2"
                          label="Confirm password"
                          type="password"
                          id="password2"
                          autoComplete="current-password"
                          value={password2}
                          onChange={(event) => {
                            onChange(event);
                          }}
                          InputProps={{
                            startAdornment: (
                              <InputAdornment position="start">
                                <LockIcon style={{ color: '#428bca' }} />
                              </InputAdornment>
                            ),
                            className: classes.TextField,
                          }}
                        />
                      </Grid>
                    </Grid>

                    <FormControlLabel
                      control={<Checkbox value="remember" color="primary" />}
                      label="Remember Me"
                    />

                    <Button
                      type="submit"
                      fullWidth
                      variant="contained"
                      color="primary"
                      className={classes.submit}
                    >
                      Register
                    </Button>
                    <Grid container>
                      <Grid item xs>
                        <a href="/#">Forgot password?</a>
                      </Grid>
                      <Grid item>
                        <Link to="/login" style={{ textDecoration: 'none' }}>
                          {'Already have an account, login here'}
                        </Link>
                      </Grid>
                    </Grid>
                    <Box mt={5}>
                      <Copyright />
                    </Box>
                  </form>
                </div>
              </Grid>

              <Grid item xs={false} sm={4} md={8} className={classes.image}>
                <div className={classes.overlay}></div>
              </Grid>
            </Grid>
          </ThemeProvider>
        </div>
      )}
    </Spring>
  );
};

const mapStateToProps = function (state) {
  return {
    isAuthenticated: state.auth.isAuthenticated,
  };
};

export default connect(mapStateToProps, { setAlert, register })(Register);
