import React from 'react';
import { connect } from 'react-redux';
import { getJobs, setCurrent, clearCurrent } from '../../action/job';
import Spinner from '../layout/Spinner';

/** Material UI */
import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';
import { createMuiTheme, ThemeProvider } from '@material-ui/core/styles';

/** Depend Item */
import JobItem from '../Job/JobItem';
import JobItemSmall from '../Job/JobItemSmall';
import JobDetails from '../Job/JobDetails';

/** Reponsive  */
import { useMediaQuery } from 'react-responsive';

const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#428bca',
    },
    secondary: {
      main: '#d9534f',
    },
  },
});

const useStyles = makeStyles((theme) => ({
  spinner: {
    position: 'absolute',
    left: '50%',
    top: '50%',
  },
}));

export const Job = ({ loading, jobs, getJobs, setCurrent, clearCurrent }) => {
  const classes = useStyles();

  React.useEffect(() => {
    getJobs();
  }, []);

  /** Responsive */
  const isSmall = useMediaQuery({ minWidth: 0, maxWidth: 599 });
  const isMedium = useMediaQuery({ minWidth: 600, maxWidth: 951 });

  if (loading) {
    return (
      <div className={classes.spinner}>
        <Spinner color="#0275d8" loading={true} size="50px" />
      </div>
    );
  }

  return (
    <ThemeProvider theme={theme}>
      <Grid container>
        <Grid item xs={12} sm={4} md={5}>
          {isSmall && (
            <div
              style={{
                width: '100vw',
                height: 'fit-content',
                overflow: 'auto',
                whiteSpace: 'nowrap',
              }}
            >
              {jobs.map((item, index) => (
                <JobItemSmall key={index} item={item} />
              ))}
            </div>
          )}

          {!isSmall && (
            <div
              style={{
                height: '100vh',
                overflow: 'auto',
              }}
            >
              {jobs.map((item, index) => (
                <JobItem key={index} item={item} />
              ))}
            </div>
          )}
        </Grid>
        {/** Start Job Details */}
        <Grid item xs={12} sm={8} md={7}>
          <div
            style={{
              height: '100vh',
              overflow: 'auto',
            }}
          >
            <JobDetails />
          </div>
        </Grid>
        {/** End Job Details */}
      </Grid>
    </ThemeProvider>
  );
};

const mapStateToProps = (state) => ({
  loading: state.job.loading,
  jobs: state.job.jobs,
});

export default connect(mapStateToProps, { getJobs, setCurrent, clearCurrent })(
  Job
);
