import React, { useState } from 'react';

/** Redux */
import { connect } from 'react-redux';
import { addExperience } from '../../../action/profile';

/** Material UI */
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import TextField from '@material-ui/core/TextField';
import InputAdornment from '@material-ui/core/InputAdornment';
import {
  makeStyles,
  createMuiTheme,
  ThemeProvider,
} from '@material-ui/core/styles';
import Button from '../../Components/components/CustomButtons/Button';
import FormControl from '@material-ui/core/FormControl';

/** Material UI Icon */
import WorkIcon from '@material-ui/icons/Work';
import EditIcon from '@material-ui/icons/Edit';
import CalendarTodayIcon from '@material-ui/icons/CalendarToday';

/** React External Component */
import Calendar from 'react-calendar';
import 'react-datepicker/dist/react-datepicker.css';
import 'react-calendar/dist/Calendar.css';
import { toast } from 'react-toastify';

const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#428bca',
    },
    secondary: {
      main: '#111',
    },
    success: {
      main: '#5cb85c',
    },
    danger: {
      main: '#d9534f',
    },
  },
});

const useStyles = makeStyles((theme) => ({
  button: {
    width: '35%',
    margin: 'auto',
  },
  itemTitle: {
    fontSize: '0.85em',
    color: '#428bca',
    fontWeight: '300',
  },
  marginTop: {
    marginTop: '1.5em',
  },
  calenderTitle: {
    fontWeight: '350',
    fontSize: '1.15em',
    marginTop: '1.15em',
    marginEnd: '1.15em',
  },
}));

const InitExperience = ({ addExperience }) => {
  const classes = useStyles();
  const [formData, setFormData] = useState({
    title: null,
    company: null,
    description: null,
    current: true,
  });
  const [startDate, setStartDate] = useState(new Date());
  const [endDate, setEndDate] = useState(new Date());
  const { title, company, description, current } = formData;

  const onChange = (event) => {
    event.preventDefault();
    setFormData({
      ...formData,
      [event.target.name]: event.target.value,
    });
  };

  const onSubmit = (event) => {
    event.preventDefault();

    if (!title || !company || !description) {
      for (const property in formData) {
        if (!formData[property]) {
          (function () {
            toast.error(`Please fill in "${property}" fields !`);
          })();
        }
      }

      return;
    }

    const postForm = {
      title,
      company,
      description,
      current,
      from: startDate,
      to: endDate,
    };
    console.log(postForm);

    addExperience(postForm);
    // setFormData({
    //   title: null,
    //   company: null,
    //   from: null,
    //   to: null,
    //   description: null,
    //   current: true,
    // });
  };

  return (
    <React.Fragment>
      <ThemeProvider theme={theme}>
        <Typography
          color="primary"
          variant="h6"
          align="center"
          gutterBottom
          style={{
            fontFamily: 'Quicksand',
          }}
        >
          <WorkIcon color="primary" /> Edit your work experience
        </Typography>
        <form
          onSubmit={(event) => {
            onSubmit(event);
          }}
        >
          <Grid container spacing={3}>
            <Grid item xs={12} md={6} style={{ marginTop: '2em' }}>
              <FormControl fullWidth>
                <TextField
                  required
                  id="company"
                  label="Company"
                  name="company"
                  autoComplete="company"
                  fullWidth
                  onChange={onChange}
                  value={company}
                  InputProps={{
                    startAdornment: (
                      <InputAdornment position="start" color="primary">
                        <i
                          className="fa fa-building"
                          style={{ color: '#428bca', fontSize: '1.15em' }}
                        ></i>
                      </InputAdornment>
                    ),
                  }}
                />
                <TextField
                  required
                  id="title"
                  label="Title"
                  name="title"
                  autoComplete="title"
                  fullWidth
                  onChange={onChange}
                  value={title}
                  InputProps={{
                    startAdornment: (
                      <InputAdornment position="start" color="primary">
                        <i
                          className="fas fa-briefcase"
                          style={{ color: '#428bca', fontSize: '1.15em' }}
                        ></i>
                      </InputAdornment>
                    ),
                  }}
                />
              </FormControl>
            </Grid>
            <Grid item xs={12} md={6}>
              <FormControl fullWidth>
                <Typography
                  color="primary"
                  variant="h6"
                  align="left"
                  className={classes.calenderTitle}
                >
                  <EditIcon color="primary" /> Job description
                </Typography>

                <TextField
                  className={classes.marginTop}
                  label="Job description"
                  multiline
                  rows={4}
                  variant="outlined"
                  style={{ width: '100%' }}
                  name="description"
                  value={description}
                  onChange={(event) => {
                    onChange(event);
                  }}
                />
              </FormControl>
            </Grid>

            <Grid item xs={12} md={6}>
              <FormControl fullWidth>
                <Typography
                  color="primary"
                  variant="h6"
                  align="center"
                  className={classes.calenderTitle}
                >
                  <CalendarTodayIcon color="primary" />
                  Started date
                </Typography>
                <Calendar onChange={(date) => setStartDate(date)} />
              </FormControl>
            </Grid>
            <Grid item xs={12} md={6}>
              <FormControl fullWidth>
                <Typography
                  color="primary"
                  variant="h6"
                  align="center"
                  className={classes.calenderTitle}
                >
                  <CalendarTodayIcon color="primary" />
                  Ended date
                </Typography>
                <Calendar onChange={(date) => setEndDate(date)} />
              </FormControl>
            </Grid>
            <Button
              variant="contained"
              color="primary"
              round
              className={classes.button}
              type="submit"
            >
              Update user information
            </Button>
          </Grid>
        </form>
      </ThemeProvider>
    </React.Fragment>
  );
};
export default connect(null, { addExperience })(InitExperience);
