import React from 'react';

export const filePdf = (id) => {
  let URL;
  if (!process.env.NODE_ENV || process.env.NODE_ENV === 'development') {
    URL = 'http://localhost:5000/api';
  } else {
    URL = 'Production';
  }

  const url = `${URL}/pdf/pdf/fileid/${id}`;

  return (
    <a href={url} target="_blank">
      {' '}
      CV{' '}
    </a>
  );
};
