import React from 'react';
import { connect } from 'react-redux';
import { getMyJobs, setCurrent } from '../../action/job';

/** Material UI  */
import { makeStyles } from '@material-ui/core/styles';
import { createMuiTheme } from '@material-ui/core/styles';
import { ThemeProvider } from '@material-ui/styles';

/** Material Component */
import Grid from '@material-ui/core/Grid';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import Avatar from '@material-ui/core/Avatar';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardContent from '@material-ui/core/CardContent';
import WorkIcon from '@material-ui/icons/Work';

/** Depend Component */
import JobItemAlt from '../Job/JobItemAlt';
import Spinner from '../layout/Spinner';
import HireTable from './HireTable';

/** Reponsive  */
import { useMediaQuery } from 'react-responsive';

const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#428bca',
    },
    secondary: {
      main: '#d9534f',
    },
  },
});

const useStyles = makeStyles((theme) => ({
  spinner: {
    position: 'absolute',
    left: '50%',
    top: '50%',
  },
  avatar: {
    backgroundColor: '#428bca',
  },
  root: {
    padding: '0em',
  },
  layout: {
    backgroundColor: '#e5e5e5',
  },
  list: {
    padding: '1em',
  },
}));

export const Management = ({ jobs, job, loading, getMyJobs, setCurrent }) => {
  const classes = useStyles();
  const [value, setValue] = React.useState(0);
  /** Responsive */
  const isSmall = useMediaQuery({ minWidth: 0, maxWidth: 599 });

  React.useEffect(() => {
    getMyJobs();
  }, []);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  if (loading) {
    return (
      <div className={classes.spinner}>
        <Spinner color="#0275d8" loading={true} size="50px" />
      </div>
    );
  }

  return (
    <React.Fragment>
      <ThemeProvider theme={theme}>
        <Grid container className={classes.layout}>
          <Grid item xs={12} sm={5} md={4} className={classes.list}>
            {!isSmall && (
              <div
                style={{
                  height: '100vh',
                  overflow: 'auto',
                }}
              >
                <Card className={classes.root}>
                  <CardHeader
                    avatar={
                      <Avatar aria-label="recipe" className={classes.avatar}>
                        <WorkIcon />
                      </Avatar>
                    }
                    title={
                      <span
                        style={{
                          fontFamily: 'Quicksand',
                          fontWeight: '650',
                          fontSize: '1.5em',
                        }}
                      >
                        All post jobs
                      </span>
                    }
                    subheader={
                      <span
                        style={{
                          fontFamily: 'Quicksand',
                          fontWeight: '650',
                          fontSize: '1.25em',
                          color: '#428bca',
                        }}
                      >
                        Total: 5 jobs
                      </span>
                    }
                  />

                  <CardContent style={{ padding: '0em' }}>
                    <List
                      component="nav"
                      style={{ padding: '0em' }}
                      aria-label="mailbox folders"
                    >
                      {jobs &&
                        jobs.map((item, index) => (
                          <ListItem button>
                            <JobItemAlt key={index} item={item} />
                          </ListItem>
                        ))}

                      {!jobs && <h4>You have not posted any job yet ! </h4>}
                    </List>
                  </CardContent>
                </Card>
              </div>
            )}

            {isSmall && (
              <Card className={classes.root}>
                <CardHeader
                  avatar={
                    <Avatar aria-label="recipe" className={classes.avatar}>
                      <WorkIcon />
                    </Avatar>
                  }
                  title={
                    <span
                      style={{
                        fontFamily: 'Quicksand',
                        fontWeight: '650',
                        fontSize: '1.5em',
                      }}
                    >
                      All post jobs
                    </span>
                  }
                  subheader={
                    <span
                      style={{
                        fontFamily: 'Quicksand',
                        fontWeight: '650',
                        fontSize: '1.25em',
                        color: '#428bca',
                      }}
                    >
                      Total: 5 jobs
                    </span>
                  }
                />

                <CardContent style={{ padding: '0em' }}>
                  <div
                    style={{
                      width: '100vw',
                      height: 'fit-content',
                      overflow: 'auto',
                      whiteSpace: 'nowrap',
                    }}
                  >
                    {jobs.map((item, index) => (
                      <ListItem button>
                        <JobItemAlt key={index} item={item} />
                      </ListItem>
                    ))}
                  </div>
                </CardContent>
              </Card>
            )}
          </Grid>

          <Grid item xs={12} sm={7} md={8}>
            <div
              style={{
                height: '100vh',
                overflow: 'auto',
                padding: '1.5em',
              }}
            >
              {job && <HireTable applicant={job.applicant} />}
              {!job && <h5>You have not posted any job yet ! </h5>}
            </div>
          </Grid>
        </Grid>
      </ThemeProvider>
    </React.Fragment>
  );
};

const mapStateToProps = (state) => ({
  loading: state.job.loading,
  jobs: state.job.jobs,
  job: state.job.job,
});

export default connect(mapStateToProps, { getMyJobs, setCurrent })(Management);
