import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import { getJobDetails } from '../../action/job';
import FileImage from '../../action/utils/getImage';
import Spinner from '../layout/Spinner';
import Parser from 'html-react-parser';

/** Material UI  */
import { makeStyles } from '@material-ui/core/styles';
import { createMuiTheme } from '@material-ui/core/styles';
import { ThemeProvider } from '@material-ui/styles';

/** Material Component */
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import Chip from '@material-ui/core/Chip';

/** Icon */
import VisibilityIcon from '@material-ui/icons/Visibility';
import DoneIcon from '@material-ui/icons/Done';
import FavoriteIcon from '@material-ui/icons/Favorite';
import SettingsIcon from '@material-ui/icons/Settings';
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos';
import CodeIcon from '@material-ui/icons/Code';
import Avatar from '@material-ui/core/Avatar';
import ContactPhoneIcon from '@material-ui/icons/ContactPhone';
import PeopleIcon from '@material-ui/icons/People';
import CloudUploadIcon from '@material-ui/icons/CloudUpload';
import PublicIcon from '@material-ui/icons/Public';
import LocalShippingIcon from '@material-ui/icons/LocalShipping';
import InsertDriveFileRoundedIcon from '@material-ui/icons/InsertDriveFileRounded';

const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#428bca',
    },
    secondary: {
      main: '#d9534f',
    },
  },
});

const useStyles = makeStyles((theme) => ({
  layout: {
    textAlign: 'text-center',
    color: '#428bca',
  },
  cardGrid: {
    paddingLeft: '1em',
    paddingRight: '1em',
  },
}));

export const JobDetails = ({ job }) => {
  const classes = useStyles();
  const getRemote = (remote) => {
    if (remote === 'Temporalily due to Covid 19') {
      return (
        <span
          style={{
            color: '#0275d8',
            fontWeight: '750',
          }}
        >
          <i className="fas fa-user-md"></i> Temporalily due to Covid 19{' '}
        </span>
      );
    }

    if (remote === 'Remote Job') {
      return (
        <span
          style={{
            color: '#5bc0de',
            fontWeight: '750',
          }}
        >
          <i className="fas fa-globe-europe"></i> Remote Job{' '}
        </span>
      );
    }

    if (remote === 'Visa Sponsors') {
      return (
        <span
          style={{
            color: '#d9534f',
            fontWeight: '750',
          }}
        >
          <i className="fas fa-truck"></i> Visa Sponsors{' '}
        </span>
      );
    }
  };

  if (job) {
    return (
      <React.Fragment>
        <ThemeProvider theme={theme}>
          <Grid container className={classes.layout}>
            <div
              style={{
                textAlign: 'text-center',
              }}
            ></div>
            {/** Post job Review */}
            <div
              style={{
                padding: '1em',
                margin: '1em',
                fontFamily: 'Quicksand',
                color: '#111',
                fontSize: '1.15em',
              }}
            >
              <Grid container>
                <Grid item xs={2}>
                  {job.image && FileImage(job.image)}
                </Grid>
                <Grid
                  item
                  xs={10}
                  style={{
                    paddingLeft: '1em',
                  }}
                >
                  <h4
                    style={{
                      fontWeight: '750',
                    }}
                  >
                    {job.jobTitle}
                  </h4>
                  <h6
                    style={{
                      fontWeight: '650',
                    }}
                  >
                    {`${job.company}-${job.location}`}
                    <br />
                    <PeopleIcon color="primary" />{' '}
                    {`${job.userInfo.companySize}`}
                    <br />
                    {getRemote(job.remote)}
                  </h6>
                  <Link to="/jobpage">
                    <Button
                      variant="contained"
                      size="large"
                      color="primary"
                      endIcon={<InsertDriveFileRoundedIcon />}
                    >
                      Apply
                    </Button>
                  </Link>

                  <IconButton
                    aria-label="Favorite"
                    color="secondary"
                    size="medium"
                  >
                    <FavoriteIcon />
                  </IconButton>
                </Grid>
              </Grid>

              {/** Divider */}
              <div
                style={{
                  background: '#ccc',
                  height: '1px',
                  width: '100%',
                  marginTop: '1em',
                  marginBottom: '1em',
                }}
              ></div>

              {/** Technology */}
              <div>
                <h5
                  style={{
                    fontWeight: '700',
                  }}
                >
                  Technology
                </h5>
                {job.stack &&
                  job.stack.map((item, index) => (
                    <Chip
                      key={index}
                      avatar={
                        <Avatar>
                          <CodeIcon />
                        </Avatar>
                      }
                      label={item}
                      color="primary"
                    />
                  ))}
              </div>

              {/** Job Description */}
              <div>
                <h5
                  style={{
                    fontWeight: '700',
                  }}
                >
                  We need you
                </h5>

                <div>{Parser(job.description)}</div>

                {job.requirement && (
                  <div>
                    <h5
                      style={{
                        fontWeight: '700',
                      }}
                    >
                      Requirement
                    </h5>
                    <div>{Parser(job.requirement)}</div>
                  </div>
                )}

                {job.preference && (
                  <div>
                    <h5
                      style={{
                        fontWeight: '700',
                      }}
                    >
                      Preference
                    </h5>
                    <div>{Parser(job.preference)}</div>
                  </div>
                )}

                <h6
                  style={{
                    fontWeight: '700',
                  }}
                >
                  Job Type :{' '}
                  <span
                    style={{
                      fontWeight: '650',
                    }}
                  >
                    {job.employmentType}
                    <br></br>
                  </span>
                </h6>

                <h6
                  style={{
                    fontWeight: '700',
                  }}
                >
                  Salary :{' '}
                  <span
                    style={{
                      fontWeight: '650',
                    }}
                  >
                    {' '}
                    <br />
                    <ArrowForwardIosIcon style={{ color: '#5bc0de' }} />
                    {`${job.salary.minimum} to ${job.salary.maximum} ${job.salary.currency} /${job.salary.paymentCircle}`}
                  </span>
                </h6>
                {/** Divider */}
                <div
                  style={{
                    background: '#ccc',
                    height: '1px',
                    width: '100%',
                    marginTop: '1em',
                    marginBottom: '1em',
                  }}
                ></div>
                <h5
                  style={{
                    fontWeight: '700',
                  }}
                >
                  <ContactPhoneIcon color="primary" /> Contact Information:
                </h5>
                <div
                  style={{
                    marginTop: '0.15em',
                    marginBottom: '0.15em',
                    border: '5px solid #ccc',
                    padding: '1em',
                  }}
                >
                  <ul
                    style={{
                      listStyle: 'none',
                    }}
                  >
                    <li>
                      {' '}
                      <span style={{ fontWeight: '700' }}>Phone Number:</span>
                    </li>
                    <li>
                      <ArrowForwardIosIcon
                        fontSize="small"
                        style={{ color: '#5cb85c' }}
                      />{' '}
                      {job.userInfo.phone}
                    </li>
                    <li>
                      {' '}
                      <span style={{ fontWeight: '700' }}>User posted:</span>
                    </li>
                    <li>
                      <ArrowForwardIosIcon
                        fontSize="small"
                        style={{ color: '#5cb85c' }}
                      />
                      {job.userInfo.name}
                    </li>
                  </ul>
                </div>
              </div>
            </div>{' '}
          </Grid>
        </ThemeProvider>
      </React.Fragment>
    );
  } else {
    return (
      <React.Fragment>
        <ThemeProvider theme={theme}>
          <h1 style={{ color: '#428bca' }}>Loading...</h1>
        </ThemeProvider>
      </React.Fragment>
    );
  }
};

const mapStateToProps = (state) => ({
  loading: state.job.loading,
  job: state.job.job,
});

export default connect(mapStateToProps)(JobDetails);
