import React from 'react';

/** UI Customize Component */
import Card from '../../../Components/components/Card/Card.js';
import CardHeader from '../../../Components/components/Card/CardHeader.js';
import ChartistGraph from 'react-chartist';
import CardBody from '../../../Components/components/Card/CardBody.js';
import CardFooter from '../../../Components/components/Card/CardFooter.js';
import AccessTime from '@material-ui/icons/AccessTime';

/** Material UI */
import { makeStyles } from '@material-ui/core/styles';

import {
  whiteColor,
  primaryColor,
  grayColor,
  hexToRgb,
} from '../../../Components/assets/jss/material-dashboard-react.js';

const dashboardStyle = {
  successText: {
    color: primaryColor[0],
  },
  upArrowCardCategory: {
    width: '16px',
    height: '16px',
  },
  stats: {
    color: whiteColor[0],
    display: 'inline-flex',
    fontSize: '12px',
    fontWeight: '600',
    lineHeight: '22px',
    '& svg': {
      top: '4px',
      width: '16px',
      height: '16px',
      position: 'relative',
      marginRight: '3px',
      marginLeft: '3px',
    },
    '& .fab,& .fas,& .far,& .fal,& .material-icons': {
      top: '4px',
      fontSize: '16px',
      position: 'relative',
      marginRight: '3px',
      marginLeft: '3px',
    },
  },
  cardCategory: {
    color: whiteColor[0],
    margin: '0',
    fontSize: '14px',
    marginTop: '0',
    paddingTop: '10px',
    marginBottom: '0',
    fontWeight: '600',
  },
  cardCategoryWhite: {
    color: 'rgba(' + hexToRgb(whiteColor) + ',.62)',
    margin: '0',
    fontSize: '14px',
    marginTop: '0',
    marginBottom: '0',
  },
  cardTitle: {
    color: whiteColor[0],
    marginTop: '0px',
    minHeight: 'auto',
    fontWeight: '300',
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: '3px',
    textDecoration: 'none',
    '& small': {
      color: whiteColor[1],
      fontWeight: '400',
      lineHeight: '1',
    },
  },
  cardTitleWhite: {
    color: whiteColor,
    marginTop: '0px',
    minHeight: 'auto',
    fontWeight: '300',
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: '3px',
    textDecoration: 'none',
    '& small': {
      color: grayColor[1],
      fontWeight: '400',
      lineHeight: '1',
    },
  },
  graphStyle: {
    '& .ct-label': { color: '#fff' },
    '& .ct-bar': {
      stroke: '#fff',
    },
    '& .ct-point': {
      stroke: '#fff',
    },
    '& .ct-grids line': {
      stroke: '#fff',
    },
  },
};
const useStyles = makeStyles(dashboardStyle);

const BarGraph = () => {
  const classes = useStyles();
  var delays2 = 100,
    durations2 = 1500;

  const emailsSubscriptionChart = {
    data: {
      labels: [
        'Jan',
        'Feb',
        'Mar',
        'Apr',
        'Mai',
        'Jun',
        'Jul',
        'Aug',
        'Sep',
        'Oct',
        'Nov',
      ],
      series: [[542, 443, 320, 780, 553, 453, 326, 434, 568, 610, 756]],
    },
    options: {
      axisX: {
        showGrid: false,
      },
      low: 0,
      high: 1000,
      chartPadding: {
        top: 0,
        right: 5,
        bottom: 0,
        left: 0,
      },
    },
    responsiveOptions: [
      [
        'screen and (max-width: 640px)',
        {
          seriesBarDistance: 5,
          axisX: {
            labelInterpolationFnc: function (value) {
              return value[0];
            },
          },
        },
      ],
    ],
    animation: {
      draw: function (data) {
        if (data.type === 'bar') {
          data.element.animate({
            opacity: {
              begin: (data.index + 1) * delays2,
              dur: durations2,
              from: 0,
              to: 1,
              easing: 'ease',
            },
          });
        }
      },
    },
  };

  return (
    <Card chart>
      <CardHeader color="primary">
        <ChartistGraph
          className={classes.graphStyle}
          data={emailsSubscriptionChart.data}
          type="Bar"
          options={emailsSubscriptionChart.options}
          responsiveOptions={emailsSubscriptionChart.responsiveOptions}
          listener={emailsSubscriptionChart.animation}
        />
      </CardHeader>
      <CardBody>
        <h4 className={classes.cardTitle}>Email Subscriptions</h4>
        <p className={classes.cardCategory}>Last Campaign Performance</p>
      </CardBody>
      <CardFooter chart>
        <div className={classes.stats}>
          <AccessTime /> campaign sent 2 days ago
        </div>
      </CardFooter>
    </Card>
  );
};

export default BarGraph;
