import React from 'react';
import { Link } from 'react-router-dom';

/** Spring */
import { makeStyles, withStyles } from '@material-ui/core/styles';
import { createMuiTheme } from '@material-ui/core/styles';
import { MuiThemeProvider } from '@material-ui/core/styles';
import VisibilitySensor from 'react-visibility-sensor';
import Card from '@material-ui/core/Card';
import Grid from '@material-ui/core/Grid';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';

/** React Responsive */
import { useMediaQuery } from 'react-responsive';

/** Image */
import Card1 from '../img/card1.jpg';
import Card2 from '../img/card2.jpg';
import Card3 from '../img/card3.jpg';

import AOS from 'aos';
import 'aos/dist/aos.css';
// You can also use <link> for styles
// ..
AOS.init();

const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#428bca',
    },
    secondary: {
      main: '#111',
    },
  },
});

const useStyles = makeStyles((theme) => ({
  root: {
    maxWidth: 345,
  },
  media: {
    height: 140,
  },
  title: {
    fontFamily: 'Quicksand',
    fontWeight: '500',
    fontSize: '2em',
    color: '#428bca',
    margin: 'auto',
  },
  titleHeadline: {
    fontFamily: 'Poiret One',
    fontWeight: '300',
    fontSize: '55px',
    color: '#333',
    margin: 'auto',
    marginTop: '3em',
    marginBottom: '2em',
  },
  titleHeadlineSmall: {
    fontFamily: 'Quicksand',
    fontWeight: '100',
    fontSize: '30px',
    color: '#333',
    margin: 'auto',
    marginTop: '2em',
    marginBottom: '1em',
  },
  titleParagraph: {
    fontFamily: 'Quicksand',
    fontWeight: '500',
    fontSize: '1.25em',
    color: '#696969',
    margin: 'auto',
    marginTop: '3em',
    marginBottom: '2em',
  },
  cardGrid: {
    padding: '1em',
  },
  card: {
    width: '100%',
    boxShadow: '2px 2px 5px 2px rgba(66, 139, 202,0.35)',
    border: '1px #5B5B5B solid',
  },
  cardTitle: {
    fontFamily: 'Quicksand',
    fontWeight: '800',
  },
  divider: {
    height: '3px',
    width: '60px',
    margin: 'auto',
    backgroundColor: '#428bca',
    marginTop: '5em',
    marginBottom: '1em',
  },
  divider_small: {
    height: '3px',
    width: '60px',
    margin: 'auto',
    backgroundColor: '#428bca',
    marginTop: '3em',
    marginBottom: '1em',
  },
  button: {
    fontSize: '1.5em',
  },
}));

const CustomButtonLarge = withStyles({
  root: {
    margin: 'auto',
    boxShadow: 'none',
    textTransform: 'none',
    fontSize: 25,
    padding: '15px 15px',
    border: '2px solid',
    lineHeight: 1.5,
    color: '#fff',
    backgroundColor: '#428bca',
    borderColor: '#428bca',
    borderRadius: '0px',
    fontFamily: 'Quicksand',
    '&:hover': {
      backgroundColor: '#fff',
      color: '#428bca',
      borderColor: '#428bca',
      boxShadow: 'none',
    },
    '&:active': {
      boxShadow: 'none',
      backgroundColor: '#0275d8',
      borderColor: '#005cbf',
    },
    '&:focus': {
      boxShadow: '0 0 0 0.2rem rgba(0,123,255,.5)',
    },
  },
})(Button);

const CustomButtonSmall = withStyles({
  root: {
    margin: 'auto',
    boxShadow: 'none',
    textTransform: 'none',
    fontSize: 16,
    padding: '10px 10px',
    border: '2px solid',
    lineHeight: 1.5,
    color: '#fff',
    backgroundColor: '#428bca',
    borderColor: '#428bca',
    borderRadius: '0px',
    fontFamily: 'Quicksand',
    '&:hover': {
      backgroundColor: '#fff',
      color: '#428bca',
      borderColor: '#428bca',
      boxShadow: 'none',
    },
    '&:active': {
      boxShadow: 'none',
      backgroundColor: '#0275d8',
      borderColor: '#005cbf',
    },
    '&:focus': {
      boxShadow: '0 0 0 0.2rem rgba(0,123,255,.5)',
    },
  },
})(Button);

const Part3 = () => {
  const classes = useStyles();
  const [show, setShow] = React.useState(false);
  const isSmall = useMediaQuery({ minWidth: 0, maxWidth: 599 });

  function onChange(isVisible) {
    if (isVisible) {
      setShow(true);
    }
  }

  return (
    <Grid
      container
      style={{
        overflow: 'hidden',
      }}
    >
      {/** Title and headline */}
      <Grid
        xs={12}
        style={{
          textAlign: 'center',
        }}
      >
        <div data-aos="fade-up-right">
          <div className={classes.divider}></div>
          <span className={classes.title}>Always for developers</span>
        </div>
      </Grid>

      <Grid
        xs={12}
        style={{
          textAlign: 'center',
          padding: '1em',
        }}
      >
        {/** Content  */}
        <div data-aos="fade-right">
          <Grid container>
            <Grid item xs={1} sm={2} md={2}></Grid>
            <Grid item xs={10} sm={8} md={8}>
              {!isSmall && (
                <span className={classes.titleHeadline}>
                  Helping devs solve real-life issues
                </span>
              )}
              {isSmall && (
                <span className={classes.titleHeadlineSmall}>
                  Helping devs solve real-life issues
                </span>
              )}
              <p className={classes.titleParagraph}>
                Devmeet is a simple social media site for developers share their
                profile and experience, finding potential employers. HR can also
                utilize the site to acquire the best talent for your team.
              </p>
            </Grid>

            <Grid item xs={1} sm={2} md={2}></Grid>
          </Grid>
        </div>
      </Grid>
      <Grid item xs={12} sm={12} md={4} className={classes.cardGrid}>
        <div data-aos="fade-right">
          <Card className={classes.card}>
            <CardActionArea>
              <CardMedia
                component="img"
                alt="Card Title"
                height="300"
                image={Card1}
                title="Card Title"
              />
              <CardContent>
                <Typography
                  gutterBottom
                  variant="h5"
                  component="h2"
                  className={classes.cardTitle}
                >
                  Publish stories
                </Typography>

                <Typography variant="body1" color="textSecondary" component="p">
                  Learn a new programming trick or technique? Share it with the
                  developer community and become an expert. You can also show
                  your recent projects.
                </Typography>
              </CardContent>
            </CardActionArea>
          </Card>
        </div>
      </Grid>

      <Grid item xs={12} sm={12} md={4} className={classes.cardGrid}>
        <div data-aos="fade-up">
          <Card className={classes.card}>
            <CardActionArea>
              <CardMedia
                component="img"
                alt="Card Title"
                height="300"
                image={Card2}
                title="Card Title"
              />
              <CardContent>
                <Typography
                  gutterBottom
                  variant="h5"
                  component="h2"
                  className={classes.cardTitle}
                >
                  Post your profile
                </Typography>
                <Typography variant="body1" color="textSecondary" component="p">
                  Expose yourself in thousand of potential employers. Want to
                  find a job, seeking talent developers ? Devmmet will help you
                  find the best contract.
                </Typography>
              </CardContent>
            </CardActionArea>
          </Card>
        </div>
      </Grid>

      <Grid item xs={12} sm={12} md={4} className={classes.cardGrid}>
        <div data-aos="fade-left">
          <Card className={classes.card}>
            <CardActionArea>
              <CardMedia
                component="img"
                alt="Card Title"
                height="300"
                image={Card3}
                title="Card Title"
              />
              <CardContent>
                <Typography
                  gutterBottom
                  variant="h5"
                  component="h2"
                  className={classes.cardTitle}
                >
                  Start your new blog
                </Typography>
                <Typography variant="body1" color="textSecondary" component="p">
                  Build your own brand and start a blog on your domain for free
                  with Devmeet. Write stories on your blog and share them with
                  Devmeet community directly.
                </Typography>
              </CardContent>
            </CardActionArea>
          </Card>
        </div>
      </Grid>

      <Grid item xs={12}>
        <div data-aos="fade-up-right">
          <div
            style={{
              textAlign: 'center',
              marginBottom: '2.5em',
              marginTop: '2.5em',
            }}
          >
            {!isSmall && (
              <Link to="/register" style={{ textDecoration: 'none' }}>
                {' '}
                <CustomButtonLarge>Learn More</CustomButtonLarge>{' '}
              </Link>
            )}
            {isSmall && (
              <Link to="/register" style={{ textDecoration: 'none' }}>
                {' '}
                <CustomButtonSmall>Learn More</CustomButtonSmall>{' '}
              </Link>
            )}
          </div>
        </div>
      </Grid>
    </Grid>
  );
};

export default Part3;
