import React from 'react';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import ButtonGroup from '@material-ui/core/ButtonGroup';

import FacebookIcon from '@material-ui/icons/Facebook';
import TwitterIcon from '@material-ui/icons/Twitter';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import IconButton from '@material-ui/core/IconButton';

/** React Responsive */
import { useMediaQuery } from 'react-responsive';
function Copyright() {
  /** Responsive */
  const isSmall = useMediaQuery({ minWidth: 0, maxWidth: 599 });
  const isMedium = useMediaQuery({ minWidth: 600, maxWidth: 951 });

  return (
    <React.Fragment>
      {!isSmall && [
        <Typography
          variant="h5"
          color="textSecondary"
          align="center"
          style={{
            color: '#fff',
            fontFamily: 'Quicksand',
          }}
        >
          {'Copyright © '}
          <span style={{ color: '#fff' }}>devmeet.com</span>{' '}
          {new Date().getFullYear()}
          {'all rights reserved.'}
        </Typography>,
      ]}

      {isSmall && [
        <Typography
          variant="body1"
          color="textSecondary"
          align="center"
          style={{
            color: '#fff',
            fontFamily: 'Quicksand',
          }}
        >
          {'Copyright © '}
          <span style={{ color: '#fff' }}>devmeet.com</span>{' '}
          {new Date().getFullYear()}
          {'all rights reserved.'}
        </Typography>,
      ]}
    </React.Fragment>
  );
}

const Part12 = () => {
  return (
    <div
      style={{
        height: '30vh',
        width: '100vw',
        background: '#428bca',
        padding: '3em',
      }}
    >
      <Copyright />
      <div style={{ textAlign: 'center', color: '#fff' }}>
        <ButtonGroup
          variant="text"
          color="primary"
          aria-label="text primary button group"
        >
          <IconButton>
            <FacebookIcon fontSize="large" style={{ color: '#fff' }} />
          </IconButton>
          <IconButton>
            <LinkedInIcon fontSize="large" style={{ color: '#fff' }} />
          </IconButton>
          <IconButton>
            <TwitterIcon fontSize="large" style={{ color: '#fff' }} />
          </IconButton>
        </ButtonGroup>
      </div>
    </div>
  );
};

export default Part12;
