import React from 'react';
import '../style/Part4.css';

import { Link } from 'react-router-dom';
/** Spring */
import { Spring } from 'react-spring/renderprops';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import { createMuiTheme } from '@material-ui/core/styles';
import { MuiThemeProvider } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import VisibilitySensor from 'react-visibility-sensor';
import Button from '@material-ui/core/Button';

/** React Responsive */
import { useMediaQuery } from 'react-responsive';

import ThumbsUpDownIcon from '@material-ui/icons/ThumbsUpDown';
import LaptopIcon from '@material-ui/icons/Laptop';
import DevicesIcon from '@material-ui/icons/Devices';
import WorkIcon from '@material-ui/icons/Work';
import SearchIcon from '@material-ui/icons/Search';
import CodeIcon from '@material-ui/icons/Code';
import PeopleIcon from '@material-ui/icons/People';

const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#428bca',
    },
    secondary: {
      main: '#111',
    },
  },
});

const useStyles = makeStyles((theme) => ({
  root: {
    maxWidth: 345,
  },
  media: {
    height: 140,
  },
  title: {
    fontFamily: 'Quicksand',
    fontWeight: '600',
    fontSize: '1.65em',
    color: '#fff',
    margin: 'auto',
  },
  titleSmall: {
    fontFamily: 'Quicksand',
    fontWeight: '600',
    fontSize: '1.15em',
    color: '#fff',
    margin: 'auto',
  },
  titleHeadline: {
    fontFamily: "'Poiret One', cursive",
    fontWeight: '100',
    fontSize: '40px',
    color: '#fff',
    margin: 'auto',
    marginTop: '3em',
    marginBottom: '3em',
  },
  titleHeadlineSmall: {
    fontFamily: "'Poiret One', cursive",
    fontWeight: '100',
    fontSize: '25px',
    color: '#fff',
    margin: 'auto',
    marginTop: '1em',
    marginBottom: '1.5em',
  },
  titleParagraph: {
    fontFamily: 'Quicksand',
    fontWeight: '100',
    fontSize: '1.15em',
    color: '#fff',
    margin: 'auto',
    marginTop: '1.5em',
    marginBottom: '1.5em',
  },
  cardGrid: {
    padding: '1em',
  },
  card: {
    width: '100%',
    boxShadow: '2px 2px 5px 2px rgba(66, 139, 202,0.35)',
    border: '1px #5B5B5B solid',
  },
  cardTitle: {
    fontFamily: 'Quicksand',
    fontWeight: '800',
  },
  divider: {
    height: '3px',
    width: '60px',
    backgroundColor: '#fff',
    marginBottom: '1em',
  },
  divider_small: {
    height: '3px',
    width: '60px',
    backgroundColor: '#fff',
    marginTop: '0.5em',
    marginBottom: '0.5em',
  },
  button: {
    fontSize: '1.5em',
  },
  feature: {
    fontFamily: "'Poiret One', cursive",
    fontWeight: '600',
    fontSize: '23px',
    color: '#fff',
    marginBottom: '2.5em',
  },
  featureGrid: {
    textAlign: 'left',
    color: '#fff',
    marginTop: '1.5em',
    marginBottom: '2em',
    padding: '1em',
  },
  featureGridSmall: {
    textAlign: 'left',
    color: '#fff',
    marginTop: '0.5em',
    marginBottom: '0.5em',
  },
  featureSmall: {
    fontFamily: "'Poiret One', cursive",
    fontWeight: '600',
    fontSize: '17px',
    color: '#fff',
    marginBottom: '0.5em',
  },
  icon: {
    color: '#fff',
  },
  containerLarge: {
    paddingLeft: '4em',
    paddingRight: '4em',
    paddingTop: '1.5em',
  },
  containerSmall: {
    paddingLeft: '0.5em',
    paddingRight: '0.5em',
    paddingTop: '0.5em',
  },
}));

const CustomButtonLarge = withStyles({
  root: {
    margin: 'auto',
    boxShadow: 'none',
    textTransform: 'none',
    fontSize: 13,
    padding: '15px 15px',
    border: '2px solid',
    lineHeight: 1.5,
    color: '#428bca',
    backgroundColor: '#fff',
    borderColor: '#fff',
    borderRadius: '0px',
    fontFamily: 'Quicksand',
    '&:hover': {
      backgroundColor: '#428bca',
      color: '#fff',
      borderColor: '#fff',
      boxShadow: 'none',
    },
    '&:active': {
      boxShadow: 'none',
      backgroundColor: '#0275d8',
      borderColor: '#005cbf',
    },
    '&:focus': {
      boxShadow: '0 0 0 0.2rem rgba(0,123,255,.5)',
    },
  },
})(Button);

const CustomButtonSmall = withStyles({
  root: {
    margin: 'auto',
    boxShadow: 'none',
    textTransform: 'none',
    fontSize: 13,
    padding: '10px 10px',
    border: '2px solid',
    lineHeight: 1.5,
    color: '#428bca',
    backgroundColor: '#fff',
    borderColor: '#fff',
    borderRadius: '0px',
    fontFamily: 'Quicksand',
    '&:hover': {
      backgroundColor: '#fff',
      color: '#428bca',
      borderColor: '#fff',
      boxShadow: 'none',
    },
    '&:active': {
      boxShadow: 'none',
      backgroundColor: '#0275d8',
      borderColor: '#005cbf',
    },
    '&:focus': {
      boxShadow: '0 0 0 0.2rem rgba(0,123,255,.5)',
    },
  },
})(Button);

const Part4 = () => {
  const classes = useStyles();
  const [show, setShow] = React.useState(false);
  const isSmall = useMediaQuery({ minWidth: 0, maxWidth: 599 });

  return (
    <div className="part-4-container">
      <div className="part-4-overlay">
        <Grid
          container
          className={isSmall ? classes.containerSmall : classes.containerLarge}
        >
          <Grid
            item
            xs={12}
            sm={10}
            md={6}
            lg={8}
            style={{
              textAlign: 'left',
            }}
          >
            <div data-aos="zoom-in">
              {!isSmall && [
                <div className={classes.divider}></div>,
                <span className={classes.title}>Always for developers</span>,
              ]}
              {isSmall && [
                <div className={classes.divider_small}></div>,
                <span className={classes.titleSmall}>
                  Always for developers
                </span>,
              ]}

              <br />
              {!isSmall && (
                <span className={classes.titleHeadline}>
                  All in ones site for you !
                </span>
              )}
              <br />
            </div>
          </Grid>
          <Grid item xs={false} sm={2} md={6} lg={4}>
            {false}
          </Grid>
          <br />
          {/* List of feature  */}
          <Grid item xs={12} sm={6} md={4}>
            <div data-aos="zoom-in-up">
              {!isSmall && (
                <div className={classes.featureGrid}>
                  <ThumbsUpDownIcon fontSize="large" />
                  <span className={classes.feature}>
                    {' '}
                    Contract Optimization
                  </span>
                  <br />
                  <p style={{ marginTop: '1.5em' }}>
                    Filter feature enable quick search for regional employer
                  </p>
                </div>
              )}

              {isSmall && (
                <div className={classes.featureGridSmall}>
                  <ThumbsUpDownIcon />
                  <span className={classes.featureSmall}>
                    {' '}
                    Contract Optimization
                  </span>
                  <p>
                    Filter feature enable quick search for regional employer
                  </p>
                </div>
              )}
            </div>
          </Grid>
          <Grid item xs={12} sm={6} md={4}>
            <div data-aos="zoom-in-down">
              {!isSmall && (
                <div className={classes.featureGrid}>
                  <PeopleIcon fontSize="large" />
                  <span className={classes.feature}>
                    {' '}
                    Personal Social Media{' '}
                  </span>
                  <p style={{ marginTop: '1.5em' }}>
                    Fully functional social media
                  </p>
                </div>
              )}

              {isSmall && (
                <div className={classes.featureGridSmall}>
                  <PeopleIcon />,
                  <span className={classes.featureSmall}>
                    {' '}
                    Personal Social Media{' '}
                  </span>
                  <p>Fully functional social media</p>
                </div>
              )}
            </div>
          </Grid>

          <Grid item xs={12} sm={6} md={4}>
            <div data-aos="zoom-in-left">
              {!isSmall && (
                <div className={classes.featureGrid}>
                  <DevicesIcon fontSize="large" />
                  <span className={classes.feature}>
                    {' '}
                    Device Compatibility{' '}
                  </span>
                  <p>Working in all devices: androids, ios...</p>
                </div>
              )}
              {isSmall && (
                <div className={classes.featureGridSmall}>
                  <DevicesIcon />,
                  <span className={classes.featureSmall}>
                    {' '}
                    Device Compatibility{' '}
                  </span>
                  <p>Working in all devices: androids, ios...</p>
                </div>
              )}
            </div>
          </Grid>

          <Grid item xs={12} sm={6} md={4}>
            <div data-aos="zoom-in-right">
              {!isSmall && (
                <div className={classes.featureGrid}>
                  <SearchIcon fontSize="large" />
                  <span className={classes.feature}>
                    {' '}
                    Thousand of job post{' '}
                  </span>
                  <p>Sheer volume of jobs available for you</p>
                </div>
              )}
              {isSmall && (
                <div className={classes.featureGridSmall}>
                  <SearchIcon />
                  <span className={classes.featureSmall}>
                    {' '}
                    Thousand of job post{' '}
                  </span>
                  <p>Sheer volume of jobs available for you</p>
                </div>
              )}
            </div>
          </Grid>

          <Grid item xs={12} sm={6} md={4}>
            <div data-aos="zoom-out">
              {!isSmall && (
                <div className={classes.featureGrid}>
                  <WorkIcon fontSize="large" />
                  <span className={classes.feature}> Talent acquisition </span>
                  <p>Review potential employee via their profile and CV</p>
                </div>
              )}
              {isSmall && (
                <div className={classes.featureGridSmall}>
                  <WorkIcon />
                  <span className={classes.featureSmall}>
                    {' '}
                    Talent acquisition{' '}
                  </span>
                  <p>Review potential employee via their profile and CV</p>
                </div>
              )}
            </div>
          </Grid>
          <Grid item xs={12} sm={6} md={4}>
            <div data-aos="zoom-out-up">
              {!isSmall && (
                <div className={classes.featureGrid}>
                  <CodeIcon fontSize="large" />
                  <span className={classes.feature}>
                    {' '}
                    Utility tools for developers{' '}
                  </span>
                  <p>Built-in utility tools for day to day task</p>
                </div>
              )}
              {isSmall && (
                <div className={classes.featureGridSmall}>
                  <CodeIcon />,
                  <span className={classes.featureSmall}>
                    {' '}
                    Utility tools for developers{' '}
                  </span>
                  <p>Built-in Utility tools for day to day task</p>
                </div>
              )}
            </div>
          </Grid>

          <Grid item xs={12} sm={12} md={12}>
            <div data-aos="zoom-out-down">
              <div
                style={{
                  textAlign: 'left',
                }}
              >
                {!isSmall && (
                  <Link to="/register" style={{ textDecoration: 'none' }}>
                    {' '}
                    <CustomButtonLarge>Learn More</CustomButtonLarge>{' '}
                  </Link>
                )}
                {isSmall && (
                  <Link to="/register" style={{ textDecoration: 'none' }}>
                    {' '}
                    <CustomButtonSmall>Learn More</CustomButtonSmall>{' '}
                  </Link>
                )}
              </div>
            </div>
          </Grid>
        </Grid>
      </div>
    </div>
  );
};

export default Part4;
