import React from 'react';
import Logo from '../../img/300Logo.png';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { Redirect } from 'react-router-dom';

const Unauthorized = ({ isAuthenticated }) => {
  if (isAuthenticated) {
    return <Redirect to="/dashboard" />;
  }
  return (
    <div class="container-fluid">
      <div class="row ">
        <div className="col-md-8 mx-auto text-center">
          <img src={Logo} alt="Logo" width="300" />
          <h5 style={{ fontWeight: '400' }}>
            Unauthorized access, you have to login first. If you have an account
            <Link to="/login">
              <span className="text-primary"> login here </span>
            </Link>
            , or
            <Link to="/register">
              <span className="text-primary"> create one </span>
            </Link>
            to join our community{' '}
          </h5>
        </div>
      </div>
    </div>
  );
};
const mapStateToProps = (state) => ({
  isAuthenticated: state.auth.isAuthenticated,
});

export default connect(mapStateToProps)(Unauthorized);
