import React, { useState, Component } from 'react';
import { connect } from 'react-redux';
import { updateForm, updateImage } from '../../../action/job';

/** Material UI  */
import { makeStyles } from '@material-ui/core/styles';
import { createMuiTheme } from '@material-ui/core/styles';
import { ThemeProvider } from '@material-ui/styles';

/** Material Component */
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import Input from '@material-ui/core/Input';
import InputAdornment from '@material-ui/core/InputAdornment';
import TextField from '@material-ui/core/TextField';
import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import Tooltip from '@material-ui/core/Tooltip';
import Icon from '@material-ui/core/Icon';

/** Material Icon */
import CreateIcon from '@material-ui/icons/Create';
import DoubleArrowIcon from '@material-ui/icons/DoubleArrow';
import WorkSharpIcon from '@material-ui/icons/WorkSharp';
import BusinessSharpIcon from '@material-ui/icons/BusinessSharp';
import LinkSharpIcon from '@material-ui/icons/LinkSharp';
import ExploreSharpIcon from '@material-ui/icons/ExploreSharp';
import IconButton from '@material-ui/core/IconButton';
import PhotoCameraSharpIcon from '@material-ui/icons/PhotoCameraSharp';
import HighlightOffIcon from '@material-ui/icons/HighlightOff';
import CloudUploadIcon from '@material-ui/icons/CloudUpload';

/** Reponsive  */
import { useMediaQuery } from 'react-responsive';

const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#428bca',
    },
    secondary: {
      main: '#111',
    },
  },
});

const useStyles = makeStyles((theme) => ({
  label: {
    fontFamily: 'Quicksand',
    fontWeight: '700',
    fontSize: '1.5em',
  },
  labelSelect: {
    fontFamily: 'Quicksand',
    fontWeight: '700',
    fontSize: '1.1em',
  },
  FormControl: {
    marginTop: '1em',
    marginBottom: '1em',
  },

  inputText: {
    fontFamily: 'Quicksand',
    fontWeight: '600',
    fontSize: '1em',
  },
  input: {
    display: 'none',
  },
}));

export const Step2 = ({ updateForm, updateImage }) => {
  const classes = useStyles();
  const [image, setImage] = useState(null);
  const [url, setURL] = useState(null);

  /** Responsive */
  const isSmall = useMediaQuery({ minWidth: 0, maxWidth: 599 });
  const isMedium = useMediaQuery({ minWidth: 600, maxWidth: 951 });

  function readURL(input) {
    if (input.files && input.files[0]) {
      const reader = new FileReader();

      reader.onload = function (e) {
        setURL(e.target.result);
      };

      reader.readAsDataURL(input.files[0]); // convert to base64 string
    }
  }

  React.useEffect(() => {
    if (image) {
      updateImage(image);
    }
  }, [image]);

  return (
    <React.Fragment>
      <ThemeProvider theme={theme}>
        <Grid container>
          <Grid item xs={1} sm={2} md={2}></Grid>
          <Grid item xs={10} sm={8} md={8}>
            {isSmall && [
              <Typography
                color="primary"
                align="center"
                variant="h6"
                style={{
                  width: '100%',
                  fontWeight: '500',
                  margin: 'auto',
                  fontFamily: 'Quicksand',
                }}
              >
                <CreateIcon fontSize="large" /> Dev Meet Jobs for Employer
              </Typography>,
              <Typography align="center" variant="h6">
                🔥
              </Typography>,
              <Typography
                align="center"
                variant="h6"
                color="primary"
                style={{
                  width: '100%',
                  fontWeight: '500',
                  margin: 'auto',
                  fontFamily: 'Quicksand',
                }}
              >
                Let's get started
              </Typography>,
            ]}
            {!isSmall && [
              <Typography
                color="primary"
                align="center"
                variant="h4"
                style={{
                  width: '70%',
                  fontWeight: '500',
                  margin: 'auto',
                  fontFamily: 'Quicksand',
                }}
              >
                <CreateIcon fontSize="large" /> Dev Meet Jobs for Employer
              </Typography>,
              <Typography align="center" variant="h6">
                🔥
              </Typography>,
              <Typography
                align="center"
                variant="h5"
                color="primary"
                style={{
                  width: '80%',
                  fontWeight: '500',
                  margin: 'auto',
                  fontFamily: 'Quicksand',
                }}
              >
                Let's get started
              </Typography>,
            ]}

            <Grid container style={{ marginTop: '1em' }}>
              <Grid
                item
                xs={12}
                sm={6}
                md={6}
                style={{
                  paddingRight: '0.5em',
                }}
              >
                <FormControl className={classes.FormControl} fullWidth>
                  {/**
                   * Company Name
                   */}
                  <InputLabel htmlFor="CompanyName" className={classes.label}>
                    Company name <span style={{ color: '#FF0000	' }}>*</span>
                  </InputLabel>

                  <Input
                    name="CompanyName"
                    onChange={(event) =>
                      updateForm('company', event.target.value)
                    }
                    startAdornment={
                      <InputAdornment position="start">
                        <BusinessSharpIcon style={{ color: '#428bca' }} />
                      </InputAdornment>
                    }
                    inputprops={{
                      classes: {
                        input: classes.inputText,
                      },
                    }}
                  />
                </FormControl>

                <FormControl className={classes.FormControl} fullWidth>
                  {/**
                   * Job Title
                   */}
                  <InputLabel htmlFor="JobTitle" className={classes.label}>
                    Job title <span style={{ color: '#FF0000	' }}>*</span>
                  </InputLabel>

                  <Input
                    name="JobTitle"
                    onChange={(event) =>
                      updateForm('jobTitle', event.target.value)
                    }
                    startAdornment={
                      <InputAdornment position="start">
                        <WorkSharpIcon style={{ color: '#428bca' }} />
                      </InputAdornment>
                    }
                    InputProps={{
                      classes: {
                        input: classes.inputText,
                      },
                    }}
                  />
                </FormControl>
              </Grid>
              <Grid
                item
                xs={12}
                sm={6}
                md={6}
                style={{
                  paddingLeft: '0.5em',
                }}
              >
                <FormControl className={classes.FormControl} fullWidth>
                  {/**
                   * Location
                   */}
                  <InputLabel htmlFor="Location" className={classes.label}>
                    Location <span style={{ color: '#FF0000	' }}>*</span>
                  </InputLabel>

                  <Input
                    name="Location"
                    onChange={(event) =>
                      updateForm('location', event.target.value)
                    }
                    startAdornment={
                      <InputAdornment position="start">
                        <ExploreSharpIcon style={{ color: '#428bca' }} />
                      </InputAdornment>
                    }
                    InputProps={{
                      classes: {
                        input: classes.inputText,
                      },
                    }}
                  />
                </FormControl>

                <FormControl fullWidth className={classes.FormControl}>
                  <InputLabel htmlFor="remote" className={classes.labelSelect}>
                    Allowed remote work ?{' '}
                    <span style={{ color: '#FF0000	' }}>*</span>
                  </InputLabel>

                  <Select
                    labelId="remote"
                    id="remote"
                    onChange={(event) =>
                      updateForm('remote', event.target.value)
                    }
                    defaultValue=""
                  >
                    <MenuItem value={'No'}>No</MenuItem>
                    <MenuItem value={'Temporalily due to Covid 19'}>
                      Temporalily due to Covid 19
                    </MenuItem>
                    <MenuItem value={'Remote Job'}>Remote Job</MenuItem>
                    <MenuItem value={'Visa Sponsors'}>Visa Sponsors</MenuItem>
                  </Select>
                </FormControl>
              </Grid>
              <Grid item xs={6} sm={8} md={9}>
                {/**
                 * Image
                 */}
                <input
                  id="icon-button-file"
                  accept="image/*"
                  type="file"
                  name="file"
                  className={classes.input}
                  onChange={(event) => {
                    readURL(event.target);
                    setImage(event.target.files[0]);
                  }}
                />

                {!isSmall && [
                  <label htmlFor="icon-button-file">
                    <IconButton
                      color="primary"
                      aria-label="upload picture"
                      component="span"
                    >
                      <PhotoCameraSharpIcon />
                      <br />
                      <span
                        style={{
                          fontSize: '0.75em',
                        }}
                      >
                        Upload your company Logo
                      </span>
                    </IconButton>
                  </label>,
                  <div>
                    {url && (
                      <span>
                        <Tooltip title="Clear Image">
                          <Button
                            variant="outlined"
                            color="primary"
                            startIcon={<HighlightOffIcon />}
                            onClick={() => {
                              setURL(null);
                            }}
                          >
                            Clear
                          </Button>
                        </Tooltip>
                        <br />
                      </span>
                    )}
                  </div>,
                ]}

                {isSmall && [
                  <label htmlFor="icon-button-file">
                    <IconButton
                      color="primary"
                      aria-label="upload picture"
                      component="span"
                    >
                      <PhotoCameraSharpIcon />
                    </IconButton>
                  </label>,
                  <div>
                    {url && (
                      <span>
                        <Tooltip title="Clear Image">
                          <IconButton>
                            <HighlightOffIcon
                              color="primary"
                              onClick={() => {
                                setURL(null);
                              }}
                            />
                          </IconButton>
                        </Tooltip>
                        <br />
                        <Button
                          variant="contained"
                          color="primary"
                          startIcon={<CloudUploadIcon />}
                        >
                          Upload
                        </Button>
                      </span>
                    )}
                  </div>,
                ]}
              </Grid>
              <Grid item xs={6} sm={4} md={3}>
                {url && (
                  <img
                    src={url}
                    width=""
                    style={{
                      objectFit: 'cover',
                      width: '100%',
                      height: '100%',
                    }}
                  />
                )}
              </Grid>
            </Grid>
          </Grid>
          <Grid item xs={1} sm={2} md={2}></Grid>
        </Grid>
      </ThemeProvider>
    </React.Fragment>
  );
};

export default connect(null, { updateForm, updateImage })(Step2);
