import React from 'react';
import { Link } from 'react-router-dom';
import { makeStyles } from '@material-ui/core/styles';
import { createMuiTheme } from '@material-ui/core/styles';
import { ThemeProvider } from '@material-ui/styles';
import Paper from '@material-ui/core/Paper';
import Stepper from '@material-ui/core/Stepper';
import Step from '@material-ui/core/Step';
import StepLabel from '@material-ui/core/StepLabel';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import Avatar from '@material-ui/core/Avatar';

/** Material UI Icon */
import RecentActorsRoundedIcon from '@material-ui/icons/RecentActorsRounded';
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos';
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';

/** Spring */
import { Spring } from 'react-spring/renderprops';

import Hint1 from './Hint/Hint1';
import Hint2 from './Hint/Hint2';
import Hint3 from './Hint/Hint3';
import InitUser from './InitUser';
import InitEducation from './InitEducation';
import InitExperience from './InitExperience';
import { Grid } from '@material-ui/core';

const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#428bca',
    },
    secondary: {
      main: '#111',
    },
  },
});

function Copyright() {
  return (
    <Typography variant="body2" color="textSecondary" align="center">
      {'Copyright © '}
      <Link color="primary" href="https://material-ui.com/">
        devmeet.com
      </Link>{' '}
      {new Date().getFullYear()}
      {'.'}
    </Typography>
  );
}

const useStyles = makeStyles((theme) => ({
  appBar: {
    position: 'relative',
  },
  layout: {
    width: 'auto',
    marginLeft: theme.spacing(1),
    [theme.breakpoints.up(600 + theme.spacing(2) * 2)]: {
      width: 650,
      height: 'fit-content',
      marginLeft: '2em',
      marginRight: 'auto',
      marginBottom: '2em',
      marginTop: '2em',
    },
    boxShadow: '2px 2px 5px 2px rgba(0,0,0,0.35)',
    border: '1px #5B5B5B solid',
    borderRadius: '0.75em',
  },
  paper: {
    marginTop: theme.spacing(3),
    marginBottom: theme.spacing(3),
    padding: theme.spacing(2),
    [theme.breakpoints.up(600 + theme.spacing(3) * 2)]: {
      marginTop: theme.spacing(6),
      marginBottom: theme.spacing(6),
      padding: theme.spacing(3),
    },
  },
  stepper: {
    padding: theme.spacing(3, 0, 5),
  },
  buttons: {
    display: 'flex',
    justifyContent: 'flex-end',
  },
  button: {
    marginTop: theme.spacing(3),
    marginLeft: theme.spacing(1),
  },
  avatar: {
    margin: 'auto',
    background: '#428bca',
    color: '#fff',
  },
}));

const steps = ['User Information', 'User Education', 'User Experience'];

function getStepContent(step) {
  switch (step) {
    case 0:
      return <InitUser />;
    case 1:
      return <InitEducation />;
    case 2:
      return <InitExperience />;
    default:
      throw new Error('Unknown step');
  }
}

function getStepHint(step) {
  switch (step) {
    case 0:
      return <Hint1 />;
    case 1:
      return <Hint2 />;
    case 2:
      return <Hint3 />;
    default:
      return <div>{false}</div>;
  }
}

export default function InitProfile() {
  const classes = useStyles();
  const [activeStep, setActiveStep] = React.useState(0);

  const handleNext = () => {
    setActiveStep(activeStep + 1);
  };

  const handleBack = () => {
    setActiveStep(activeStep - 1);
  };

  return (
    <React.Fragment>
      <ThemeProvider theme={theme}>
        <Grid container>
          <Grid item xs={8}>
            <main className={classes.layout}>
              <Paper className={classes.paper}>
                <Avatar
                  className={classes.avatar}
                  style={{ width: 50, height: 50 }}
                >
                  <RecentActorsRoundedIcon style={{ fontSize: 30 }} />
                </Avatar>

                <Typography
                  component="h1"
                  variant="h4"
                  align="center"
                  style={{
                    fontFamily: 'Quicksand',
                  }}
                >
                  Complete you profile
                </Typography>
                <Stepper activeStep={activeStep} className={classes.stepper}>
                  {steps.map((label) => (
                    <Step key={label}>
                      <StepLabel>{label}</StepLabel>
                    </Step>
                  ))}
                </Stepper>
                <React.Fragment>
                  {activeStep === steps.length ? (
                    <React.Fragment>
                      <Typography
                        variant="h5"
                        gutterBottom
                        style={{
                          fontFamily: 'Quicksand',
                        }}
                      >
                        Your profile is completed, now you can browse news feed
                        in{' '}
                        <Link to="/posts" style={{ color: '#428bca' }}>
                          here
                        </Link>
                      </Typography>
                      <Typography variant="subtitle1">
                        You can edit profile everytime at the{' '}
                        <Link to="/edit-profile" style={{ color: '#428bca' }}>
                          edit item
                        </Link>{' '}
                        of the side bar
                      </Typography>
                    </React.Fragment>
                  ) : (
                    <React.Fragment>
                      <Spring
                        from={{ opacity: 0 }}
                        to={{ opacity: 1 }}
                        config={{ duration: 1000 }}
                      >
                        {(props) => (
                          <div style={props}>{getStepContent(activeStep)}</div>
                        )}
                      </Spring>

                      <div className={classes.buttons}>
                        {activeStep !== 0 && (
                          <Button
                            variant="text"
                            color="primary"
                            onClick={handleBack}
                            className={classes.button}
                            startIcon={<ArrowBackIosIcon />}
                          >
                            Back
                          </Button>
                        )}
                        <Button
                          variant="text"
                          color="primary"
                          onClick={handleNext}
                          className={classes.button}
                          endIcon={<ArrowForwardIosIcon />}
                        >
                          {activeStep === steps.length - 1 ? 'Finish' : 'Next'}
                        </Button>
                      </div>
                    </React.Fragment>
                  )}
                </React.Fragment>
              </Paper>
              <Copyright />
            </main>
          </Grid>
          <Grid item xs={4}>
            {getStepHint(activeStep)}
          </Grid>
        </Grid>
      </ThemeProvider>
    </React.Fragment>
  );
}
