import React from 'react';
import IconButton from '@material-ui/core/IconButton';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import MenuIcon from '@material-ui/icons/Menu';
import { Link } from 'react-router-dom';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Typography from '@material-ui/core/Typography';

/** Material Icon */
import DynamicFeedIcon from '@material-ui/icons/DynamicFeed';
import MailIcon from '@material-ui/icons/Mail';
import DashboardIcon from '@material-ui/icons/Dashboard';
import InboxIcon from '@material-ui/icons/MoveToInbox';
import EditIcon from '@material-ui/icons/Edit';
import SupervisedUserCircleSharpIcon from '@material-ui/icons/SupervisedUserCircleSharp';
import CodeSharpIcon from '@material-ui/icons/CodeSharp';
import LiveHelpSharpIcon from '@material-ui/icons/LiveHelpSharp';
import ChatBubbleSharpIcon from '@material-ui/icons/ChatBubbleSharp';
import SettingsSharpIcon from '@material-ui/icons/SettingsSharp';
import AccountBoxSharpIcon from '@material-ui/icons/AccountBoxSharp';
import WorkSharpIcon from '@material-ui/icons/WorkSharp';
import SchoolIcon from '@material-ui/icons/School';
import WorkIcon from '@material-ui/icons/Work';
import GroupAddIcon from '@material-ui/icons/GroupAdd';
import DescriptionIcon from '@material-ui/icons/Description';
import BallotIcon from '@material-ui/icons/Ballot';
import WorkOutlineIcon from '@material-ui/icons/WorkOutline';

const options = [
  <Link to="/dashboard" style={{ textDecoration: 'none', color: '#428bca' }}>
    <DashboardIcon fontSize="small" /> DashBoard
  </Link>,
  <Link to="/edit-profile" style={{ textDecoration: 'none', color: '#428bca' }}>
    <AccountBoxSharpIcon fontSize="small" /> Edit Profile
  </Link>,
  <Link
    to="/edit-experience"
    style={{ textDecoration: 'none', color: '#428bca' }}
  >
    <WorkSharpIcon fontSize="small" /> Edit Experience
  </Link>,
  <Link
    to="/edit-education"
    style={{ textDecoration: 'none', color: '#428bca' }}
  >
    <SchoolIcon fontSize="small" /> Edit Education
  </Link>,
  <Link to="/posts" style={{ textDecoration: 'none', color: '#428bca' }}>
    <DynamicFeedIcon fontSize="small" /> Feed
  </Link>,
  <Link to="/dev" style={{ textDecoration: 'none', color: '#428bca' }}>
    <SupervisedUserCircleSharpIcon fontSize="small" /> People
  </Link>,
  <Link to="/code" style={{ textDecoration: 'none', color: '#428bca' }}>
    <CodeSharpIcon fontSize="small" /> Code
  </Link>,
  <Link to="/job" style={{ textDecoration: 'none', color: '#428bca' }}>
    <WorkIcon fontSize="small" /> Jobs
  </Link>,
  <Link to="/hire" style={{ textDecoration: 'none', color: '#428bca' }}>
    <GroupAddIcon fontSize="small" /> Hire
  </Link>,
  <Link to="/management" style={{ textDecoration: 'none', color: '#428bca' }}>
    <BallotIcon fontSize="small" /> Management
  </Link>,
];

const ITEM_HEIGHT = 48;

export default function Collapse() {
  const [anchorEl, setAnchorEl] = React.useState(null);
  const open = Boolean(anchorEl);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <div>
      <IconButton
        aria-label="more"
        aria-controls="long-menu"
        aria-haspopup="true"
        onClick={handleClick}
      >
        <MenuIcon />
      </IconButton>
      <Menu
        id="long-menu"
        anchorEl={anchorEl}
        keepMounted
        open={open}
        onClose={handleClose}
        PaperProps={{
          style: {
            maxHeight: ITEM_HEIGHT * 4.5,
            width: '20ch',
          },
        }}
      >
        {options.map((option) => (
          <MenuItem
            key={option}
            selected={option === 'Pyxis'}
            onClick={handleClose}
          >
            {option}
          </MenuItem>
        ))}
      </Menu>
    </div>
  );
}
