import React from 'react';
import { connect } from 'react-redux';
import { getJobs, setCurrent, clearCurrent } from '../../action/job';
import { Link } from 'react-router-dom';
import Spinner from '../layout/Spinner';

/** Material UI */
import {
  makeStyles,
  createMuiTheme,
  ThemeProvider,
} from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';

/** Depend Item */
import JobItem from '../Job/JobItem';

const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#428bca',
    },
    secondary: {
      main: '#d9534f',
    },
  },
});

const useStyles = makeStyles((theme) => ({
  spinner: {
    position: 'absolute',
    left: '50%',
    top: '50%',
  },
  root: {
    maxWidth: '100%',
    height: '100vh',
    position: '-webkit-sticky',
    position: 'sticky',
    top: 0,
  },
}));

export const JobBanner = ({
  loading,
  jobs,
  getJobs,
  setCurrent,
  clearCurrent,
}) => {
  const classes = useStyles();

  React.useEffect(() => {
    getJobs();
  }, []);

  return (
    <ThemeProvider theme={theme}>
      <Card className={classes.root}>
        <CardActionArea>
          <CardMedia
            component="img"
            alt="Jobs"
            height="50"
            image="https://moneydotcomvip.files.wordpress.com/2017/02/170217_facebookjobs1.jpg"
            title="Jobs"
          />
          <CardContent>
            {loading ? (
              <div className={classes.spinner}>
                <Spinner color="#0275d8" loading={true} size="50px" />
              </div>
            ) : (
              <div
                style={{
                  height: '100vh',
                  overflow: 'auto',
                }}
              >
                {jobs.map((item, index) => (
                  <Link
                    to="jobpage"
                    key={index}
                    style={{ textDecoration: 'none' }}
                  >
                    <JobItem item={item} />
                  </Link>
                ))}
              </div>
            )}
          </CardContent>
        </CardActionArea>
      </Card>
    </ThemeProvider>
  );
};

const mapStateToProps = (state) => ({
  loading: state.job.loading,
  jobs: state.job.jobs,
});

const mapDispatchToProps = {};

export default connect(mapStateToProps, { getJobs, setCurrent, clearCurrent })(
  JobBanner
);
