import React from 'react';
import Grid from '@material-ui/core/Grid';
import VisibilitySensor from 'react-visibility-sensor';
import Typography from '@material-ui/core/Typography';

/** Spring */
import { Spring } from 'react-spring/renderprops';
import { makeStyles } from '@material-ui/core/styles';
import { createMuiTheme } from '@material-ui/core/styles';
import '../style/Part2.css';

/** Icon */
import SupervisorAccountIcon from '@material-ui/icons/SupervisorAccount';
import AllInboxIcon from '@material-ui/icons/AllInbox';
import ThumbsUpDownIcon from '@material-ui/icons/ThumbsUpDown';

/** React Responsive */
import { useMediaQuery } from 'react-responsive';

const useStyles = makeStyles((theme) => ({
  wrapper: {
    color: '#fff',
    fontFamily: 'Quicksand',
    fontWeight: '500',
    fontSize: '1.15em',
    paddingTop: '5em',
    paddingBottom: '5em',
    paddingLeft: '1em',
    paddingRight: '1em',
    overflow: 'hidden',
  },
  item: {
    padding: '1em',
    textAlign: 'left',
  },
  number: {
    fontSize: '5rem',
    fontFamily: "'Poiret One', cursive",
  },
  number_small: {
    fontSize: '3rem',
    fontFamily: "'Poiret One', cursive",
  },
  icon_large: {
    fontSize: '4.5rem',
    margin: 'auto',
  },
  icon_small: {
    fontSize: '3rem',
    margin: 'auto',
  },
}));

const Part2 = () => {
  const classes = useStyles();
  const [show, setShow] = React.useState(false);
  const isSmall = useMediaQuery({ minWidth: 0, maxWidth: 599 });

  function onChange(isVisible) {
    if (isVisible) {
      setShow(!show);
    }
  }
  return (
    <VisibilitySensor onChange={onChange}>
      <header className="stats-header ">
        {!isSmall && (
          <Grid container className={classes.wrapper}>
            <Spring
              from={{
                opacity: 0,
                marginLeft: -300,
                number: 0,
              }}
              to={{ opacity: 1, marginLeft: 0, number: 900 }}
              config={{ duration: 2000 }}
              reset={{ show }}
            >
              {(props) => (
                <Grid xs={12} sm={12} md={4} className={classes.item}>
                  <Grid container>
                    <Grid item xs={12} sm={6} md={12} lg={6}>
                      <Typography variant="h1" component="h3">
                        <span className={classes.number}>
                          {props.number.toFixed()}
                        </span>
                      </Typography>
                    </Grid>
                    <Grid item xs={12} sm={6} md={12} lg={6}>
                      <SupervisorAccountIcon className={classes.icon_large} />

                      <h2>Members</h2>
                      <p>
                        For 5 years devmeet have accumulated hundreds of members
                        constantly interact with each others
                      </p>
                    </Grid>
                  </Grid>
                </Grid>
              )}
            </Spring>

            <Spring
              from={{ opacity: 0, marginLeft: 300, number: 0 }}
              to={{ opacity: 1, marginLeft: 0, number: 300 }}
              config={{ duration: 2000 }}
              reset={{ show }}
            >
              {(props) => (
                <Grid xs={12} sm={12} md={4} className={classes.item}>
                  <Grid container>
                    <Grid item xs={12} sm={6} md={12} lg={6}>
                      <Typography variant="h1" component="h3">
                        <span className={classes.number}>
                          {props.number.toFixed()}
                        </span>
                      </Typography>
                    </Grid>
                    <Grid item xs={12} sm={6} md={12} lg={6}>
                      <AllInboxIcon className={classes.icon_large} />
                      <h2>Jobs posted</h2>
                      <p>
                        HR and employer can utilize devmeet job post section to
                        acquire high qualified talents
                      </p>
                    </Grid>
                  </Grid>
                </Grid>
              )}
            </Spring>

            <Spring
              from={{ opacity: 0, marginLeft: 300, number: 0 }}
              to={{ opacity: 1, marginLeft: 0, number: 100 }}
              config={{ duration: 2000 }}
              reset={{ show }}
            >
              {(props) => (
                <Grid xs={12} sm={12} md={4} className={classes.item}>
                  <Grid container>
                    <Grid item xs={12} sm={6} md={12} lg={6}>
                      <Typography variant="h1" component="h3">
                        <span className={classes.number}>
                          {props.number.toFixed()}
                        </span>
                      </Typography>
                    </Grid>
                    <Grid item xs={12} sm={6} md={12} lg={6}>
                      <ThumbsUpDownIcon className={classes.icon_large} />
                      <h2>Success Contract</h2>
                      <p>
                        Fast and responsive support team help to connect
                        employee and employer
                      </p>
                    </Grid>
                  </Grid>
                </Grid>
              )}
            </Spring>
          </Grid>
        )}

        {isSmall && (
          <Grid container className={classes.wrapper}>
            <Spring
              from={{
                opacity: 0,
                marginLeft: -300,
                number: 0,
              }}
              to={{ opacity: 1, marginLeft: 0, number: 900 }}
              config={{ duration: 2000 }}
              reset={{ show }}
            >
              {(props) => (
                <Grid xs={12} sm={12} md={4} className={classes.item}>
                  <Grid container>
                    <Grid item xs={6}>
                      <Typography variant="h1" component="h3">
                        <span className={classes.number_small}>
                          {props.number.toFixed()}
                        </span>
                      </Typography>
                    </Grid>
                    <Grid item xs={6}>
                      <Typography variant="h1" component="h3">
                        <span className={classes.number_small}>
                          <SupervisorAccountIcon
                            className={classes.icon_small}
                          />
                        </span>
                      </Typography>
                    </Grid>
                    <Grid item xs={12}>
                      <h4>Jobs posted</h4>
                      <p>
                        HR and employer can utilize devmeet job post section to
                        acquire high qualified talents
                      </p>
                    </Grid>
                  </Grid>
                </Grid>
              )}
            </Spring>

            <Spring
              from={{ opacity: 0, marginLeft: 300, number: 0 }}
              to={{ opacity: 1, marginLeft: 0, number: 300 }}
              config={{ duration: 2000 }}
              reset={{ show }}
            >
              {(props) => (
                <Grid xs={12} sm={12} md={4} className={classes.item}>
                  <Grid container>
                    <Grid item xs={6}>
                      <Typography variant="h1" component="h3">
                        <span className={classes.number_small}>
                          {props.number.toFixed()}
                        </span>
                      </Typography>
                    </Grid>
                    <Grid item xs={6}>
                      <Typography variant="h1" component="h3">
                        <span className={classes.number_small}>
                          <AllInboxIcon className={classes.icon_small} />
                        </span>
                      </Typography>
                    </Grid>

                    <Grid item xs={12}>
                      <h4>Jobs posted</h4>
                      <p>
                        HR and employer can utilize devmeet job post section to
                        acquire high qualified talents
                      </p>
                    </Grid>
                  </Grid>
                </Grid>
              )}
            </Spring>

            <Spring
              from={{ opacity: 0, marginLeft: 300, number: 0 }}
              to={{ opacity: 1, marginLeft: 0, number: 100 }}
              config={{ duration: 2000 }}
              reset={{ show }}
            >
              {(props) => (
                <Grid xs={12} sm={12} md={4} className={classes.item}>
                  <Grid container>
                    <Grid item xs={6}>
                      <Typography variant="h1" component="h3">
                        <span className={classes.number_small}>
                          {props.number.toFixed()}
                        </span>
                      </Typography>
                    </Grid>
                    <Grid item xs={6}>
                      <Typography variant="h1" component="h3">
                        <span className={classes.number_small}>
                          <ThumbsUpDownIcon className={classes.icon_small} />
                        </span>
                      </Typography>
                    </Grid>

                    <Grid item xs={12}>
                      <h4>Success Contract</h4>
                      <p>
                        Fast and responsive support team help to connect
                        employee and employer
                      </p>
                    </Grid>
                  </Grid>
                </Grid>
              )}
            </Spring>
          </Grid>
        )}
      </header>
    </VisibilitySensor>
  );
};

export default Part2;
