import {
  PROFILE_ERROR,
  GET_PROFILE,
  CLEAR_PROFILE,
  UPDATE_PROFILE,
  GET_PROFILES,
  GET_REPOS,
  SEARCH_PROFILE,
} from '../action/type';
import { setAlert } from '../action/alert';
import api from '../reducers/utils/baseURL';

// Get profile of user
export const getCurrentProfile = () => async (dispatch) => {
  try {
    const res = await api.get('/profile/me');
    dispatch({
      type: GET_PROFILE,
      payload: res.data,
    });
  } catch (error) {
    dispatch({
      type: PROFILE_ERROR,
      payload: {
        msg: error.response.statusText,
        status: error.response.status,
      },
    });
  }
};

// Get profile of all users
export const getUsersProfile = () => async (dispatch) => {
  try {
    const res = await api.get('/profile');
    dispatch({
      type: GET_PROFILES,
      payload: res.data,
    });
  } catch (error) {
    try {
      dispatch({
        type: PROFILE_ERROR,
        payload: {
          msg: error.response.statusText,
          status: error.response.status,
        },
      });
    } catch (TypeError) {
      dispatch({
        type: PROFILE_ERROR,
        payload: {
          msg: 'Empty requirement field',
        },
      });
    }
  }
};

