// Search Profile
export const searchProfile = (searchTerm) => async (dispatch) => {
  if (searchTerm === '') {
    dispatch(setAlert('Empty search term !', 'danger'));
    return;
  }

  dispatch({
    type: SEARCH_PROFILE,
    payload: searchTerm,
  });
};
