import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Collapse from '@material-ui/core/Collapse';
import Avatar from '@material-ui/core/Avatar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import { blue } from '@material-ui/core/colors';
import FavoriteIcon from '@material-ui/icons/Favorite';
import ShareIcon from '@material-ui/icons/Share';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import HighlightOffSharpIcon from '@material-ui/icons/HighlightOffSharp';
import SchoolIcon from '@material-ui/icons/School';
import FormGroup from '@material-ui/core/FormGroup';
import MenuItem from '@material-ui/core/MenuItem';
import Select from '@material-ui/core/Select';
import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';
import Grid from '@material-ui/core/Grid';
import FormHelperText from '@material-ui/core/FormHelperText';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import RadioGroup from '@material-ui/core/RadioGroup';
import Radio from '@material-ui/core/Radio';
import BeenhereIcon from '@material-ui/icons/Beenhere';
import BookmarksIcon from '@material-ui/icons/Bookmarks';

/** Use Spring */
import { Spring } from 'react-spring/renderprops';

/** Redux */
import { connect } from 'react-redux';
import { updateForm } from '../../../../action/job';

/** Draft JS  */
import CustomToolbarEditor from '../../Editor';

const useStyles = makeStyles((theme) => ({
  root: {
    maxWidth: 550,
    border: '2px solid #ccc',
    margin: 'auto',
  },
  media: {
    height: 0,
    paddingTop: '56.25%', // 16:9
  },
  expand: {
    transform: 'rotate(0deg)',
    marginLeft: 'auto',
    transition: theme.transitions.create('transform', {
      duration: theme.transitions.duration.shortest,
    }),
  },
  expandOpen: {
    transform: 'rotate(180deg)',
  },
  avatar: {
    backgroundColor: blue[400],
  },
  span: {
    fontFamily: 'Quicksand',
    fontWeight: '600',
    fontSize: '1.15em',
  },
  helperText: {
    fontFamily: 'Quicksand',
    fontWeight: '600',
    fontSize: '1.15em',
  },
  InputLabel: {
    fontFamily: 'Quicksand',
    fontWeight: '700',
  },
  formControl: {
    width: '10em',
    marginLeft: '0.5em',
    marginRight: '0.5em',
  },

  input: {
    fontFamily: 'Quicksand',
    fontWeight: '600',
    fontSize: '1em',
  },
}));

const Preference = ({ index, del, updateForm, form }) => {
  const classes = useStyles();
  const [text, setText] = React.useState('');

  return (
    <Spring
      from={{ opacity: 0 }}
      to={{ opacity: 1 }}
      leave={{ opacity: 0 }}
      config={{ duration: 1000 }}
    >
      {(props) => (
        <div style={props}>
          <Card className={classes.root}>
            <CardHeader
              avatar={
                <Avatar aria-label="recipe" className={classes.avatar}>
                  <BookmarksIcon />
                </Avatar>
              }
              action={
                <IconButton aria-label="settings">
                  <BeenhereIcon style={{ color: '#008000' }} />
                </IconButton>
              }
              title={
                <span
                  style={{
                    fontFamily: 'Quicksand',
                    fontWeight: '600',
                    fontSize: '1.25em',
                  }}
                >
                  Preference
                </span>
              }
            />
            <CardContent>
              <Grid container>
                <CustomToolbarEditor content="preference" />
              </Grid>
            </CardContent>
          </Card>
        </div>
      )}
    </Spring>
  );
};

const mapStateToProps = (state) => ({
  form: state.job.form,
});

export default connect(mapStateToProps, { updateForm })(Preference);
