import React, { Fragment } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import Spinner from '../../layout/Spinner';
import '../Dashboard/style/style.css';

/** Material UI */
import { makeStyles } from '@material-ui/core/styles';
import { createMuiTheme } from '@material-ui/core/styles';

/** Material Icon*/
import SchoolIcon from '@material-ui/icons/School';

/** UI Customize Component */
import Table from '../../Components/components/Table/Table.js';
import Card from '../../Components/components/Card/Card.js';
import CardHeader from '../../Components/components/Card/CardHeader.js';
import CardBody from '../../Components/components/Card/CardBody.js';

const useStyles = makeStyles((theme) => ({
  spinner: {
    position: 'absolute',
    left: '50%',
    top: '50%',
  },
  table: {
    marginTop: '4em',
    boxShadow: '2px 2px 5px 2px rgba(0,0,0,0.35)',
  },
  icon: {
    cursor: 'pointer',
  },
  card: {
    marginTop: '4em',
    boxShadow: '2px 2px 5px 2px rgba(0,0,0,0.35)',
  },
}));

const IndividualEducation = ({ loading, education }) => {
  const classes = useStyles();

  if (loading) {
    return (
      <div className={classes.spinner}>
        <Spinner color="#0275d8" loading={true} size="50" />
      </div>
    );
  }

  return (
    <Fragment>
      <Card className={classes.table}>
        <CardHeader color="primary">
          <h4>
            <SchoolIcon /> Education
          </h4>
          <p>Click the "Edit Education" tab to add or remove new item</p>
        </CardHeader>
        <CardBody>
          <Table
            tableHeaderColor="primary"
            tableHead={['School', 'Degree', 'From', 'To', 'Graduated', 'GPA']}
            tableData={education}
          />
        </CardBody>
      </Card>
    </Fragment>
  );
};

IndividualEducation.propTypes = {
  loading: PropTypes.bool,
};
const mapStateToProps = (state) => ({
  loading: state.profile.loading,
});
export default connect(mapStateToProps)(IndividualEducation);
