import React from 'react';
import { Link } from 'react-router-dom';
import { likePost, unlikePost, delPost } from '../../action/post';
import { connect } from 'react-redux';
import { setAlert } from '../../action/alert';
import FileImage from '../../action/utils/getImage';

/** Material UI */
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import { createMuiTheme, ThemeProvider } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import IconButton from '@material-ui/core/IconButton';

/** Local Customize Component */
import Card from '../Components/components/Card/Card.js';
import CardHeader from '../Components/components/Card/CardHeader.js';
import CardBody from '../Components/components/Card/CardBody.js';
import CardFooter from '../Components/components/Card/CardFooter.js';
import CardAvatar from '../Components/components/Card/CardAvatar.js';

/** Material Icon */
import ThumbUpIcon from '@material-ui/icons/ThumbUp';
import ThumbDownIcon from '@material-ui/icons/ThumbDown';
import ShareIcon from '@material-ui/icons/Share';
import SmsSharpIcon from '@material-ui/icons/SmsSharp';
import TwitterIcon from '@material-ui/icons/Twitter';
import FacebookIcon from '@material-ui/icons/Facebook';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import MoreHorizIcon from '@material-ui/icons/MoreHoriz';
import PublicIcon from '@material-ui/icons/Public';

/** Reponsive  */
import { useMediaQuery } from 'react-responsive';

/** Utils */
var moment = require('moment');

const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#428bca',
    },
    secondary: {
      main: '#111',
    },
    success: {
      main: '#5cb85c',
    },
    danger: {
      main: '#d9534f',
    },
  },
});

const CssTextField = withStyles({
  root: {
    '& label.Mui-focused': {
      color: '#428bca',
    },
    '& .MuiInput-underline:after': {
      borderBottomColor: '#428bca',
    },
    '& .MuiOutlinedInput-root': {
      '& fieldset': {
        borderColor: '#428bca',
      },
      '&:hover fieldset': {
        borderColor: '#428bca',
      },
      '&.Mui-focused fieldset': {
        borderColor: '#428bca',
      },
    },
  },
})(TextField);

const useStyles = makeStyles((theme) => ({
  spinner: {
    position: 'absolute',
    left: '50%',
    top: '50%',
  },
  table: {
    marginTop: '4em',
    boxShadow: '2px 2px 5px 2px rgba(0,0,0,0.35)',
  },
  content: {
    paddingLeft: '2em',
    paddingRight: '1em',
    paddingTop: '1em',
  },
  cardCategoryWhite: {
    color: 'rgba(255,255,255,.62)',
    fontSize: '14px',
  },
  cardTitleWhite: {
    color: '#FFFFFF',
    minHeight: 'auto',
    fontWeight: '300',
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: '3px',
    textDecoration: 'none',
  },
  icon: {
    cursor: 'pointer',
  },
  card: {
    marginTop: '4em',
    boxShadow: '2px 2px 5px 2px rgba(0,0,0,0.35)',
  },
  IconButton: {
    marginRight: '5px',
  },
  profileItem: {
    borderRight: '#428bca solid 1px',
  },
  profileItemFeature: {
    fontWeight: '300',
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    color: '#111',
  },
  description: {
    width: '50%',
    margin: 'auto',
    marginBottom: '0.75em',
  },
  cardCategory: {
    fontWeight: '300',
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    color: '#7a7979',
  },
  cardTitle: {
    fontWeight: '300',
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
  },
  feedColumn: {
    paddingLeft: '0.75em',
    paddingRight: '0.75em',
  },
  newPost: {
    boxShadow: '1px 1px 3px 1px rgba(0,0,0,0.35)',
    border: '1px solid gray',
    padding: '1.25em',
  },
  Post: {
    boxShadow: '1px 1px 3px 1px rgba(0,0,0,0.35)',
    border: '1px solid gray',
    marginTop: '3.5em',
    marginBottom: '3.5em',
  },
  TextField: {
    marginTop: '0.5em',
    marginBottom: '0.5em',
  },
}));

const PostItem = ({
  user_id,
  delPost,
  likePost,
  unlikePost,
  setAlert,
  post: { _id, text, name, likes, date, comments, user, image },
}) => {
  const classes = useStyles();
  const [anchorEl, setAnchorEl] = React.useState(null);
  const [anchor, setAnchor] = React.useState(null);

  /** Responsive */
  const isSmall = useMediaQuery({ minWidth: 0, maxWidth: 599 });
  const isMedium = useMediaQuery({ minWidth: 600, maxWidth: 951 });

  /**
   * More Button
   * --> Delete Post
   */
  const handleClick = (event) => {
    setAnchor(event.currentTarget);
  };

  const handleClose = () => {
    if (user_id === user) {
      delPost(_id);
    } else {
      setAlert("You can not delete others's posts ", 'danger');
    }

    setAnchor(null);
  };

  const handleClickShare = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleCloseShare = () => {
    setAnchorEl(null);
  };

  return (
    <ThemeProvider theme={theme}>
      <Grid item xs={12} sm={12} md={12}>
        <Card className={classes.Post}>
          {/**
           * Card Header
           */}

          <Grid
            container
            style={{
              padding: '1em',
            }}
          >
            <Grid item xs={3} sm={1} md={1}>
              <CardAvatar>
                <a href="#pablo" onClick={(e) => e.preventDefault()}>
                  <img
                    src={`https://avatars.dicebear.com/api/avataaars/${user}.svg?`}
                    alt="..."
                    width="auto"
                    height="auto"
                    style={{ borderRadius: '50%' }}
                  />
                </a>
              </CardAvatar>
            </Grid>
            <Grid item xs={9} sm={11} md={11} style={{ paddingLeft: '1em' }}>
              <h4
                style={{
                  fontSize: '1.55em',
                  fontWeight: '700',
                }}
              >
                {name}
              </h4>
              <span
                style={{
                  color: '#559BDB',
                  fontStyle: 'italic',
                  fontSize: '1.25em',
                  fontWeight: '600',
                  textTransform: 'none',
                }}
              >
                {moment(date).format('Do MMM Y')}{' '}
                <i
                  className="fas fa-globe-africa"
                  style={{ color: '#428bca', marginLeft: '0.5em' }}
                ></i>
              </span>
            </Grid>
          </Grid>

          {/**
           * Card Body
           */}
          <CardBody>
            <Grid container>
              <Grid
                item
                xs={12}
                sm={12}
                md={12}
                style={{
                  paddingLeft: '1em',
                  paddingRight: '1em',
                  paddingTop: '0em',
                  paddingBottom: '0em',
                }}
              >
                <CssTextField
                  multiline
                  rows={1}
                  rowsMax={6}
                  value={text}
                  InputProps={{
                    readOnly: true,
                    style: {
                      fontSize: 19,
                      fontWeight: 450,
                      color: 'black',
                      fontFamily: 'Quicksand',
                    },
                  }}
                  style={{
                    width: '100%',
                    marginBottom: '0px',
                    marginTop: '0px',
                  }}
                />
                {image && FileImage(image)}
              </Grid>
            </Grid>
          </CardBody>
          <CardFooter
            style={{
              marginTop: '0em',
            }}
          >
            <Grid container>
              <Grid item xs={11} sm={11} md={11}>
                <Link to={`/post/${_id}`} style={{ textDecoration: 'none' }}>
                  <span
                    style={{
                      color: 'grey',
                      fontSize: '1.15em',
                      fontWeight: '650',
                      marginLeft: '0.95em',
                      marginRight: '0.75em',
                    }}
                  >
                    {likes.length} likes {comments.length} Comments
                  </span>
                </Link>
              </Grid>
              <Grid item xs={1} sm={1} md={1}>
                <IconButton
                  onClick={handleClick}
                  size="small"
                  aria-label="setting"
                >
                  <MoreHorizIcon />
                </IconButton>
                <Menu
                  id="simple-menu"
                  anchorEl={anchor}
                  keepMounted
                  open={Boolean(anchor)}
                  onClose={() => setAnchor(null)}
                >
                  <MenuItem
                    onClick={() => {
                      handleClose();
                    }}
                  >
                    Delete post
                  </MenuItem>
                </Menu>
              </Grid>

              {!isSmall ? (
                <Grid container>
                  <Grid item xs={3} sm={3} md={3}>
                    <Button
                      size="large"
                      color="primary"
                      className={classes.button}
                      startIcon={<ThumbUpIcon />}
                      onClick={() => {
                        likePost(_id);
                      }}
                      style={{
                        marginLeft: '0.75em',
                        marginRight: '0.75em',
                      }}
                    >
                      Like
                    </Button>
                  </Grid>

                  <Grid item xs={3} sm={3} md={3}>
                    <Button
                      size="large"
                      color="primary"
                      className={classes.button}
                      startIcon={<ThumbDownIcon />}
                      onClick={() => {
                        unlikePost(_id);
                      }}
                      style={{
                        marginLeft: '0.75em',
                        marginRight: '0.75em',
                      }}
                    >
                      Unlike
                    </Button>
                  </Grid>

                  <Grid item xs={3} sm={3} md={3}>
                    <Link
                      to={`/post/${_id}`}
                      style={{ textDecoration: 'none' }}
                    >
                      <Button
                        size="large"
                        color="primary"
                        className={classes.button}
                        startIcon={<SmsSharpIcon />}
                        style={{
                          marginLeft: '0.75em',
                          marginRight: '0.75em',
                        }}
                      >
                        Comment
                      </Button>
                    </Link>
                  </Grid>

                  <Grid item xs={3} sm={3} md={3}>
                    <Button
                      size="large"
                      color="primary"
                      className={classes.button}
                      startIcon={<ShareIcon />}
                      onClick={handleClickShare}
                      style={{
                        marginLeft: '0.75em',
                        marginRight: '0.75em',
                      }}
                    >
                      Share
                    </Button>

                    <Menu
                      id="simple-menu"
                      anchorEl={anchorEl}
                      keepMounted
                      open={Boolean(anchorEl)}
                      onClose={handleCloseShare}
                    >
                      <MenuItem onClick={handleCloseShare}>
                        <FacebookIcon style={{ color: '#3b5998' }} /> Facebook
                      </MenuItem>
                      <MenuItem onClick={handleCloseShare}>
                        {' '}
                        <TwitterIcon style={{ color: '#00acee' }} /> Twitter
                      </MenuItem>
                      <MenuItem onClick={handleCloseShare}>
                        {' '}
                        <LinkedInIcon style={{ color: '#0e76a8' }} /> Pinterest
                      </MenuItem>
                    </Menu>
                  </Grid>
                </Grid>
              ) : (
                <Grid container>
                  <Grid item xs={3} sm={3} md={3}>
                    <Button
                      size="large"
                      color="primary"
                      className={classes.button}
                      startIcon={<ThumbUpIcon />}
                      onClick={() => {
                        likePost(_id);
                      }}
                      style={{
                        marginLeft: '0.75em',
                        marginRight: '0.75em',
                      }}
                    ></Button>
                  </Grid>

                  <Grid item xs={3} sm={3} md={3}>
                    <Button
                      size="large"
                      color="primary"
                      className={classes.button}
                      startIcon={<ThumbDownIcon />}
                      onClick={() => {
                        unlikePost(_id);
                      }}
                      style={{
                        marginLeft: '0.75em',
                        marginRight: '0.75em',
                      }}
                    ></Button>
                  </Grid>

                  <Grid item xs={3} sm={3} md={3}>
                    <Link to={`/post/${_id}`}>
                      <Button
                        size="large"
                        color="primary"
                        className={classes.button}
                        startIcon={<SmsSharpIcon />}
                        style={{
                          marginLeft: '0.75em',
                          marginRight: '0.75em',
                        }}
                      ></Button>
                    </Link>
                  </Grid>

                  <Grid item xs={3} sm={3} md={3}>
                    <Button
                      size="large"
                      color="primary"
                      className={classes.button}
                      startIcon={<ShareIcon />}
                      onClick={handleClickShare}
                      style={{
                        marginLeft: '0.75em',
                        marginRight: '0.75em',
                      }}
                    ></Button>

                    <Menu
                      id="simple-menu"
                      anchorEl={anchorEl}
                      keepMounted
                      open={Boolean(anchorEl)}
                      onClose={handleCloseShare}
                    >
                      <MenuItem onClick={handleCloseShare}>
                        <FacebookIcon style={{ color: '#3b5998' }} /> Facebook
                      </MenuItem>
                      <MenuItem onClick={handleCloseShare}>
                        {' '}
                        <TwitterIcon style={{ color: '#00acee' }} /> Twitter
                      </MenuItem>
                      <MenuItem onClick={handleCloseShare}>
                        {' '}
                        <LinkedInIcon style={{ color: '#0e76a8' }} /> Pinterest
                      </MenuItem>
                    </Menu>
                  </Grid>
                </Grid>
              )}
            </Grid>
          </CardFooter>
        </Card>
      </Grid>
    </ThemeProvider>
  );
};

const mapStateToProps = (state) => ({
  user_id: state.auth.user._id,
});

export default connect(mapStateToProps, {
  likePost,
  unlikePost,
  delPost,
  setAlert,
})(PostItem);
