import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import {
  getPosts,
  addPost,
  likePost,
  unlikePost,
  delPost,
  getThread,
  addComment,
  delComment,
} from '../../action/post';
import FileImage from '../../action/utils/getImage';

import { connect } from 'react-redux';
import { setAlert } from '../../action/alert';
import Spinner from '../layout/Spinner';

/** Material UI */
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import ButtonGroup from '@material-ui/core/ButtonGroup';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import { createMuiTheme, ThemeProvider } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import IconButton from '@material-ui/core/IconButton';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import InputAdornment from '@material-ui/core/InputAdornment';

/** Local Customize Component */
import Card from '../Components/components/Card/Card.js';
import CardHeader from '../Components/components/Card/CardHeader.js';
import CardBody from '../Components/components/Card/CardBody.js';
import CardFooter from '../Components/components/Card/CardFooter.js';
import CardAvatar from '../Components/components/Card/CardAvatar.js';

/** Material Icon */
import ThumbUpIcon from '@material-ui/icons/ThumbUp';
import ThumbDownIcon from '@material-ui/icons/ThumbDown';
import ShareIcon from '@material-ui/icons/Share';
import SmsSharpIcon from '@material-ui/icons/SmsSharp';
import TwitterIcon from '@material-ui/icons/Twitter';
import FacebookIcon from '@material-ui/icons/Facebook';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import QuestionAnswerSharpIcon from '@material-ui/icons/QuestionAnswerSharp';
import SendIcon from '@material-ui/icons/Send';
import MoreVertIcon from '@material-ui/icons/MoreVert';

/** Spring */
import { Spring } from 'react-spring/renderprops';

/** Reponsive  */
import { useMediaQuery } from 'react-responsive';

/** Utils */
var moment = require('moment');

const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#428bca',
    },
    secondary: {
      main: '#111',
    },
    success: {
      main: '#5cb85c',
    },
    danger: {
      main: '#d9534f',
    },
  },
});

const useStyles = makeStyles((theme) => ({
  spinner: {
    position: 'absolute',
    left: '50%',
    top: '50%',
  },
  table: {
    marginTop: '4em',
    boxShadow: '2px 2px 5px 2px rgba(0,0,0,0.35)',
    border: '1px #5B5B5B solid',
  },
  content: {
    paddingLeft: '2em',
    paddingRight: '1em',
    paddingTop: '1em',
    background: '#e5e5e5',
  },
  cardCategoryWhite: {
    color: 'rgba(255,255,255,.62)',
    fontSize: '14px',
  },
  cardTitleWhite: {
    color: '#FFFFFF',
    minHeight: 'auto',
    fontWeight: '300',
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: '3px',
    textDecoration: 'none',
  },
  icon: {
    cursor: 'pointer',
  },
  card: {
    marginTop: '4em',
    boxShadow: '2px 2px 5px 2px rgba(0,0,0,0.35)',
  },
  IconButton: {
    marginRight: '5px',
  },
  profileItem: {
    borderRight: '#428bca solid 1px',
  },
  profileItemFeature: {
    fontWeight: '300',
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    color: '#111',
  },
  description: {
    width: '50%',
    margin: 'auto',
    marginBottom: '0.75em',
  },
  cardCategory: {
    fontWeight: '300',
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    color: '#7a7979',
  },
  cardTitle: {
    fontWeight: '300',
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
  },
  feedColumn: {
    paddingLeft: '0.75em',
    paddingRight: '0.75em',
  },
  newPost: {
    boxShadow: '1px 1px 3px 1px rgba(0,0,0,0.35)',
    border: '1px solid gray',
    padding: '1.25em',
  },
  Post: {
    boxShadow: '1px 1px 3px 1px rgba(0,0,0,0.35)',
    border: '1px solid gray',
    padding: '1.25em',
    marginTop: '1em',
    marginBottom: '1em',
  },
  TextField: {
    marginTop: '0.5em',
    marginBottom: '0.5em',
  },
}));

const CssTextField = withStyles({
  root: {
    '& label.Mui-focused': {
      color: '#428bca',
    },
    '& .MuiInput-underline:after': {
      borderBottomColor: '#428bca',
    },
    '& .MuiOutlinedInput-root': {
      '& fieldset': {
        borderColor: '#428bca',
      },
      '&:hover fieldset': {
        borderColor: '#428bca',
      },
      '&.Mui-focused fieldset': {
        borderColor: '#428bca',
      },
    },
  },
})(TextField);

const PostDetails = ({
  match,
  getThread,
  addComment,
  delComment,
  likePost,
  unlikePost,
  loading,
  post,
  user_id,
  setAlert,
}) => {
  const classes = useStyles();
  const [anchorEl, setAnchorEl] = React.useState(null);
  const [anchor, setAnchor] = React.useState(null);
  const [comment, setComment] = useState({
    text: '',
  });

  /** Responsive */
  const isSmall = useMediaQuery({ minWidth: 0, maxWidth: 599 });
  const isMedium = useMediaQuery({ minWidth: 600, maxWidth: 951 });

  useEffect(() => {
    getThread(match.params.post_id);
  }, []);

  const onChange = (event) => {
    setComment({
      text: event.target.value,
    });
  };

  const sendComment = () => {
    addComment(comment, post);
    setComment({
      text: '',
    });
  };

  const handleClick = (event) => {
    setAnchor(event.currentTarget);
  };

  const handleClose = (comment) => {
    if (user_id === comment.user) {
      delComment(comment, post);
    } else {
      setAlert("You can not delete others's comments ", 'danger');
    }

    setAnchor(null);
  };

  const handleClickShare = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleCloseShare = () => {
    setAnchorEl(null);
  };

  if (loading === true || post === null) {
    return (
      <div className={classes.spinner}>
        <Spinner color="#0275d8" loading={true} size="50px" />
      </div>
    );
  }

  return (
    <Spring
      from={{ opacity: 0 }}
      to={{ opacity: 1 }}
      config={{ duration: 150 }}
    >
      {(props) => (
        <div style={props}>
          <ThemeProvider theme={theme}>
            <Grid container>
              <Grid
                item
                xs={12}
                sm={12}
                md={7}
                style={{
                  marginTop: '1em',
                  paddingLeft: '1em',
                  paddingRight: '1em',
                }}
              >
                <Card className={classes.Posts}>
                  {/**
                   * Card Header
                   */}
                  <CardHeader color="primary">
                    <Grid container>
                      <Grid item xs={2} sm={2} md={2}>
                        <CardAvatar>
                          <a href="#pablo" onClick={(e) => e.preventDefault()}>
                            <img
                              src={`https://avatars.dicebear.com/api/avataaars/${post.user}.svg?`}
                              alt="..."
                              width="auto"
                              height="auto"
                              style={{ borderRadius: '50%' }}
                            />
                          </a>
                        </CardAvatar>
                      </Grid>
                      <Grid item xs={10} sm={10} md={10}>
                        <h5>{post.name}</h5>
                        <h6 style={{ color: '#f0f0f0', fontStyle: 'italic' }}>
                          Posted at {moment(post.date).format('Do MMM Y')}
                        </h6>
                      </Grid>
                    </Grid>
                  </CardHeader>
                  {/**
                   * Card Body
                   */}
                  <CardBody>
                    <Grid container>
                      <Grid
                        item
                        xs={12}
                        sm={12}
                        md={12}
                        style={{
                          paddingLeft: '1em',
                          paddingRight: '1em',
                          paddingTop: '1em',
                          paddingBottom: '0em',
                        }}
                      >
                        <CssTextField
                          multiline
                          rows={1}
                          rowsMax={6}
                          value={post.text}
                          InputProps={{
                            readOnly: true,
                            style: {
                              fontSize: 19,
                              fontWeight: 450,
                              color: 'black',
                              fontFamily: 'Quicksand',
                            },
                          }}
                          style={{
                            width: '100%',
                            marginBottom: '0px',
                            marginTop: '0px',
                          }}
                        />

                        {post.image && FileImage(post.image)}
                      </Grid>
                    </Grid>
                  </CardBody>
                  <CardFooter>
                    <Grid container>
                      {/**
                       * Likes and comments amount
                       */}
                      <Grid item xs={12} sm={12} md={12}>
                        <span
                          style={{
                            color: 'grey',
                            fontSize: '1.15em',
                            fontWeight: '650',
                            marginLeft: '0.95em',
                            marginRight: '0.75em',
                          }}
                        >
                          {post && post.likes && (
                            <span>{post.likes.length} likes </span>
                          )}

                          {post && post.comments && (
                            <span>{post.comments.length} comments</span>
                          )}
                        </span>
                      </Grid>
                      {/**
                       * Button lists
                       */}
                      {!isSmall ? (
                        <Grid container>
                          <Grid item xs={3} sm={3} md={3}>
                            <Button
                              size="large"
                              color="primary"
                              className={classes.button}
                              startIcon={<ThumbUpIcon />}
                              onClick={() => {
                                likePost(post._id);
                              }}
                              style={{
                                marginLeft: '0.75em',
                                marginRight: '0.75em',
                              }}
                            >
                              Like
                            </Button>
                          </Grid>

                          <Grid item xs={3} sm={3} md={3}>
                            <Button
                              size="large"
                              color="primary"
                              className={classes.button}
                              startIcon={<ThumbDownIcon />}
                              onClick={() => {
                                unlikePost(post._id);
                              }}
                              style={{
                                marginLeft: '0.75em',
                                marginRight: '0.75em',
                              }}
                            >
                              Unlike
                            </Button>
                          </Grid>

                          <Grid item xs={3} sm={3} md={3}>
                            <Button
                              size="large"
                              color="primary"
                              className={classes.button}
                              startIcon={<SmsSharpIcon />}
                              style={{
                                marginLeft: '0.75em',
                                marginRight: '0.75em',
                              }}
                            >
                              Comment
                            </Button>
                          </Grid>

                          <Grid item xs={3} sm={3} md={3}>
                            <Button
                              size="large"
                              color="primary"
                              className={classes.button}
                              startIcon={<ShareIcon />}
                              onClick={handleClickShare}
                              style={{
                                marginLeft: '0.75em',
                                marginRight: '0.75em',
                              }}
                            >
                              Share
                            </Button>

                            <Menu
                              id="simple-menu"
                              anchorEl={anchorEl}
                              keepMounted
                              open={Boolean(anchorEl)}
                              onClose={handleCloseShare}
                            >
                              <MenuItem onClick={handleCloseShare}>
                                <FacebookIcon style={{ color: '#3b5998' }} />{' '}
                                Facebook
                              </MenuItem>
                              <MenuItem onClick={handleCloseShare}>
                                {' '}
                                <TwitterIcon
                                  style={{ color: '#00acee' }}
                                />{' '}
                                Twitter
                              </MenuItem>
                              <MenuItem onClick={handleCloseShare}>
                                {' '}
                                <LinkedInIcon
                                  style={{ color: '#0e76a8' }}
                                />{' '}
                                Pinterest
                              </MenuItem>
                            </Menu>
                          </Grid>
                        </Grid>
                      ) : (
                        <Grid container>
                          <Grid item xs={3} sm={3} md={3}>
                            <Button
                              size="large"
                              color="primary"
                              className={classes.button}
                              startIcon={<ThumbUpIcon />}
                              onClick={() => {
                                likePost(post._id);
                              }}
                              style={{
                                marginLeft: '0.75em',
                                marginRight: '0.75em',
                              }}
                            ></Button>
                          </Grid>

                          <Grid item xs={3} sm={3} md={3}>
                            <Button
                              size="large"
                              color="primary"
                              className={classes.button}
                              startIcon={<ThumbDownIcon />}
                              onClick={() => {
                                unlikePost(post._id);
                              }}
                              style={{
                                marginLeft: '0.75em',
                                marginRight: '0.75em',
                              }}
                            ></Button>
                          </Grid>

                          <Grid item xs={3} sm={3} md={3}>
                            <Button
                              size="large"
                              color="primary"
                              className={classes.button}
                              startIcon={<SmsSharpIcon />}
                              style={{
                                marginLeft: '0.75em',
                                marginRight: '0.75em',
                              }}
                            ></Button>
                          </Grid>

                          <Grid item xs={3} sm={3} md={3}>
                            <Button
                              size="large"
                              color="primary"
                              className={classes.button}
                              startIcon={<ShareIcon />}
                              onClick={handleClickShare}
                              style={{
                                marginLeft: '0.75em',
                                marginRight: '0.75em',
                              }}
                            ></Button>

                            <Menu
                              id="simple-menu"
                              anchorEl={anchorEl}
                              keepMounted
                              open={Boolean(anchorEl)}
                              onClose={handleCloseShare}
                            >
                              <MenuItem onClick={handleCloseShare}>
                                <FacebookIcon style={{ color: '#3b5998' }} />{' '}
                                Facebook
                              </MenuItem>
                              <MenuItem onClick={handleCloseShare}>
                                {' '}
                                <TwitterIcon
                                  style={{ color: '#00acee' }}
                                />{' '}
                                Twitter
                              </MenuItem>
                              <MenuItem onClick={handleCloseShare}>
                                {' '}
                                <LinkedInIcon
                                  style={{ color: '#0e76a8' }}
                                />{' '}
                                Pinterest
                              </MenuItem>
                            </Menu>
                          </Grid>
                        </Grid>
                      )}

                      {/**
                       * Comment Bar
                       */}
                      <Grid item xs={12} sm={12} md={12}>
                        <Grid container>
                          <Grid item xs={10} sm={10} md={11}>
                            {/**
                             * Text Field to send Posts
                             */}
                            <CssTextField
                              InputProps={{
                                startAdornment: (
                                  <InputAdornment position="start">
                                    <QuestionAnswerSharpIcon color="primary" />
                                  </InputAdornment>
                                ),
                              }}
                              className={classes.TextField}
                              label="Comment..."
                              variant="outlined"
                              multiline
                              rows={1}
                              rowsMax={4}
                              value={comment.text}
                              onChange={(event) => {
                                onChange(event);
                              }}
                              style={{
                                marginLeft: '0.75em',
                                width: '90%',
                              }}
                              InputProps={{
                                style: {
                                  fontSize: 18,
                                  fontWeight: 450,
                                  color: 'black',
                                  fontFamily: 'Quicksand',
                                },
                              }}
                            />
                          </Grid>
                          <Grid
                            item
                            xs={2}
                            sm={2}
                            md={1}
                            style={{
                              alignText: 'center',
                            }}
                          >
                            <IconButton
                              color="primary"
                              round
                              onClick={() => {
                                sendComment();
                              }}
                              style={{
                                marginTop: '0.5em',
                              }}
                            >
                              <SendIcon />
                            </IconButton>
                          </Grid>

                          {/**
                           * List of comments
                           */}
                          {post &&
                            post.comments &&
                            post.comments.map((comment) => (
                              <Grid container style={{ marginTop: '1em' }}>
                                <Grid
                                  item
                                  xs={2}
                                  sm={2}
                                  md={2}
                                  style={{
                                    textAlign: 'center',
                                  }}
                                >
                                  <a
                                    href="#pablo"
                                    onClick={(e) => e.preventDefault()}
                                  >
                                    <img
                                      src={`https://avatars.dicebear.com/api/avataaars/${comment.user}.svg?`}
                                      alt="..."
                                      width="35"
                                      height="auto"
                                      style={{
                                        borderRadius: '50%',
                                        boxShadow:
                                          '1px 1px 3px 1px rgba(0,0,0,0.35)',
                                      }}
                                    />
                                  </a>
                                </Grid>
                                <Grid
                                  item
                                  xs={9}
                                  sm={9}
                                  md={9}
                                  style={{
                                    paddingLeft: '1em',
                                  }}
                                >
                                  <h5>{comment.name}</h5>
                                  <h6
                                    style={{
                                      color: '#6e6e6e',
                                      fontStyle: 'italic',
                                    }}
                                  >
                                    Posted at{' '}
                                    {moment(comment.date).format('Do MMM Y')}
                                  </h6>
                                  <CssTextField
                                    className={classes.TextField}
                                    multiline
                                    rows={1}
                                    rowsMax={3}
                                    value={comment.text}
                                    onChange={(event) => {
                                      onChange(event);
                                    }}
                                    style={{
                                      marginLeft: '0.75em',
                                      width: '90%',
                                    }}
                                    InputProps={{
                                      readOnly: true,
                                      style: {
                                        fontSize: 17,
                                        fontWeight: 450,
                                        color: 'black',
                                        fontFamily: 'Quicksand',
                                      },
                                    }}
                                  />
                                </Grid>
                                <Grid item xs={1} sm={1} md={1}>
                                  <IconButton
                                    onClick={handleClick}
                                    size="small"
                                    aria-label="setting"
                                  >
                                    <MoreVertIcon />
                                  </IconButton>

                                  <Menu
                                    id="simple-menu"
                                    anchorEl={anchor}
                                    keepMounted
                                    open={Boolean(anchor)}
                                    onClose={() => setAnchor(null)}
                                  >
                                    <MenuItem
                                      onClick={() => {
                                        handleClose(comment);
                                      }}
                                    >
                                      Delete comment
                                    </MenuItem>
                                  </Menu>
                                </Grid>
                              </Grid>
                            ))}
                        </Grid>
                      </Grid>

                      {/**
                       * Display of comment
                       */}
                    </Grid>
                  </CardFooter>
                </Card>
              </Grid>
            </Grid>
          </ThemeProvider>
        </div>
      )}
    </Spring>
  );
};

const mapStateToMap = (state) => ({
  loading: state.post.loading,
  post: state.post.post,
  user_id: state.auth.user._id,
});

export default connect(mapStateToMap, {
  getThread,
  addComment,
  delComment,
  likePost,
  unlikePost,
  setAlert,
})(PostDetails);
