import {
  ADD_JOB,
  GET_JOBS,
  GET_JOB,
  DELETE_JOB,
  GET_TOTAL_JOB,
  JOB_ERR,
  UPDATE_FORM,
  SET_CURRENT,
  CLEAR_CURRENT,
} from './type';
import { fileUpload } from '../action/utils/upload';
import { setAlert } from '../action/alert';
import api from '../reducers/utils/baseURL';

/**
 * Update form data
 */
export const updateForm = (name, value) => (dispatch) => {
  dispatch({
    type: UPDATE_FORM,
    payload: {
      name,
      value,
    },
  });
};

/**
 * Update image to post
 */
export const updateImage = (Image) => async (dispatch) => {
  try {
    /** Handle Image */
    let Image_id;
    fileUpload(Image)
      .then((res) => {
        Image_id = res.data.file.id;
      })
      .then(() => {
        dispatch({
          type: UPDATE_FORM,
          payload: {
            name: 'image',
            value: Image_id,
          },
        });
      });
  } catch (error) {
    dispatch({
      type: JOB_ERR,
      payload: {
        msg: error.response.statusText,
        status: error.response.status,
      },
    });
  }
};

/**
 * Add New Job
 */
export const addJob = (form) => async (dispatch) => {
  try {
    const {
      company,
      location,
      jobTitle,
      remote,
      image,
      companyName,
      companySize,
      phone,
      name,
      description,
      contract,
      salary,
      employmentType,
      stack,
      preference,
      requirement,
    } = form;
    const newJobPost = {
      company,
      location,
      jobTitle,
      remote,
      image,
      userInfo: {
        companyName,
        companySize,
        phone,
        name,
      },
      employmentType,
      contract,
      salary,
      description,
      requirement,
      preference,
      stack,
    };
    const body = JSON.stringify(newJobPost);
    const config = {
      headers: {
        'Content-Type': 'application/json',
      },
    };

    const res = await api.post('/job', body, config);

    dispatch(setAlert('New job post added !', 'success'));

    dispatch({
      type: ADD_JOB,
      payload: res.data,
    });
  } catch (error) {
    dispatch({
      type: JOB_ERR,
      payload: {
        msg: error.response.statusText,
        status: error.response.status,
      },
    });
  }
};

/**
 * Get One Job
 */
export const getJobDetails = (id) => async (dispatch) => {
  try {
    const res = await api.get(`/job/${id}`);

    dispatch({
      type: GET_JOB,
      payload: res.data,
    });
  } catch (error) {
    dispatch({
      type: JOB_ERR,
      payload: {
        msg: error.response.statusText,
        status: error.response.status,
      },
    });
  }
};

/**
 * Get All Job
 */
export const getJobs = (id) => async (dispatch) => {
  try {
    const res = await api.get('/job');

    dispatch({
      type: GET_JOBS,
      payload: res.data,
    });

    dispatch({
      type: SET_CURRENT,
      payload: res.data[0]._id,
    });
  } catch (error) {
    dispatch({
      type: JOB_ERR,
      payload: {
        msg: error.response.statusText,
        status: error.response.status,
      },
    });
  }
};

/**
 * Get My Job
 */
export const getMyJobs = (id) => async (dispatch) => {
  try {
    const res = await api.get('/job/info/user');

    dispatch({
      type: GET_JOBS,
      payload: res.data,
    });

    dispatch({
      type: SET_CURRENT,
      payload: res.data[0] ? res.data[0]._id : 'None',
    });
  } catch (error) {
    dispatch({
      type: JOB_ERR,
      payload: {
        msg: error.response.statusText,
        status: error.response.status,
      },
    });
  }
};

/**
 * Set current
 */
export const setCurrent = (id) => async (dispatch) => {
  dispatch({
    type: SET_CURRENT,
    payload: id,
  });
};

/**
 * Clear Current
 */
export const clearCurrent = () => async (dispatch) => {
  dispatch({
    type: CLEAR_CURRENT,
  });
};
