import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import FileImage from '../../../action/utils/getImage';
import { addJob } from '../../../action/job';

import Parser from 'html-react-parser';

/** Material UI  */
import { makeStyles } from '@material-ui/core/styles';
import { createMuiTheme } from '@material-ui/core/styles';
import { ThemeProvider } from '@material-ui/styles';

/** Material Component */
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import Chip from '@material-ui/core/Chip';

/** Icon */
import VisibilityIcon from '@material-ui/icons/Visibility';
import DoneIcon from '@material-ui/icons/Done';
import FavoriteIcon from '@material-ui/icons/Favorite';
import SettingsIcon from '@material-ui/icons/Settings';
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos';
import CodeIcon from '@material-ui/icons/Code';
import Avatar from '@material-ui/core/Avatar';
import ContactPhoneIcon from '@material-ui/icons/ContactPhone';
import PeopleIcon from '@material-ui/icons/People';
import CloudUploadIcon from '@material-ui/icons/CloudUpload';
import PublicIcon from '@material-ui/icons/Public';
import LocalShippingIcon from '@material-ui/icons/LocalShipping';

/** Reponsive  */
import { useMediaQuery } from 'react-responsive';
const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#428bca',
    },
    secondary: {
      main: '#d9534f',
    },
  },
});

const useStyles = makeStyles((theme) => ({
  layout: {
    textAlign: 'text-center',
    color: '#428bca',
  },
  cardGrid: {
    paddingLeft: '1em',
    paddingRight: '1em',
  },
  card: {
    boxShadow: '2px 2px 5px 2px rgba(66, 139, 202,0.35)',
    border: '1px #5B5B5B solid',
  },
}));

export const Step6 = ({ form, addJob }) => {
  const classes = useStyles();

  /** Responsive */
  const isSmall = useMediaQuery({ minWidth: 0, maxWidth: 599 });
  const isMedium = useMediaQuery({ minWidth: 600, maxWidth: 951 });

  const getRemote = (remote) => {
    if (remote === 'Temporalily due to Covid 19') {
      return (
        <span
          style={{
            color: '#0275d8',
            fontWeight: '750',
          }}
        >
          <i className="fas fa-user-md"></i> Temporalily due to Covid 19{' '}
        </span>
      );
    }

    if (remote === 'Remote Job') {
      return (
        <span
          style={{
            color: '#5bc0de',
            fontWeight: '750',
          }}
        >
          <i className="fas fa-globe-europe"></i> Remote Job{' '}
        </span>
      );
    }

    if (remote === 'Visa Sponsors') {
      return (
        <span
          style={{
            color: '#d9534f',
            fontWeight: '750',
          }}
        >
          <i className="fas fa-truck"></i> Visa Sponsors{' '}
        </span>
      );
    }
  };

  return (
    <React.Fragment>
      <ThemeProvider theme={theme}>
        <Grid container className={classes.layout}>
          <Grid item xs={false} sm={1} md={2}></Grid>
          <Grid item xs={12} sm={10} md={8}>
            <div
              style={{
                textAlign: 'text-center',
              }}
            >
              {isSmall && (
                <Typography
                  align="center"
                  variant="h6"
                  style={{
                    fontWeight: '600',
                    margin: 'auto',
                    fontFamily: 'Quicksand',
                  }}
                >
                  <VisibilityIcon /> Preview Job Post
                </Typography>
              )}

              {!isSmall && (
                <Typography
                  align="center"
                  variant="h4"
                  style={{
                    fontWeight: '600',
                    margin: 'auto',
                    fontFamily: 'Quicksand',
                  }}
                >
                  <VisibilityIcon fontSize="large" /> Preview Job Post
                </Typography>
              )}
            </div>
            {/** Post job Review */}
            <div
              style={{
                border: '15px solid #ccc',
                padding: '1em',
                margin: '1em',
                fontFamily: 'Quicksand',
                color: '#111',
                fontSize: '1em',
              }}
            >
              <Grid container style={{ padding: '1.5em' }}>
                <Grid item xs={2}>
                  {form.image && FileImage(form.image)}
                </Grid>
                <Grid
                  item
                  xs={10}
                  style={{
                    paddingLeft: '1em',
                  }}
                >
                  <h5
                    style={{
                      fontWeight: '700',
                    }}
                  >
                    {form.jobTitle}
                  </h5>
                  <h6
                    style={{
                      fontWeight: '400',
                    }}
                  >
                    {`${form.company} ${form.location}`}
                    <br />
                    <PeopleIcon color="primary" /> {`${form.companySize}`}
                    <br />
                    {getRemote(form.remote)}
                  </h6>

                  <Button
                    variant="contained"
                    size="large"
                    color="primary"
                    endIcon={<DoneIcon />}
                  >
                    Apply
                  </Button>
                  <IconButton
                    aria-label="Favorite"
                    color="secondary"
                    size="medium"
                  >
                    <FavoriteIcon />
                  </IconButton>
                </Grid>
              </Grid>

              {/** Divider */}
              <div
                style={{
                  background: '#ccc',
                  height: '1px',
                  width: '100%',
                  marginTop: '1em',
                  marginBottom: '1em',
                }}
              ></div>

              {/** Technology */}
              <div>
                <h5
                  style={{
                    fontWeight: '700',
                  }}
                >
                  Technology
                </h5>
                {form.stack &&
                  form.stack.map((item, index) => (
                    <Chip
                      avatar={
                        <Avatar>
                          <CodeIcon />
                        </Avatar>
                      }
                      label={item}
                      color="primary"
                    />
                  ))}
              </div>

              {/** Job Description */}
              <div>
                <h5
                  style={{
                    fontWeight: '700',
                  }}
                >
                  We need you
                </h5>

                <div>{Parser(form.description)}</div>

                {form.requirement && (
                  <div>
                    <h5
                      style={{
                        fontWeight: '700',
                      }}
                    >
                      Requirement
                    </h5>
                    <div>{Parser(form.requirement)}</div>
                  </div>
                )}

                {form.preference && (
                  <div>
                    <h5
                      style={{
                        fontWeight: '700',
                      }}
                    >
                      Preference
                    </h5>
                    <div>{Parser(form.preference)}</div>
                  </div>
                )}

                <h6
                  style={{
                    fontWeight: '700',
                  }}
                >
                  Job Type :{' '}
                  <span
                    style={{
                      fontWeight: '500',
                    }}
                  >
                    {form.employmentType}
                    <br></br>
                    {form.contract &&
                      Object.entries(form.contract).map(
                        (item, index) =>
                          item[1] && (
                            <span
                              style={{ color: '#111' }}
                            >{`${item[0]}, `}</span>
                          )
                      )}
                  </span>
                </h6>

                <h6
                  style={{
                    fontWeight: '700',
                  }}
                >
                  Salary :{' '}
                  <span
                    style={{
                      fontWeight: '500',
                    }}
                  >
                    {`${form.salary.minimum} to ${form.salary.maximum} ${form.salary.currency} /${form.salary.paymentCircle}`}
                  </span>
                </h6>
                {/** Divider */}
                <div
                  style={{
                    background: '#ccc',
                    height: '1px',
                    width: '100%',
                    marginTop: '1em',
                    marginBottom: '1em',
                  }}
                ></div>
                <h5
                  style={{
                    fontWeight: '700',
                  }}
                >
                  <ContactPhoneIcon color="primary" /> Contact Information:
                </h5>
                <div
                  style={{
                    marginTop: '0.15em',
                    marginBottom: '0.15em',
                    border: '5px solid #ccc',
                    paddingTop: '1em',
                  }}
                >
                  <ul
                    style={{
                      listStyle: 'none',
                    }}
                  >
                    <li>
                      {' '}
                      <span style={{ fontWeight: '700' }}>Phone Number:</span>
                    </li>
                    <li>
                      <ArrowForwardIosIcon
                        fontSize="small"
                        style={{ color: '#5cb85c' }}
                      />{' '}
                      {form.phone}
                    </li>
                    <li>
                      {' '}
                      <span style={{ fontWeight: '700' }}>User posted:</span>
                    </li>
                    <li>
                      <ArrowForwardIosIcon
                        fontSize="small"
                        style={{ color: '#5cb85c' }}
                      />
                      {form.name}
                    </li>
                  </ul>
                </div>
              </div>
            </div>{' '}
            {/** Upload Button */}
            <div
              style={{
                height: '1px',
                width: '100%',
                marginTop: '0.25em',
                marginBottom: '0.25em',
              }}
            ></div>
            <div
              style={{
                width: '60%',
                margin: 'auto',
              }}
            >
              <Button
                fullWidth
                variant="contained"
                color="primary"
                startIcon={<CloudUploadIcon />}
                size="large"
                onClick={() => {
                  addJob(form);
                }}
              >
                Post
              </Button>
            </div>
          </Grid>
          <Grid item xs={false} sm={1} md={2}></Grid>
        </Grid>
      </ThemeProvider>
    </React.Fragment>
  );
};

const mapStateToProps = (state) => ({
  form: state.job.form,
});

export default connect(mapStateToProps, { addJob })(Step6);
