import React from 'react';

/** Spring */
import { Spring } from 'react-spring/renderprops';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import { createMuiTheme } from '@material-ui/core/styles';
import { MuiThemeProvider } from '@material-ui/core/styles';
import VisibilitySensor from 'react-visibility-sensor';
import Card from '@material-ui/core/Card';
import Grid from '@material-ui/core/Grid';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';

/** React Responsive */
import { useMediaQuery } from 'react-responsive';
import AccessTimeIcon from '@material-ui/icons/AccessTime';
/** Image */
import dev1 from '../img/dev1.jpg';
import dev2 from '../img/dev2.jpg';
import dev3 from '../img/dev3.jpg';
import dev4 from '../img/dev4.jpg';
import dev5 from '../img/dev5.jpg';
import dev6 from '../img/dev6.jpg';

const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#428bca',
    },
    secondary: {
      main: '#111',
    },
  },
});

const useStyles = makeStyles((theme) => ({
  root: {
    maxWidth: 345,
  },
  media: {
    height: 140,
  },
  title: {
    fontFamily: 'Quicksand',
    fontWeight: '500',
    fontSize: '1.55em',
    color: '#428bca',
    margin: 'auto',
  },
  titleHeadline: {
    fontFamily: 'Poiret One',
    fontWeight: '300',
    fontSize: '55px',
    color: '#428bca',
    margin: 'auto',
    marginTop: '3em',
    marginBottom: '2em',
  },
  titleHeadlineSmall: {
    fontFamily: 'Poiret One',
    fontWeight: '100',
    fontSize: '30px',
    color: '#428bca',
    margin: 'auto',
    marginTop: '2em',
    marginBottom: '1em',
  },
  titleParagraph: {
    fontFamily: 'Quicksand',
    fontWeight: '100',
    fontSize: '1.15em',
    color: '#979797',
    margin: 'auto',
    marginTop: '3em',
    marginBottom: '2em',
  },
  cardGrid: {
    padding: '1em',
    height: 'fit-content',
  },
  card: {
    width: '100%',
    marginBottom: '1em',
    boxShadow: '2px 2px 5px 2px rgba(66, 139, 202,0.35)',
    transition: 'boxShadow .5s linear',
    '&:hover': {
      boxShadow: ' 20px 20px 30px -6px rgba(66,139,202,1)',
    },
  },
  cardTitle: {
    fontFamily: 'Quicksand',
    fontWeight: '800',
  },
  divider: {
    height: '3px',
    width: '60px',
    margin: 'auto',
    backgroundColor: '#428bca',
    marginTop: '5em',
    marginBottom: '1em',
  },
  divider_small: {
    height: '3px',
    width: '60px',
    margin: 'auto',
    backgroundColor: '#428bca',
    marginTop: '3em',
    marginBottom: '1em',
  },
  button: {
    fontSize: '1.5em',
  },
}));

const data = [
  {
    img: dev1,
    title: 'Liam Elijah',
    date: '23/2/2019',
    height: '250',
  },
  {
    img: dev2,
    title: 'Noah Lucas',
    date: '10/7/2019',
    height: '300',
  },
  {
    img: dev3,
    title: 'William Mason',
    date: '15/9/2019',
    height: '400',
  },
  {
    img: dev4,
    title: 'James Ethan',
    date: '23/10/2019',
    height: '150',
  },
  {
    img: dev5,
    title: 'Oliver Daniel',
    date: '5/8/2019',
    height: '350',
  },
  {
    img: dev6,
    title: 'Benjamin Matthew',
    date: '2/12/2019',
    height: '200',
  },
];

const Part8 = () => {
  const classes = useStyles();
  const [show, setShow] = React.useState(false);
  const isSmall = useMediaQuery({ minWidth: 0, maxWidth: 599 });

  function onChange(isVisible) {
    if (isVisible) {
      setShow(true);
    }
  }

  return (
    <VisibilitySensor onChange={onChange}>
      <Grid container>
        <Grid
          xs={12}
          style={{
            textAlign: 'center',
          }}
        >
          <div data-aos="fade-down-right">
            <div className={classes.divider}></div>
            {!isSmall && (
              <span className={classes.titleHeadline}>Developer story</span>
            )}
            {isSmall && (
              <span className={classes.titleHeadlineSmall}>
                Developer story
              </span>
            )}
          </div>
        </Grid>
        <Grid
          xs={12}
          style={{
            textAlign: 'center',
            padding: '1em',
          }}
        ></Grid>
        <Grid container alignContent="flex-start">
          <Grid item xs={12} sm={6} md={4} style={{ padding: '1em' }}>
            {data.slice(0, 2).map((item) => (
              <div data-aos="fade-left">
                <Card className={classes.card}>
                  <CardActionArea>
                    <CardMedia
                      component="img"
                      alt="Card Title"
                      height={item.height}
                      image={item.img}
                      title="Card Title"
                    />
                    <CardContent>
                      <Typography
                        gutterBottom
                        variant="h5"
                        component="h2"
                        className={classes.cardTitle}
                      >
                        {item.title}
                      </Typography>

                      <Button color="default" startIcon={<AccessTimeIcon />}>
                        {item.date}
                      </Button>
                    </CardContent>
                  </CardActionArea>
                </Card>
              </div>
            ))}
          </Grid>
          <Grid item xs={12} sm={6} md={4} style={{ padding: '1em' }}>
            {data.slice(2, 4).map((item) => (
              <div data-aos="fade-up-right">
                <Card className={classes.card}>
                  <CardActionArea>
                    <CardMedia
                      component="img"
                      alt="Card Title"
                      height={item.height}
                      image={item.img}
                      title="Card Title"
                    />
                    <CardContent>
                      <Typography
                        gutterBottom
                        variant="h5"
                        component="h2"
                        className={classes.cardTitle}
                      >
                        {item.title}
                      </Typography>

                      <Button color="default" startIcon={<AccessTimeIcon />}>
                        {item.date}
                      </Button>
                    </CardContent>
                  </CardActionArea>
                </Card>
              </div>
            ))}
          </Grid>
          <Grid item xs={12} sm={6} md={4} style={{ padding: '1em' }}>
            {data.slice(4, 6).map((item) => (
              <div data-aos="fade-up-left">
                <Card className={classes.card}>
                  <CardActionArea>
                    <CardMedia
                      component="img"
                      alt="Card Title"
                      height={item.height}
                      image={item.img}
                      title="Card Title"
                    />
                    <CardContent>
                      <Typography
                        gutterBottom
                        variant="h5"
                        component="h2"
                        className={classes.cardTitle}
                      >
                        {item.title}
                      </Typography>

                      <Button color="default" startIcon={<AccessTimeIcon />}>
                        {item.date}
                      </Button>
                    </CardContent>
                  </CardActionArea>
                </Card>
              </div>
            ))}
          </Grid>
        </Grid>
      </Grid>
    </VisibilitySensor>
  );
};

export default Part8;
