import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { loadUser, logout } from '../../action/auth';
import { useLocation } from 'react-router-dom';

/** Local component */
import Logo from '../img/300Logo.png';

/** Material UI Component */
import { createMuiTheme } from '@material-ui/core/styles';
import { ThemeProvider } from '@material-ui/styles';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import AccountCircle from '@material-ui/icons/AccountCircle';
import MenuItem from '@material-ui/core/MenuItem';
import Menu from '@material-ui/core/Menu';
import Avatar from '@material-ui/core/Avatar';
import AccountBoxSharpIcon from '@material-ui/icons/AccountBoxSharp';
import WorkSharpIcon from '@material-ui/icons/WorkSharp';
import SchoolIcon from '@material-ui/icons/School';

/** Logo Material Icon */
import PeopleIcon from '@material-ui/icons/People';
import ExitToAppIcon from '@material-ui/icons/ExitToApp';

/** Reponsive  */
import { useMediaQuery } from 'react-responsive';

/** Related Component */
import Collapse from './Res/Collapse';

const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#428bca',
    },
    secondary: {
      main: '#111',
    },
  },
});

const useStyles = makeStyles((theme) => ({
  root: {
    flexFrow: 1,
    width: '100%',
  },
  logo: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
    fontWeight: '200',
    marginLeft: theme.spacing(2),
    fontFamily: 'Quicksand',
  },
  avatar: {
    backgroundColor: 'primary',
  },
  buttonGroup: {
    position: 'absolute',
    right: '0',
  },
  link: {
    textDecoration: 'none',
    color: 'inherit',
  },
}));

const NavBar = ({ isAuthenticated, user, logout, loadUser, loading }) => {
  const classes = useStyles();
  const [anchorEl, setAnchorEl] = React.useState(null);
  const open = Boolean(anchorEl);

  const [name, setName] = useState(user ? user.name : 'Devmeet');
  const [avatar, setAvatar] = useState(
    user
      ? `https://avatars.dicebear.com/api/male/${user._id}.svg?`
      : 'https://avatars.dicebear.com/api/code/25121997.svg'
  );
  const isSmall = useMediaQuery({ minWidth: 0, maxWidth: 599 });
  const isExtra = useMediaQuery({ minWidth: 0, maxWidth: 320 });

  useEffect(() => {
    if (user) {
      setName(user.name);
      setAvatar(`https://avatars.dicebear.com/api/avataaars/${user._id}.svg?`);
    }
  }, [user]);

  let location = useLocation();
  switch (location.pathname) {
    case '/':
    case '/login':
    case '/register':
      return <div>{false}</div>;
  }

  const handleMenu = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <ThemeProvider theme={theme}>
      <AppBar className={classes.root} position="sticky" color="primary">
        <Toolbar>
          {isSmall && <Collapse />}

          {!isExtra && (
            <Avatar alt="Remy Sharp" src={avatar} className={classes.avatar} />
          )}
          {!isSmall && (
            <Typography variant="h5" className={classes.title}>
              {name}
            </Typography>
          )}

          {/** Developers */}
          <div className={classes.buttonGroup}>
            <Link to="/dev" className={classes.link}>
              <IconButton color="inherit">
                <PeopleIcon />
              </IconButton>
            </Link>

            {/** Account */}
            <IconButton onClick={handleMenu} color="inherit">
              <AccountCircle />
            </IconButton>

            {/** Menu */}
            <Menu
              id="simple-menu"
              anchorEl={anchorEl}
              keepMounted
              open={Boolean(anchorEl)}
              onClose={handleClose}
            >
              {/** Profile */}
              <Link
                to="/edit-profile"
                style={{ textDecoration: 'none', color: '#428bca' }}
              >
                <MenuItem>
                  <AccountBoxSharpIcon /> Profile
                </MenuItem>
              </Link>

              {/** Experience */}
              <Link
                to="/edit-experience"
                style={{ textDecoration: 'none', color: '#428bca' }}
              >
                <MenuItem>
                  <WorkSharpIcon />
                  Experience
                </MenuItem>
              </Link>

              {/** Education */}
              <Link
                to="/edit-education"
                style={{ textDecoration: 'none', color: '#428bca' }}
              >
                <MenuItem>
                  <SchoolIcon />
                  Education
                </MenuItem>
              </Link>
            </Menu>

            {/** Logout */}

            <IconButton
              color="inherit"
              onClick={() => {
                logout();
              }}
            >
              <ExitToAppIcon />
            </IconButton>
          </div>
        </Toolbar>
      </AppBar>
    </ThemeProvider>
  );
};

NavBar.propTypes = {
  isAuthenticated: PropTypes.bool,
  user: PropTypes.object,
};

const mapStateToProps = (state) => ({
  isAuthenticated: state.auth.isAuthenticated,
  user: state.auth.user,
  loading: state.auth.loading,
});

export default connect(mapStateToProps, { loadUser, logout })(NavBar);
