import React from 'react';
import GridItem from '../../Components/components/Grid/GridItem.js';
import Button from '../../Components/components/CustomButtons/Button.js';
import Card from '../../Components/components/Card/Card.js';
import CardAvatar from '../../Components/components/Card/CardAvatar.js';
import CardBody from '../../Components/components/Card/CardBody.js';
import { makeStyles } from '@material-ui/core/styles';
import { createMuiTheme, ThemeProvider } from '@material-ui/core/styles';

const useStyles = makeStyles((theme) => ({
  card: {
    boxShadow: '2px 2px 5px 2px rgba(0,0,0,0.35)',
    marginRight: '0.5em',
    marginLeft: '0.5em',
  },
  cardShowCase: {
    marginTop: '5em',
    boxShadow: '2px 2px 5px 2px rgba(0,0,0,0.35)',
    marginRight: '0.5em',
    border: '1px #5B5B5B solid',
  },
}));

const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#428bca',
    },
    secondary: {
      main: '#111',
    },
    success: {
      main: '#5cb85c',
    },
    danger: {
      main: '#d9534f',
    },
  },
});

const EditProfileShowCase = ({ user, bio, status }) => {
  const classes = useStyles();

  return (
    <ThemeProvider theme={theme}>
      <GridItem xs={12} sm={12} md={4}>
        <Card profile className={classes.cardShowCase}>
          <CardAvatar profile>
            <img
              src={`https://avatars.dicebear.com/api/avataaars/${user._id}.svg?`}
              alt="..."
            />
          </CardAvatar>
          <CardBody profile>
            <h6 className={classes.cardCategory}>{status.toUpperCase()}</h6>
            <h4 className={classes.cardTitle}>{user.name}</h4>
            <p>{bio}</p>
            <Button color="primary" round>
              Follow
            </Button>
          </CardBody>
        </Card>
      </GridItem>
    </ThemeProvider>
  );
};

export default EditProfileShowCase;
