import React from 'react';
import { connect } from 'react-redux';

/** Material UI  */
import { makeStyles, withStyles } from '@material-ui/core/styles';
import { createMuiTheme } from '@material-ui/core/styles';
import { ThemeProvider } from '@material-ui/styles';

/** Material Component */
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import FormGroup from '@material-ui/core/FormGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Switch from '@material-ui/core/Switch';
import RadioGroup from '@material-ui/core/RadioGroup';
import Radio from '@material-ui/core/Radio';

/** Material Icon */
import LibraryBooksSharpIcon from '@material-ui/icons/LibraryBooksSharp';
import DeleteIcon from '@material-ui/icons/Delete';

/** Add Qualification */
import Preference from './AddQualification/Preference';
import Requirement from './AddQualification/Requirement';

/** Redux */
import { updateForm } from '../../../action/job';

/** Reponsive  */
import { useMediaQuery } from 'react-responsive';

const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#428bca',
    },
    secondary: {
      main: '#111',
    },
  },
});

const useStyles = makeStyles((theme) => ({
  avatar: {
    margin: 'auto',
    background: '#428bca',
    color: '#fff',
  },
  title: {
    textAlign: 'center',
    color: '#428bca',
    fontSize: '1.25em',
  },
  FormControl: {
    marginTop: '2em',
    marginBottom: '1em',
    width: '14em',
  },
  label: {
    fontFamily: 'Quicksand',
    fontWeight: '700',
    fontSize: '1.5em',
  },
  labelSelect: {
    fontFamily: 'Quicksand',
    fontWeight: '700',
    fontSize: '1.5em',
  },
  InputLabel: {
    fontFamily: 'Quicksand',
    fontWeight: '700',
  },
  dividerColor: {
    color: '#111',
  },
  button: {
    marginLeft: '0.5em',
    marginRight: '0.5em',
    marginTop: '3em',
  },

  span: {
    fontFamily: 'Quicksand',
    fontWeight: '600',
    fontSize: '1em',
  },
}));

/**
 * Switch Button
 */

const IOSSwitch = withStyles((theme) => ({
  root: {
    width: 42,
    height: 26,
    padding: 0,
    margin: theme.spacing(1),
  },
  switchBase: {
    padding: 1,
    '&$checked': {
      transform: 'translateX(16px)',
      color: theme.palette.common.white,
      '& + $track': {
        backgroundColor: '#428bca',
        opacity: 1,
        border: 'none',
      },
    },
    '&$focusVisible $thumb': {
      color: '#52d869',
      border: '6px solid #fff',
    },
  },
  thumb: {
    width: 24,
    height: 24,
  },
  track: {
    borderRadius: 26 / 2,
    border: `1px solid ${theme.palette.grey[400]}`,
    backgroundColor: theme.palette.grey[50],
    opacity: 1,
    transition: theme.transitions.create(['background-color', 'border']),
  },
  checked: {},
  focusVisible: {},
}))(({ classes, ...props }) => {
  return (
    <Switch
      focusVisibleClassName={classes.focusVisible}
      disableRipple
      classes={{
        root: classes.root,
        switchBase: classes.switchBase,
        thumb: classes.thumb,
        track: classes.track,
        checked: classes.checked,
      }}
      {...props}
    />
  );
});

export const Step5 = ({ updateForm }) => {
  const classes = useStyles();
  const [selection, setSelection] = React.useState('');
  const [qualification, setQualification] = React.useState([]);
  const [ask, setAsk] = React.useState(true);

  /** Responsive */
  const isSmall = useMediaQuery({ minWidth: 0, maxWidth: 599 });

  const handleChange = (event: React.ChangeEvent<{ value: unknown }>) => {
    setSelection(event.target.value);
    changeQualification(event.target.value, qualification.length);
  };

  const changeQualification = (newQualification) => {
    switch (newQualification) {
      case 'Preference':
        qualification.push({
          component: <Preference />,
        });
        break;

      case 'Requirement':
        qualification.push({
          component: <Requirement />,
        });
        break;
      default:
        break;
    }
  };

  const deleteQualification = (key) => {
    setQualification([]);
    updateForm('preference', null);
    updateForm('requirement', null);
  };

  return (
    <React.Fragment>
      <ThemeProvider theme={theme}>
        <Grid container>
          <Grid item xs={false} sm={2} md={2}></Grid>
          <Grid item xs={12} sm={8} md={8}>
            {!isSmall && [
              <Typography
                color="primary"
                align="left"
                variant="h4"
                style={{
                  fontWeight: '600',
                  fontFamily: 'Quicksand',
                }}
              >
                Find the right candidate
              </Typography>,
              <Typography
                color="primary"
                align="left"
                variant="h5"
                style={{
                  fontWeight: '500',
                  fontFamily: 'Quicksand',
                  marginTop: '1.25em',
                }}
              >
                <LibraryBooksSharpIcon fontSize="large" /> Applicant
                qualifications
              </Typography>,
              <Grid container style={{ marginTop: '1em' }}>
                <Grid item xs={9} style={{ paddingRight: '2em' }}>
                  <Typography
                    color="secondary"
                    align="left"
                    variant="h6"
                    style={{
                      fontWeight: '650',
                      fontFamily: 'Quicksand',
                      marginTop: '0.5em',
                    }}
                  >
                    Ask applicants about their qualifications
                  </Typography>
                  ,
                  <Typography
                    color="secondary"
                    align="left"
                    variant="body1"
                    style={{
                      fontWeight: '650',
                      fontFamily: 'Quicksand',
                      marginTop: '0.25em',
                    }}
                  >
                    You can ask jobseekers to confirm their years of experience,
                    level of education, etc. Their answers are included as part
                    of the application.
                  </Typography>
                </Grid>

                <Grid
                  item
                  xs={3}
                  style={{
                    borderLeft: '1px #428bca solid',
                  }}
                >
                  <FormGroup
                    style={{
                      marginTop: '2em',
                      marginLeft: '2em',
                    }}
                  >
                    <FormControlLabel
                      control={
                        <IOSSwitch
                          checked={ask}
                          name="checkedB"
                          onChange={() => setAsk(!ask)}
                        />
                      }
                      label="Ask"
                    />
                  </FormGroup>
                </Grid>
              </Grid>,
              <div
                style={{
                  marginTop: '1em',
                  marginBottom: '1em',
                  color: '#111',
                  background: '#111',
                  height: '1px',
                  width: '90%',
                }}
              ></div>,
              <Typography
                color="secondary"
                align="left"
                variant="body2"
                style={{
                  fontWeight: '650',
                  fontFamily: 'Quicksand',
                  marginTop: '0.25em',
                  marginBottom: '1em',
                }}
              >
                Specify requirements that you want applicants to meet below.
                Jobseekers will be asked to confirm these when they apply for
                your job.
              </Typography>,
            ]}

            {isSmall && [
              <Typography
                color="primary"
                align="left"
                variant="h6"
                style={{
                  fontWeight: '600',
                  fontFamily: 'Quicksand',
                }}
              >
                Find the right candidate
              </Typography>,
              <Typography
                color="primary"
                align="left"
                variant="h6"
                style={{
                  fontWeight: '500',
                  fontFamily: 'Quicksand',
                  marginTop: '1.25em',
                }}
              >
                <LibraryBooksSharpIcon /> Applicant qualifications
              </Typography>,
              <Grid container style={{ marginTop: '1em' }}>
                <Grid item xs={9} style={{ paddingRight: '2em' }}>
                  <Typography
                    color="secondary"
                    align="left"
                    variant="h6"
                    style={{
                      fontWeight: '650',
                      fontFamily: 'Quicksand',
                      marginTop: '0.5em',
                    }}
                  >
                    Ask applicants about their qualifications
                  </Typography>
                  ,
                  <Typography
                    color="secondary"
                    align="left"
                    variant="body1"
                    style={{
                      fontWeight: '650',
                      fontFamily: 'Quicksand',
                      marginTop: '0.25em',
                    }}
                  >
                    You can ask jobseekers to confirm their years of experience,
                    level of education, etc. Their answers are included as part
                    of the application.
                  </Typography>
                </Grid>

                <Grid
                  item
                  xs={3}
                  style={{
                    borderLeft: '1px #428bca solid',
                  }}
                >
                  <FormGroup
                    style={{
                      marginTop: '2em',
                      marginLeft: '2em',
                    }}
                  >
                    <FormControlLabel
                      control={
                        <IOSSwitch
                          checked={ask}
                          name="checkedB"
                          onChange={() => setAsk(!ask)}
                        />
                      }
                      label="Ask"
                    />
                  </FormGroup>
                </Grid>
              </Grid>,
              <div
                style={{
                  marginTop: '1em',
                  marginBottom: '1em',
                  color: '#111',
                  background: '#111',
                  height: '1px',
                  width: '90%',
                }}
              ></div>,
              <Typography
                color="secondary"
                align="left"
                variant="body2"
                style={{
                  fontWeight: '650',
                  fontFamily: 'Quicksand',
                  marginTop: '0.25em',
                  marginBottom: '1em',
                }}
              >
                Specify requirements that you want applicants to meet below.
                Jobseekers will be asked to confirm these when they apply for
                your job.
              </Typography>,
            ]}

            {/**
             * Qualifications
             */}
            {qualification.length !== 0 &&
              qualification.map((item, idx) => (
                <div
                  key={idx}
                  style={{ marginTop: '1em', marginBottom: '1em' }}
                >
                  {item.component}
                </div>
              ))}

            {/**
             * Selector
             */}
            <FormControl
              required
              className={classes.FormControl}
              variant="outlined"
            >
              <InputLabel id="label">
                <span
                  style={{
                    fontFamily: 'Quicksand',
                    color: '#111',
                  }}
                >
                  Add qualifications
                </span>
              </InputLabel>

              <Select
                label={
                  <span
                    style={{
                      fontFamily: 'Quicksand',
                      color: '#111',
                    }}
                  >
                    Add qualifications
                  </span>
                }
                labelId="label"
                id="Selector-id"
                value={selection}
                onChange={handleChange}
                onClose={() => {
                  setSelection('');
                }}
              >
                <MenuItem
                  style={{ color: '#428bca', fontFamily: 'Quicksand' }}
                  value={'Preference'}
                >
                  Preference
                </MenuItem>
                <MenuItem
                  style={{ color: '#428bca', fontFamily: 'Quicksand' }}
                  value={'Requirement'}
                >
                  Requirement
                </MenuItem>
              </Select>
            </FormControl>
            {qualification.length !== 0 && (
              <Button
                variant="outlined"
                color="primary"
                className={classes.button}
                startIcon={<DeleteIcon />}
                onClick={() => {
                  deleteQualification();
                }}
              >
                <span
                  style={{
                    fontWeight: '600',
                  }}
                >
                  CLEAR ALL
                </span>
              </Button>
            )}

            <Typography
              color="secondary"
              align="left"
              variant="h6"
              style={{
                fontWeight: '650',
                fontFamily: 'Quicksand',
                marginTop: '1em',
                marginBottom: '1em',
              }}
            >
              Automatic screening filter
            </Typography>

            <div
              style={{
                margin: 'auto',
                width: 'fit-content',
              }}
            >
              <Typography
                color="secondary"
                align="left"
                variant="body1"
                style={{
                  fontWeight: '750',
                  fontFamily: 'Quicksand',
                  marginTop: '1.15em',
                  marginBottom: '0.25em',
                }}
              >
                Only notify me of candidates who say they meet all 'Required'
                criteria above.
              </Typography>

              <RadioGroup
              //row={true}
              //   value={value}
              //   onChange={handleChange}
              >
                <FormControlLabel
                  value="Yes, help me save time"
                  control={<Radio color="primary" />}
                  label={
                    <span className={classes.span}>
                      {' '}
                      Yes, help me save time{' '}
                    </span>
                  }
                />
                <FormControlLabel
                  value="No, notify me of all applicants"
                  control={<Radio color="primary" />}
                  label={
                    <span className={classes.span}>
                      {' '}
                      No, notify me of all applicants{' '}
                    </span>
                  }
                />
              </RadioGroup>
            </div>
          </Grid>
          <Grid item xs={false} sm={2} md={2}></Grid>
        </Grid>
      </ThemeProvider>
    </React.Fragment>
  );
};

export default connect(null, { updateForm })(Step5);
