import React from 'react';
import { connect } from 'react-redux';
import FileImage from '../../action/utils/getImage';

/** Material UI  */
import { makeStyles } from '@material-ui/core/styles';
import { createMuiTheme } from '@material-ui/core/styles';
import { ThemeProvider } from '@material-ui/styles';
import Grid from '@material-ui/core/Grid';

/** Icon */
import BusinessIcon from '@material-ui/icons/Business';
import LocationOnIcon from '@material-ui/icons/LocationOn';

import { setCurrent, clearCurrent } from '../../action/job';

const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#428bca',
    },
    secondary: {
      main: '#111',
    },
    success: {
      main: '#5cb85c',
    },
    danger: {
      main: '#d9534f',
    },
  },
});

const useStyles = makeStyles((theme) => ({
  item: {
    padding: '0.5em',
    '&:hover': {
      background: 'rgba(0, 136, 240, 0.1)',
      borderLeft: '2px solid #428bca',
      cursor: 'pointer',
    },
  },
}));

const JobItem = ({ item, setCurrent }) => {
  const classes = useStyles();

  const { jobTitle, image, company, location, _id } = item;

  return (
    <ThemeProvider theme={theme}>
      <Grid
        container
        className={classes.item}
        onClick={() => {
          setCurrent(_id);
        }}
      >
        <Grid item xs={1}>
          {image && FileImage(image)}
        </Grid>
        <Grid
          item
          xs={11}
          style={{
            paddingLeft: '1em',
            fontWeight: '650',
            fontSize: '0.65em',
          }}
        >
          <h6
            style={{
              fontWeight: '700',
              color: '#0275d8',
            }}
          >
            {jobTitle}
          </h6>
          <h6
            style={{
              fontWeight: '650',
            }}
          >
            <BusinessIcon style={{ color: '#f0ad4e' }} /> {company}
            <br />
            <LocationOnIcon style={{ color: '#d9534f' }} /> {location}
          </h6>
        </Grid>
      </Grid>
    </ThemeProvider>
  );
};

export default connect(null, { setCurrent, clearCurrent })(JobItem);
