import React, { useState } from 'react';

/** Material UI Component */
import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';
import { createMuiTheme, ThemeProvider } from '@material-ui/core/styles';

/** Material UI Icon */
import CodeIcon from '@material-ui/icons/Code';
import SaveIcon from '@material-ui/icons/Save';
import Button from '@material-ui/core/Button';
import Autocomplete from '@material-ui/lab/Autocomplete';
import TextField from '@material-ui/core/TextField';

/** UI Customize Component */
import Card from '../Components/components/Card/Card.js';

/** Import List of programming */
import { programmingLanguages } from './programmingList';

/** Redux */
import { connect } from 'react-redux';
import { addCode } from '../../action/code';

/** Code Editor */
import Editor from 'react-simple-code-editor';
import { highlight, languages } from 'prismjs/components/prism-core';
import 'prismjs/components/prism-clike';
import 'prismjs/components/prism-javascript';
import 'prismjs/themes/prism-funky.css';

/** External React Component */
import { toast } from 'react-toastify';
import CodeSnippetList from './CodeSnippetList';

/** Material UI Icon */
import EmojiObjectsIcon from '@material-ui/icons/EmojiObjects';

const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#428bca',
    },
    secondary: {
      main: '#111',
    },
    success: {
      main: '#5cb85c',
    },
    danger: {
      main: '#d9534f',
    },
  },
});

const useStyles = makeStyles((theme) => ({
  spinner: {
    position: 'absolute',
    left: '50%',
    top: '50%',
  },
  CodeEditor: {
    marginTop: '4em',
    boxShadow: '2px 2px 5px 2px rgba(0,0,0,0.35)',
    padding: '1em',
  },
  button: {
    margin: '0.5em',
  },
  hint: {
    marginTop: '4em',
    boxShadow: '2px 2px 5px 2px rgba(0,0,0,0.35)',
    padding: '1em',
    width: 'auto',
  },
}));

/** Submit
 * code
 * to
 * database */

const Code = ({ addCode }) => {
  const classes = useStyles();
  const [description, setDescription] = useState('');
  const [language, setLanguage] = useState([]);
  const [content, setContent] = useState(`
  # Write your code here 
  `);

  const submit = () => {
    if (!description) {
      toast.info('Please type description of code snippet');
      return;
    }
    if (!language) {
      toast.info('Please type language programming of code snippet');
      return;
    }
    if (!content) {
      toast.info('Please type content of code snippet');
      return;
    }

    addCode({
      description,
      content,
      language,
    });

    setDescription('');
    setLanguage(null);
    setContent(`
    # Write your code here 
    `);
  };

  return (
    <ThemeProvider theme={theme}>
      <Grid container>
        <Grid
          item
          xs={12}
          sm={12}
          md={9}
          style={{
            paddingLeft: '1em',
            paddingRight: '1em',
          }}
        >
          <Card className={classes.CodeEditor}>
            <h5 style={{ color: '#428bca' }}>
              <CodeIcon /> Code Editor
            </h5>
            <h6 style={{ color: '#428bca', fontStyle: 'italic' }}>
              Type or paste your code snippet here
            </h6>
            <TextField
              required
              value={description}
              id="Description"
              label="Description"
              onChange={(event) => setDescription(event.target.value)}
              InputProps={{
                style: {
                  fontFamily: 'Quicksand',
                },
              }}
            />

            {/** Code
             * Editor
             */}
            <Editor
              value={content}
              onValueChange={(content) => setContent(content)}
              highlight={(content) => highlight(content, languages.js)}
              padding={14}
              style={{
                fontFamily: 'monospace',
                fontSize: 16,
                boxShadow: '2px 2px 5px 2px rgba(0,0,0,0.35)',
                background: '#fff',
                height: 'fit-content',
              }}
            />
            {/** Button
             * and
             * Language
             * Selection */}
            <Grid
              container
              style={{
                marginTop: '1.25em',
              }}
            >
              <Grid item xs={6} sm={6} md={4}>
                <Button
                  variant="contained"
                  color="primary"
                  size="large"
                  className={classes.button}
                  startIcon={<SaveIcon />}
                  onClick={() => {
                    submit();
                  }}
                >
                  Save
                </Button>
              </Grid>
              <Grid item xs={6} sm={6} md={8}>
                <Autocomplete
                  color="primary"
                  multiple
                  options={programmingLanguages}
                  getOptionLabel={(option) => option.name}
                  fullWidth
                  renderInput={(params) => (
                    <TextField
                      {...params}
                      label="Language"
                      variant="outlined"
                      color="primary"
                    />
                  )}
                  onChange={(event, value) => {
                    setLanguage(value.map((item) => item.name));
                  }}
                />
              </Grid>
            </Grid>
          </Card>
        </Grid>
        <Grid
          item
          xs={12}
          sm={12}
          md={3}
          style={{ paddingLeft: '1em', paddingRight: '1em' }}
        >
          <Card className={classes.hint}>
            <h5 style={{ color: '#428bca' }}>
              <EmojiObjectsIcon /> Code Snippet
            </h5>
            <h6 style={{ color: '#428bca' }}>
              Code Snippet let you save reusable code for quick reference. Click
              on 'Copy to clip board' to copy code snippet content
            </h6>
          </Card>
        </Grid>
      </Grid>
      {/** Code
       * Snippet */}
      <Grid container>
        <CodeSnippetList />
      </Grid>
    </ThemeProvider>
  );
};

export default connect(null, { addCode })(Code);
