/** Import React Framework */
import React, { useEffect } from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';

/** Import from Component */
import FrontPage from './component/page/FrontPage/FrontPage';
import Login from './component/page/FrontPage/Login';
import DashBoard from './component/page/Dashboard/DashBoard';
import EditProfile from './component/page/Dashboard/EditProfile';
import EditEducation from './component/page/Dashboard/EditEducation';
import EditExperience from './component/page/Dashboard/EditExperience';
import Register from './component/page/FrontPage/Register';
import Dev from './component/page/Dev/Dev';
import Alert from './component/layout/Alert';
import PrivateRoute from './component/private/PrivateRoute';
import Unauthorized from './component/page/Dump/Unauthorized';
import IndividualProfile from './component/page/Profile/IndividualProfile';
import Posts from './component/Post/Posts';
import PostDetails from './component/Post/PostDetails';
import NavBar from './component/layout/NavBar';
import NavButton from './component/layout/NavButton';
import SideBar from './component/layout/SideBar';
import InitProfile from './component/page/InitProfile/InitProfile';
import Code from './component/Code/Code';
import Hire from './component/Hire/Hire';
import Job from './component/Job/Job';
import JobPage from './component/Job/JobPage';
import Management from './component/Hire/HireManagement';
import { ToastContainer } from 'react-toastify';
import { Sample } from './component/Sample';

/** Import from Redux */
import { Provider } from 'react-redux';
import { loadUser } from './action/auth';
import Store from './Store';

/** Import utils function */
import setAuthToken from './reducers/utils/setAuthToken';

/** Material UI */
import Grid from '@material-ui/core/Grid';

/** Checking on token */
if (localStorage.getItem('token')) {
  setAuthToken(localStorage.getItem('token'));
}

function App() {
  useEffect(() => {
    Store.dispatch(loadUser());
  }, []);

  return (
    <Provider store={Store}>
      <Router>
        <Route exact path="/" component={FrontPage} />
        <Route exact path="/login" component={Login} />
        <Route exact path="/register" component={Register} />
        <Alert />
        <Grid container component="main">
          <Grid item xs>
            <SideBar />
          </Grid>
          <Grid item xs={12} sm={11} md={10}>
            <NavBar />
            <Switch>
              <Route exact path="/unauthorized" component={Unauthorized} />
              <Route exact path="/init" component={InitProfile} />
              <PrivateRoute path="/code" component={Code} />
              <PrivateRoute path="/dev" component={Dev} />
              <PrivateRoute
                path="/profile/:user_id"
                component={IndividualProfile}
              />
              <PrivateRoute path="/management" component={Management} />
              <PrivateRoute path="/hire" component={Hire} />
              <PrivateRoute path="/job" component={Job} />
              <PrivateRoute path="/jobpage" component={JobPage} />
              <PrivateRoute path="/posts" component={Posts} />
              <PrivateRoute path="/dashboard" component={DashBoard} />
              <PrivateRoute path="/edit-profile" component={EditProfile} />
              <PrivateRoute path="/post/:post_id" component={PostDetails} />
              <PrivateRoute path="/edit-education" component={EditEducation} />
              <PrivateRoute
                path="/edit-experience"
                component={EditExperience}
              />
            </Switch>
            <NavButton />
          </Grid>
        </Grid>

        <ToastContainer
          position="top-right"
          autoClose={5000}
          hideProgressBar={true}
          newestOnTop={false}
          closeOnClick
          rtl={false}
          pauseOnFocusLoss
          draggable
          pauseOnHover
        />
        {/*<Footer />*/}
      </Router>
    </Provider>
  );
}

export default App;
