import React from 'react';
/** Spring */
import { Spring } from 'react-spring/renderprops';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import { createMuiTheme } from '@material-ui/core/styles';
import { MuiThemeProvider } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import VisibilitySensor from 'react-visibility-sensor';
import Button from '@material-ui/core/Button';
import ButtonGroup from '@material-ui/core/ButtonGroup';
import FormControl from '@material-ui/core/FormControl';
import TextField from '@material-ui/core/TextField';
import InputAdornment from '@material-ui/core/InputAdornment';
import AccountCircle from '@material-ui/icons/AccountCircle';
import EmailIcon from '@material-ui/icons/Email';
import EditIcon from '@material-ui/icons/Edit';

/** React Responsive */
import { useMediaQuery } from 'react-responsive';
import SendIcon from '@material-ui/icons/Send';
const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#428bca',
    },
    secondary: {
      main: '#111',
    },
  },
});

const CustomButtonLarge = withStyles({
  root: {
    margin: 'auto',
    boxShadow: 'none',
    textTransform: 'none',
    fontSize: 20,
    padding: '15px 15px',
    border: '2px solid',
    lineHeight: 1.5,
    color: '#fff',
    backgroundColor: '#428bca',
    borderColor: '#428bca',
    borderRadius: '0px',
    fontFamily: 'Quicksand',
    marginBottom: '3em',
    '&:hover': {
      backgroundColor: '#fff',
      color: '#428bca',
      borderColor: '#428bca',
      boxShadow: 'none',
    },
    '&:active': {
      boxShadow: 'none',
      backgroundColor: '#0275d8',
      borderColor: '#005cbf',
    },
    '&:focus': {
      boxShadow: '0 0 0 0.2rem rgba(0,123,255,.5)',
    },
  },
})(Button);

const CustomButtonSmall = withStyles({
  root: {
    margin: 'auto',
    boxShadow: 'none',
    textTransform: 'none',
    fontSize: 14,
    padding: '10px 10px',
    border: '2px solid',
    lineHeight: 1.5,
    color: '#fff',
    backgroundColor: '#428bca',
    borderColor: '#428bca',
    borderRadius: '0px',
    fontFamily: 'Quicksand',
    marginBottom: '2em',
    '&:hover': {
      backgroundColor: '#fff',
      color: '#428bca',
      borderColor: '#428bca',
      boxShadow: 'none',
    },
    '&:active': {
      boxShadow: 'none',
      backgroundColor: '#0275d8',
      borderColor: '#005cbf',
    },
    '&:focus': {
      boxShadow: '0 0 0 0.2rem rgba(0,123,255,.5)',
    },
  },
})(Button);

const useStyles = makeStyles((theme) => ({
  title: {
    fontFamily: 'Quicksand',
    fontWeight: '600',
    fontSize: '1.65em',
    color: '#428bca',
    margin: 'auto',
    marginTop: '5em',
  },
  titleSmall: {
    fontFamily: 'Quicksand',
    fontWeight: '600',
    fontSize: '0.85em',
    color: '#428bca',
    margin: 'auto',
    marginTop: '1.5em',
  },
  titleHeadline: {
    fontFamily: "'Poiret One', cursive",
    fontWeight: '600',
    fontSize: '35px',
    color: '#428bca',
    margin: 'auto',
    marginBottom: '1em',
  },
  titleHeadlineSmall: {
    fontFamily: "'Poiret One', cursive",
    fontWeight: '600',
    fontSize: '18px',
    color: '#428bca',
    margin: 'auto',
    marginBottom: '1em',
  },
  divider: {
    height: '3px',
    width: '60px',
    backgroundColor: '#428bca',
    marginTop: '2em',
    marginBottom: '1em',
    margin: 'auto',
  },
  dividerSmall: {
    height: '3px',
    width: '60px',
    backgroundColor: '#428bca',
    marginTop: '2em',
    marginBottom: '1em',
    margin: 'auto',
  },
  icon: {
    color: '#428bca',
    fontSize: '55px',
  },
  iconSmall: {
    color: '#428bca',
    fontSize: '30px',
  },
  form: {
    padding: '1em',
  },
}));

const Part11 = () => {
  const classes = useStyles();
  const isSmall = useMediaQuery({ minWidth: 0, maxWidth: 599 });
  return (
    <MuiThemeProvider theme={theme}>
      <Grid container>
        <Grid xs={12}>
          <div data-aos="fade-right">
            <div className={classes.divider}></div>
            {!isSmall && (
              <div
                style={{
                  textAlign: 'center',
                  display: 'flex',
                  alignItems: 'center',
                }}
              >
                <span className={classes.titleHeadline}>
                  <div
                    style={{
                      display: 'flex',
                      alignItems: 'center',
                    }}
                  >
                    Contact
                    <SendIcon
                      fontSize="large"
                      style={{ marginLeft: '0.25em' }}
                    />
                  </div>
                </span>
              </div>
            )}
            {isSmall && (
              <div
                style={{
                  textAlign: 'center',
                  display: 'flex',
                  alignItems: 'center',
                }}
              >
                <span className={classes.titleHeadlineSmall}>
                  <div
                    style={{
                      display: 'flex',
                      alignItems: 'center',
                    }}
                  >
                    Location
                    <SendIcon style={{ marginLeft: '0.25em' }} />
                  </div>
                </span>
              </div>
            )}
          </div>
        </Grid>

        {/** Form */}
        <Grid item xs={false} sm={3} md={3}></Grid>
        <Grid item xs={12} sm={6} md={6}>
          <div data-aos="fade-right">
            <FormControl className={classes.form} fullWidth>
              <TextField
                id="standard-search"
                label=<span className={classes.titleHeadlineSmall}>
                  Your Name
                </span>
                name="name"
                variant="outlined"
                InputProps={{
                  endAdornment: (
                    <InputAdornment position="end">
                      <AccountCircle color="primary" />
                    </InputAdornment>
                  ),
                }}
              />
            </FormControl>
          </div>
          <div data-aos="fade-right" data-aos-offset="100">
            <FormControl className={classes.form} fullWidth>
              <TextField
                id="standard-search"
                label=<span className={classes.titleHeadlineSmall}>Email</span>
                name="email"
                variant="outlined"
                InputProps={{
                  endAdornment: (
                    <InputAdornment position="end">
                      <EmailIcon color="primary" />
                    </InputAdornment>
                  ),
                }}
              />
            </FormControl>
          </div>

          <div data-aos="fade-right" data-aos-offset="100">
            <FormControl className={classes.form} fullWidth>
              <TextField
                id="standard-search"
                label=<span className={classes.titleHeadlineSmall}>
                  Message
                </span>
                multiline
                rows={3}
                rowsMax={6}
                name="measage"
                variant="outlined"
                InputProps={{
                  endAdornment: (
                    <InputAdornment position="end">
                      <EditIcon color="primary" />
                    </InputAdornment>
                  ),
                }}
              />
            </FormControl>
          </div>
        </Grid>
        <Grid item xs={false} sm={3} md={3}></Grid>
        <Grid item xs={12}>
          <div data-aos="zoom-out-left">
            <div
              style={{
                textAlign: 'center',
                marginBottom: '1.5em',
                marginTop: '1.5em',
              }}
            >
              {!isSmall && <CustomButtonLarge>Submit</CustomButtonLarge>}
              {isSmall && <CustomButtonSmall>Submit</CustomButtonSmall>}
            </div>
          </div>
        </Grid>
      </Grid>
    </MuiThemeProvider>
  );
};

export default Part11;
