import React from 'react';

import { makeStyles } from '@material-ui/core/styles';
import { createMuiTheme, ThemeProvider } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Slide from '@material-ui/core/Slide';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import TextField from '@material-ui/core/TextField';

import CustomTabs from '../../../Components/components/CustomTabs/CustomTabs.js';
import Tasks from '../../../Components/components/Tasks/Tasks.js';

import BugReport from '@material-ui/icons/BugReport';
import Code from '@material-ui/icons/Code';
import Cloud from '@material-ui/icons/Cloud';
import Add from '@material-ui/icons/Add';
import DescriptionIcon from '@material-ui/icons/Description';
import ListAltIcon from '@material-ui/icons/ListAlt';
import EditIcon from '@material-ui/icons/Edit';

import { toast } from 'react-toastify';

/** React Responsive */
import { useMediaQuery } from 'react-responsive';

var bugs = [
  'main.scss is not updating index.html',
  '(SyntaxError): Unexpected token < in JSON at position 0',
  'Adding socket io connection between client and server? ',
  'NPM not working. npm run start nor npm run deploy',
];
var website = [
  "Menu Doesn't works on IE",
  "Contact Page isn't responding on portrait Iphone X",
  "menu button doesn't come out",
];
var server = [
  '1xx informational response – the request was received, continuing process',
  '2xx successful – the request was successfully received, understood, and accepted',
  '3xx redirection – further action needs to be taken in order to complete the request',
  '4xx client error – the request contains bad syntax or cannot be fulfilled',
  '5xx server error – the server failed to fulfil an apparently valid request',
];

const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#428bca',
    },
    secondary: {
      main: '#111',
    },
    success: {
      main: '#5cb85c',
    },
    danger: {
      main: '#d9534f',
    },
  },
});

const useStyles = makeStyles({
  text: {
    fontFamily: 'Quicksand',
    fontSize: '2em',
    fontWeight: '700',
    color: '#ccc',
  },
  textSmall: {
    fontFamily: 'Quicksand',
    fontSize: '1.25em',
    fontWeight: '700',
    color: '#ccc',
  },
  textDialog: {
    fontFamily: 'Quicksand',
    fontSize: '1em',
    fontWeight: '700',
    color: '#428bca',
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
  },
});

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

const TaskList = () => {
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);
  const [select, setSelect] = React.useState('');
  const [text, setText] = React.useState('');
  const isSmall = useMediaQuery({ minWidth: 0, maxWidth: 599 });

  const handleChange = (event) => {
    setSelect(event.target.value);
  };

  const handleAdd = () => {
    if (select === 'Bugs') {
      bugs.push(text);
    }
    if (select === 'Website') {
      website.push(text);
    }
    if (select === 'Server') {
      server.push(text);
    }
    toast.success('Task added !');

    setSelect('None');
    setText('');
    setOpen(false);
  };

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const range = (n) => {
    const arr = new Array();
    for (let i = 0; i < n; i++) {
      arr.push(i);
    }
    return arr;
  };

  return (
    <ThemeProvider theme={theme}>
      <CustomTabs
        title="Tasks:"
        headerColor="primary"
        tabs={[
          {
            tabName: 'Bugs',
            tabIcon: BugReport,
            tabContent: (
              <Tasks
                checkedIndexes={[0, 3]}
                tasksIndexes={range(bugs.length)}
                tasks={bugs}
              />
            ),
          },
          {
            tabName: 'Website',
            tabIcon: Code,
            tabContent: (
              <Tasks
                checkedIndexes={[0, 3]}
                tasksIndexes={range(website.length)}
                tasks={website}
              />
            ),
          },
          {
            tabName: 'Server',
            tabIcon: Cloud,
            tabContent: (
              <Tasks
                checkedIndexes={[1]}
                tasksIndexes={range(server.length)}
                tasks={server}
              />
            ),
          },
          {
            tabName: 'Add',
            tabIcon: Add,
            tabContent: (
              <div className={!isSmall ? classes.text : classes.textSmall}>
                <div
                  style={
                    !isSmall
                      ? {
                          border: '5px #ccc solid',
                          borderRadius: '10px',
                          height: 'fit-content',
                          width: '50%',
                          margin: 'auto',
                          padding: '2em',
                          textAlign: 'center',
                          cursor: 'pointer',
                        }
                      : {
                          border: '5px #ccc solid',
                          borderRadius: '10px',
                          height: 'fit-content',
                          width: '50%',
                          margin: 'auto',
                          padding: '0.5em',
                          textAlign: 'center',
                          cursor: 'pointer',
                        }
                  }
                  onClick={handleClickOpen}
                >
                  <div>
                    <DescriptionIcon
                      style={
                        !isSmall
                          ? {
                              fontSize: '3.5em',
                            }
                          : {
                              fontSize: '2.5em',
                            }
                      }
                    />
                  </div>
                  ADD NEW TASK
                </div>
              </div>
            ),
          },
        ]}
      />
      <Dialog
        open={open}
        TransitionComponent={Transition}
        keepMounted
        onClose={handleClose}
        aria-labelledby="alert-dialog-slide-title"
        aria-describedby="alert-dialog-slide-description"
      >
        <DialogTitle color="primary" id="alert-dialog-slide-title">
          {"Use Google's location service?"}
        </DialogTitle>
        <DialogContent className={classes.textDialog}>
          <span className={classes.textDialog}>
            <ListAltIcon /> Type :
          </span>
          <br></br>
          <FormControl variant="outlined" className={classes.formControl}>
            <InputLabel color="primary" id="demo-simple-select-outlined-label">
              <span className={classes.textDialog}>Type Bug</span>
            </InputLabel>
            <Select
              labelId="demo-simple-select-outlined-label"
              id="demo-simple-select-outlined"
              value={select}
              onChange={handleChange}
              label="Type Bug"
              color="primary"
            >
              <MenuItem value="">
                <em>None</em>
              </MenuItem>
              <MenuItem value={'Bugs'} style={{ color: '#428bca' }}>
                <BugReport /> Bugs
              </MenuItem>
              <MenuItem value={'Website'} style={{ color: '#428bca' }}>
                <Code /> Website
              </MenuItem>
              <MenuItem value={'Server'} style={{ color: '#428bca' }}>
                <Cloud /> Server
              </MenuItem>
            </Select>
          </FormControl>
          <br></br>
          <span className={classes.textDialog}>
            <EditIcon /> Content :
          </span>
          <br></br>
          <FormControl variant="outlined" className={classes.formControl}>
            <TextField
              id="outlined-textarea"
              label="Task content"
              placeholder="Task content"
              multiline
              variant="outlined"
              onChange={(event) => {
                setText(event.target.value);
              }}
              value={text}
            />
          </FormControl>
        </DialogContent>
        <DialogActions>
          <Button variant="outlined" onClick={handleClose} color="primary">
            Close
          </Button>
          <Button variant="outlined" onClick={handleAdd} color="primary">
            Add
          </Button>
        </DialogActions>
      </Dialog>
    </ThemeProvider>
  );
};

export default TaskList;
