import React from 'react';
import Spinner from '../layout/Spinner';
import Grid from '@material-ui/core/Grid';
/** UI Customize Component */
import Button from '../Components/components/CustomButtons/Button.js';

const RepoItem = ({ repo }) => {
  if (repo !== undefined) {
    const {
      description,
      stargazers_count,
      watchers_count,
      language,
      forks,
      default_branch,
      name,
      html_url,
    } = repo;
    return (
      <Grid item xs={12} sm={6} md={6}>
        <div
          style={{
            margin: '0.5em',
            padding: '0.85em',
            border: '2px solid #d1d5da',
            borderRadius: '3px',
            boxSizing: 'border-box',
          }}
        >
          <div className="text-left  mx-auto" style={{ width: '90%' }}>
            <h5 className="text-primary" style={{ fontWeight: '500' }}>
              {name}
            </h5>

            <div className="text-primary p-0">
              <p className="font-weight-light text-primary">
                Description:{' '}
                <span className="text-secondary">{description}</span>
                <br></br>
                Branch : <span className="text-muted">{default_branch}</span>
                <br></br>
                <span>
                  <i
                    className="fas fa-stream ml-2"
                    style={{ color: '#FFA500' }}
                  />{' '}
                  {language}
                  <i
                    className="fas fa-star ml-2"
                    style={{ color: '#FFFF00' }}
                  />{' '}
                  {stargazers_count}
                  <i
                    className="fas fa-code-branch ml-2"
                    style={{ color: '#5bc0de' }}
                  ></i>{' '}
                  {forks}
                  <i
                    className="fas fa-eye ml-2"
                    style={{ color: '#222' }}
                  ></i>{' '}
                  {watchers_count}
                </span>
              </p>

              <a href={html_url} style={{ textDecoration: 'none' }}>
                <Button color="primary">Visit Repository</Button>
              </a>
              <hr />
            </div>
          </div>
        </div>
      </Grid>
    );
  } else {
    return (
      <div>
        <Spinner />
      </div>
    );
  }
};

export default RepoItem;
