import React from 'react';
import { connect } from 'react-redux';
import fileImage from '../../action/utils/getImage';
import Parser from 'html-react-parser';

/** Material UI  */
import { makeStyles } from '@material-ui/core/styles';
import { createMuiTheme } from '@material-ui/core/styles';
import { ThemeProvider } from '@material-ui/styles';

/** Material Component */
import Grid from '@material-ui/core/Grid';
import IconButton from '@material-ui/core/IconButton';
import Chip from '@material-ui/core/Chip';

/** Icon */
import FavoriteIcon from '@material-ui/icons/Favorite';
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos';
import CodeIcon from '@material-ui/icons/Code';
import Avatar from '@material-ui/core/Avatar';
import ContactPhoneIcon from '@material-ui/icons/ContactPhone';
import PeopleIcon from '@material-ui/icons/People';

/** Local Import */
import JobSubmit from './JobSubmit';

/** External React Component */
import { toast } from 'react-toastify';

const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#428bca',
    },
    secondary: {
      main: '#d9534f',
    },
  },
});

const useStyles = makeStyles((theme) => ({
  layout: {
    textAlign: 'text-center',
    color: '#428bca',
    padding: '2em',
  },
  cardGrid: {
    paddingLeft: '1em',
    paddingRight: '1em',
  },
}));

export const JobDetails = ({ job }) => {
  const classes = useStyles();

  const getRemote = (remote) => {
    if (remote === 'Temporalily due to Covid 19') {
      return (
        <span
          style={{
            color: '#0275d8',
            fontWeight: '750',
          }}
        >
          <i className="fas fa-user-md"></i> Temporalily due to Covid 19{' '}
        </span>
      );
    }

    if (remote === 'Remote Job') {
      return (
        <span
          style={{
            color: '#5bc0de',
            fontWeight: '750',
          }}
        >
          <i className="fas fa-globe-europe"></i> Remote Job{' '}
        </span>
      );
    }

    if (remote === 'Visa Sponsors') {
      return (
        <span
          style={{
            color: '#d9534f',
            fontWeight: '750',
          }}
        >
          <i className="fas fa-truck"></i> Visa Sponsors{' '}
        </span>
      );
    }
  };

  if (job) {
    return (
      <React.Fragment>
        <ThemeProvider theme={theme}>
          <Grid container className={classes.layout}>
            {/** Post job Review */}
            <Grid item xs={12} sm={10} md={8}>
              <Grid container>
                <Grid item xs={2}>
                  {job.image && fileImage(job.image)}
                </Grid>
                <Grid
                  item
                  xs={10}
                  style={{
                    paddingLeft: '1em',
                  }}
                >
                  <h3
                    style={{
                      color: '#428bca',
                      fontWeight: '750',
                    }}
                  >
                    {job.jobTitle}
                  </h3>
                  <h5
                    style={{
                      fontWeight: '650',
                    }}
                  >
                    {`${job.company}-${job.location}`}
                    <br />
                    <PeopleIcon color="primary" />{' '}
                    {`${job.userInfo.companySize}`}
                    <br />
                    {getRemote(job.remote)}
                    <IconButton
                      aria-label="Favorite"
                      color="secondary"
                      size="medium"
                      onClick={() => {
                        toast.error('You like this job !');
                      }}
                    >
                      <FavoriteIcon />
                    </IconButton>
                  </h5>

                  <JobSubmit />
                </Grid>
              </Grid>
            </Grid>

            {/** Divider */}
            <div
              style={{
                background: '#ccc',
                height: '1px',
                width: '100%',
                marginTop: '1em',
                marginBottom: '1em',
              }}
            ></div>

            {/** Technology */}
            <div>
              <h5
                style={{
                  fontWeight: '700',
                }}
              >
                Technology
              </h5>
              {job.stack &&
                job.stack.map((item, index) => (
                  <Chip
                    avatar={
                      <Avatar>
                        <CodeIcon />
                      </Avatar>
                    }
                    label={item}
                    color="primary"
                  />
                ))}
            </div>

            {/** Job Description */}
            <div>
              <h5
                style={{
                  fontWeight: '700',
                }}
              >
                We need you
              </h5>

              <div>{Parser(job.description)}</div>

              {job.requirement && (
                <div>
                  <h5
                    style={{
                      fontWeight: '700',
                    }}
                  >
                    Requirement
                  </h5>
                  <div>{Parser(job.requirement)}</div>
                </div>
              )}

              {job.preference && (
                <div>
                  <h5
                    style={{
                      fontWeight: '700',
                    }}
                  >
                    Preference
                  </h5>
                  <div>{Parser(job.preference)}</div>
                </div>
              )}

              <h6
                style={{
                  fontWeight: '700',
                }}
              >
                Job Type :{' '}
                <span
                  style={{
                    fontWeight: '650',
                  }}
                >
                  {job.employmentType}
                  <br></br>
                </span>
              </h6>

              <h6
                style={{
                  fontWeight: '700',
                }}
              >
                Salary :{' '}
                <span
                  style={{
                    fontWeight: '650',
                  }}
                >
                  {' '}
                  <br />
                  <ArrowForwardIosIcon style={{ color: '#5bc0de' }} />
                  {`${job.salary.minimum} to ${job.salary.maximum} ${job.salary.currency} /${job.salary.paymentCircle}`}
                </span>
              </h6>
              {/** Divider */}
              <div
                style={{
                  background: '#ccc',
                  height: '1px',
                  width: '100%',
                  marginTop: '1em',
                  marginBottom: '1em',
                }}
              ></div>
              <h5
                style={{
                  fontWeight: '700',
                }}
              >
                <ContactPhoneIcon style={{ color: '#428bca' }} /> Contact
                Information:
              </h5>
              <div
                style={{
                  marginTop: '0.15em',
                  marginBottom: '0.15em',
                  border: '5px solid #ccc',
                  padding: '1em',
                }}
              >
                <ul
                  style={{
                    listStyle: 'none',
                    fontSize: '1.15em',
                  }}
                >
                  <li>
                    {' '}
                    <span style={{ fontWeight: '700' }}>Phone Number:</span>
                  </li>
                  <li>
                    <ArrowForwardIosIcon
                      fontSize="small"
                      style={{ color: '#5cb85c' }}
                    />{' '}
                    {job.userInfo.phone}
                  </li>
                  <li>
                    {' '}
                    <span style={{ fontWeight: '700' }}>User posted:</span>
                  </li>
                  <li>
                    <ArrowForwardIosIcon
                      fontSize="small"
                      style={{ color: '#5cb85c' }}
                    />
                    {job.userInfo.name}
                  </li>
                </ul>
              </div>
            </div>
            {/** Dialog */}
          </Grid>
        </ThemeProvider>
      </React.Fragment>
    );
  } else {
    return (
      <React.Fragment>
        <ThemeProvider theme={theme}>
          <h1 style={{ color: '#428bca' }}>Loading...</h1>
        </ThemeProvider>
      </React.Fragment>
    );
  }
};

const mapStateToProps = (state) => ({
  loading: state.job.loading,
  job: state.job.job,
});

export default connect(mapStateToProps)(JobDetails);
